import { Segment } from 'base-segment';
// #------------------------------------------# //
// #---- Segment (/index/segment-content) ----# //

class Content extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $filter, $location, $state, rAF, ssAsync, ssDateByOrgHlp, ssFormLibraryApi, ssPacketsApi, ssFormTemplatesApi, ssStatusApi, ssListSvc, ssModalSvc, ssEnum, enumFormTemplateStatus) {
		//Paging
		var currentPage = 0;
		const pageSize = 50;
		var totalRetrieved = 0;

		var getFormsPromise = null;
		const routeVm = $scope.routeVm;

		// hookup segment logic
		const logic = $injector.get('segmentIndexContentLogic');// eslint-disable-line

		// added to routeVm as well for accessing in children components
		const vm = routeVm.vm = $scope.vm = {
			showInfScroll: true,
			sorting: false,
			sortCriteria: [
				{ label: 'Recently Modified', value: { field: 'changedutc', dir: 'desc' }},
				{ label: 'Template Name A-Z', value: { field: 'name', dir: 'asc' } }
			],
			statuses: enumFormTemplateStatus.getEnums()
				.filter(enm => enm.value !== 2).reverse(),
			selectedStatus: 'All',
			selectedCategory: 'All',
			search: ''
		};
		vm.sortBy = vm.sortCriteria[0].value;

		// Some variables are on routeVm because it's passed down to the template/packet display components
		routeVm.loading = false;
		routeVm.displayTemplates = [];
		routeVm.loadFormTemplates = fetchTemplateObjects; // Keeping the same function name exposed on the routeVm for the child components
		routeVm.refreshData = refreshData; 
	
		//Reset page settings and fetch the data
		function refreshData() {
			currentPage = 0;
			totalRetrieved = 0;
			routeVm.displayTemplates = [];
			fetchTemplateObjects();
		}

		// When sorting, we want to keep our location on the page
		$scope.$watch('vm.sortBy', (newval, oldval) => {
			if (newval === oldval || routeVm.displayTemplates == null || routeVm.displayTemplates.length === 0) { return; }
			vm.sortBy = newval;
			vm.sorting = true;
			fetchTemplateObjects();
		});

		// When filtering, we want to reset our location to the top of the page (the first page)
		$scope.$watchGroup(['vm.search','vm.selectedCategory','vm.selectedStatus'], (newval, oldval) => {
			if (newval === oldval) { return; }
			refreshData();
		});

		// method to handle scrolling
		$scope.scrollData = function scrollData() {
			if (!vm.showInfScroll || routeVm.loading) { return; }
			vm.scrolling = true;
			fetchTemplateObjects();
		};
		
		function fetchTemplateObjects() {

			routeVm.loading = true;
			routeVm.loadingMessage = 'Loading Form Templates';

			// Cancel any previous fetch that is still in progress
			if (getFormsPromise != null) {
				getFormsPromise.$abort();
			}	

			getFormsPromise = ssAsync(function* () {
				try {
					
					var requestConfig = {};

					// get the form template categories if they don't already exist
					if(!routeVm.categories) {
						routeVm.categories = yield ssListSvc.getByExternalId('form.categories');
					}

					// Update infinite scroll (paging)
					currentPage = vm.sorting ? currentPage : currentPage + 1;
					var offset = (currentPage - 1) * pageSize;
					totalRetrieved = vm.sorting ? totalRetrieved : totalRetrieved + pageSize;
					
					// Add the limit and offset queries to the requestConfig object
					requestConfig.offset = vm.sorting ? 0 : offset;
					requestConfig.limit = vm.sorting ? totalRetrieved : pageSize;
					
					// Add the sorting
					requestConfig = Object.assign(requestConfig, addSort(vm.sortBy));

					// Add the filtering
					if (vm.selectedStatus != null && vm.selectedStatus != "All"){
						requestConfig = Object.assign(requestConfig, addFilter("status", vm.selectedStatus));
					}
					if (vm.selectedCategory != null && vm.selectedCategory != "All"){
						requestConfig = Object.assign(requestConfig, addFilter("category", vm.selectedCategory));
					}
					if (vm.search != null && vm.search.length > 0){
						requestConfig = Object.assign(requestConfig, addFilter("name", vm.search));
					}

					// Run the endpoint
					var templateObjectsResult = yield ssFormLibraryApi.queryTemplateObjects(requestConfig);

					// Get a total count
					$scope.count = parseInt(templateObjectsResult.$headers['total-results'], 10);

					// Fill out data on each template object like packet info, category name, date strings, and step properties
					var templateObjects = extendFormTemplates(templateObjectsResult);

					// Reset the displayTemplates if sorting, otherwise append the next page
					routeVm.displayTemplates = vm.sorting ? [...templateObjects] : [...routeVm.displayTemplates, ...templateObjects];

					// Figure out when we've reached the end of the list
					vm.showInfScroll = totalRetrieved < $scope.count;

				} catch (e) {
					vm.showInfScroll = false;
					//TODO comment this out after debugging
					error('Error getting data', e);
				} finally {
					routeVm.loading = false;
					vm.sorting = false;
					vm.scrolling = false;
					getFormsPromise = null;
				}

				rAF(function () {
					routeVm.loading = false;
					$scope.$emit('template-objects.data-loaded');
				}, $scope);

			})();
			
			return getFormsPromise;
		}

		// create the necessary properties on each form template
		function extendFormTemplates(templates) {

			templates.forEach(template => {

			//	these properties are common to both form templates and packets
				template.isForm = template._templateId != null;
				template.isPacket = template._templateId == null;
				template.searchText = `${template._name}`; //TODO: can remove this most likely

			// 	get the packet count
				if(template.isPacket) {
					template.detailsOpen = false;
					ssAsync(function* () {
						let packet = yield ssPacketsApi.get({ id: template.id });
						template.packetCount = packet._formTemplateIds.length;
					})();
				}

				routeVm.extendCommonProps(template);
			});

			return templates;
		}

		// method to add properties common to form templates and packet form templates (templates within a packet)
		routeVm.extendCommonProps = function(template) {
			let modified = ssDateByOrgHlp.utcIsoToRelativeMoment(template._modifiedDt, routeVm.org);

			template.modifiedText = modified.calendar(null, { // eslint-disable-line
					sameDay: '[Today @] LT',
					lastDay: '[Yesterday @] LT',
					lastWeek: 'ddd [@] LT',
					sameElse: 'L [@] LT'
				});
			
			// TODO: we should pull this off the form template itself
			// '_category.name' is not yet resolved on this endpoint, so we need to determine it here
			if (template._category) {
				template._category.name = mapCategory(template._category.id);
			}

			// Get the names and associated actions for the workflow steps
			if(template._steps.length) {
				template._steps = template._steps.filter(step => step._stepAction !== 3);
				template._steps.forEach(step => {
					step.participantLabel = step._stepParticipant.participantType == null ? 'undefined' : ssEnum.stepParticipantType.byValue(step._stepParticipant.participantType);
					step.stepActionLabel = step._stepAction == null ? 'undefined' : ssEnum.stepAction.byValue(step._stepAction);
				});
			}
		};

		// Map a category id to a name
		function mapCategory(id) {
			var ret = '';
			var tmp;

			if (id == null) { return ''; }
			tmp = routeVm.categories.filter(v => { return v.value === id; });
			if (tmp.length > 0) {
				ret = tmp[0].description;
			}
			return ret;
		}
		
		/**
		 * return an object containing the 'sort' query value that is passed to the enpoint
		 * @param {obj} sort - The 'value' property of the selected sort option
		 */
		function addSort(sort) {
			var o = {};
			o['sort[' + sort.field + ']'] = sort.dir;
			return o;
		}

		/**
		 * return an object containing the 'filter' query value that is passed to the enpoint
		 * @param {string} filter - The 'name' property of the selected filter option
		 * @param {object} value - The 'value' property of the selected filter option
		 */
		function addFilter(filter, value) {
			var o = {};
			o['filter['+ filter +']'] = value;
			return o;
		}

		// Do the initial call to load the template objects
		fetchTemplateObjects();

	}
}

// #-- END Segment (/index/segment-content) --# //
// #------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentIndexContent', () => new Content('content'));
