import {Component} from 'base-component';
// #----------------------------------------------------# //
// #---- Component (cmp-properties-custom-dropdown) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	fields: '='
};

class PropertiesCustomDropdown extends Component {
	/*@ngInject*/
	controller($scope, enumFieldWidths) {
		// view model for tracking state
		const vm = $scope.vm = {
			newOption: '',
			field: $scope.fields[0],
			widths: enumFieldWidths.getEnums().filter((w) => w.name !== 'Large' && w.name !== 'Full')
		};

		if (vm.field._options == null) {
			vm.field._options = [];
		}
	}
}

// #-- END Component (cmp-properties-custom-dropdown) --# //
// #----------------------------------------------------# //

angular.module('ec-forms-components').directive('cmpPropertiesCustomDropdown', () => new PropertiesCustomDropdown(props, 'properties-custom-dropdown') );
