import {Segment} from 'base-segment';
// #---------------------------------------------------# //
// #---- Segment (/packet-builder/segment-content) ----# //

class Content extends Segment {
	/*@ngInject*/
	controller($injector, $scope) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line

		// hookup segment logic
		const logic = $injector.get('segmentPacketBuilderContentLogic'); // jshint ignore:line

		$scope.packetVm = routeVm;

		// view model for tracking state
		const vm = $scope.vm = { // jshint ignore:line
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
		};
	}
}

// #-- END Segment (/packet-builder/segment-content) --# //
// #---------------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentPacketBuilderContent', () => new Content('content') );
