import {Segment} from 'base-segment';
// #--------------------------------------------------# //
// #---- Segment (/form-tracking/segment-content) ----# //

class Content extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $filter, $location, ssAsync, ssEnum, moment, ssFormInstancesApi, ssActiveUserSvc, ssDateByOrgHlp) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // eslint-disable-line

		routeVm.getFormsPromise = null;
		routeVm.getFormCountsPromise = null;

		// hookup segment logic
		const logic = $injector.get('segmentFormTrackingContentLogic'); // jshint ignore:line

		// view model for tracking state
		const vm = $scope.vm = { // jshint ignore:line
		};

		const resolveMethod = $location.search().resolvemethod;

		if (resolveMethod === 'ReadModel' || resolveMethod === 'Mongo') {
			vm.resolveMethod = resolveMethod;
		}

		const go = $scope.go = { // eslint-disable-line
			urgent() {
				return {
					api: ssFormInstancesApi.queryByOrg,
					config: {
						include: 'steps',
						'filter[dueDateUtc]': ssDateByOrgHlp.safeNowRelativeTo(routeVm.org).add(4, 'days').toISOString(),
						'filter[status]': `${ssEnum.formStatus.incomplete},${ssEnum.formStatus.rejected}`,
						'from': routeVm.dateSent.from.toISOString(),
						'to': routeVm.dateSent.to.toISOString(),
						limit: 50,
						resolvemethod: vm.resolveMethod
					}
				};
			},
			denied() {

				return {
					api: ssFormInstancesApi.queryByOrg,
					config: {
						include: 'steps',
						'filter[status]': ssEnum.formStatus.rejected,
						'from': routeVm.dateSent.from.toISOString(),
						'to': routeVm.dateSent.to.toISOString(),
						limit: 50,
						resolvemethod: vm.resolveMethod
					}
				};
			},
			notStarted() {

				return {
					api: ssFormInstancesApi.queryNotStarted,
					config: {
						include: 'steps',
						'from': routeVm.dateSent.from.toISOString(),
						'to': routeVm.dateSent.to.toISOString(),
						limit: 50,
						resolvemethod: vm.resolveMethod
					}
				};
			},
			inProgress() {

				return {
					api: ssFormInstancesApi.queryInProgress,
					config: {
						include: 'steps',
						'from': routeVm.dateSent.from.toISOString(),
						'to': routeVm.dateSent.to.toISOString(),
						limit: 50,
						resolvemethod: vm.resolveMethod
					}
				};
			},
			complete() {

				return {
					api: ssFormInstancesApi.queryByOrg,
					config: {
						include: 'steps',
						'filter[status]': ssEnum.formStatus.complete,
						'from': routeVm.dateSent.from.toISOString(),
						'to': routeVm.dateSent.to.toISOString(),
						limit: 50,
						resolvemethod: vm.resolveMethod
					}
				};
			}
		};

		ssAsync(function* () {
			try {
				vm.emplId = yield ssActiveUserSvc.getIdentityByTypeSync('employee');
			} catch(e) {
				console && console.log && console.log('Error getting employee ID', e);
			}
		})();
	}
}

// #-- END Segment (/form-tracking/segment-content) --# //
// #--------------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentFormTrackingContent', () => new Content('content') );
