// #---------------------------------# //
// #---- Helper (statusLabelHlp) ----# //

function statusLabelHlp(ssDateByOrgHlp, enumFormTemplateStatus) {

	function getLabel(obj, org) {
		var status,
			statusLabel;

		if (obj._statusId == null) {
			statusLabel = enumFormTemplateStatus.byValue(0);
		} else {
			statusLabel = enumFormTemplateStatus.byValue(obj._statusId);
		}

		status = statusLabel.toLowerCase();

		if (obj._statusHistory) {
			for (let i = 0; i < obj._statusHistory.length; i++) {
				let from = ssDateByOrgHlp.utcIsoToRelativeMoment(obj._statusHistory[i]._from, org);
				let statusId = obj._statusHistory[i]._statusId;

				let nowUTC = ssDateByOrgHlp.utcIsoToRelativeMoment(moment().utc().toISOString(), org);

				if (nowUTC < from && statusLabel === 'Inactive') {
					if (statusId === enumFormTemplateStatus.active) {
						let formattedDate = from.format('L');
						statusLabel = `Inactive until ${formattedDate}`;
					}
				}
			}
		}

		return { status, statusLabel };
	}

	return {
		getLabel
	};
}

// #-- END Helper (statusLabelHlp) --# //
// #---------------------------------# //

angular.module('ec-forms-helpers').factory('statusLabelHlp', statusLabelHlp);
