import {Segment} from 'base-segment';
// #------------------------------------------------------# //
// #---- Segment (/form-instance/segment-attachments) ----# //

class Attachments extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $window, ssAsync, filenameHlp, ssFormTemplatesApi) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm;

		// hookup segment logic
		const logic = $injector.get('segmentInboxFormInstanceAttachmentsLogic');// eslint-disable-line

		// view model for tracking state
		const vm = $scope.vm = { // eslint-disable-line
			loading: true
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line
			download: function(attachment) {
					getFileData(attachment);
			}
		};

		function getFileData(fileToOpen = null){
			ssAsync(function*() {
				let ids = routeVm.formTemplate._files.map(file => file._fileId);
				try {
					var files = yield ssFormTemplatesApi.getFiles({}, {
						ids: ids
					});
	
					vm.files = files.map(file => {
						let ext = filenameHlp.getExtension(file._filename);
						file.icon = filenameHlp.getIcon(ext);
	
						return file;
					}).filter(removePDFOverlayFile);
				} catch(e) {
					console.error('Error getting file records', e);
				} finally {
					vm.loading = false;
					if (fileToOpen) {
						$window.open(vm.files.find(x => x.id == fileToOpen.id)._url)
					}
				}
			})();
		}
		
		getFileData();

		// checks to see if an attachment file is the file used in the PDF overlay (used to remove the PDF overlay file from the attachments list)
		function removePDFOverlayFile(file) {
			return routeVm.formTemplate._printPreviewDefinition ? file.id !== routeVm.formTemplate._printPreviewDefinition.templateFileId : true;
		}
	}
}

// #-- END Segment (/form-instance/segment-attachments) --# //
// #------------------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentFormInstanceAttachments', () => new Attachments('attachments') );
