import { State } from 'base-state';
// #---------------------------------------# //
// #---- State (/form-builder/preview) ----# //


// view model that is shared by all segments on this route,
// used for segment coordination. Define initial state here.
const routeVm = {
	added: [],
	props: false,
	addingFields: false
};

const stateName = 'formBuilder.preview';

// config object for ui-router
// http://angular-ui.github.io/ui-router/site/#/api/ui.router.state.$stateProvider#methods_state
const stateConfig = {
	url: '/preview?returnTo&showPDFoverlay',
	templateUrl: 'routes/form-builder/preview/preview.html',
	resolve: {
		form: /*@ngInject*/ function($stateParams, ssAsync, ssFormTemplatesApi, $sce) {
			
			routeVm.hasPDFOverlay = routeVm.showPDFOverlay = false;

			return ssAsync(function*() {
				try {
					routeVm.formData = yield ssFormTemplatesApi.get({
						id: $stateParams.formId,
						include: 'steps,formFields,statusHistory,files'
					});

					// Check to see if the form template has a PDF overlay, if you have a file you have an overlay
					if (routeVm.formData._printPreviewDefinition && routeVm.formData._printPreviewDefinition.templateFileId != null) {
						// retrieve the PDF form object
						let formOverlay = yield ssFormTemplatesApi.generatePDFOverlay({
							formTemplateId: routeVm.formData.id
						});

						if (formOverlay._string !== null) {
							// sanitize the '_string' property as a trusted URL for use in the iframe
							routeVm.PDFOverlayUrl = $sce.trustAsResourceUrl(formOverlay._string);

							// set a flag to toggle form versions
							routeVm.hasPDFOverlay = true;

							// show the PDF if we have arrived here from Overlay Builder
							routeVm.showPDFOverlay = JSON.parse($stateParams.showPDFoverlay);
						}
					}

				} catch (e) {
					console && console.log && console.log('Error getting form', e);
				}
			})();
		},
		$title: ['$title', $title => {
			return $title + ' (Preview)';
		}],
	},
	$permissions: (permissionSvc) => permissionSvc.isPermittedAction('formTemplates', 'view') ||
		permissionSvc.isPermittedAction('formTemplates', 'edit' ||
			permissionSvc.isPermittedAction('formTemplates', 'create'))
};



// #-- END State (/form-builder/preview) --# //
// #---------------------------------------# //

angular.module('ec-forms').config( /*@ngInject*/ ($stateProvider) => {
	$stateProvider.state(`base.${stateName}`, new State(stateConfig, routeVm));
});
