import { State } from 'base-state';
// #------------------------# //
// #---- State (/index) ----# //

// view model that is shared by all segments on this route,
// used for segment coordination. Define initial state here.
const routeVm = {};

const stateName = 'index';

// config object for ui-router
// http://angular-ui.github.io/ui-router/site/#/api/ui.router.state.$stateProvider#methods_state
const stateConfig = {
	url: '/',
	templateUrl: 'routes/index/index.html',
	resolve: {
		org: /*@ngInject*/ function(ssAsync, ssOrganizationSvc, ssToastHlp) {
			return ssAsync(function*() {
				try {
					routeVm.org = yield ssOrganizationSvc.getActiveOrg();
				} catch (err) {
					ssToastHlp.error(
						'There was an error retrieving organization data. Due Dates may not display. Please see the console for more detail.',
						'', false);
					console && console.error && console.error('Error getting Org data', err);
				}
			})();
		},
		$title: /*@ngInject*/ () => {
			return 'Frontline Central - Form Templates';
		}
	},
	$permissions(permissionSvc) {
		return (
			permissionSvc.isPermittedAction('formTemplates', 'create') ||
			permissionSvc.isPermittedAction('formTemplates', 'edit') ||
			permissionSvc.isPermittedAction('formTemplates', 'send') ||
			permissionSvc.isPermittedAction('formTemplates', 'archive') ||
			permissionSvc.isPermittedAction('packets', 'create') ||
			permissionSvc.isPermittedAction('packets', 'edit') ||
			permissionSvc.isPermittedAction('packets', 'send') ||
			permissionSvc.isPermittedAction('packets', 'archive')
		);
	}
};

// #-- END State (/index) --# //
// #------------------------# //

angular.module('ec-forms').config( /*@ngInject*/ ($stateProvider) => {
	sidekick.events.on('feature-flags-ready', (sff) => {
		if(sff.checkState('fc-enable-merged-forms-modules')) {
			stateConfig.redirectToState = 'base.formTemplates';
		}
		$stateProvider.state(`base.${stateName}`, new State(stateConfig, routeVm));
	}, {greedy: true});
});
