import {Component} from 'base-component';
// #--------------------------------------------# //
// #---- Component (cmp-tracked-form-group) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	search: '=',
	sentBySort: '=',
	sortBy: '=',
	category: '=',
	type: '@',
	endpoint: '&',
	location: '=',
	count: '=?',
	emplId: '='
};

class TrackedFormGroup extends Component {
	/*@ngInject*/
	controller($scope, $location, ssAsync, ssEnum, ssFormInstancesApi, ssStorage, ssToastHlp, ssDateByOrgHlp, ssDateHlp, ssDueDateHlp) {
		
		const routeVm = $scope.routeVm = $scope.$root.utils.findInParentScope('routeVm', $scope);

		const pageSize = 50;
		var currentPage = 0;
		var initialLoad = true;
		var formsAndPackets = [];

		const vm = $scope.vm = { // eslint-disable-line
			loading: false,
			showInfScroll: false,
			formsAndPackets: []
		};

		const go = $scope.go = { // eslint-disable-line

			//refresh the data when a form status is changed (archive, complete)
			refreshData: ssAsync(function* (form) {
				try {
					formsAndPackets = vm.formsAndPackets = [];
					currentPage = 0;
					yield fetchFormData();
					yield fetchFormCounts();
				} catch (e) {
					console && console.error && console.error('Error getting forms', e);
				}
			})
		};

	//	Function to handle scrolling
		$scope.scrollData = function scrollData() {
			if (vm.scrolling) { return; }
			vm.scrolling = true;
			fetchFormData();
		}

 	//	refresh the counts
		function fetchFormCounts() {
			//	abort if a call is already in progress
			if (routeVm.getFormCountsPromise != null) {
				routeVm.getFormCountsPromise.$abort();
				routeVm.getFormCountsPromise = null;
			}

			routeVm.getFormCountsPromise = ssAsync(function* () {
				try {
					let filters = {
						'from': routeVm.dateSent.from.toISOString(),
						'to': routeVm.dateSent.to.toISOString()
					};

					//	retrieve any stored filters from localstorage
					let storedFilters = ssStorage.load('trackFormsFilters');
					// set the scope properties to values saved in localstorage if they exist and also if this the first time the data is being fetched
					if (initialLoad && storedFilters) {
						$scope.category = storedFilters[0].category;
						$scope.search = storedFilters[0].search;
						$scope.location = storedFilters[0].location;
						$scope.sentBySort = storedFilters[0].sentby;
						$scope.sortBy = storedFilters[0].sort;
					}

					// Set endpoint search filter
					if ($scope.search != null && $scope.search.length > 0) {
						filters['filter[search]'] = $scope.search;
					}

					// Set endpoint location filter
					if ($scope.location != null && $scope.location !== '-1') {
						filters['filter[locationIds]'] = $scope.location;
					}
					
					// Set endpoint sort
					if ($scope.sortBy === 'recentlysent') {
						filters['sort['+$scope.sortBy+']'] = 'desc';
					} else {
						filters['sort['+$scope.sortBy+']'] = 'asc';
					}

					// Set endpoint 'sent by' sort
					if ($scope.sentBySort != null && $scope.sentBySort !== '0') {
						delete filters['filter[formadministrator]'];
						delete filters['filter[creatorId]'];
						filters['filter['+$scope.sentBySort+']'] = $scope.emplId;
					}

					if (resolveMethod() !== null) {
						filters['resolvemethod'] = resolveMethod();
					}

					routeVm.counts = yield ssFormInstancesApi.getFormCounts(filters);
				} catch(e) {
					ssToastHlp.warn('There was a problem getting the total counts for each section. Try refreshing your browser.', 'Form Counts');
					console && console.log && console.log('Error getting form counts', e);
				} finally {
					routeVm.getFormCountsPromise = null;
				}
			})();
		}

		function resolveMethod() {
			const resolveMethod = $location.search().resolvemethod;

			if (resolveMethod === 'ReadModel' || resolveMethod === 'Mongo') {
				return resolveMethod;
			} else {
				return null;
			}
		}

	// refresh the data
		function fetchFormData() {
			vm.loading = true;

			if (routeVm.getFormsPromise != null) {
				routeVm.getFormsPromise.$abort();
				routeVm.getFormsPromise = null;
			}

			routeVm.getFormsPromise = ssAsync(function* () {
				try {
				//	update infinite scroll
					currentPage++;
					var offset = (currentPage - 1) * pageSize;

				//	get the passed in endpoint object
					var endpoint = $scope.endpoint();

				//	add the limit and offset queries to the config object
					endpoint.config.limit = pageSize;
					endpoint.config.offset = offset;

				//	retrieve any stored filters from localstorage
					let storedFilters = ssStorage.load('trackFormsFilters');
				//	set the scope properties to values saved in localstorage if they exist and also if this the first time the data is being fetched
					if (initialLoad && storedFilters) {
						$scope.category = storedFilters[0].category;
						$scope.search = storedFilters[0].search;
						$scope.location = storedFilters[0].location;
						$scope.sentBySort = storedFilters[0].sentby;
						$scope.sortBy = storedFilters[0].sort;
					} 
				//	Set endpoint search filter
					if ($scope.search != null && $scope.search.length > 0) {
						endpoint.config['filter[search]'] = $scope.search;
					}
				//	Set endpoint location filter
					if ($scope.location != null && $scope.location !== '-1') {
						endpoint.config['filter[locationIds]'] = $scope.location;
					}

				//	Set endpoint sort
					if ($scope.sortBy === 'recentlysent') {
						endpoint.config['sort['+$scope.sortBy+']'] = 'desc';
					} else {
						endpoint.config['sort['+$scope.sortBy+']'] = 'asc';
					}

				//	Set endpoint 'sent by' sort
					if ($scope.sentBySort != null && $scope.sentBySort !== '0') {
						delete endpoint.config['filter[formadministrator]'];
						delete endpoint.config['filter[creatorId]'];
						endpoint.config['filter['+$scope.sentBySort+']'] = $scope.emplId;
					}
					try {
						var data = yield endpoint.api(endpoint.config);

						if (data) {
							// Process the forms
							data.forEach(extendFormObj);
							// Make packets
							formsAndPackets = makeFormsAndPacketList(data);
							// update the array of total forms in view
							vm.formsAndPackets = [...vm.formsAndPackets, ...formsAndPackets];
							// figure out when we've reached the end of the list
							vm.showInfScroll = data.length && data.length < parseInt(data.$headers['total-results'], 10);
						}
					} catch(e) {
						throw e;
					}
				} catch(e) {
					vm.showInfScroll = false;
					ssToastHlp.error('There was an error retrieving Form Instances', '');
					console && console.log && console.log('Error getting forms', e);
				} finally {
					vm.loading = false;
					vm.scrolling = false;
					initialLoad = false;
					routeVm.getFormsPromise = null;
				}
			})();
		};

	//	watches that refresh all data (updates all counts)
		$scope.$watchGroup(['sortBy','search','sentBySort','location','routeVm.dateSent'], (newval, oldval) => {
			if (initialLoad || newval === oldval ) {
				return;
			}

			ssStorage.save('trackFormsFilters', [{
				'category': routeVm.selectedCategory,
				'sort': routeVm.sortBy,
				'search': routeVm.search,
				'sentby': routeVm.sentByParticipant,
				'location': routeVm.sentToLocation
			}], { persistent: true, expires: false });

			go.refreshData();
		});

		$scope.$watch('type', (newval, oldval) => {
			// also runs when page kicks off
			fetchFormData();
			fetchFormCounts();
		});

		/**
		* Assign additional properties to a form instance object
		*
		* @param {obj} form - The form instance object on which to operate
		*/
		function extendFormObj(form) {
			let formStatus = ssEnum.formStatus.byValue(form._statusId).toLowerCase();
			form.isPacket = false;
			form.primaryRecipient = form._employee;
			form.creatorId = form._creator.employeeId;
			form.isComplete = formStatus === 'complete';
			form.isRejected = formStatus === 'rejected';
			form.dueDateStatus = ssDueDateHlp.dueStatus(form, routeVm.org);
			form.totalSteps = form._steps.filter(step => step._stepAction !== 3).length;
			form.searchText = `${form.name} ${form.primaryRecipient.name}`;
			form.sentDate = ssDateByOrgHlp.utcIsoToRelativeMoment(form._createdUtc, routeVm.org).format('M/D/YY');
			if (form._lastReminderSentUtc) {
				form.reminderSentDate = ssDateByOrgHlp.utcIsoToRelativeMoment(form._lastReminderSentUtc, routeVm.org).format('M/D/YY')
			}
			currentStepActions(form);
			stepStatus(form);
		}

		/**
		* Set some properties on the form instance based on the current step
		*
		* @param {obj} form - The form instance object on which to operate
		*/
		function currentStepActions(form) {
			if (form.currentStep == null) { return; }

			form.currentStepIndex = form._steps.indexOf(form.currentStep);
			form.prevStep = (form.currentStepIndex === 0) ? form._steps[0] : form._steps[form.currentStepIndex - 1];
			form.prevStepNum = form._steps.indexOf(form.prevStep) + 1;
			form.currentStepAction = ssEnum.stepAction.byValue(form.currentStep._stepAction) || 'undefined';
			if (form.currentStep._stepParticipant == null) { return; }
			form.isUser = $scope.emplId === form.currentStep._stepParticipant.employeeIds[0] ? true : false;
			form.currentStepParticipantName = form.currentStep._stepParticipant.name || 'undefined';

			let currentStepMeta = `${form.currentStepAction} | ${form.currentStepParticipantName}`;

			if(form.isRejected) {
				let latestSubmission;
				if(form.currentStep._stepSubmissions.length) {
					// get the latest step submission object by the submission date
					latestSubmission = form.currentStep._stepSubmissions.reduce((acc, cur) => {
						return cur.submissionDateUtc > acc.submissionDateUtc ? cur : acc;
					}, {submissionDateUtc: ssDateHlp.foreverPast()});
					currentStepMeta = `Denied by ${latestSubmission.firstName} ${latestSubmission.lastName}`;
				} else {
					currentStepMeta = 'Form Denied';
				}
			}
			form.currentStepMeta = currentStepMeta;
		}

		/**
		** set the status for each step
		** @param {obj} form - The form instance object on which to operate
		**/
		function stepStatus(form) {
			if (form.currentStepIndex == null) { return; }
			var statusSet = false;
			form._steps.forEach((step) => {
				if (step.id === form._steps[form.currentStepIndex].id) {
					step.stepStatus = ssEnum.formStatus.byValue(form._statusId);
					statusSet = true;
				} else {
					step.stepStatus = (statusSet) ? ssEnum.formStatus.byValue(0) : ssEnum.formStatus.byValue(1);
				}
				step.stepStatus = step.stepStatus.toLowerCase();
			});
		}

		/**
		* create a separate form packets object
		*
		* @param {Array} formArr - The array of forms to separate.
		* @param {obj} map - The empty object to put packets in
		*/
		function makeFormsAndPacketList(formArr) {
			const packetMap = new Map();
			return formArr
				.map((form) => {
					var packet; // bucket for packet to be put in

					// if the form is not in a packet, don't wrap it in one... duh!
					if (!form.isInPacket) { return form; }

					// check if we already know about this packet
					if (!packetMap.has(form._packetInstance.instanceId)) {
						let completeCount = form._packetInstance.completeFormInstanceIds.length;
						let incompleteCount = form._packetInstance.incompleteFormInstanceIds.length;

						let newPacket = {
							type: $scope.type,
							_employee: form._employee,
							name: form._packetInstance.name,
							isPacket: true,
							primaryRecipient: form.primaryRecipient,
							creatorId: form.creatorId,
							searchText: `${form._packetInstance.name} ${form.primaryRecipient.name}`,
							currentStepMeta: null,
							dueDateStatus: null,
							sentDate: form.sentDate,
							isRejected: false,
							isComplete: incompleteCount === 0,
							content: [],
							completeCount, incompleteCount,
							totalFormsCount: completeCount + incompleteCount
						};
						newPacket.packetCount = newPacket.isComplete ? 'complete' : `${newPacket.completeCount}/${newPacket.totalFormsCount} forms complete`;

						// track this packet so we can add more forms later if needed
						packetMap.set(form._packetInstance.instanceId, newPacket);
					}

					// put packet... into bucket
					packet = packetMap.get(form._packetInstance.instanceId);
					// add the form
					packet.content.push(form);

				/*	
				**	Determine the due date status (indicator) for a packet
				**	based on the due status of included forms...
				**	'overdue': there is at least one overdue form included
				**	'due soon': there are no overdue forms but there is at least one form due soon
				**	null: There are no forms with a due date
				*/
					packet.dueDateStatus = (() => {
						let overdueForms = packet.content.filter((form) => form.dueDateStatus && form.dueDateStatus.class === 'overdue');
						let dueSoonForms = packet.content.filter((form) => form.dueDateStatus &&form.dueDateStatus.class === 'due-soon');
						if(overdueForms.length) {
							return {
								class: 'overdue'
							}
						} else if(dueSoonForms.length) {
							return {
								class: 'due-soon'
							}
						} else {
							return null;
						}
					})();

				/*	
				**	Determine the displayed meta data for a packet
				**	Will only display a message if there is at least one rejected form in the packet
				*/
					packet.currentStepMeta = (() => {
						let rejectedForms = packet.content.filter(form => form.isRejected);
						if (rejectedForms.length) {
							packet.isRejected = true;
							if (rejectedForms.length === 1) {
								return `form denied by ${rejectedForms[0].currentStepParticipantName}`;
							} else if (rejectedForms.length > 1) {
							return `${rejectedForms.length} forms denied`;
							}
						} else {
							return '';
						}
					})();

					// if this is the 2nd or more form in this packet,
					// roll it in by removing this form from the list
					if (packet.content.length > 1) { return null; }

					packet.countByType = (type) => {
						return filter()
					};

					// map in out lovely el packeet-o
					return packet;
				})
			//	filter out all of the forms that are now in packets
				.filter(form => form != null);
		}
	}
}

// #-- END Component (cmp-tracked-form-group) --# //
// #--------------------------------------------# //

angular.module('ec-forms-components').directive('cmpTrackedFormGroup', () => new TrackedFormGroup(props, 'tracked-form-group') );
