import { Component } from 'base-component';
// #-------------------------------------# //
// #---- Component (cmp-attachments) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	label: '@',
};

class Attachments extends Component {
	/*@ngInject*/
	controller($scope, $q, $window, $stateParams, filenameHlp, ssAsync, ssFormTemplatesApi, ssToastHlp, FileUploader, ssAuthSvc, ssModalSvc) {

		var uploader, intendedFile;
		const MAX_FILESIZE = 52428800;
		const MAX_FILESIZE_ERROR_MSG = 'The file upload size cannot exceed 50MB. Please upload a smaller file.';
		const FILE_UPLOAD_ERROR_TITLE = 'File Upload Failed';
		const FILE_UPLOAD_ERROR_MESSAGE = 'The selected file did not upload correctly. Please try again.';
		const FILE_DOWNLOAD_ERROR_TITLE = 'File Download Failed';
		const FILE_DOWNLOAD_ERROR_MSG = 'The selected file did not download correctly. Please try again.';
		const FILE_ASSOCIATION_ERROR_MESSAGE = 'There was an error associating file with form template';
		const FILE_EXISTS_ERROR_MSG = 'You have already uploaded a file by that name.'
		const FILE_LOADING_ERROR_MESSAGE = 'There was an error getting the attachments for this form template';
		const INVALID_EXT_ERROR_MSG = 'The file you have selected does not have a valid extension. Please add one or choose another file'
		const removeConfirmation = {
			title: 'You will lose all your overlay mappings',
			content: 'Removing this file will result in losing all the mappings done on the current form.',
			primaryText: 'Continue',
			secondaryText: 'Cancel'
		};

		const UPLOADER_CONFIG = {
			withCredentials: false,
			headers: {
				Authorization: `Bearer ${ssAuthSvc.token.access}`
			},
			filters: [{
				name: 'validExt',
				fn: function(fileItem) {
					if (!filenameHlp.getExtension(fileItem.name) || !filenameHlp.getValidExtensions().includes(filenameHlp.getExtension(fileItem.name))) {
						ssToastHlp.error(INVALID_EXT_ERROR_MSG, FILE_UPLOAD_ERROR_TITLE);
						return false;
					};
					return true;
				}
			}, {
				name: 'fileSize',
				fn: function(fileItem) {
					if (fileItem.size != null && fileItem.size >= 0 && fileItem.size >= MAX_FILESIZE) {
						ssToastHlp.error(MAX_FILESIZE_ERROR_MSG, FILE_UPLOAD_ERROR_TITLE);
						return false;
					};
					return true;
				}
			}, {
				name: 'fileDupe',
				fn: function(fileItem) {
					if (vm.files.find(file => file.filename === fileItem.name)) {
						ssToastHlp.error(FILE_EXISTS_ERROR_MSG, FILE_UPLOAD_ERROR_TITLE);
						return false;
					};
					return true;
				}
			}]
		};

		// view model for tracking state
		const vm = $scope.vm = {
			files: [],
			processing: null,
			processingStatus: null,
		};

		let form;

		initUploader();

		uploader.onAfterAddingFile = function(fileItem) {
			$scope.statusElem.text('');
			fileItem.removeAfterUpload = true;
			vm.processing = true;
			vm.processingStatus = 'adding attachment...';

			ssFormTemplatesApi.attachFile({}, {
				filename: fileItem.file.name,
				fileType: 2
			}, null, false).then(file => {
				intendedFile = file;
				intendedFile.filename = file._filename;
				fileItem.updatedUrl = ssFormTemplatesApi.getUploadFileUrl({ id: intendedFile.id });
				fileItem.upload();
			}).catch((err) => {
				if (err.status === 422) {
					ssToastHlp.error(err.data.errors.Title, FILE_UPLOAD_ERROR_TITLE);
				}
				vm.processing = false;
				vm.processingStatus = '';
				uploader.clearQueue();
			});
		};

		uploader.onBeforeUploadItem = function(fileItem) {
			fileItem.url = fileItem.updatedUrl;
		};

		uploader.onErrorItem = function(fileItem, response, status, headers) {
			vm.processing = false;
			vm.processingStatus = '';
			ssToastHlp.error(FILE_UPLOAD_ERROR_MESSAGE, FILE_UPLOAD_ERROR_TITLE);
			fileItem.remove();
		};

		uploader.onCancelItem = function(fileItem, response, status, headers) {
			vm.processing = false;
			vm.processingStatus = '';
			ssToastHlp.info('attachment upload cancelled.');
			fileItem.remove();
		};

		uploader.onSuccessItem = function(fileItem, response, status, headers) {
			ssAsync(function*() {
				try {
					yield ssFormTemplatesApi.associateFile({
						id: $stateParams.formId
					}, {
						fileId: intendedFile.id
					});
					yield form.$refresh();
					yield loadAttachments();
					$scope.statusElem.text('the file has been uploaded successfully');
				} catch (e) {
					vm.processing = false;
					vm.processingStatus = '';
					ssToastHlp.error(FILE_ASSOCIATION_ERROR_MESSAGE, FILE_UPLOAD_ERROR_TITLE);
					console && console.error && console.error('Error associating file with form template', e);
				} finally {
					uploader.clearQueue();
				}
			})();
		};

		uploader.onCompleteAll = function(fileItem) {
			uploader.clearQueue();
			uploader.destroy();
			initUploader();
		};

		const go = $scope.go = { // eslint-disable-line
			download(id) {
				ssFormTemplatesApi.getFileUrl({
					id: id
				}).then(success => {
					$window.open(success._string, '_blank');
				}).catch((e) => {
					ssToastHlp.error(FILE_DOWNLOAD_ERROR_MSG, FILE_DOWNLOAD_ERROR_TITLE);
					console && console.error && console.error('Error downloading the attachment', e);
				});
			},

			delete(file) {
				// if (form._printPreviewDefinition && file._fileId === form._printPreviewDefinition.templateFileId) {
				// 	ssModalSvc.showDestructive(removeConfirmation).then(() => {
				// 		deleteFile(file);
				// 	});
				// } else {
				deleteFile(file);
				// }
			}
		};

		function deleteFile(file) {
			$scope.statusElem.text('');
			vm.processing = true;
			vm.processingStatus = 'deleting attachment...';

			ssAsync(function*() {
				try {
					yield file.$remove();
					yield form.$refresh();
					yield loadAttachments();
					$scope.statusElem.text('the file has been successfully deleted');
				} catch (e) {
					vm.processing = false;
					vm.processingStatus = '';
					console && console.error && console.error('Error deleting attachment', e);
				} finally {
					uploader.clearQueue();
				}
			})();
		}

		// checks to see if a file matches the PDF overlay file (used to filter the PDF overlay out of the file list)
		function filterPDFOverlayFile(files) {
			return files.filter((file) => {
				return form._printPreviewDefinition ? file._fileId !== form._printPreviewDefinition.templateFileId : true;
			});
		}

		function initUploader() {
			uploader = $scope.uploader = new FileUploader(UPLOADER_CONFIG);
		}

		function loadAttachments({ initLoad = false } = {}) {
			return ssAsync(function*() {
				if (initLoad) {
					vm.processing = true;
					vm.processingStatus = 'loading attachments...';
				}
				form = yield ssFormTemplatesApi.get({
					id: $stateParams.formId,
					fields: 'printPreviewDefinition',
					include: 'files'
				});

				var promises = form._files.map(x => ssFormTemplatesApi.getFile({ id: x._fileId }));

				return $q.all(promises).then((files) => {
					form._files.forEach(function(file) {
						let _file = files.find(x => x.id === file._fileId);
						file.filename = _file._filename;
						let ext = filenameHlp.getExtension(file.filename);
						file.icon = filenameHlp.getIcon(ext);
					});
					vm.files = [...form._files];
					vm.files = filterPDFOverlayFile(vm.files);
				}).catch((err) => {
					ssToastHlp.error(FILE_LOADING_ERROR_MESSAGE, FILE_UPLOAD_ERROR_TITLE);
					console && console.error && console.error(err);
				}).finally(() => {
					vm.processing = false;
					vm.processingStatus = '';
				});
			})();
		}

		loadAttachments({ initLoad: true });
	}

	link(scope, elem, attrs) {
		scope.statusElem = angular.element(elem[0].querySelector('.attachment-status-alert'));
	}
}

// #-- END Component (cmp-attachments) --# //
// #-------------------------------------# //

angular.module('ec-forms-components').directive('cmpAttachments', () => new Attachments(props, 'attachments'));
