import {Component} from 'base-component';
// #-----------------------------------------------# //
// #---- Component (cmp-step-checkbox-display) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	step: '=',
	onLoad: '&?'
};

class StepCheckboxDisplay extends Component {
	/*@ngInject*/
	controller($scope, ssEventSvc, fieldsHlp) {

		$scope.routeVm = $scope.$root.utils.findInParentScope('routeVm', $scope)
		// view model for tracking state
		const vm = $scope.vm = { // jshint ignore:line
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
			selectOption(field, opt) {
				ssEventSvc.emit('pvb.select-option', field, opt);
			},
			isSelected(field, opt) {
				return $scope.routeVm.selectedBox.formFieldGuids[0] === field.id && $scope.routeVm.selectedBox.matchValue === opt;
			},
			numerateLabel(targetField, group) {
				let numerator = '';
				let dupes = group.filter((field) => {
					return targetField.resolvedLabel === field._label || targetField.resolvedLabel === field.config.uniqueLabel;
				});
				if(dupes.length > 1) {
					numerator = dupes.indexOf(targetField) > 0 ? `(${dupes.indexOf(targetField) + 1})` : numerator;
				}
				return (numerator ? `${targetField.resolvedLabel} ${numerator}` : targetField.resolvedLabel);
			},
			showGroup(group) {
				return !(group.fields.every(field => field._options.length === 0));
			}
		}
	}
}

// #-- END Component (cmp-step-checkbox-display) --# //
// #-----------------------------------------------# //

angular.module('ec-forms-components').directive('cmpStepCheckboxDisplay', () => new StepCheckboxDisplay(props, 'step-checkbox-display') );
