import {Component} from 'base-component';
// #------------------------------------------# //
// #---- Component (cmp-form-send-status) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	org: '=',
	form: '=',
	batch: '=',
	onComplete: '&?'
};

class FormSendStatus extends Component {
	/*@ngInject*/
	controller($scope, ssAsync, $q, ssFormInstancesApi, $timeout, ssToastHlp, ssStorage, moment, $filter, $sce, ssDateByOrgHlp) {
		// view model for tracking state
		const vm = $scope.vm = {
			progressMessage: '',
			status: '',
			sendStatusText: '',
			statusHelperText: '',
			percentComplete: '0%',
			loading: true,
			inProgress: false,
			showRemove: true,
			loadingMessage: ''
		};

		var completeOnLoad = false;
		var firstPoll = true;
		const formType = $scope.form._objectType === "FormTemplate" ? 'Form' : 'Packet';

		vm.formattedRecipientLength = $filter('number')($scope.batch._includedEmployeeIds.length, 0)

	//	Set the default status & helper text	
		vm.sendStatusText = `Sending ${formType} to ${vm.formattedRecipientLength}`;
		vm.statusHelperText = $sce.trustAsHtml('<strong>This may take several minutes.</strong> Sender will be notified and emailed when complete.');

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line

			removeBatch() { 
				$scope.onComplete({batch: $scope.batch});
			}, 
			
			archiveBatch() {
				vm.loadingMessage = 'Archiving batch...';
				vm.loading = true;
				vm.status._isArchived = true;
				vm.status.$save().then(() => {
					go.removeBatch();
					ssToastHlp.success('', 'Batch successfully archived');
				}).catch((e) => {
					console.error(e);
					ssToastHlp.error('', 'There was a problem archiving the batch. Please try again');
				}).finally(() => {
					vm.loadingMessage = '';
					vm.loading = false;
				});
			}

		};

		function checkStatus() {

 			var deferred = $q.defer();
 			vm.loadingMessage = 'Preparing batch...'

 			function checking() {

				ssFormInstancesApi.getBatchStatus({
					id: $scope.batch.id
				}).then((status) => {
					vm.loading = false;
					vm.loadingMessage = '';

				//	assign the returned status object to the view model
					vm.status = status;

				//	create some properly formatted number strings for the view
					vm.numInBatch = $filter('number')(vm.status._numInBatch, 0);
					vm.numFailed = $filter('number')(vm.status._numFailed, 0);
					vm.numComplete = $filter('number')(vm.status._numComplete, 0)

					switch(vm.status._status) {
						case 0:
							vm.progressMessage = 'Preparing to send';
							vm.progressMessageClass = 'waiting';
							vm.sendStatusText = `Preparing ${formType} for ${vm.formattedRecipientLength}`;
							break;
						case 1: 
							vm.inProgress = true;
							vm.sendStatusText = `Sending ${formType} to ${vm.formattedRecipientLength}`;
							vm.percentComplete = vm.status._percentComplete + '%';
							break;
						case 3: 
							vm.inProgress = true;
							vm.percentComplete = '100%';
							break;
						default:
							break;
					}

					if (vm.status._status === 3 || vm.status._status === 4 || vm.status._status === 5) {
						completeOnLoad = firstPoll;
						$timeout(function() {
							deferred.resolve();
						}, 3000);
					} else {
						firstPoll = false;
						$timeout(checking, 3000);
					}

				});
 			}
 			checking();
			return deferred.promise;
		}

		function resolvedBatchState() {

			vm.inProgress = false; 

			let sentMoment = ssDateByOrgHlp.utcIsoToRelativeMoment(vm.status._endedAtUtc, $scope.org);

			let sentDate = sentMoment.calendar(null, {
				sameDay: '[Sent Today at] LT',
				lastDay: '[Sent Yesterday at] LT',
				sameElse: '[Sent on] l [at] h:mm a'
			});

			vm.statusHelperText = $sce.trustAsHtml(`${sentDate}`);

			if(!vm.status._numFailed) {
				vm.sendStatusText = `${formType} sent to ${vm.numInBatch}`;
				vm.progressMessage = 'Success!';
				vm.progressMessageClass = 'success';
				if(!completeOnLoad) { 
					ssToastHlp.success('', `"${$scope.form._name}" sent successfully to ${vm.numInBatch} ${vm.status._numInBatch > 1 ? 'people' : 'person'}`, false);
				}

				$timeout(function() {
					go.removeBatch();
				}, 3000);

			} else {
				vm.hasFailures = true;
				vm.sendStatusText = `${formType} sent to ${vm.numInBatch - vm.numFailed} employees`;
				vm.progressMessage = `${vm.numFailed} failed to send`;
				vm.progressMessageClass = 'error';
				if(!completeOnLoad) { 
					ssToastHlp.standard('', `"${$scope.form._name}" sent to ${vm.numInBatch} people, but only ${vm.numComplete} succeeded`, false);
				}
			}

		}

		checkStatus().then(() => {
			resolvedBatchState();
		});

	}
}

// #-- END Component (cmp-form-send-status) --# //
// #------------------------------------------# //

angular.module('ec-forms-components').directive('cmpFormSendStatus', () => new FormSendStatus(props, 'form-send-status') );
