import {Segment} from 'base-segment';
// #------------------------------------------------------------# //
// #---- Segment (/form-builder/builder/segment-properties) ----# //

class Properties extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $element, $compile, $animate) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; //eslint-disble-line

		// hookup segment logic
		const logic = $injector.get('segmentFormBuilderBuilderPropertiesLogic'); //eslint-disble-line

		const formVm = $scope.formVm; // eslint-disable-line
		const duration = '0.3'; // in seconds
		const hide = {
			left: '-120%'
		};
		const show = {
			left: 0
		};
		let childScope;
		let propsAreVisible = false;
		let viewProps = document.getElementsByClassName('view-props')[0];
		let propsElem = angular.element(viewProps);
		let componentElem;

		// view model for tracking state
		const vm = $scope.vm = { //eslint-disble-line
			form: formVm.formData
		};

		//	setting this property here to avoid a reset when adding steps/participants (used in 'cmp-properties-step-permissions')
		$scope.formVm.suppressEditAccessModal = false;

		$scope.$on('$destroy', $scope.$root.$on('component-properties', (event, type, data) => {
			// If we're clicking on the already selected field group...
			if ($scope.data === data && propsAreVisible) {return;}
			// If no component reference was passed...
			if (!type) {
				if(propsAreVisible) {
					// ...hide existing properties panel...
					$animate.animate($element, show, hide, '', {
						duration: duration
					}).then(() => {
						$animate.leave(componentElem);
						propsElem.html('');
						// ...set this flag to 'false' so the animate runs from 'hide' to 'show' the next time a properties component reference is passed :) 
						propsAreVisible = false;
					});
				}
				return;
			}

			if (childScope != null) {
				childScope.$destroy();
			}

			childScope = $scope.$new(false);
			$scope.data = data;

			// defaults are for smart and custom fields
			let attr = 'fields';
			let node = type;

			if(['cmp-properties-step', 'cmp-properties-form-complete', 'cmp-properties-profile-review'].includes(type)) {
				attr = 'step';
			}

			if (!propsAreVisible) {
				componentElem = $compile(`<${node} ${attr}="data" form="vm.form"></${node}>`)(childScope);
				// clear it then append; otherwise angular calls the toString on the element
				$animate.enter(componentElem, propsElem).then(() => {
					$animate.animate($element, hide, show, '', {
						duration: duration
					});
				});
				propsAreVisible = true;
			} else {
				$animate.animate($element, show, hide, '', {
					duration: duration
				}).then(() => {
					// clear it then append; otherwise angular calls the toString on the element
					propsElem.html('');
					$animate.leave(componentElem).then(() => {
						componentElem = $compile(`<${node} ${attr}="data" form="vm.form"></${node}>`)(childScope);
						$animate.enter(componentElem, propsElem).then(() => {
							$animate.animate($element, hide, show, '', {
								duration: duration
							});
						});
					});
				});
			}

			event.stopPropagation();
		}));
	}
}

// #-- END Segment (/form-builder/builder/segment-properties) --# //
// #------------------------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentFormBuilderBuilderProperties', () => new Properties('properties') );
