import {Component} from 'base-component';
// #------------------------------------------------# //
// #---- Component (cmp-properties-credentials) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	fields: '='
};

class PropertiesCredentials extends Component {
	/*@ngInject*/

	controller($scope, ssEventSvc, ssValidationHlp, ssFlagSvc) {
		const routeVm = $scope.routeVm = $scope.$root.utils.findInParentScope('routeVm', $scope);

		// get 'showActive' value from form fields
		var showActiveInitVal;
		$scope.fields[0]._externalData.split('.').length == 4 ? showActiveInitVal = true : showActiveInitVal = false;
		
		// view model for tracking state
		const vm = $scope.vm = {
			fields: $scope.fields,
			canEdit: true,
			editableFieldLabels: false, // can a user change fields' labels
			showActive: showActiveInitVal
		};

		$scope.validator = {
			label: ssValidationHlp.string({ maxLength: 25 })
		};

		const go = $scope.go = { //eslint-disable-line
			hideHeadings: (field) => {
				return field._fieldType != 'heading';
			}
		};

		$scope.$watch('vm.showActive', (nv, ov) => {
			if (nv != ov){
				ssEventSvc.emit('cred-select-active', nv, $scope.fields);
			}
		});

		// If org has feature flag on, they can change field label names
		ssFlagSvc.ready.then(() => {
			if (ssFlagSvc.checkState('fc-enable-editable-form-labels')) {
				vm.editableFieldLabels = true;
			}
		});

		// protect against all heading fields that may not have this attribute that already exist
		if ($scope.fields[0]._active == null) {
			$scope.fields[0]._active = true;
		}
	}
}


// #-- END Component (cmp-properties-credentials) --# //
// #------------------------------------------------# //

angular.module('ec-forms-components').directive('cmpPropertiesCredentials', () => new PropertiesCredentials(props, 'properties-credentials') );
