import {Component} from 'base-component';
// #--------------------------------------------------# //
// #---- Component (cmp-properties-notifications) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	step: '='
};

class PropertiesNotifications extends Component {
	/*@ngInject*/
	controller($scope, ssEnum, ssAsync, ssEmployeeApi) {
		// view model for tracking state
		const vm = $scope.vm = {
			stepEvents: new GetStepEventsJson()
		};

		const go = $scope.go = {
			eventClicked(event, action){
				action.eventAction.forEach((v) => {
					handleAction(action.checked, event, v);
				});
			}
		}

		let step = $scope.step;

	//	get the step event action enums for each event, and if the "email others" action is present, then find that matching action and add its employee ID to the array in memory
		if(step._stepEvents && step._stepEvents.length){
			step._stepEvents.forEach(function(stepEvent){
				let eventActionIds = stepEvent.eventActions.map(x => x.eventAction);
				let employeeIds = [];
				if(eventActionIds.includes(ssEnum.stepEventAction.emailOthers)){
					let emailOtherEvent = stepEvent.eventActions.find(function(a){ return a.eventAction === ssEnum.stepEventAction.emailOthers});
					employeeIds = emailOtherEvent.employeeIds;
				}
			//	find matching event from the list of events
				let stepAction = vm.stepEvents.find(x => x.eventType === stepEvent.eventType);
				if(stepAction){
					stepAction.eventActions.forEach(function(action){
					//	mark the matching action of any assigned actions as checked
						let markChecked = action.eventAction.every(function(a){ return eventActionIds.indexOf(a) > -1 });
						if(markChecked) { action.checked = true }
						// if there are employee Ids present, add that ID to the "email" array of the matching action
						if(employeeIds.length && action.eventAction.includes(ssEnum.stepEventAction.emailOthers)){
							ssAsync(function* () {
								action.emails = yield ssEmployeeApi.queryByIds({
									fields: 'firstName,lastName'
								},{ ids: employeeIds });
							})();
						}
					});
				}
			})
		}

		vm.stepEvents.forEach(function(event, eventIdx){
			event.eventActions.forEach(function(action, actionIdx){
				if(action.emails){
					$scope.$watch(`vm.stepEvents[${eventIdx}].eventActions[${actionIdx}].emails.length`,function(nv, ov){
						if(nv === ov) { return; }
						let eventIndex = eventIdx;
						let actionIndex = actionIdx;
						handleEmail(vm.stepEvents[eventIndex], vm.stepEvents[eventIndex].eventActions[actionIndex]);
					})
				}
			})
		})

	//	resets the event action values when switching step action
		$scope.$watch('step._stepAction', (nv, ov) => {
			if (nv === ov) { return; }

			if (nv === ssEnum.stepAction.formEntry) {
				step._stepEvents = [{
					eventType: ssEnum.stepEventType.onSubmit,
					eventActions: []
				}];
			} else if (nv === ssEnum.stepAction.reviewApproveOrDeny) {
				step._stepEvents = [{
					eventType: ssEnum.stepEventType.onApprove,
					eventActions: []
				}, {
					eventType: ssEnum.stepEventType.onDeny,
					eventActions: []
				}];
			} else if (nv === ssEnum.stepAction.reviewForm) {
				step._stepEvents = [{
					eventType: ssEnum.stepEventType.onReview,
					eventActions: []
				}];
			} else if (nv === ssEnum.stepAction.formCompleted) {
				step._stepEvents = [{
					eventType: ssEnum.stepEventType.onComplete,
					eventActions: []
				}];
			} else if (nv === ssEnum.stepAction.employeeProfileReview) {
				step._stepEvents = [{
					eventType: ssEnum.stepEventType.onEmployeeProfileReview,
					eventActions: []
				}];
			}
			vm.stepEvents = new GetStepEventsJson();
		});

		/**
		 * Handle a step action change request and toggles the action on or off
		 *
		 * @param {boolean} value the new value to save for the action - on or off
		 * @param {int} actionIndex Some kind of MAGIC number that matches up with the array position in the step event array. Kinda scary.
		 * @param {int} eventAction The event action type to update, follows the StepEventActionEnum
		 */
		function handleAction(checked, eventProp, eventAction) {
			let event = step._stepEvents.find(x => x.eventType === eventProp.eventType);
			let actions = event.eventActions;
			if(checked){
				let action = { eventAction };
				event.eventActions = [...actions, action];
			}else{
				event.eventActions = actions.filter(a => a.eventAction !== eventAction);
			}
			step._stepEvents = step._stepEvents.slice(); // hack to trigger change

		}

		function handleEmail(event, action) {
			let stepEvent = step._stepEvents.find(x => x.eventType === event.eventType);
			if(stepEvent){
				let stepAction = stepEvent.eventActions.find(x => x.eventAction === ssEnum.stepEventAction.emailOthers);
				if(stepAction){
					stepAction.employeeIds = action.emails.map(val => val.id);
				}
			}
			step._stepEvents = step._stepEvents.slice();
		}

		function GetStepEventsJson() {
			return [{
				title: 'On Submit',
				dataAuto: 'on-submit',
				eventType: ssEnum.stepEventType.onSubmit,
				eventActionEnum: ssEnum.stepAction.formEntry,
				eventActions: [{
					title: 'Email Employee',
					dataAuto: 'email-employee',
					eventAction: [ssEnum.stepEventAction.emailEmployee],
					checked: false
				}, {
					title: 'Email Form Sender',
					dataAuto: 'email-form-sender',
					eventAction: [ssEnum.stepEventAction.emailFormSender],
					checked: false
				}, {
					title: 'Email Employee\'s Supervisor',
					dataAuto: 'email-employee-supervisor',
					eventAction: [ssEnum.stepEventAction.emailSupervisor],
					checked: false,
					isDisabled: () => step._stepParticipant.participantType === ssEnum.stepParticipantType.employeeSSupervisor
				}, {
					title: 'Email Others',
					dataAuto: 'email-others',
					eventAction: [ssEnum.stepEventAction.emailOthers],
					checked: false,
					emails: []
				}]
			}, {
				title: 'On Review',
				dataAuto: 'on-review',
				eventType: ssEnum.stepEventType.onReview,
				eventActionEnum: ssEnum.stepAction.reviewForm,
				eventActions: [{
					title: 'Email Employee',
					dataAuto: 'email-employee',
					eventAction: [ssEnum.stepEventAction.emailEmployee],
					checked: false
				}, {
					title: 'Email Form Sender',
					dataAuto: 'email-form-sender',
					eventAction: [ssEnum.stepEventAction.emailFormSender],
					checked: false
				}, {
					title: 'Email Employee\'s Supervisor',
					dataAuto: 'email-employee-supervisor',
					eventAction: [ssEnum.stepEventAction.emailSupervisor],
					checked: false,
					isDisabled: () => step._stepParticipant.participantType === ssEnum.stepParticipantType.employeeSSupervisor
				}, {
					title: 'Email Others',
					dataAuto: 'email-others',
					eventAction: [ssEnum.stepEventAction.emailOthers],
					checked: false,
					emails: []
				}]
			}, {
				title: 'On Approve',
				dataAuto: 'on-approve',
				eventType: ssEnum.stepEventType.onApprove,
				eventActionEnum: ssEnum.stepAction.reviewApproveOrDeny,
				eventActions: [{
					title: 'Email Employee',
					dataAuto: 'email-employee',
					eventAction: [ssEnum.stepEventAction.emailEmployee],
					checked: false
				}, {
					title: 'Email Form Sender',
					dataAuto: 'email-form-sender',
					eventAction: [ssEnum.stepEventAction.emailFormSender],
					checked: false
				}, {
					title: 'Email Employee\'s Supervisor',
					dataAuto: 'email-employee-supervisor',
					eventAction: [ssEnum.stepEventAction.emailSupervisor],
					checked: false,
					isDisabled: () => step._stepParticipant.participantType === ssEnum.stepParticipantType.employeeSSupervisor
				}, {
					title: 'Email Others',
					dataAuto: 'email-others',
					eventAction: [ssEnum.stepEventAction.emailOthers],
					checked: false,
					emails: []
				}]
			}, {
				title: 'On Deny',
				dataAuto: 'on-deny',
				eventType: ssEnum.stepEventType.onDeny,
				eventActionEnum: ssEnum.stepAction.reviewApproveOrDeny,
				eventActions: [{
					title: 'Email Employee',
					dataAuto: 'email-employee',
					eventAction: [ssEnum.stepEventAction.emailEmployee],
					checked: false
				}, {
					title: 'Email Form Sender',
					dataAuto: 'email-form-sender',
					eventAction: [ssEnum.stepEventAction.stopWorkFlow, ssEnum.stepEventAction.emailFormSender],
					checked: false
				}, {
					title: 'Email Employee\'s Supervisor',
					dataAuto: 'email-employee-supervisor',
					eventAction: [ssEnum.stepEventAction.emailSupervisor],
					checked: false,
					isDisabled: () => step._stepParticipant.participantType === ssEnum.stepParticipantType.employeeSSupervisor
				}, {
					title: 'Email Others',
					dataAuto: 'email-others',
					eventAction: [ssEnum.stepEventAction.stopWorkFlow, ssEnum.stepEventAction.emailOthers],
					checked: false,
					emails: []
				}]
			}, {
				title: 'On Review', //On Employee Profile Review
				dataAuto: 'on-employee-profile-review',
				eventType: ssEnum.stepEventType.onEmployeeProfileReview,
				eventActionEnum: ssEnum.stepAction.employeeProfileReview,
				eventActions: [{
					title: 'Email Employee',
					dataAuto: 'email-employee',
					eventAction: [ssEnum.stepEventAction.emailEmployee],
					checked: false
				}, {
					title: 'Email Form Sender',
					dataAuto: 'email-form-sender',
					eventAction: [ssEnum.stepEventAction.emailFormSender],
					checked: false
				}, {
					title: 'Email Employee\'s Supervisor',
					dataAuto: 'email-employee-supervisor',
					eventAction: [ssEnum.stepEventAction.emailSupervisor],
					checked: false,
					isDisabled: () => step._stepParticipant.participantType === ssEnum.stepParticipantType.employeeSSupervisor
				}, {
					title: 'Email Others',
					dataAuto: 'email-others',
					eventAction: [ssEnum.stepEventAction.emailOthers],
					checked: false,
					emails: []
				}]
			}];
		}
	}
}

// #-- END Component (cmp-properties-notifications) --# //
// #--------------------------------------------------# //

angular.module('ec-forms-components').directive('cmpPropertiesNotifications', () => new PropertiesNotifications(props, 'properties-notifications') );
