import { Component } from 'base-component';
// #-----------------------------------------# //
// #---- Component (cmp-form-list-group) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	search: '=',
	count: '=',
	filter: '=',
	type: '@',
	endpoint: '&'
};

const stepActionLabels = {
	0: 'Complete Form',
	1: 'Review Form',
	2: 'Review & Approve',
	3: 'View Form',
	4: 'Review Profile' 
};

class FormListGroup extends Component {
	/*@ngInject*/
	controller($scope, $filter, $state, $stateParams, $location, rAF, enumStepAction, enumFormStatus, ssAsync, ssObjectHlp, ssDateHlp, ssDateByOrgHlp, ssDueDateHlp) {

		const routeVm = $scope.$root.utils.findInParentScope('routeVm', $scope);
		var currentPage = 0;
		const pageSize = 50;
		var totalRetrieved = 0;
		var viewType = $scope.type; //Current|Sent|Completed - does not directly tie to a form status (Incomplete|Complete|Rejected|Archived)
		var emplId;

		// view model for tracking state
		const vm = $scope.vm = { // eslint-disable-line
			formsAndPackets: [],
			showInfScroll: true,
			loading: false,
			refreshing: false,
			sorting: false,
			sortCriteria: [{
				name: 'Most Recent',
				value: {
					field: 'recentlysent',
					dir: 'desc'
				}
			}, {
				name: 'Due Date',
				value: {
					field: 'duedate',
					dir: 'asc'
				}
			}, {
				name: 'Form/Packet Name (A-Z)',
				value: {
					field: 'formname',
					dir: 'asc'
				}
			}],
			type: viewType
		};

		vm.sortBy = vm.sortCriteria[1].value;

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line
			complete(form) {
				let url = $state.href('base.formInstance', {
					formInstanceId: form.id,
					returnUrl: $location.url(),
					target: '_blank'
				});
				window.open(url, '_blank');
			},

			toggleDetails(formObj) {
				formObj.showDetails = !formObj.showDetails;
			}
		};

		//Reset page settings and fetch the data
		function refreshData() {
			currentPage = 0;
			totalRetrieved = 0;
			vm.formsAndPackets = [];
			fetchFormData();
		}

		//	Sorting
		$scope.$watch('vm.sortBy', (newval, oldval) => {
			if (newval === oldval || vm.formsAndPackets == null || vm.formsAndPackets.length === 0) { return; }
			vm.sortBy = newval;
			vm.sorting = true;
			fetchFormData();
		});

		// filter the forms by search value
		$scope.$watch('search', (newval, oldval) => {
			if (newval === oldval) { return; }
			refreshData(); 	// A new search should reset the page that we are on.
		});

		// method to handle scrolling
		$scope.scrollData = function scrollData() {
			if ($scope.filter !== $scope.type && currentPage < 0) { return; }
			if (!vm.showInfScroll || vm.loading) { return; }
			fetchFormData();
		};

		function fetchFormData() {

			vm.loading = true;

			return ssAsync(function* () {
				try {
					// 	update infinite scroll
					currentPage = vm.sorting ? currentPage : currentPage + 1;
					var offset = (currentPage - 1) * pageSize;
					totalRetrieved = vm.sorting ? totalRetrieved : totalRetrieved + pageSize;
					// 	get the passed in endpoint object
					var endpoint = $scope.endpoint();
					// 	pass in the employee ID thats retrieved from the endpoint call
					emplId = endpoint.config.id;
					// 	add the limit and offset queries to the endpoint config object
					endpoint.config.limit = vm.sorting ? totalRetrieved : pageSize;
					endpoint.config.offset = vm.sorting ? 0 : offset;
					//	Add the sort filter
					endpoint.config = Object.assign(endpoint.config, sortOrNull(vm.sortBy));
					//	run the endpoint
					var allForms = endpoint.config.id ? yield endpoint.api(endpoint.config) : null;
					// 	get a total count
					$scope.count = parseInt(allForms.$headers['total-results'], 10);
					// 	create all the neceesary properties for our view
					processFormData(allForms);
					// 	figure out when we've reached the end of the list
					vm.showInfScroll = totalRetrieved < $scope.count;
				} catch (e) {
					vm.showInfScroll = false;
					console && console.error && console.error('Error getting data', e);
				} finally {
					vm.loading = false;
					vm.sorting = false;
				}

				rAF(function () {
					vm.loading = false;
					$scope.$emit('forms-inbox.data-loaded');
				}, $scope);

			})();
		}

		/**
		 * Initial method to process the form data
		 * @param {arr} forms - The collection on which to operate
		 */
		function processFormData(forms) {
			forms.forEach(form => {
				// determine if the user is the primary recipient
				form.userIsPrimary = form._employee.employeeId !== null && form._employee.employeeId === emplId;
				// set the necessary current step properties
				setCurrentStepProps(form);
				// determine the displayed form status
				form.formStatus = getStatus(form);
				//	get a friendly due date string if a due date exists
				if(form._dueDateUtc){
					form.dueDateStatus = ssDueDateHlp.dueStatus(form, routeVm.org);
				}
			});
			// create the packets
			var formsAndPackets = makeFormAndPacketList(forms);
			// update the viewable form list based on whether we are sorting them or retrieving them
			vm.formsAndPackets = vm.sorting ? [...formsAndPackets] : [...vm.formsAndPackets, ...formsAndPackets];
		}

		/**
		 * Determine the displayed form status
		 * @param {obj} form - The form instance object on which to operate
		 */
		function getStatus(form) {

			if (!form.currentStep) { return 'No Workflow'; }

			if (form.isUserStep) {
				if (form.isRejected) {
					var deniedDate = ssDateByOrgHlp.utcIsoToRelativeMoment(form.currentStep._submittedDtUtc, routeVm.org).format('MM/DD/YY');
					return `Denied ${deniedDate} ${getDeniedBy(form)}`;
				}
				if (form.currentStep._lastModifiedDtUtc && !form.isSubmitted && !form.isComplete) {
					var modifiedDate = ssDateByOrgHlp.utcIsoToRelativeMoment(form.currentStep._lastModifiedDtUtc, routeVm.org).format('MM/DD/YY');
					return `Progress Saved ${modifiedDate}`;
				}
			} else if (form.isSubmitted) {
					var lastUserStepIndex = getLastUserStepIndex(form._steps);
					var submittedDate = ssDateByOrgHlp.utcIsoToRelativeMoment(form._steps[lastUserStepIndex]._submittedDtUtc, routeVm.org).format('MM/DD/YY');
					return `Submitted ${submittedDate}`;
			} else if (form.isComplete) {
					var formCompletedDate = ssDateByOrgHlp.utcIsoToRelativeMoment(form._completionDateUtc, routeVm.org).format('MM/DD/YY');
					return `Completed ${formCompletedDate}`;
			} else if(form.isPending){
				return 'Workflow Pending';
			} else {
				return '';
			}
		}

		/**
		 * create the required properties on the form instance
		 * @param {obj} form - The form instance object on which to operate
		 */
		function setCurrentStepProps(form) {

			form.isUserStep = false;
			form.isPending = false;
			form.isSubmitted = false;
			form.isComplete = false;
			form.isRejected = false;
			form.rejectedByUser = false;
			form.stepActionName = 'undefined';
			form.stepActionLabel = null;

			// if current step doesn't exist, return
			if (!form.currentStep) {
				return;
			}

			// set the action button label for the current step action
			form.stepActionLabel = form.currentStep._stepAction !== null ? stepActionLabels[form.currentStep._stepAction] : stepActionLabels[3];

			// If '_stepAction' is 0 (form entry), we replace its enum name otherwise we use it as is
			if (form.currentStep._stepAction === enumStepAction.formEntry) {
				form.stepActionName = 'Fill Out Form';
			} else {
				form.stepActionName = enumStepAction.byValue(form.currentStep._stepAction);
			}

			form.isComplete = form.currentStep._stepAction === enumStepAction.formCompleted

			//	check to see if form is complete and if so, we can return
			if (form.isComplete) {
				return;
			}

			/*	
			**	resolved edit permission from server
			**	Adding Glean so that failures at least resolve to canEdit false
			*/
			const CAN_EDIT = ssObjectHlp.glean(form, _ => _.currentStep._meta.permissions.permittedActions.edit, false);

			//	get the indexes of the current step and the last step in which the user is a participant to determine if the form is submitted (If the user does not have any steps past the current step)
			var currStepIndex = form._steps.indexOf(form.currentStep);
			var lastUserStepIndex = getLastUserStepIndex(form._steps);
			form.isSubmitted = currStepIndex >= lastUserStepIndex && form._steps[lastUserStepIndex]._submittedDtUtc != null && !ssDateHlp.isForeverPast(form._steps[lastUserStepIndex]._submittedDtUtc);

			// is the user the participant in the current step?
			if (form.currentStep._stepParticipant != null && angular.isArray(form.currentStep._stepParticipant.employeeIds)) {
				form.isUserStep = form.currentStep._stepParticipant.employeeIds[0] === emplId;
			}

			//	check to see if form is rejected, and if the current user is the one who rejected it
			if (form.isUserStep && form._statusId === enumFormStatus.rejected) {
				form.isRejected = true;
				if (form.currentStep._stepAction === 2) {
					form.rejectedByUser = true;
				}
			}

			form.isPending = !form.isUserStep && !form.isSubmitted && !CAN_EDIT;
		}

		/**
		 * Retrieve the index of the last step in which the user is the participant
		 * @param {obj} form - The form instance object on which to operate
		 */
		function getLastUserStepIndex(steps) {
			for (let i = steps.length - 1; i >= 0; i--) {
				if (steps[i]._stepParticipant == null || !angular.isArray(steps[i]._stepParticipant.employeeIds)) { continue; }
				if (steps[i]._stepParticipant.employeeIds[0] === emplId) {
					return steps.indexOf(steps[i]);
				}
			}
		}

		function getDeniedBy(form) {
			if (!form.rejectedByUser && form.isUserStep) {
				var ind = form._steps.indexOf(form.currentStep) + 1;
				return `by ${form._steps[ind]._stepParticipant.name}`;
			}
			return '';
		}

		function getPacketCompletionDate(forms) {
			let latestForm = forms.reduce((prev, curr) => {
				return prev._completionDateUtc > curr._completionDateUtc ? prev : curr;
			});
			return ssDateByOrgHlp.utcIsoToRelativeMoment(latestForm._completionDateUtc, routeVm.org).format('MM/DD/YY');
		}

		/**
		 * create a separate packets array
		 * @param {arr} formArr - The collection on which to operate
		 */
		function makeFormAndPacketList(formArr) {
			const packetMap = new Map();
			return formArr
				.map(function (form, idx, arr) {
					var packet, completeCount, incompleteCount, totalFormsCount;
					form.isPacket = false;

					// if the form is not in a packet, don't wrap it in one... duh!
					if (!form.isInPacket) { return form; }
					
					// check if we already know about this packet
					if (!packetMap.has(form._packetInstance.instanceId)) {
						packetMap.set(form._packetInstance.instanceId, {
							name: form._packetInstance.name,
							isPacket: true,
							isRejected: false,
							showDetails: false,
							content: []
						});
					}

					completeCount = form._packetInstance.completeFormInstanceIds.length;
					incompleteCount = form._packetInstance.incompleteFormInstanceIds.length;
					totalFormsCount = completeCount + incompleteCount;

					packet = packetMap.get(form._packetInstance.instanceId);
					packet.content.push(form);

					/*	
					**	Determine the due date status (indicator) for a packet
					**	based on the due status of included forms...
					**	'overdue': there is at least one overdue form included
					**	'due soon': there are no overdue forms but there is at least one form due soon
					**	null: There are no forms with a due date
					*/
					packet.dueDateStatus = (() => {
						let overdueForms = packet.content.filter((form) => form.dueDateStatus && form.dueDateStatus.class === 'overdue');
						let dueSoonForms = packet.content.filter((form) => form.dueDateStatus &&form.dueDateStatus.class === 'due-soon');
						if(overdueForms.length) {
							return {
								class: 'overdue'
							}
						} else if(dueSoonForms.length) {
							return {
								class: 'due-soon'
							}
						} else {
							return null;
						}
					})();

				//	determine displayed status for a packet
					packet.formStatus = (() => {
						let rejectedForms = packet.content.filter(form => form.isRejected);
						if (rejectedForms.length) {
							packet.isRejected = true;
							if (rejectedForms.length === 1) {
								return `form denied ${getDeniedBy(rejectedForms[0])}`;
							} else if (rejectedForms.length > 1) {
								return `${rejectedForms.length} forms denied`;
							}
						} else if (incompleteCount === 0) {
							return `Completed ${getPacketCompletionDate(packet.content)}`;
						} else {
							return `${completeCount}/${totalFormsCount} Forms Completed`;
						}
					})();

					if (packet.content.length > 1) { return null; }
					return packet;
				})
				.filter(form => form != null);
		}

		/**
		 * Apply the form sort to the list of forms
		 *
		 * @param {string} sortBy - The sort string that determines how to sort the list.
		 * @param {Array} forms - The list of forms to sort.
		 */
		function applySort(sortBy, forms, dir) {
			var reverse;
			reverse = dir === 'DESC' ? true : false;
			return $filter('orderBy')(forms, `_${sortBy}`, reverse);
		}

		/**
		 * return an object containing the 'sort' query value that is passed to the enpoint
		 * @param {obj} sort - The 'value' property of the selected sort option
		 */
		function sortOrNull(sort) {
			var o = {};
			o['sort[' + sort.field + ']'] = sort.dir;
			return o;
		}

	}
}

// #-- END Component (cmp-form-list-group) --# //
// #-----------------------------------------# //

angular.module('ec-forms-components').directive('cmpFormListGroup', () => new FormListGroup(props, 'form-list-group'));
