function formsHlp(ssEnum, ssSaveInterrupterSvc, ssFormInstancesApi) {
// #---- Helper (formsHlp) ----# //

	const defaultPermissions = {
		participantType: null,
		employeeId: '',
		canEdit: false,
		canView: true,
		canViewSensitive: false,
		canEditSensitive: false,
	};

	const _hlp = {

		initSaveInterrupter(form) {
			// clear any registered models
			ssSaveInterrupterSvc.clear();
			// reset & register form steps
			form._steps.forEach(step => {
				if (step.$$resetChangesDetected) {
					step.$$resetChangesDetected();
				}
			});
			ssSaveInterrupterSvc.register(form._steps);
			// reset & register form fields
			if(form._formFields) {
				form._formFields.forEach(field => {
					if (field.$$resetChangesDetected) {
						field.$$resetChangesDetected();
					}
				});
				ssSaveInterrupterSvc.register(form._formFields);
			}
			// reset & register the form
			form.$$resetChangesDetected();
			ssSaveInterrupterSvc.register(form);
		},

		makeStep: (config = {}) => {
			let defaultStep = {
				_name: '',
				_fieldOrder: '',
				fieldGroups: [],
				_stepAction: null,
				_stepParticipant: {
					participantType: null,
					employeeIds: []
				},
				_stepEvents: [{
					eventType: ssEnum.stepEventType.onSubmit,
					eventActions: []
				}],
				_permissions: []
			};
			return Object.assign({}, defaultStep, config);
		},

		updateStepOrder: (form, steps) => {
			let formReviewStep, formCompletedStep;

			// if 'steps' is not passed in, then derive that value from the form
			if(!steps) {
				steps = form._steps.filter((step) => {
						return step._stepAction !== ssEnum.stepAction.formCompleted && step._stepAction !== ssEnum.stepAction.employeeProfileReview;
					});
			}

			// look for an existing form complete step...
			formCompletedStep = form._steps.find((step) => {
				return step._stepAction === ssEnum.stepAction.formCompleted;
			});
			// look for an exisitng employee review step...	
			formReviewStep = form._steps.find((step) => {
				return step._stepAction === ssEnum.stepAction.employeeProfileReview;
			});
			// reset the step order; don't forget the last step
			if (formReviewStep) {
				return `${steps.map(s => s.id).join(',')},${formReviewStep.id},${formCompletedStep.id}`;
			} else {
				return `${steps.map(s => s.id).join(',')},${formCompletedStep.id}`;
			}
		},

		updateFieldOrder: (steps) => {
			steps.forEach(step => {
				if(step.fieldGroups) {
					let currentFieldOrder = step.fieldGroups
					.map(g => g.map(f => f.id).join(','))
					.join(',');
					if(step._fieldOrder.trim() !== currentFieldOrder.trim()) {
						step._fieldOrder = currentFieldOrder;
					}
				}
			});
		},

		sortByOrderField(arr, order) {
			if (order == null) { return; }
			if (arr == null) { return; }
			const orderArr = order.split(',');
			const dataBucket = {};

			// drop all the data objects in the bucket
			for (let idx = arr.length; idx--;) {
				let obj = arr.pop();
				dataBucket[obj.id] = obj;
			}

			// push them back onto the array in the right order
			orderArr.forEach(id => {
				if (id in dataBucket) {
					arr.push(dataBucket[id]);
					delete dataBucket[id];
				}
			});

			// append anything that's left over
			Object.keys(dataBucket).forEach(id => {
				if (dataBucket[id] == null) { return; }
				arr.push(dataBucket[id]);
			});

			return arr;
		},

	/** 
	*	Builds an array of simplified step participant objects
	*	@param {Object} steps : form steps array
	*/
		buildParticipantList(steps) {
			let parts = [];
			steps.forEach(step => {
			//	skip last step or steps without a participant
				if (step._stepAction === ssEnum.stepAction.formCompleted || step._stepParticipant.participantType == null) { return; }
			// Do not include specific participants with no employee ID
				if(step._stepParticipant.participantType === ssEnum.stepParticipantType.specificPerson && !step._stepParticipant.employeeIds.length) { return; }
			//	make sure the employee id is a valid value
				let spEmpId = angular.isArray(step._stepParticipant.employeeIds) && step._stepParticipant.employeeIds.length ? step._stepParticipant.employeeIds[0] : '';
			//	if we already added this step participant, return
				if(parts.find((part) => _hlp.matchByParticipant(part, step._stepParticipant))) { 
					return; 
				};
			//	if not, add a new one
				parts.push({
					participantType: step._stepParticipant.participantType,
					employeeId: spEmpId
				});
			});
			return parts;
		},


	/**
	*	Match permission objects to step participants by participant type and/or employee ID
	*	@param {Object} sourceA : object that should include either 'employeeID'(string) or 'employeeIds'(array) & 'participantType'(int) properties 
	*	@param {Object} sourceB : object that should include either 'employeeID'(string) or 'employeeIds'(array) & 'participantType'(int) properties 
	*/
		matchByParticipant(sourceA, sourceB){
			let sourceAId = sourceA.employeeIds && sourceA.employeeIds.length ? sourceA.employeeIds[0] : sourceA.employeeId || ''
			let sourceBId = sourceB.employeeIds && sourceB.employeeIds.length ? sourceB.employeeIds[0] : sourceB.employeeId || '';
			return sourceA.participantType === sourceB.participantType && sourceAId === sourceBId;
		},
	/** 
	*	Removes any step permission objects that dont match up to a step participant
	*	@param {Object} step : form steps array
	*	@param {Array} participants : an array of step participant objects
	*/
		removePermissions(step, participants) {
			let removed = [];

			if(step._permissions.length) {
				for (let i = 0; i <= step._permissions.length - 1; i++) {
					let perm = step._permissions[i];
				//	determine if there is a participant that matches the permission object
					let item = participants.find(part => _hlp.matchByParticipant(part, perm));
				//	if not, splice it out
					if (!item || item === undefined) {
						removed = step._permissions.splice(i, 1);
					}
				}
			}
			return removed;
		},

	/**
	*	Assign step permissions
	*	@param {Object} permObj : step permission object to update
	*	@param {Object} partObj : step participant object used to match step permission object
	*/
		assignStepAccess(permObj, partObj, options = {}) {
			let keysToAssign = ['canEdit', 'canViewSensitive', 'canEditSensitive'];
			if(!permObj.canView) {
				permObj.canView = true;
			}
			Object.keys(permObj).forEach((key) => {
				if(keysToAssign.includes(key)) {
					permObj[key] = _hlp.matchByParticipant(permObj, partObj);
					// disable 'canEditSensitive' for HRMS orgs
					if(options.isHRMS && key === 'canEditSensitive') {
						permObj[key] = false;
					}
				}
			});
			return permObj;
		},

	/**
	*	Copies step permission settings from one object to another
	*	@param {Object} target : step permission object to update
	*	@param {Object} source : step permission object to copy 
	*/
		copyStepAccess(target, source) {
			return Object.assign(target, {
				canView: source.canView,
				canEdit: source.canEdit, 
				canViewSensitive: source.canViewSensitive,
				canEditSensitive: source.canEditSensitive,
			});
		},

		updatePermissions({steps, currentStep, newStepParticipant = {}, oldStepParticipant = {}, options = {}}) {

			const participants = _hlp.buildParticipantList(steps);

			steps.forEach((step, i) => {
			// skip last step
				if (step._stepAction === ssEnum.stepAction.formCompleted) { return; }

			// skip employee profile review step
				if (step._stepAction === ssEnum.stepAction.employeeProfileReview) { return; }

			//	remove permissions that dont match up to a participant
				let removed = _hlp.removePermissions(step, participants);
				
			//	process the permissions objects based on the updated participant list
				participants.forEach(part => {
				//	Look for a permission object that matches a participant...
					let matchingPerm = step._permissions.find(perm => _hlp.matchByParticipant(part, perm));
				//	...we found a matching permission object...
					if (matchingPerm) {
					//	...and we're on the currently selected step...
						if(currentStep === step) {
							if(_hlp.matchByParticipant(matchingPerm, newStepParticipant)) {
								_hlp.assignStepAccess(matchingPerm, step._stepParticipant, options);
							}
							if(_hlp.matchByParticipant(matchingPerm, oldStepParticipant)) {
								_hlp.assignStepAccess(matchingPerm, step._stepParticipant, options);
							}
						}
					//	Do nothing if we're not on the current step
						return;
					}
				//	we haven't found a matching permission object, so create a new one for this participant and set its properties
					let newPerm = Object.assign({}, defaultPermissions, {
						participantType: part.participantType,
						employeeId: part.employeeId
					});

					if (step._stepAction === ssEnum.stepAction.employeeProfileReview) { 
						newPerm.canView = false;
					}
				//	If new permission object is replacing an existing one, copy the existing one to the new one, otherwise we assume it's a unique step participant, and assign step access rights based on whether or not the new participant matches the current step participant
					if(removed.length) {
						newPerm = _hlp.copyStepAccess(newPerm, removed[0])
					} else {
						newPerm = _hlp.assignStepAccess(newPerm, step._stepParticipant, options);
					}
				//	insert the new step permission object at the same index as the matching participant object
					step._permissions.splice(participants.indexOf(part), 0, newPerm);
				});
			});
		},

		generatePDFOverlay(formInstance, showSensitiveFields) {
			return Promise.resolve()
			.then(() => {
				let formFieldValues = formInstance._steps.filter((step) => {
					return step._stepAction !== ssEnum.stepAction.formCompleted
				}).map((step) => {
					return step._formFieldValues;
				}).reduce((a, c) => {
					a = typeof a === 'string' ? JSON.parse(a) : a;
					return Object.assign(a, JSON.parse(c));
				});
				return formFieldValues;
			})
			.then((formFieldValues) => {
				return ssFormInstancesApi.generatePDFOverlay({
					formInstanceid: formInstance.id,
					formFieldValues: formFieldValues,
					showSensitive: showSensitiveFields
				});
			});
		}

	}

	return _hlp;

// #-- END Helper (formsHlp) --# //
// #---------------------------# //
};

// function formsHlp() { return _hlp; }
angular.module('ec-forms-helpers').factory('formsHlp', formsHlp);
