import {Segment} from 'base-segment';
// #-----------------------------------------------# //
// #---- Segment (/form-instance/segment-form) ----# //

class Form extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $stateParams, $location, $anchorScroll, $timeout, $q, $filter, $sce, ssAsync, ssEventSvc, ssFormInstancesApi, ssEnum, ssActiveUserSvc, ssToastHlp, ssModalSvc, ssFlagSvc, fieldRendererHlp, formsHlp, rAF) {

		const routeVm = $scope.routeVm;
		// hookup segment logic
		const logic = $injector.get('segmentFormInstanceFormLogic');
		const stepValues = $scope.stepValues = {};
		const sensitiveFieldDefIds = [
			"aa736224-2a09-490a-b069-7cc945a040ea",
			"ba2d2021-59b6-45b9-9e4b-c501d064e012"
		]
		var customMessage = '';

		// view model that is shared by all segments on this route, used for segment coordination
		const vm = $scope.vm = {
			steps: [],
			selectedStep: null,
			loading: false,
			submissionEvent: '',
			employeeProfileReview: ssEnum.stepAction.employeeProfileReview,
			formComplete: ssEnum.formStatus.complete,
			cachedSourceFields: {}
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line
			selectStep(step) {
				if(step._stepAction === ssEnum.stepAction.formCompleted) return;
				if(vm.selectedStep && vm.selectedStep.id === step.id) return;
				vm.selectedStep = step || vm.steps[0];
				routeVm.setFieldValidations();
				scrollToStep(step);
				ssEventSvc.emit('select-summary-step', step, false);
				$scope.$emit('ss-validate-reset');
			},

			submitForm(step) {
				let submittedStepIndex = vm.steps.findIndex(s => s.id === step.id);
				// we are submitting a step before/or equal to the current step
				if (submittedStepIndex <= routeVm.formInstance._currentStepIndex) {
					submitForm(step);
					return;
				}

				// we are trying to submit a step after the current step
				let config = {
					secondaryText: 'Cancel',
					alwaysOnTop: true,
					size: 'standard',
				};

				// vm.steps does not include the form complete step so only need to add 1 to see if submittedStep is the last step
				if (submittedStepIndex + 1 === vm.steps.length) {
					// last step
					config.title = 'Are you sure you want to submit this form?';
					config.primaryText = 'Submit Form';
					config.content = 'You are about to submit a form with incomplete workflow steps.';
				} else {
					// are you sure step
					config.title = 'Are you sure you want to submit this step?';
					config.primaryText = 'Submit Step';
					config.content = 'There are incomplete workflow steps prior to this step. Are you sure you want to submit this step?';
				}
				ssModalSvc.showStandard(config).then(() => {
					submitForm(step);
				});
			},

			submitStep() {
				vm.submissionEvent = 'submitStep';
				customMessage = 'submitted';
			},

			acknowledgeStep() {
				vm.submissionEvent = 'reviewForm';
				customMessage = 'acknowledged';
			},

			approveStep() {
				vm.submissionEvent = 'approveForm';
				customMessage = 'approved';
			},

			denyStep($event, step) {
				vm.submissionEvent = 'declineForm';
				customMessage = 'denied';
				go.submitForm(step);
			},

			reviewProfile() {
				vm.submissionEvent = 'profileReviewed';
				customMessage = 'submitted';
			},

			profileValueDisplay(reviewStepFieldValue, step) {

				if(reviewStepFieldValue.profileValue == null || !reviewStepFieldValue.profileValue.toString()) return;
				var val;
				
				let phonefieldIds = [
					'5e989212-29f0-43e4-8d02-1a6a047b7e48',
					'0977c048-185c-4eec-8800-74842a7fbb4f',
					'f658f159-910a-485f-89b7-f20559222f73',
					'14827dc2-9d54-400d-a556-47656bc72f83',
					'e0ec2374-be6a-4d6f-9f12-5cbf58f5a5cc',
					'4efae0cb-6d68-4eeb-8ce3-031b11678275',
					'3a4799b5-168e-4f19-91a6-27299c543f69'
				];
				
				let field = step.fields.find(f => { return f.id === reviewStepFieldValue.formFieldId });

				if (field._options && field._options.length > 0) {
					if(field._fieldDefId === '07ebdcec-0ff6-46ad-92aa-121b8573b8a6') {
						let matchingStateEnum = ssEnum.states.enums.find((e) => {
							return e.value === reviewStepFieldValue.profileDisplayValue;
						});
						if(matchingStateEnum != null) { val = matchingStateEnum.name };
					} else {
						//	in the review step, read-only profile values come down as enums 
						//	using field._options to resolve enum to friendly display value 
						let option = field._options.find(o => { return field._options.indexOf(o) === reviewStepFieldValue.profileDisplayValue});
						if (option != null) {
							val = option;
						}
					}
				} else if (field._fieldType === 'super-input-datepicker' || field._fieldType === 'super-iso-org-date'){
					val = $filter('date')(reviewStepFieldValue.profileDisplayValue, 'MM/dd/yyyy'); 
				} else if (field._fieldType === 'super-input-checkbox') {
					val = (reviewStepFieldValue.profileDisplayValue === true) ? 'Yes' : 'No';
				} else if (phonefieldIds.includes(field._fieldDefId)) {
					val = reviewStepFieldValue.profileDisplayValue.replace(/(\d{3})(\d{3})(\d{4})/,"($1) $2-$3")
				} else {
					val = reviewStepFieldValue.profileDisplayValue;
				}
				return val;
			}
		};

		function setRedirectLocation() {
			let returnUrl = !$stateParams.returnUrl || !$stateParams.returnUrl.length ? setLocationByParticipant() : $stateParams.returnUrl;
			function setLocationByParticipant() {
				let userId = ssActiveUserSvc.getEmployeeSync().id;
				let participantIds = routeVm.formInstance._steps.filter((step) =>  { return step._stepParticipant !== null}).map((step) => {
					return step._stepParticipant.employeeIds[0];
				});
				return participantIds.includes(userId) ? '/ec-forms/my-forms' : '/ec-dashboard';
			}
			return returnUrl;
		}

		function submitForm(step) {
			routeVm.isLoading = true;
			routeVm.loadingStatus = 'Submitting step...';
			//	force JSON API to think the step has been changed in case no change is made
			if (step._stepAction === ssEnum.stepAction.employeeProfileReview) {
				step.instance._employeeProfileReviewStepFieldValues.forEach(field => {
					delete field.sourceField;
				});

				step.instance._employeeProfileReviewStepFieldValues = step.instance._employeeProfileReviewStepFieldValues.slice();
			} else {
				step.instance.$$forceChangedState('formFieldValues');
			}

			step.instance.$save(true, false, false)
			.then(() => {
				//	reset the status ID in case the form was rejected
				if (routeVm.formInstance._statusId === ssEnum.formStatus.rejected) {
					return ssFormInstancesApi.updateStatus({
						id: routeVm.formInstance.id,
						newStatus: 0
					});
				} else {
					return Promise.resolve();
				}
			})
			.then(()=> {
				return ssFormInstancesApi.post({
					id: routeVm.formInstance.id,
					event: vm.submissionEvent,
					stepId: step.id
				}, null, false);
			})
			.then(checkWorkflow)
			.then(() => {
				ssToastHlp.success(``,`form step ${customMessage}`, false);
				$location.url(setRedirectLocation());
			})
			.catch((err) => {
				ssToastHlp.error(``, err.data.errors.Title, false);
				routeVm.isLoading = false;
				routeVm.loadingStatus = '';
				console && console.error && console.error('Error saving form', err);
			})
		}

		// check the '_inWorkflow' property of a form instance to determine if its safe to return to inbox
		function checkWorkflow() {

			var deferred = $q.defer();
			const MAXATTEMPTS = 24;
			let attempts = 0;

			function checking() {

				attempts++;

				if(attempts === MAXATTEMPTS) {
					deferred.reject();
					return;
				}

				ssAsync(function* () {

					var form = yield ssFormInstancesApi.get({
						id: routeVm.formInstance.id
					});

					if (form && form._inWorkflow === false) {
						deferred.resolve(form);
					} else {
						$timeout(checking, 500);
					}

				})();
			}
			checking();
			return deferred.promise;
		}

		//	This will determine whether or not individual fields of a step should ignore validation based on the position of each step relative to the position of the currently selected step (validation should not fire on any step that appears AFTER the currently selected step). Any fields that appear in steps before or in the currently selected step will only ignore validation if they are not required, or the field is required, redacted & has a valid value behind the redaction.
		routeVm.setFieldValidations = function() {
			if(vm.selectedStep == null) return;
			const selectedStepIndex = vm.steps.indexOf(vm.steps.find((vmstep) => vmstep.id === vm.selectedStep.id));
			vm.steps.forEach((step) => {
				const index = vm.steps.indexOf(vm.steps.find(vmstep => vmstep.id === step.id));
				step.fields.forEach((field) => {
					if(field._fieldDefId == null) { return; }
					if(index > selectedStepIndex) {
						field.ignoreValidations = true
					} else {
						let fieldValue = step.instance.formFieldValues[field.id] ? step.instance.formFieldValues[field.id].toString() : '';
						field.ignoreValidations = !field._required ? true : !!field.isRedacted && !!fieldValue;
					}
				});
			});
		}

		function scrollToStep(step) {
			const clickedStepIdx = vm.steps.indexOf(vm.steps.find(vmstep => vmstep.id === step.id));
			$anchorScroll('fb-step-' + clickedStepIdx);
			$anchorScroll.yOffset = 10;
		}


		function setToggleVisibility() {
		/* 
		**	When Rich Text Editor is ENABLED ('fc-enable-rich-text-editor' = true):
			Sets the visibility of the 'display sensitive fields' toggle according to the existence of any 'sensitive' fields, including token fields ('_sensitve: true');

		**	When Rich Text Editor is DISABLED ('fc-enable-rich-text-editor' = false):
			Sets the visibility of the 'display sensitive fields' toggle according to the 'viewSensitive' meta permission of the form instance step that includes the sensitive fields. Since all current sensitive fields are part of the same field group (personal) and can only appear on one step, this logic is ok for now.
		*/

			if(ssFlagSvc.checkState('fc-enable-rich-text-editor')){
				routeVm.showToggle = !!routeVm.formTemplate._formFields.find(f => f._sensitive === true);
			} else {
				let stepWithSensitiveFields = vm.steps.find((step) => {
					return step.fields.find(field => field._sensitive === true || sensitiveFieldDefIds.includes(field._fieldDefId));
				});
				routeVm.showToggle = stepWithSensitiveFields ? stepWithSensitiveFields.instance._meta.permissions.permittedActions.viewSensitive : false;
			}
		}

		var processForm = routeVm.processForm = ssAsync(function*() {
			try {
				vm.steps = [];
				formsHlp.sortByOrderField(routeVm.formTemplate._steps, routeVm.formTemplate._stepOrder);
				
				routeVm.formTemplate._steps.forEach((step, index, arr) => {
				//	find the matching form instance step
					step.instance = routeVm.formInstance._steps.find(s => s._formTemplateStepId === step.id);

				//	if we cant find a matching form instance step OR the step action of the step is 'complete form', return
					if (step.instance == null || step._stepAction === ssEnum.stepAction.formCompleted) { return; }

				//	create a default 'formFieldValues' object if it doesnt already exist
					if (typeof step.instance._formFieldValues !== 'string' || step.instance._formFieldValues.length === 0) {
						step.instance._formFieldValues = '{}'; // will become json
					}

				//	copy reference of the matching instance step to the stepValues map
					stepValues[step.instance.id] = step.instance;

				//	bind non-json-api property to json-api property
					fieldRendererHlp.bindConfigObject($scope, `stepValues['${step.instance.id}']._formFieldValues`, `stepValues['${step.instance.id}'].formFieldValues`);

					if (step.instance._lastModifiedDtUtc == null) {
						step.instance.$$forceChangedState('formFieldValues');
					}

				//	If there are no fields then we can assume user doesn't have permissions to view the step and stop here.
					if(!routeVm.formTemplate._formFields.length) { return; }

				//	collect the fields that belong to the step
					step.fields = routeVm.formTemplate._formFields.filter(f => !f._tokenField && f._step.id === step.id);

				//	process the steps for visibility & editability
					step = logic.processStep({ tStep: step, formInstance: routeVm.formInstance });
					logic.processFields(step, routeVm.showSensitiveFields);
					step.fieldGroups = logic.makeFieldGroups(step);

				//	Process employee profile review step (if it exists)
					if (step._stepAction === ssEnum.stepAction.employeeProfileReview) {
						step.instance._employeeProfileReviewStepFieldValues.forEach((reviewStepFieldValue, fieldIndex) => {
							// create a property to hold a copy of the 'profileValue' property. this will be used the updated display text so we never patch 'profileValue'
							reviewStepFieldValue.profileDisplayValue = JSON.parse(JSON.stringify(reviewStepFieldValue.profileValue));
							// find the source field and attach it to the profile field object
							let sourceField = routeVm.formTemplate._steps
								.find(s => s.id === reviewStepFieldValue.sourceStepId).fields
								.find(f => f.id === reviewStepFieldValue.sourceFormFieldId);
							reviewStepFieldValue.sourceField = sourceField;
							/*
							Watch any changes in the form field values of the source field's parent step and update the form field value of the corresponding employee profile review step field
							*/
							$scope.$watch(`routeVm.formInstance._steps[${index}]._employeeProfileReviewStepFieldValues[${fieldIndex}].sourceField.step.formFieldValues['${reviewStepFieldValue.sourceFormFieldId}']`, (nv, ov) => {
								if (nv != null && nv != ov) {
									routeVm.formInstance._steps[index]._employeeProfileReviewStepFieldValues[fieldIndex].formFieldValue = nv;
								}
							});
						});
					}
					vm.steps.push(step);
				});
			} catch(err) {
				throw err;
			}
		});

		ssEventSvc.on('select-form-step', (step) => {
			go.selectStep(step);
		}, $scope);

		ssAsync(function*() {
			try {
			//	set-up the loading indicators
				routeVm.isLoading = true;
			//	Load PDF
				if (routeVm.showPDFOverlay) {
					routeVm.loadingStatus = 'Generating PDF...';
					routeVm.PDFdata = yield formsHlp.generatePDFOverlay(routeVm.formInstance, false);
				//	sanitize the '_string' property as a trusted URL for use in the iFrame
					document.getElementById("form-overlay-frame").src = $sce.trustAsResourceUrl(routeVm.PDFdata.data.data.attributes.string);
				} else {
					routeVm.loadingStatus = 'Loading form...';
				}
			//	process the form
				yield processForm();
			//	find & highlight the current step
				let currentStep = routeVm.formTemplate._steps.find(step => step.id === routeVm.formInstance._currentStepId);
				go.selectStep(currentStep);
			// set sensitive field toggle visiibility
				setToggleVisibility();
			//	finish
				routeVm.isLoading = false;
				routeVm.loadingStatus = '';
			} catch(err) {
				routeVm.isLoading = false;
				routeVm.loadingStatus = '';
				ssToastHlp.error('There was a problem processing the form', 'Error');
				console && console.error && console.error(err);
			}
		})();
	}
}

// #-- END Segment (/form-instance/segment-form) --# //
// #-----------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentFormInstanceForm', () => new Form('form') );
