import {Component} from 'base-component';
// #-------------------------------------------------------# //
// #---- Component (cmp-properties-custom-sectionhead) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	fields: '='
};

class PropertiesCustomSectionhead extends Component {
	/*@ngInject*/
	controller($scope) {
		// view model for tracking state
		const vm = $scope.vm = { //eslint-disable-line
			field: $scope.fields[0]
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { //eslint-disable-line
		};
	}
}

// #-- END Component (cmp-properties-custom-sectionhead) --# //
// #-------------------------------------------------------# //

angular.module('ec-forms-components').directive('cmpPropertiesCustomSectionhead', () => new PropertiesCustomSectionhead(props, 'properties-custom-sectionhead') );
