import {Segment} from 'base-segment';
// #----------------------------------------# //
// #---- Segment (/send/segment-header) ----# //

class Header extends Segment {
	/*@ngInject*/
	controller($injector, $scope, ssAsync, ssFormInstancesApi, ssStatusApi, ssActiveUserSvc, ssToastHlp, ssStorage, ssArrayHlp, ssModalSvc, $location, $q, enumFormTemplateStatus, ssDateByOrgHlp, ssOrganizationSvc) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm;

		// hookup segment logic
		const logic = $injector.get('segmentSendHeaderLogic');
		var recipientCacheKey;
		var senderFunction;

		const vm = $scope.vm = {
			isPacket: routeVm.templateType.toLowerCase() === 'packet',
			isForm: routeVm.templateType.toLowerCase() === 'formtemplate',
			isSending: false,
			item: routeVm.data,
			batches: [],
			batchStyles: {}
		};

		const type = vm.isPacket ? 'Packet' : 'Form';

		const go = $scope.go = {

			preview(template) {
				$location.url(`/ec-forms/form-builder/${template.id}/preview?returnTo=/ec-forms/send/formTemplate/${template.id}`);
			},

			send(recipients = [], bulkRecipients = [], exclusions = []) {
				//	throw an error if the form template is employee-initiated and the formAdministratorType is sender
				if(vm.item._isEmployeeInitiated && vm.item._formAdministrator.formAdministratorType === 1) {
					ssToastHlp.error('An Employee-Initiated form cannot be started when the sender of the form is also the Form Administrator.', '', false);
					return;
				}

				//	if template does not have a form administrator value, deactivate the template and redirect to form template settings page
				if(vm.item._formAdministrator && (!vm.item._formAdministrator.formAdministratorType || vm.item._formAdministrator.formAdministratorType <= 0)) {
					ssModalSvc.showDestructive({
						title: 'Form Administrator Required',
						content: 'You must select a form administrator before sending this form.',
						primaryText: 'Edit',
						secondaryText: 'Cancel'
					}).then(() => {
						ssAsync(function* () {
							yield ssStatusApi.update({ id: vm.item.id }, {
								statusId: enumFormTemplateStatus.inactive
							});
							$location.url(`/ec-forms/form-builder/${vm.item.id}/settings`);
						})();
					});
				} else {
					var baseAttrObj = {};
					vm.isSending = true;
					//	Gather all the recipient & excluded recipient IDs and filter out any duplicates
					let allRecipients = [...recipients, ...bulkRecipients];
					let allexclusions = [...exclusions];

					//	get the default attributes
					Object.assign(baseAttrObj, logic.getDefaultBatchObj());

					//	Add "as of" date (if it exists)
					if (routeVm.postData.asOf) {
						// Using date helper here only as this is where we are posting to the server.
						// safeAsOfDateRelativeTo requires a date with no time, so we set it to the start of the day, then that gets set to noon org converted to UTC
						Object.assign(baseAttrObj, { asOf: ssDateByOrgHlp.safeAsOfDateRelativeTo(moment(routeVm.postData.asOf).startOf('day'), routeVm.org) });
					}
					//	Assign the rest of the necessary props
					baseAttrObj._dueDateUtc = routeVm.postData.submissionDueDate ? ssDateByOrgHlp.endOfDayRelativeTo(moment(routeVm.postData.submissionDueDate), routeVm.org) : null;
					baseAttrObj._requestType = vm.isPacket ? 1 : 0;
					baseAttrObj._templateId = vm.item.id;
					// make sure all the included employees are unique;
					baseAttrObj._includedEmployeeIds = ssArrayHlp.unique(allRecipients.map((r) => r.id));
					// make sure all the excluded employees are unique;
					baseAttrObj._excludedEmployeeIds = ssArrayHlp.unique(allexclusions.map((r) => r.id));
					
					//	determine which endpoint to call
					senderFunction = vm.isPacket ? ssFormInstancesApi.postPacketInstanceBatch : ssFormInstancesApi.postFormInstanceBatch;

					//	make the call
					ssAsync(function*() {
						try {
							let batchRequest = yield senderFunction({
								organizationId: routeVm.org.id
							}, baseAttrObj);

							//	Push the batch to the top of the batch list
							vm.batches.unshift(batchRequest);

							//	Clear all recipients & reset filter/exclusions values
							routeVm.removeGroup();
							
							ssStorage.clear(recipientCacheKey);

						} catch(e) {
							console.error(e)
							ssToastHlp.error('', `There was an error sending "${$scope.form._name}"`, false);
						} finally {
							vm.isSending = false;
							routeVm.postData.recipients = [];
							routeVm.postData.bulkRecipients = [];
							routeVm.postData.exclusions = [];
						}
					})();
				}

			},

			removeBatch(batch) {
				let ind = vm.batches.indexOf(batch);
				vm.batches.splice(ind, 1);
			}
		};

		ssAsync(function*() {
			try {

				recipientCacheKey = `${routeVm.org.id}_template_${routeVm.data.id}`;

				//	Get all the batch requests for this form
				let allBatches = yield ssFormInstancesApi.getAllBatches({
					organizationId: routeVm.org.id,
					'filter[templateid]': vm.item.id
				});

				//	If there are no batches for this form, leave
				if(!allBatches.length) { return;}

				//	create an array of promises for retrieving batch statuses
				let batchStatusPromises = allBatches.map(batch => ssFormInstancesApi.getBatchStatus({ id: batch.id }));

				//	execute all the promises to get each batch status
				let batchStatuses = yield $q.all(batchStatusPromises);

				//	filter out any statuses that are successfully completed
				batchStatuses = batchStatuses.filter((batch) => {
					return batch._status !== 3 && !batch._isArchived || (batch._status === 3 && batch._numFailed);
				})

				//	Use the batch statuses array to determine which batch requests to show
				let currentBatches = allBatches.filter((batch)=> {
					return batchStatuses.find((status) => {
						return status.id === batch.id;
					})
				});

				vm.batches = [...currentBatches];

				//	watch the length of the batch list and adjust its height accordingly
				$scope.$on('$destroy', $scope.$watch('vm.batches.length', function(nv, ov) {
					vm.batchStyles = logic.getBatchListStyles(vm.batches);
				})
				);

			} catch(e) {
				ssToastHlp.error('', `There was an error retrieving current batches for "${vm.item._name}"`);
				console && console.error && console.error(e);
			}
		})();

		routeVm.go = {
			send() {
				const recipients = routeVm.postData.recipients;
				const bulkRecipients = routeVm.postData.bulkRecipients;
				const exclusions = routeVm.postData.exclusions;
				go.send(recipients, bulkRecipients, exclusions);
			}
		};
	}
}

// #-- END Segment (/send/segment-header) --# //
// #----------------------------------------# //

angular.module('ec-forms-segments').directive('segmentSendHeader', () => new Header('header'));
