import { Segment } from 'base-segment';
// #-----------------------------------------# //
// #---- Segment (/send/segment-content) ----# //

class Content extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $rootScope, $window, ssAsync, ssActiveUserSvc, ssEmployeeApi, ssFormTemplatesApi, enumStepParticipantType, enumFormAdministratorType, enumStepAction, ssStorage, moment, ssDateByOrgHlp, formsHlp) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm;

		// hookup segment logic
		const logic = $injector.get('segmentSendContentLogic');// jshint ignore:line

		const recipientBucketType = {
			individual: 0,
			group: 1
		}

		routeVm.postData = {
			isPacket: routeVm.templateType.toLowerCase() === 'packet',
			isForm: routeVm.templateType.toLowerCase() === 'formtemplate',
			recipients: [],
			bulkRecipients: [],
			exclusions: [],
			submissionDueDate: null,
			asOf: null
		};

		var cacheKey, template;

		const vm = $scope.vm = {
			loadingFormSender: true,
			resolvedFormSender: "loading form sender",
			useEffectiveDating: false,
			asOf: null,
			recipient: enumStepParticipantType.recipientChosenWhenSent,
			hasSubmissionDueDate: 'false',
			isPacket: routeVm.templateType === 'Packet',
			isForm: routeVm.templateType === 'FormTemplate',
			item: routeVm.data,
			datePickerOptions: {
				minDate: new Date()
			},
			selectedFilter: 'sendDetails',
			exporting: false
		};

		const go = $scope.go = { // jshint ignore:line

			export() {
				ssAsync(function* () {
					vm.exporting = true;
					var ids = routeVm.postData.recipients.map(x => x.id)
						.concat(routeVm.postData.bulkRecipients.map(x => x.id))
						.filter((value, index, self) => self.indexOf(value) === index);

					var params = {
						organizationId: routeVm.org.id,
						include: 'statusHistory,employmentHistory,addresses,phoneNumbers,emails,emergencyContacts,positions,assignments,locations,jobtypes,payschedules,supervisors'
					}
					if (vm.useEffectiveDating && vm.asOf != null) {
						params.asOfTimeUtc = ssDateByOrgHlp.safeAsOfDateRelativeTo(moment(vm.asOf).startOf('day'), routeVm.org);
					} else {
						// if we don't have a date just use today, relative to the org
						params.asOfTimeUtc = ssDateByOrgHlp.safeAsOfDateRelativeTo(moment().startOf('day'), routeVm.org);
					}

					var downloaded = yield ssEmployeeApi.exportEmployeesById(params, { ids: ids });
					$window.open(downloaded._string, '_blank', '');

					vm.exporting = false;

				})()

			}
		};

		(function() {
			try {
			//	load cached participants
				cacheKey = `${routeVm.org.id}_template_${routeVm.data.id}`;
				template = ssStorage.load(cacheKey);
				if (template == null) {
					template = {};
				} else {
					routeVm.postData.recipients = template[recipientBucketType.individual] || [];
					routeVm.postData.bulkRecipients = template[recipientBucketType.group] || [];
				}

			//	get form sender
				if ($scope.$root.isPermitted.activeUser.idmAdministrator) {
					vm.resolvedFormSender = `You (IDM Administrator)`;
				} else {
					ssAsync(function* () {
						let userKey = yield ssActiveUserSvc.getIdentityByType('employee');
						let user = yield ssEmployeeApi.get({
							id: userKey,
							fields: 'id,firstname,lastname'
						}, null, false);
						vm.resolvedFormSender = `You (${user.fullName})`;
						vm.loadingFormSender = false;
					})();
				}
			//	Do form stuff
				if (vm.isForm) {
					formsHlp.sortByOrderField(vm.item._steps, vm.item._stepOrder);
					stepParticipantDisplay(vm.item._steps);
					if (vm.item._formAdministrator.formAdministratorType === 3) {
						ssAsync(function* () {
							let admin = yield ssEmployeeApi.get({
								id: vm.item._formAdministrator.employeeId,
								fields: 'id,firstname,lastname'
							}, null, false);
							vm.formAdministrator = admin.fullName;
						})();
					} else {
						vm.formAdministrator = enumFormAdministratorType.byValue(vm.item._formAdministrator.formAdministratorType) || 'None';
					}
				} else if (vm.isPacket) {
				//	get packet forms
					ssAsync(function* () {
						if(vm.item._formTemplateIds) {
							vm.loadingPacket = true;
							let templateOrder = vm.item._formTemplateIds.toString();
							ssFormTemplatesApi.formTemplatesByIds(vm.item._formTemplateIds, { include: 'steps' }).then((templates) => {
								formsHlp.sortByOrderField(templates, templateOrder);
								vm.item.forms = templates;
								vm.item.forms.forEach(f => {
									f.steps = f._steps.filter(s => s._stepAction !== enumStepAction.formCompleted);
									formsHlp.sortByOrderField(f.steps, f._stepOrder);
								});
								vm.loadingPacket = false;
							}).catch((e) => {
								vm.loadingPacket = false;
								console && console.log && console.log('Error processing form templates in packets', e);
							});
						}
					})();
				}
			} catch(e) {
				console && console.error && console.error(e);
			}
		})();

		$scope.$watch('vm.hasSubmissionDueDate', (nv, ov) => {
			if (nv === ov || nv == null) {
				return;
			}
			if (nv === 'false') {
				routeVm.postData.submissionDueDate = '';
			}
		});

		/*
		**	save the currently selected recipients to localStorage
		**	when the user navigates away from this route or refreshes the page
		*/
		$rootScope.$on('$stateChangeStart', function (event) {
			saveRecipients({ type: recipientBucketType.individual, recipients: routeVm.postData.recipients });
			saveRecipients({ type: recipientBucketType.group, recipients: routeVm.postData.bulkRecipients });
		});

		$window.addEventListener('beforeunload', function () {
			saveRecipients({ type: recipientBucketType.individual, recipients: routeVm.postData.recipients });
			saveRecipients({ type: recipientBucketType.group, recipients: routeVm.postData.bulkRecipients });
		}, { capture: true });

		/*
		**	The following two watchers are here because UX requested that we keep the displayed date
		**	when the user unchecks the 'select an effective date..." option.
		*/
		$scope.$on('destroy', $scope.$watch('vm.useEffectiveDating', (nv, ov) => {
			if (nv === ov) { return; }
			if (nv && vm.asOf) {
				routeVm.postData.asOf = vm.asOf;
				$rootScope.$broadcast('parentAsOfDate-Change', { asOf: vm.asOf });
			} else {
				routeVm.postData.asOf = null;
				vm.asOf = ''
				$rootScope.$broadcast('parentAsOfDate-Change', { asOf: vm.asOf });
			}
		}));

		$scope.$on('destroy', $scope.$watch('vm.asOf', (nv, ov) => {
			if (nv === ov) {
				return;
			}
			// propagates the "Effective Date" to the multiple employee search widget
			$rootScope.$broadcast('parentAsOfDate-Change', { asOf: nv });
			routeVm.postData.asOf = nv;
		}));

		function stepParticipantDisplay(steps) {
			steps.forEach((step, i) => {

				//	add a custom property to each step which indicates if we should show the custom resend step message control
				step.showResendMessage = i+1 !== vm.item._steps.length ? vm.item._steps[i+1]._stepAction === enumStepAction.reviewApproveOrDeny : false; 

				if(step._stepParticipant.participantType === enumStepParticipantType.specificPerson &&step._stepParticipant.employeeIds.length) {
					step.resolvedParticipant = step._stepParticipant.name;
				} else {
					step.resolvedParticipant = enumStepParticipantType.byValue(step._stepParticipant.participantType);
				}
			});
		}

		function saveRecipients({ type, recipients }) {
			template[type] = recipients;
			ssStorage.save(cacheKey, template);
		}
		
	}

	link(scope, elem, attrs) {
		scope.$watch('routeVm.postData.recipients.length', (nv, ov) => {
			scope.$emit('ss-validate-field-event', elem.find('input').eq(0), 'ss-validate-array-field');
		});
	}
}

// #-- END Segment (/send/segment-content) --# //
// #-----------------------------------------# //

angular.module('ec-forms-segments').directive('segmentSendContent', () => new Content('content'));
