import {Component} from 'base-component';
import {getConfig} from './ckeditorConfig.js';
import {getPlaceholders} from './placeholders.js';

// #-------------------------------------# //
// #---- Component (cmp-text-editor) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	field: '=',
	disabled: '=',
	model: '='
};

const BUILDER_URL_REGEX = RegExp('\/ec-forms\/form-builder\/[0-9A-Za-z-]+\/builder');

// const config = {
// 	require: '^ngModel',
// 	replace: false
// }

function textEditor ($location, ssArrayHlp, fieldsHlp, ssFlagSvc) {

	class TextEditor extends Component {
		/*@ngInject*/
		controller($scope, $sce) {
			// view model for tracking state
			const vm = $scope.vm = {
				contentVal: $scope.field.step ? $scope.field.step.formFieldValues[$scope.field.id] : $scope.field._label,
				editorId: uniqueId(),
				editorConfig: getConfig(),
				showPlaceholder: false,
				hrmsEnabled: false
			};

			// behavior that needs to be bound to the view
			const go = $scope.go = {
				onReady: function () {
				},
			};

			$scope.SCE = $sce;
			$scope.editor = null;

			if($scope.field._tokenFields == null) {
				$scope.field['_tokenFields'] = [];
			}

			/* 
			Determine if the field is "active"; field should ONLY be active in the preview/instance when there's content, but can be active in the template when there's no content in order to show the placeholder.
			*/
			if(!BUILDER_URL_REGEX.test($location.path())) {
				$scope.field._active = !!$scope.field._label.length;
				$scope.field.$$resetChangesDetected();
			}

			$scope.placeholderTokens = getFilteredPlaceholders();

			// Check if the org is HRMS enabled, if so remove specific assignment fields
			function getFilteredPlaceholders() {
				let placeholders = getPlaceholders();
				if (ssFlagSvc.checkState('hrms')){
					var filteredPlaceHolders = placeholders.filter(p => {
						return !p.hideForHrms;
					})
					return filteredPlaceHolders;
				}
				return placeholders;
			}

			function uniqueId() {
				return 'id-' + Math.random().toString(36).substr(2, 16);
			};

		}

		link(scope, elem, attr, ngModel) {

			let editorElem;
			const CONTENT_PLACEHOLDER = '<div class="no-content-placeholder"><h5>Text Editor</h5><p><b>Click here to add content</b></p>';
			const TOKEN_START = '[[';
			const TOKEN_END = ']]';
			/*
			**	Valid token delimiters consist of 2 or more of the following characters:
			**	'[', '], '{', '}', '|'
			*/
			const IS_VALID_START_TOKEN = /[\[\{\|]{2}/.test(TOKEN_START);
			const IS_VALID_END_TOKEN = /[\]\}\|]{2}/.test(TOKEN_END);
			const TOKEN_REGEX = `${TOKEN_START}(.*?)${TOKEN_END}`;
			const ESC_TOKEN_REGEX = new RegExp(escapeRegExp(TOKEN_REGEX), "g");

			elem.ready(() => {
				editorElem = elem[0].querySelector('[contenteditable]');
				ngModel = angular.element(editorElem).controller('ngModel');

				// start off in edit mode if the field hasn't been saved
				if(!scope.field.id) {
					initEditor();
				} else {
					// otherwise, show the content or placeholder
					angular.element(editorElem).html(scope.SCE.trustAsHtml(scope.vm.contentVal || CONTENT_PLACEHOLDER));
				}
			});

			// event propagation will occur in order to display the properties panel
			elem.on('click', (evt) => {
				scope.$apply(() => {
					// if this is a form instance, return	
					if (scope.field.step) { return; }
					//	If this editor is already enabled and has data, return;
					if (editorExists()) { return; }
					// initilaize the editor
					initEditor();
				});
			});

			scope.$watch('model', (nv) => {
				angular.element(editorElem).html(scope.SCE.trustAsHtml(nv));
			});

			/* 
			we need to watch this event so we can destroy editor instances when other non text editor field groups or steps are selected.
			*/
			scope.$on('$destroy', scope.$root.$on('component-properties', (event, type, data) => {
				if(type && type !== 'cmp-properties-text-editor') destroyInstances();
			}));

			// Checks to see if a particular instance name exists and it has data
			function editorExists() {
				return CKEDITOR.currentInstance && CKEDITOR.currentInstance.name === scope.vm.editorId;
			}

			// destroys all instances except those without any content
			function destroyInstances() {
				Object.values(CKEDITOR.instances).forEach((instance) => {
					instance.destroy();
					instance = null;
				});
			}

			function initEditor(config = {}) {

				//	destroy the other editor instances to force them into 'preview' mode
				destroyInstances();

				/* here we intilaize an editor type - there a multiple varieties */

				/* INLINE editor with shared space for toolbar */
				CKEDITOR.disableAutoInline = true;

				scope.editor = CKEDITOR.inline(editorElem, Object.assign(scope.vm.editorConfig, {
					sharedSpaces: {
						top: `${scope.vm.editorId}-top`
					},
					placeholder_select: {
						format: `${TOKEN_START}%placeholder%${TOKEN_END}`,
						placeholders : scope.placeholderTokens
					},
					removePlugins: 'floatingspace,maximize,resize'
				}));

				/* FIXED editor */
				// scope.editor = CKEDITOR.replace(editorElem, Object.assign(scope.vm.editorConfig, {
				// 	autoGrow_onStartup: true,
				// 	autoGrow_maxHeight: '999999',
				// 	toolbarCanCollapse: true,
				// 	height: 250,
				// 	placeholder_select: {
				// 		format: `${TOKEN_START}%placeholder%${TOKEN_END}`,
				// 		placeholders : getPlaceholders()
				// 	},
				// 	resize_minHeight: 300,
				// }));

				if (!ngModel) return;

				scope.editor.on('instanceReady', function (evt) {
					scope.editor.setData(ngModel.$viewValue);
					initAutoComplete(evt);
				});

				// get the current set of placeholders (if they exist)
				let currentTokens = ngModel.$viewValue.toString().match(ESC_TOKEN_REGEX) || [];

				function updateModel() {
					scope.$apply(function () {
						ngModel.$setViewValue(scope.editor.getData());
						let tokens = ngModel.$modelValue.match(ESC_TOKEN_REGEX) || [];
						// create tokens only if the quantity has changed
						if(tokens.length !== currentTokens.length) {
							createTokenFields(tokens);
							currentTokens = tokens;
						}
					});
				}

				scope.editor.on('destroy', () => {
				//	when an editor is destroyed, determine if we should show the placeholder or not based on content length
					let content = !scope.field._label.length ? CONTENT_PLACEHOLDER : ngModel.$viewValue;
					angular.element(editorElem).html(scope.SCE.trustAsHtml(content));
					scope.editor = null;
				});
	
				scope.editor.on('change', updateModel);
				// scope.editor.on('key', updateModel);
				// scope.editor.on('dataReady', updateModel);

				ngModel.$render = function (value) {
					scope.editor.setData(ngModel.$viewValue);
				};

				// disallow drag and drop into an editor
				scope.editor.on( 'drop', function( evt ) {
					console && console.log && console.log(evt.data);
					console && console.log && console.log(evt.data.dragRange.startContainer.getName());
					// evt.cancel();
				});

			};

			function createTokenFields(tokens = []) {
				tokens = Array.isArray(tokens) ? tokens : [];
				let tokenFieldDefs = ssArrayHlp.unique(tokens).map((token) => {
					let tokenVal = token.replace(/\[\[|\]\]/g, '');
					let fieldDef = fieldsHlp.allFields.find((field) => {
						return field._fieldDefId && field._externalData && field._externalData === tokenVal;
					});
					fieldsHlp.updateFieldAttributes(fieldDef);
					fieldsHlp.updateFieldOptions(fieldDef);
					fieldDef['tokenField'] = true;
					fieldDef['fieldGroup'] = 'tokenFields';
				//	We need to manually strip off the underscores since token fields are not a JSON API model. This is a direct lift from line 46 of ssApiSvc
					let newFieldDef = {};
					Object.keys(fieldDef).forEach((k) => {
						let key = k;
						if (k.startsWith('_')) {
							key = k.substr(1);
							newFieldDef[key] = fieldDef[k];
						}
					});
					return newFieldDef;
				});
				scope.field._tokenFields = tokenFieldDefs;
			}
		
			function escapeRegExp(string) {
				if(IS_VALID_START_TOKEN && IS_VALID_END_TOKEN) {
					return string.replace(/[\[\]\{\}\|]/g, '\\$&');
				} else {
					console.error('token delimiters must match approved format ("[[","]]","{{","}}","||")');
				}
			}

			function textTestCallback(range) {
				if (!range.collapsed) {
					return null;
				}
				return CKEDITOR.plugins.textMatch.match(range, matchCallback);
			}

		  // 'text' param here is the 'text' property of the object returned from 'testTestCallback' function
			function matchCallback(text, offset) {
				var pattern = /\[{2}([A-Za-z]\s*|\])*$/,
				match = text.slice(0, offset)
				.match(pattern);

				if (!match) return null;

				return {
					start: match.index,
					end: offset
				};
			}
		  
			function dataCallback(matchInfo, callback) {
				var data = scope.placeholderTokens.filter(item => {
					let textOnlyQuery = matchInfo.query.replace(/\[{2}/, '');
					let regExpStr = `${textOnlyQuery}`;
					let regExp = new RegExp(regExpStr, "gi");
					return regExp.test(item.name);
				});
				callback(data);
			}

			function initAutoComplete(evt) {
				var itemTemplate = '<li data-id="{id}">{title}</li>',
				outputTemplate = `${TOKEN_START}{value}${TOKEN_END}`;
	
				var autocomplete = new CKEDITOR.plugins.autocomplete(evt.editor, {
					textTestCallback: textTestCallback,
					dataCallback: dataCallback,
					itemTemplate: itemTemplate,
					outputTemplate: outputTemplate
				});

				autocomplete.getHtmlToInsert = function(item) {
					return this.outputTemplate.output(item);
				}
			}

			CKEDITOR.on("instanceReady", function (evt) {

				let editor = evt.editor;

				// CKEDITOR.stylesSet.add( 'styleSet-' + editor.id, [
				// 	{ name: 'Heading 1', element: 'h1', styles: { 'color': 'black' } },
				// 	{ name: 'Heading 2' , element: 'h2', styles: { 'color': 'black' } },
				// 	{ name: 'CSS Style', element: 'span', attributes: { 'class': 'my_style' } },
				// 	{ name: 'Marker: Yellow', element: 'span', styles: { 'background-color': 'Yellow' } }
				// ] );

				/*
				  editor.on('dialogShow', (dialogShowEvent) => {
					console && console.log && console.log(dialogShowEvent.data);
					var name = dialogShowEvent.data.getName();
					if(name === 'tableProperties') {
						let spacing = dialogShowEvent.data._.contents.info.txtCellSpace;
						console && console.log && console.log(spacing);
						let textCellSpace = dialogShowEvent.data.getContentElement('info', 'txtCellSpace');
						console && console.log && console.log(textCellSpace);
						let contents = dialogShowEvent.data.definition.getContents();
						console && console.log && console.log(contents);
						// contents.dialogShowEvent.data.definition.removeButton('txtCellSpace');
					}
				})
				*/
			});

		}
	}

	return new TextEditor(props, 'text-editor');

}

// #-- END Component (cmp-text-editor) --# //
// #-------------------------------------# //
angular.module('ec-forms-components').directive('cmpTextEditor', textEditor);
// angular.module('super-suit').requires.push('ng.ckeditor');
