import {Segment} from 'base-segment';
// #--------------------------------------------------------# //
// #---- Segment (/form-builder/builder/segment-fields) ----# //

class Fields extends Segment {
	/*@ngInject*/
	controller($injector, $scope, ssFlagSvc, ssStorage, ngIntroService) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // eslint-disable-line

		// hookup segment logic
		const logic = $injector.get('segmentFormBuilderBuilderFieldsLogic'); // eslint-disable-line

		// intro.js based UI feature tour
		$scope.ngIntroService = ngIntroService;
		$scope.ssStorage = ssStorage;
		$scope.ssFlagSvc = ssFlagSvc;

		// view model for tracking state
		const vm = $scope.vm = { // eslint-disable-line
			fields: {
				custom: [
					{
						component: 'cmp-properties-custom-input',
						fields: 'custom_input',
						label: 'Input'
					}, {
						component: 'cmp-properties-custom-dropdown',
						fields: 'custom_dropdown',
						label: 'Dropdown'
					}, {
						component: 'cmp-properties-custom-selectbox',
						fields: 'custom_selectGroup',
						label: 'Multiple Choice'
					}, {
						component: 'cmp-properties-custom-signature',
						fields: 'custom_signature',
						label: 'Signature',
						featureFlag: {
							name: 'fc-enable-signatures',
							showIfActive: false
						}
					}, {
						component: 'cmp-properties-custom-signature',
						fields: 'custom_signatures',
						label: 'Signatures',
						featureFlag: {
							name: 'fc-enable-signatures',
							showIfActive: true
						}
					}, {
						component: 'cmp-properties-custom-date',
						fields: 'custom_date',
						label: 'Date'
					}, {
						component: 'cmp-properties-custom-fileupload',
						fields: 'custom_fileUpload',
						label: 'File Upload'
					}, {
						component: 'cmp-properties-custom-filelink',
						fields: 'custom_fileLink',
						label: 'Link'
					}, {
						component: 'cmp-properties-custom-sectionhead',
						fields: 'custom_sectionHead',
						label: 'Section Head',
						featureFlag: {
							name: 'fc-enable-rich-text-editor',
							showIfActive: false
						}
					}, {
						component: 'cmp-properties-custom-text',
						fields: 'custom_text',
						label: 'Simple Text'
					}, {
						component: '',
						fields: 'custom_textEditor',
						label: 'Text Editor',
						featureFlag: {
							name: 'fc-enable-rich-text-editor',
							showIfActive: true
						}
					}],
				employee: [{
					component: 'cmp-properties-smart',
					fields: 'legalName',
					label: 'Legal Name',
					allowMultiple: false
				}, {
					component: 'cmp-properties-smart',
					fields: 'aliasName',
					label: 'Other Name',
					allowMultiple: false
				}, {
					component: 'cmp-properties-smart',
					fields: 'address',
					label: 'Address',
					allowMultiple: false
				}, {
					component: 'cmp-properties-smart',
					fields: 'contact',
					label: 'Contact',
					allowMultiple: false
				}, {
					component: 'cmp-properties-smart',
					fields: 'personal',
					label: 'Personal',
					allowMultiple: false
				}, {
					component: 'cmp-properties-credentials',
					fields: 'credentials',
					label: 'Credentials',
					allowMultiple: false,
					featureFlag: {
						name: 'fc-enable-credentials-group',
						showIfActive: true
					}
				}, {
					component: 'cmp-properties-smart',
					fields: 'employment',
					label: 'Employment',
					allowMultiple: false
				}, {
					component: 'cmp-properties-smart',
					fields: 'primaryAssignment',
					label: 'Primary Assignment',
					allowMultiple: true,
					featureFlag: {
						name: 'fc-enable-multiple-employee-assignments',
						showIfActive: false
					}
				}, {
					component: 'cmp-properties-primary-assignment',
					fields: 'primaryAssignmentNew',
					label: 'Primary Assignment',
					allowMultiple: false,
					featureFlag: {
						name: 'fc-enable-multiple-employee-assignments',
						showIfActive: true
					}
				}, {
					component: 'cmp-properties-secondary-assignments',
					fields: 'assignments',
					label: 'Secondary Assignments',
					allowMultiple: false,
					featureFlag: {
						name: 'fc-enable-multiple-employee-assignments',
						showIfActive: true
					}
				},  {
					component: 'cmp-properties-salary',
					fields: 'assignmentsTotalSalary',
					label: 'Total Salary',
					allowMultiple: false,
					showIfHRMS: false,
					featureFlag: {
						name: 'fc-enable-multiple-employee-assignments',
						showIfActive: true
					}
				}, {
					component: 'cmp-properties-smart',
					fields: 'assignmentCompensation',
					label: 'Assignment Compensation',
					allowMultiple: true,
					showIfHRMS: false,
					featureFlag: {
						name: 'fc-enable-multiple-employee-assignments',
						showIfActive: false
					}
				}, {
					component: 'cmp-properties-smart',
					fields: 'assignmentBenefits',
					label: 'Assignment Benefit',
					allowMultiple: true,
					showIfHRMS: false,
					featureFlag: {
						name: 'fc-enable-multiple-employee-assignments',
						showIfActive: false
					}
				}, {
					component: 'cmp-properties-smart',
					fields: 'secondaryAssignment',
					label: 'Secondary Assignment',
					featureFlag: {
						name: 'fc-enable-multiple-employee-assignments',
						showIfActive: false
					},
					allowMultiple: false
				}, {
					component: 'cmp-properties-smart',
					fields: 'secondaryCompensation',
					label: 'Secondary Compensation',
					featureFlag: {
						name: 'fc-enable-multiple-employee-assignments',
						showIfActive: false
					},
					allowMultiple: false,
					showIfHRMS: false,
				}, {
					component: 'cmp-properties-smart',
					fields: 'secondaryBenefits',
					label: 'Secondary Benefit',
					featureFlag: {
						name: 'fc-enable-multiple-employee-assignments',
						showIfActive: false
					},
					allowMultiple: false,
					showIfHRMS: false,
				}, {
					component: 'cmp-properties-smart',
					fields: 'financial',
					label: 'Financial',
					allowMultiple: false,
					showIfHRMS: false,
				}, {
					component: 'cmp-properties-smart',
					fields: 'emergency',
					label: 'Emergency',
					allowMultiple: false,
					showIfHRMS: false,
				}]
			}
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line
			filterEmployeeRecordTools(field) {
				if(routeVm.isHRMSEnabled && field.hasOwnProperty('showIfHRMS')) {
					return field.showIfHRMS;
				} else {
					return checkFeatureFlag(field);
				}
			},
		};

		function checkFeatureFlag(field){
			if (field.featureFlag) {
				if (ssFlagSvc.checkState(field.featureFlag.name)) {
					return field.featureFlag.showIfActive;
				} else {
					return !field.featureFlag.showIfActive;
				};
			}
			return true;
		};
	}

	link(scope, elem) {

		angular.element(document).ready(function() {

			(function rteTour() {
				const steps = []
				if (scope.ssFlagSvc.checkState('fc-enable-signatures') && !scope.ssStorage.load('ec-forms.signatures.feature.intro')) {
					var step = {
						element: angular.element(elem[0].querySelector('#custom-field-custom_signatures'))[0],
						intro: '<h4>New Signature Feature</h4><p>You can now apply saved administrative signatures to a form before sending it.</p>'
					}
					steps.push(step)
				}
				if (scope.ssFlagSvc.checkState('fc-enable-rich-text-editor') && !scope.ssStorage.load('ec-forms.text-editor.feature.intro')) {
					var step = {
						element: angular.element(elem[0].querySelector('#custom-field-custom_textEditor'))[0],
						intro: '<h4>Introducing Rich Text Editor</h4><p>A light-weight tool that can help you build customized forms with rich text formatting.</p>'
					}
					steps.push(step)
				}
				if (steps.length > 0){
					scope.ngIntroService.setOptions({
						steps: steps,
						doneLabel: 'Got it!',
						showBullets: false,
						showStepNumbers: false,
						disableInteraction: true
					});
					scope.ngIntroService.onExit(function() {
						scope.ssStorage.save('ec-forms.text-editor.feature.intro', true, { persistent: true, expires: false });
						scope.ssStorage.save('ec-forms.signatures.feature.intro', true, { persistent: true, expires: false });
					});
					scope.ngIntroService.start();
				}
			}());

		});

	}
}

// #-- END Segment (/form-builder/builder/segment-fields) --# //
// #--------------------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentFormBuilderBuilderFields', () => new Fields('fields') );
