import {State} from 'base-state';
// #-----------------------# //
// #---- State (/send) ----# //

// view model that is shared by all segments on this route,
// used for segment coordination. Define initial state here.
const routeVm = {
	retryLoading: false
};

const stateName = 'send';

// config object for ui-router
// http://angular-ui.github.io/ui-router/site/#/api/ui.router.state.$stateProvider#methods_state
const stateConfig = {
	url: '/send/{type:[A-Za-z]+}/{id:[0-9A-Za-z-]+}',
	templateUrl: 'routes/send/send.html',
	resolve: {
		packetFormName: /*@ngInject*/ function($stateParams, ssAsync, ssFormTemplatesApi, ssPacketsApi) {
			return ssAsync(function*() {
				try {
					routeVm.templateType = $stateParams.type;

					if (routeVm.templateType === 'Packet') {
						routeVm.data = yield ssPacketsApi.get({
							id: $stateParams.id
						});
					} else {
						routeVm.data = yield ssFormTemplatesApi.get({
							id: $stateParams.id,
							include: 'steps,formFields,statusHistory'
						});
					}

					return routeVm.data._name;
				} catch (e) {
					console && console.log && console.log('Error getting form', e);
				}
			})();
		},
		org: /*@ngInject*/ function(ssAsync, ssOrganizationSvc, ssToastHlp) {
			return ssAsync(function*() {
				try {
					routeVm.org = yield ssOrganizationSvc.getActiveOrg();
				} catch (err) {
					ssToastHlp.error('There was an error retrieving organization data. Due Dates may not display. Please see the console for more detail.', '', false);
					console && console.error && console.error('Error getting Org data', err);
				}
			})();
		},
		isHRMSEnabled: /*@ngInject*/ function(ssMultiInitiativeFeatureDetectorSvc) {
			routeVm.isHRMSEnabled = ssMultiInitiativeFeatureDetectorSvc.isHRMSEnabled();
		},
		$title: /*@ngInject*/ ($stateParams, packetFormName) => {
			return `Frontline Central - Send ${$stateParams.type} ${packetFormName}`;
		}
	},
	$permissions: (permissionSvc) => permissionSvc.isPermittedAction('formTemplates', 'send') ||
		permissionSvc.isPermittedAction('packets', 'send')
};

// #-- END State (/send) --# //
// #-----------------------# //

angular.module('ec-forms').config( /*@ngInject*/ ($stateProvider) => {
	$stateProvider.state(`base.${stateName}`, new State(stateConfig, routeVm));
});
