import {Segment} from 'base-segment';
// #-----------------------------------------------------# //
// #---- Segment (/inbox/form-instance/segment-form) ----# //

class Form extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $stateParams, $location, rAF, $anchorScroll, $timeout, $q,
				$filter, ssAsync, ssEventSvc, ssFormInstancesApi, ssEnum, ssActiveUserSvc, fieldRendererHlp,
				ssToastHlp, ssSaveInterrupterSvc, ssModalSvc, formsHlp) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm;

		// hookup segment logic
		const logic = $injector.get('segmentInboxFormInstanceFormLogic');
		const stepValues = $scope.stepValues = {};

		const vm = $scope.vm = {
			steps: [],
			selectedStep: null,
			loading: false,
			submissionEvent: '',
			employeeProfileReview: ssEnum.stepAction.employeeProfileReview,
			formComplete: ssEnum.formStatus.complete,
			cachedSourceFields: {}
		};

		var customMessage = '';

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line
			selectFormObject(index, step) {
				vm.selectedStep = step;
				ssEventSvc.emit('form-instance.entry.step-clicked', step.id, false);
				$scope.$emit('ss-validate-reset');
			},
			ignoreValidations(step, selectedStep){
				const index = vm.steps.indexOf(step);
				const selectedStepIndex = vm.steps.indexOf(selectedStep);
				return index > selectedStepIndex;
			},
			submitForm(step) {
				let currentStepIndex = vm.steps.findIndex(s => s.currentStep);
				let submittedStepIndex = vm.steps.findIndex(s => s.id === step.id);

				// we are submitting a step before/or equal to the current step
				if (submittedStepIndex <= currentStepIndex) {
					submitForm(step);
					return;
				}

				// we are trying to submit a step after the current step
				let config = {
					secondaryText: 'Cancel',
					alwaysOnTop: true,
					size: 'standard',
				};

				// vm.steps does not include the form complete step so only need to add 1 to see if submittedStep is the last step
				if (submittedStepIndex + 1 === vm.steps.length) {
					// last step
					config.title = 'Are you sure you want to submit this form?';
					config.primaryText = 'Submit Form';
					config.content = 'You are about to submit a form with incomplete workflow steps.';
				} else {
					// are you sure step
					config.title = 'Are you sure you want to submit this step?';
					config.primaryText = 'Submit Step';
					config.content = 'There are incomplete workflow steps prior to this step. Are you sure you want to submit this step?';
				}
				ssModalSvc.showStandard(config).then(() => {
					// primary
					submitForm(step);
				});
			},

			submitStep(step) {
				vm.submissionEvent = 'submitStep';
				customMessage = 'submitted';
				vm.selectedStep = step;
			},

			acknowledgeStep(step) {
				vm.submissionEvent = 'reviewForm';
				customMessage = 'acknowledged';
				vm.selectedStep = step;
			},

			approveStep(step) {
				vm.submissionEvent = 'approveForm';
				customMessage = 'approved';
				vm.selectedStep = step;
			},

			denyStep(step) {
				vm.submissionEvent = 'declineForm';
				customMessage = 'denied';
				vm.selectedStep = step;
			},

			reviewProfile(step) {
				vm.submissionEvent = 'profileReviewed';
				customMessage = 'submitted';
				vm.selectedStep = step;
			},

			profileValueDisplay(reviewStepFieldValue, step) {
				var val;
				let field = step.fields.find(f => { return f.id === reviewStepFieldValue.formFieldId });

				if (field.externalSourceOptions && field.externalSourceOptions.length > 0) {
					//	in the review step, read-only profile values come down as enums 
					//	using field.externalSourceOptions to resolve enum to friendly display value 
					let option = field.externalSourceOptions.find(o => { return o.value === reviewStepFieldValue.profileValue});
					if (option != null) {
						val = option.name;
					}
				} else if (field._fieldType === 'super-input-datepicker'){
					val = $filter('date')(reviewStepFieldValue.profileValue, 'MM/dd/yyyy'); 
				} else if (field._fieldType === 'super-input-checkbox') {
					val = (reviewStepFieldValue.profileValue === true) ? 'Yes' : 'No';
				} else {
					val = reviewStepFieldValue.profileValue;
				}
		
				return val;
			}
		};

		function setRedirectLocation() {
			let returnUrl = !$stateParams.returnUrl || !$stateParams.returnUrl.length ? setLocationByParticipant() : $stateParams.returnUrl;
			function setLocationByParticipant() {
				let userId = ssActiveUserSvc.getEmployeeSync().id;
				let participantIds = routeVm.formInstance._steps.filter((step) =>  { return step._stepParticipant !== null}).map((step) => {
					return step._stepParticipant.employeeIds[0];
				});
				return participantIds.includes(userId) ? '/ec-forms/my-forms' : '/ec-dashboard';
			}
			return returnUrl;
		}

		function submitForm(step) {
			ssAsync(function*() {
				vm.loading = true;
				//	save the current instance step data
				//	force JSON API to think the step has been changed in case no change is made
				if (step._stepAction === ssEnum.stepAction.employeeProfileReview) {
					step.instance._employeeProfileReviewStepFieldValues.forEach(field => {
						delete field.sourceField;
					});

					step.instance._employeeProfileReviewStepFieldValues = step.instance._employeeProfileReviewStepFieldValues.slice();
				} else {
					step.instance.$$forceChangedState('formFieldValues');
				}

				step.instance.$save().then(success => {
					//	reset the status ID in case the form was rejected
					if (routeVm.formInstance._statusId === ssEnum.formStatus.rejected) {
						ssFormInstancesApi.updateStatus({
							id: routeVm.formInstance.id,
							newStatus: 0
						}).then(() => {
							postAndCheck(step);
						});
					} else {
						postAndCheck(step);
					}
				}).catch(error => {
					console && console.error && console.error('Error saving form', error);
					vm.loading = false;
				});
			})(); // eslint-disable-line
		}

		function postAndCheck(step) {
			ssFormInstancesApi.post({
				id: routeVm.formInstance.id,
				event: vm.submissionEvent,
				stepId: step.id
			}).then(() => {
				checkWorkflow().then((res) => {
					ssToastHlp.success(``,`form step ${customMessage}`, false);
				}).catch(() => {
					ssToastHlp.error(``,`form workflow timed out. Please refresh the page and try again`, false);
				}).finally(() => {
					$location.url(setRedirectLocation());
				});
			}).catch((error) => {
				console && console.error && console.error('Error submitting form', error);
			});
		}

		// check the '_inWorkflow' property of a form instance to determine if its safe to return to inbox
		function checkWorkflow() {

 			var deferred = $q.defer();
 			const MAXATTEMPTS = 24;
 			let attempts = 0;

 			function checking() {

 				attempts++;

 				if(attempts === MAXATTEMPTS) {
 					deferred.reject();
 					return;
 				}

 				ssAsync(function* () {

					var form = yield ssFormInstancesApi.get({
						id: routeVm.formInstance.id
					});

					if (form && form._inWorkflow === false) {
						deferred.resolve(form);
						vm.loading = false;
					} else {
						$timeout(checking, 500);
					}

				})();
 			}
 			checking();
			return deferred.promise;
		}

		(function() {
			vm.loading = true;

			formsHlp.sortByOrderField(routeVm.formTemplate._steps, routeVm.formTemplate._stepOrder);

			routeVm.formTemplate._steps.forEach((step, index, arr) => {
			//	find the matching form instance step
				step.instance = routeVm.formInstance._steps.find(s => s._formTemplateStepId === step.id);

			//	if we cant find a matching form instance step
			//	OR the step action of the step is 'complete form', return
				if (step.instance == null || step._stepAction === ssEnum.stepAction.formCompleted) { return; }

			//	copy reference of the matching instance step to the stepValues map
				stepValues[step.instance.id] = step.instance;

			//	bind non-json-api property to json-api property
				fieldRendererHlp.bindConfigObject($scope, `stepValues['${step.instance.id}']._formFieldValues`,
					`stepValues['${step.instance.id}'].formFieldValues`);

			//	create a default formFieldValues object if it doesnt already exist
				if (typeof step.instance._formFieldValues !== 'string' || step.instance._formFieldValues.length === 0) {
					step.instance._formFieldValues = '{}'; // will become json
				}

				if (step.instance._lastModifiedDtUtc == null) {
					step.instance.$$forceChangedState('formFieldValues');
				}

				// highlight the current step
				if (routeVm.formInstance._currentStepId === step.id) {
					vm.selectedStep = step;
					vm.selectedStep.currentStep = true;

					//emit the selected step so that the corresponding step can be selected in steps summary
					rAF(() => {
						ssEventSvc.emit('form-instance.entry.step-clicked', step.id);
					}, false);
				}

			//	If there are no fields then we can assume user doesn't have permissions to view the step and stop here.
				if(!routeVm.formTemplate._formFields.length) { return; }

			//	collect the fields that belong to the step
				step.fields = routeVm.formTemplate._formFields.filter(f => f._step.id === step.id);

			//	order the fields that belong to the step
				formsHlp.sortByOrderField(step.fields, step._fieldOrder);

			//	process the steps for visibility & editability
				step = logic.processStep({ tStep: step, formInstance: routeVm.formInstance });

				if (step._stepAction === ssEnum.stepAction.employeeProfileReview) {
					step.instance._employeeProfileReviewStepFieldValues.forEach((reviewStepFieldValue, fieldIndex) => {
						let sourceField = routeVm.formTemplate._steps
							.find(s => s.id === reviewStepFieldValue.sourceStepId).fields
							.find(f => f.id === reviewStepFieldValue.sourceFormFieldId);

						// the reference to the sourceField will be broken, which is needed to
						// separately PATCH review step fields as well as customize the layout with reviewWidth
						reviewStepFieldValue.sourceField = Object.assign({}, sourceField);

						// because the reference is broken, we need to cache the reference in order to set a watch
						vm.cachedSourceFields[fieldIndex] = sourceField;
						$scope.$watch(`vm.cachedSourceFields[${fieldIndex}].overrideVisibility`, (nv, ov) => {
							if (nv != null && nv != ov) {
								reviewStepFieldValue.sourceField.overrideVisibility = nv;
							}
						}); 

						// <cmp-field-renderer> is bound to an _employeeProfileReviewStepFieldValues object (reviewStepFieldValue).
						// nested within that object, we are interested in the value of the source/originating form field.
						// once we have that, we can directly update reviewStepFieldValue.formFieldValue, which is what will $save/patch 
						$scope.$watch(`routeVm.formTemplate._steps[${index}]
									.instance._employeeProfileReviewStepFieldValues[${fieldIndex}]
									.sourceField.step.formFieldValues['${reviewStepFieldValue.sourceFormFieldId}']`, (nv, ov) => {
							if (nv != null && nv != ov) {
								reviewStepFieldValue.formFieldValue = nv;
							}
						});
					});
				}
				vm.steps.push(step);
			});
			vm.loading = false;
		}());

		ssEventSvc.on('form-instance.entry.step-clicked', (stepId, scroll = true) => {
			const clickedStep = vm.steps.find(s => s.id === stepId);
			const clickedStepIdx = vm.steps.findIndex(s => s.id === stepId);
			if (clickedStep == null) { vm.selectedStep = 0; return; }
			vm.selectedStep = clickedStep;

			if (scroll) {
				$anchorScroll('fb-step-' + clickedStepIdx);
			}
		}, $scope);


	}
}

// #-- END Segment (/inbox/form-instance/segment-form) --# //
// #-----------------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentInboxFormInstanceForm', () => new Form('form'));
