import {Component} from 'base-component';
// #---------------------------------------------------# //
// #---- Component (cmp-properties-profile-review) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	form: '=',
	step: '='
};

var stepParticipantSearch;

class PropertiesProfileReview extends Component {
	/*@ngInject*/
	controller($scope, ssEnum, ssAsync, ssEmployeeApi, ssValidateHlp, ssValidationHlp, formsHlp, ssToastHlp) {
		const step = $scope.step;
		const form = $scope.form;
		const actions = $scope.actions = {};
		var initialSpecificParticipantId;

		// view model for tracking state
		const vm = $scope.vm = {
			specificParticipant: [],
			stepAction: ssEnum.stepAction,
			stepEventAction: ssEnum.stepEventAction,
			stepEventType: ssEnum.stepEventType,
			stepParticipantType: ssEnum.stepParticipantType,
			stepParticipant: ssEnum.stepParticipantType.getEnums().filter(type => { 
				if (form._isEmployeeInitiated) {
					return (type.value !== 0 && type.value !== 3);
				} else {
					return type.value !== 0;
				}
			}),
			validator: {
				required: ssValidationHlp.required()
			}
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line
			selectParticipant(option) {
				vm.buttonLabel = option.name;
				step._stepParticipant.participantType = option.value;
				step._stepParticipant.employeeIds = vm.specificParticipant = [];
			}
		};

		$scope.$watch('actions.emailEmployee', (nv, ov) => {
			if (nv === ov) {
				return;
			}

			handleAction(nv, 0, vm.stepEventAction.emailEmployee);
		});

		$scope.$watch('actions.emailFormSender', (nv, ov) => {
			if (nv === ov) {
				return;
			}

			handleAction(nv, 0, vm.stepEventAction.emailFormSender);
		});

		$scope.$watch('actions.emailSupervisor', (nv, ov) => {
			if (nv === ov) {
				return;
			}

			handleAction(nv, 0, vm.stepEventAction.emailSupervisor);
		});

		$scope.$watch('actions.emailOthers', (nv, ov) => {
			if (nv === ov) {
				return;
			}

			let action = handleAction(nv, 0, vm.stepEventAction.emailOthers);
			if (nv) {
				action.employeeIds = [];
			}
		});

		$scope.$watch('actions.emailIds.length', (nv, ov) => {
			if (nv === ov) {
				return;
			}
			handleEmail(0, 'emailIds');
		});

		$scope.$watch('step._stepParticipant', (nv, ov) => {
			if(!nv || nv === ov) { return; };
			// update the step permissions of the other steps with this participant
			formsHlp.updatePermissions({
				steps: form._steps,
				currentStep: $scope.step,
				newStepParticipant: nv,
				oldStepParticipant: ov
			});
			// directly update the permission object for this step, since it is skipped in the step permisison update logic
			$scope.step._permissions[0].participantType = nv.participantType;
			$scope.step._permissions[0].employeeId = nv.employeeIds.length ? nv.employeeIds[0] : '';
		}, true);

		(function init() {
			// Set the 'step participant dropdown menu label to a default value if there is no initial value - This emulates the functionality of the 'super-select-list' component, but since we are customizing it, we manually set this here
			if (step._stepParticipant.participantType == null) {
				vm.buttonLabel = 'Please Select';
			} else {
				vm.buttonLabel = ssEnum.stepParticipantType.byValue(step._stepParticipant.participantType);
			}
			// retrieve the specific step participant if an ID exists
			if (step._stepParticipant.employeeIds.length) {
				getEmployee(step._stepParticipant.employeeIds[0]).then((emp)=> {
					initialSpecificParticipantId = angular.copy(emp.id);
					vm.specificParticipant.push(emp);
				}).catch((err) => {
					ssToastHlp.error('there was an error retrieving the step participant name', '');
					console && console.error && console.error(err);
				});
			}
			initActions();
		}());

		$scope.$watch('vm.specificParticipant', (nv, ov) => {

			if(!nv.length || nv === ov) { return; };

			if (initialSpecificParticipantId && nv[0].id === initialSpecificParticipantId) {
				initialSpecificParticipantId = null;
				return;
			}

			if(stepParticipantSearch) { ssValidateHlp.resetValidation(stepParticipantSearch); }

			step._stepParticipant.employeeIds = vm.specificParticipant.map(emp => {
				return emp.id;
			});
		});

		function initActions() {
			actions.emailIds = [];

			if (step._stepEvents == null) {
				return;
			}

			step._stepEvents[0].eventActions.forEach(action => {
				if (action.eventAction === vm.stepEventAction.emailFormSender) {
					actions['emailFormSender'] = true;
				} else if (action.eventAction === vm.stepEventAction.emailSupervisor) {
					actions['emailSupervisor'] = true;
				} else if (action.eventAction === vm.stepEventAction.emailOthers) {
					actions['emailOthers'] = true;
					getEmployees(action.employeeIds);
				} else if (action.eventAction === vm.stepEventAction.emailEmployee) {
					actions['emailEmployee'] = true;
				}
			});

			// Default is 0 which is "On Submit", we are on the review step so we get "On Employee Profile Review" (5)
			step._stepEvents[0].eventType = vm.stepEventType.onEmployeeProfileReview;
		}

		function handleAction(value, actionIndex, eventAction) {
			var evtActions = step._stepEvents[actionIndex].eventActions;
			if (value) {
				var action = {	eventAction: eventAction };
				step._stepEvents[actionIndex].eventActions = [...evtActions, action];
				step._stepEvents = step._stepEvents.slice(); // hack to trigger change
				return action;
			} else {
				step._stepEvents[actionIndex].eventActions = evtActions.filter(a => a.eventAction !== eventAction);

				step._stepEvents = step._stepEvents.slice(); // hack to trigger change
			}
		}

		function handleEmail(index, property) {
			if (step._stepEvents[0] == null) {
				return;
			}
			let action, actions = step._stepEvents[0].eventActions;
			for (let i = 0; i < actions.length; i++) {
				action = actions[i];
				if (action.eventAction === vm.stepEventAction.emailOthers) {
					if ($scope.actions.hasOwnProperty(property)) {
						if ($scope.actions[property] == undefined || !!($scope.actions[property] == null)) {
							if (property === 'emailIds') {
								$scope.actions[property] = [];
							}
						}
						action.employeeIds = $scope.actions[property].map(val => { return val.id; });
					}

					step._stepEvents = step._stepEvents.slice(); // hack to trigger change
					break;
				}
			}
		}

		function getEmployee(id) {
			if (id == null) { return; }
			// supressing internal error handling for the get employee call so that the activation endpoint can handle all errors.
			return ssEmployeeApi.get({
				id: id,
				fields: 'firstName,lastName'
			}, '', false);
		}

		function getEmployees(ids) {
			if (ids == null || ids.length < 1) { return; }

			ssAsync(function* () {
				$scope.actions['emailIds'] = yield ssEmployeeApi.queryByIds({
					fields: 'firstName,lastName'
				},{ ids: ids });
			})();
		}

	};

	link(scope, elem, attrs) {
		// This element is needed for validation reset
		angular.element(document).ready(function () {
			stepParticipantSearch = angular.element(document.getElementById('step-participant-search'));
		});

	};
}

// #-- END Component (cmp-properties-profile-review) --# //
// #---------------------------------------------------# //

angular.module('ec-forms-components').directive('cmpPropertiesProfileReview', () => new PropertiesProfileReview(props, 'properties-profile-review') );
