import {Segment} from 'base-segment';
// #-------------------------------------------------------------# //
// #---- Segment (/packet-builder/settings/segment-settings) ----# //

class Settings extends Segment {
	/*@ngInject*/
	controller($injector, $scope, ssAsync, ssListSvc, ssFormTemplatesApi, ssValidationHlp, ssToastHlp, enumStepAction, formsHlp, rAF) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line

		// hookup segment logic
		const logic = $injector.get('segmentPacketBuilderSettingsSettingsLogic'); // jshint ignore:line

		$scope.validate = { // eslint-disable-line no-unused-vars
			conditionalRequired(val, fieldName) {
				return vm.packet._startupEmail.useStartupEmail ? ssValidationHlp.required()(val, fieldName) : true;
			}
		};

		const vm = $scope.vm = {
			loading: null,
			packet: $scope.packetVm.packetData,
			forms: [],
			stepAction: enumStepAction
		};

		if (vm.packet._startupEmail == null) {
			vm.packet._startupEmail = {};
		}

		ssAsync(function*() {
			try {
				// get form categories
				vm.categories = yield ssListSvc.getByExternalId('form.categories');
				// get packet forms
				if (vm.packet._formTemplateIds.length) {
					vm.loading = true;
					let templateOrder = vm.packet._formTemplateIds.toString();
					ssFormTemplatesApi.formTemplatesByIds(vm.packet._formTemplateIds, { include: 'steps' }).then((templates)=> {
						formsHlp.sortByOrderField(templates, templateOrder);
						vm.forms = templates;
						vm.forms.forEach(f => {
							f.steps = f._steps.filter(s => s._stepAction !== enumStepAction.formCompleted);
							formsHlp.sortByOrderField(f.steps, f._stepOrder);
							vm.loading = false;
						});
					}).catch((err) => {
						vm.loading = false;
						ssToastHlp.error('There was an error retrieving the packet forms', '');
						console && console.error && console.error('Error getting packet data', err);
					});
				}
			} catch(err) {
				ssToastHlp.error('There was an error processing form data', '');
				console && console.error && console.error('Error processing form data', err);
			}
		})();

		$scope.$watch('vm.packet._startupEmail.useStartupEmail', (nv, ov) => {
			if (nv === ov) { return; }
			if (nv) {
				rAF(() => document.getElementById(`message-textarea`).focus(), $scope);
			} else {
				vm.packet._startupEmail.message = '';
			}
		});
	}
}

// #-- END Segment (/packet-builder/settings/segment-settings) --# //
// #-------------------------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentPacketBuilderSettingsSettings', () => new Settings('settings') );
