import {Component} from 'base-component';
// #---------------------------------------# //
// #---- Component (cmp-step-renderer) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	step: '=',
	stepIndex: '='
};

class StepRenderer extends Component {
	/*@ngInject*/
	controller($scope, ssAsync, ssEmployeeApi, ssModalSvc, ssEventSvc, enumStepParticipantType, fieldsHlp, ssMultiInitiativeFeatureDetectorSvc, ssFlagSvc) {
		const formVm = $scope.formVm = $scope.$root.utils.findInParentScope('formVm', $scope);
		const routeVm = $scope.routeVm = $scope.$root.utils.findInParentScope('routeVm', $scope);
		const form = formVm.formData;
		const step = $scope.step;
		let moveTarget = null;

		// view model for tracking state
		const vm = $scope.vm = {
			stepParticipant: enumStepParticipantType,
			participantLabel: '',
			mifd: ssMultiInitiativeFeatureDetectorSvc,
		};

		$scope.$watch('stepIndex', (nv, ov) => {
			vm.stepIndex = parseInt(nv, 10);
		});

		$scope.$watch('step._stepParticipant', (nv, ov) => {
			if(!nv) { return; }
			if(nv.participantType === enumStepParticipantType.specificPerson) {
				if(nv.employeeIds && nv.employeeIds.length) {
					ssAsync(function*() {
						let emp = yield ssEmployeeApi.get({
							id: nv.employeeIds[0],
							fields: 'firstName,lastName'
						});
						vm.participantLabel = `${emp._firstName} ${emp._lastName}`;
						return;
					})();
				}
			} else if(nv.participantType || nv.participantType === 0) {
				vm.participantLabel = vm.stepParticipant.byValue(nv.participantType);
			} else {
				vm.participantLabel = 'select participant';
			}
		}, true);

		// behavior that needs to be bound to the view
		const go = $scope.go = {

			fieldGroupSettings(event, group) {
				event.stopPropagation();
				routeVm.vm.selectedFormObject = group;
				let config = group[0].config || group[0]._configJson;
				$scope.$emit('component-properties', config.cmp, group);
			},

			dropFieldGroup(event, index, item, type, step) {
			// If we are dragging an exisiting field group, return the exisitng field group...
				if (type === 'fieldGroup') {
					return item;
				}

				var droppedFields;

			//	Otherwise, it's a newly added field group
				droppedFields = fieldsHlp.getFields(item.fields, item.component);
			//  Remove certain fields (compensation, benefits, etc) for HRMS clients
				if (ssFlagSvc.checkState('hrms')){
					droppedFields = droppedFields.filter((f) =>{
						return !f._hideForHRMS;
					})
				}

			//	track field groups that don't allow multiple instances to prevent them from being added again.
				routeVm.added[item.fields] = !item.allowMultiple;

			// Display the properties panel when a field is dragged in and make it selected
				$scope.$emit('component-properties', item.component, droppedFields);
				routeVm.vm.selectedFormObject = droppedFields;

				// force model change so moving a field group allows save
				formVm.formData.$$forceChangedState('_ownerVersionNumber');
				formVm.modelChanged();

				return droppedFields;
			},

			movedFieldGroup(index, group, step) {
				// compensating for a bug in DnD where dropping a fieldGroup
					// back to an empty collection that it has previously been
					// the index is 1 instead of 0
					// \_()_/ -- whatevs
				if (step.fieldGroups.length === 0) {
					index = 0;
				}
				// setup the move target for the move action to pick up later
				moveTarget = {
					index, step
				};
				// add/remove field group
				moveTarget.step.fieldGroups[moveTarget.index] = group.filter(f => true);
				step.fieldGroups.splice(index, 1);
				// force model change so moving a field group allows save
				formVm.formData.$$forceChangedState('_ownerVersionNumber');
				formVm.modelChanged();
			},

			removeFieldGroup(group, step) {
			//	remove the group name from the 'added' collection
				removeAddedFields(group);
			//	splice out the field group from the step
				step.fieldGroups.splice(step.fieldGroups.indexOf(group), 1);

				formVm.updateFormFields();
			//	emit an event to update the visibility of the employee profile review step
				ssEventSvc.emit('emp-record-commit', false, group);
				formVm.modelChanged();
			},

			removeStep($event, index, list) {
				$event.stopPropagation();
				let selectedStepIndex;
				let removedStep;

			//	remove any associated field group names from the 'added' collection
				$scope.step.fieldGroups.forEach(removeAddedFields);

			//	determine the index of the step that should be selected once a step is removed
				if(list.indexOf(routeVm.vm.selectedFormObject) > index) {
					selectedStepIndex = list.indexOf(routeVm.vm.selectedFormObject) - 1;
				} else {
					selectedStepIndex = index-1 < 0 ? 0 : index-1;
				}
			//	show modal warning only if there are fields
				if ($scope.step.fieldGroups == null || $scope.step.fieldGroups.length === 0) {
				//	splice the step out of the lists and update the selected step
					removedStep = list.splice(index, 1)[0];
					form._steps.splice(form._steps.indexOf(removedStep), 1);
					$scope.$emit('component-properties', 'cmp-properties-step', list[selectedStepIndex]);
					routeVm.vm.selectedFormObject = list[selectedStepIndex];
				} else {
					var modalConfig = {
						title: 'Remove Step',
						content: `Removing this step will also remove any fields that are assigned to it.`,
						primaryText: 'Remove Step',
						secondaryText: 'Cancel',
						alwaysOnTop: true
					};
					ssModalSvc.showDestructive(modalConfig).then(() => {
						removedStep = list.splice(index, 1)[0];
					//	splice the step out of the lists and update the selected step
						form._steps.splice(form._steps.indexOf(removedStep), 1);
						$scope.$emit('component-properties', 'cmp-properties-step', list[selectedStepIndex]);
						routeVm.vm.selectedFormObject = list[selectedStepIndex];
					});
				}

				//	Since we have separated the removal of form fields from this logic, We need to also emit this event here to correctly remove any field groups that belonged to the removed step from the collection that saves the current fields which are to commit to the employee record
				$scope.step.fieldGroups.forEach((group) => {
					ssEventSvc.emit('emp-record-commit', false, group);
				});
				
				formVm.modelChanged();
			}
		};

		function removeAddedFields(group) {
			if (group[0]._fieldGroup && group[0]._fieldGroup.length) {
				routeVm.added[group[0]._fieldGroup] = false;
			}
		}

	}
}

// #-- END Component (cmp-step-renderer) --# //
// #---------------------------------------# //

angular.module('ec-forms-components').directive('cmpStepRenderer', () => new StepRenderer(props, 'step-renderer') );
