import { State } from 'base-state';
// #---------------------------------------# //
// #---- State (/form-builder/builder) ----# //


// view model that is shared by all segments on this route,
// used for segment coordination. Define initial state here.
const routeVm = {
	added: [],
	props: false,
	addingFields: false
};

const stateName = 'formBuilder.builder';

// config object for ui-router
// http://angular-ui.github.io/ui-router/site/#/api/ui.router.state.$stateProvider#methods_state
const stateConfig = {
	url: '/builder?deactivate',
	templateUrl: 'routes/form-builder/builder/builder.html',
	resolve: {
		$title: ['$title', $title => {
			return $title + ' (Form Builder)';
		}],
		isHRMSEnabled: /*@ngInject*/ function(ssMultiInitiativeFeatureDetectorSvc) {
			routeVm.isHRMSEnabled = ssMultiInitiativeFeatureDetectorSvc.isHRMSEnabled();
		}
	},
	$permissions: (permissionSvc) => permissionSvc.isPermittedAction('formTemplates', 'create') || permissionSvc.isPermittedAction('formTemplates', 'edit')
};



// #-- END State (/form-builder/builder) --# //
// #---------------------------------------# //

angular.module('ec-forms').config( /*@ngInject*/ ($stateProvider) => {
	$stateProvider.state(`base.${stateName}`, new State(stateConfig, routeVm));
});
