import {Segment} from 'base-segment';
// #-----------------------------------------# //
// #---- Segment (/index/segment-header) ----# //

class Header extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $location, ssAsync, ssValidationHlp, ssFormTemplatesApi, ssPacketsApi, ssToastHlp, enumFormAdministratorType) {
		const routeVm = $scope.routeVm; // eslint-disable-line

		// hookup segment logic
		const logic = $injector.get('segmentIndexHeaderLogic'); // eslint-disable-line

		const validator = $scope.validator = {
			required: ssValidationHlp.required()
		};

		$scope.formAdminEnums = enumFormAdministratorType;

		const vm = $scope.vm = {
			openMenu: false,
			invalidConfig: true,
			specificAdminNotRequired: true,
			form: {
				instanceType: null,
				instanceTitle: null,
				isEmployeeInitiated: false,
				category: {
					id: null
				},
				formAdministrator: {
					employeeId: null,
					formAdministratorType: null
				}
			},
			specificAdmin: [],
			isCreating: false,
			formAdminTypes: $scope.formAdminEnums.getEnums()
		};

		const go = $scope.go = { // eslint-disable-line

			create() {
				if (vm.isCreating) {return;}
				vm.isCreating = true;

				var api = vm.form.instanceType === 'form' ? ssFormTemplatesApi : ssPacketsApi;

				ssAsync(function*() {
					try {
						vm.template = yield api.add({}, {
							'_name': vm.form.instanceTitle,
							'_isEmployeeInitiated': vm.form.isEmployeeInitiated,
							'_category': vm.form.category,
							'_formAdministrator': vm.form.formAdministrator
						});

						$location.path(`/ec-forms/${vm.form.instanceType}-builder/${vm.template.id}/builder`);

					} catch (e) {
						ssToastHlp.error('', `Error creating ${vm.form.instanceType}`);
						console.error(`Error creating ${vm.form.instanceType}`, e);
					} finally {
						vm.isCreating = false;
					}
				})();
			},

			toggleMenu() {
				if(vm.openMenu === true){return;}
				vm.openMenu = !vm.openMenu;
				go.reset();
			},

			reset() {
				vm.form.instanceType = null;
				vm.form.instanceTitle = null;
				vm.form.isEmployeeInitiated = false;
				vm.form.formAdministrator.employeeId = null;
				vm.form.formAdministrator.formAdministratorType = null;
				vm.form.category.id = null;
				vm.specificAdmin = [];
			},

			cancel() {
				vm.openMenu = false;
				go.reset();
			}
		};

		
	/* 
	**	Here we create a collection of properties to watch whose values will determine the state of the 'create' button (enabled/disabled). The collection of watched propereties needs to changed based on what fields are rendered on screen, which is based on the type of form template we are creating.
	*/
		var propsToWatch = [
			'vm.form.category.id',
			'vm.form.instanceTitle',
			'vm.specificAdminNotRequired',
			'vm.form.formAdministrator.formAdministratorType'
		]

		var deregPropsWatch;

		$scope.$on('destroy', $scope.$watch('vm.form.instanceType', (nv, ov) => {

			if(!nv || nv===ov) {return;}

			let watchedProps;

			vm.altLabel = nv === 'form' ? 'template' : '';

		//	call the deregistration of the currently watched props if a watch already exists;
			if (deregPropsWatch) { deregPropsWatch() };

		//	if the user is creating a packet, then strip off the 'form admin' property from the collection of watch properties, otherwise, use the original collection
			if(nv !== 'form') {
				watchedProps = propsToWatch.slice().filter((p) => p !== 'vm.form.formAdministrator.formAdministratorType')
			} else {
				watchedProps = propsToWatch;
			}

		//	enables/disables the 'create' button based on these expressions having a value
			deregPropsWatch = $scope.$watchGroup(watchedProps, (nv, ov) => {
				vm.invalidConfig = nv.some(elem => (typeof elem !== 'number' && !elem) || (typeof elem === 'number' && elem < 0));
			});

		}));

	//	call the deregistration of the currently watched props when we leave;
		$scope.$on('destroy', deregPropsWatch);

	//	This ensures that we can't select 'form sender' as the form administrator for an employeed-initiated form template	
		$scope.$on('$destroy', $scope.$watch('vm.form.isEmployeeInitiated', (nv, ov) => {
			if(nv) {
				if(vm.form.formAdministrator.formAdministratorType === 1) {
					vm.form.formAdministrator.formAdministratorType = null;
				}
				vm.formAdminTypes = vm.formAdminTypes.filter((f) => {
					return f.value !== 1;
				})
			} else {
				vm.formAdminTypes = $scope.formAdminEnums.getEnums();
			}
		}));

	}

	link(scope, elem, attrs) {

		var deregWatch;

		scope.$on('$destroy', scope.$watch('vm.form.formAdministrator.formAdministratorType', (nv, ov) => {

			scope.vm.specificAdmin = [];

			if(nv === scope.formAdminEnums.specificPerson) {
				scope.vm.specificAdminNotRequired = false;
				deregWatch = scope.$watch('vm.specificAdmin', (nv, ov) => {
					if(!nv.length) {
						scope.vm.specificAdminNotRequired = false;
					} else {
						scope.vm.specificAdminNotRequired = true;
						scope.vm.form.formAdministrator.employeeId = nv[0].id;
					}
				});
			} else {
				scope.vm.specificAdminNotRequired = true;
				if(typeof deregWatch === 'function'){
					deregWatch();
				}
			}
		}));
	}
}

// #-- END Segment (/index/segment-header) --# //
// #-----------------------------------------# //

angular.module('ec-forms-segments').directive('segmentIndexHeader', () => new Header('header'));
