import {Component} from 'base-component';
// #----------------------------------------------# //
// #---- Component (cmp-secondary-assignment) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	field: '=',
	formfieldvalues: '='
};

class SecondaryAssignment extends Component {
	/*@ngInject*/
	controller($scope, fieldRendererHlp, ssFlagSvc) {
		const field = $scope.field;
		const formfieldvalues = $scope.formfieldvalues;

		fieldRendererHlp.bindConfigObject($scope, 'field._configJson', 'field.config');

		$scope.$watch(fieldRendererHlp.getVisibility(field), val => vm.visibility = val);

		// view model for tracking state
		const vm = $scope.vm = {
			formfieldvalues: formfieldvalues,
			hrmsEnabled: false
		};

		ssFlagSvc.ready.then(() => {
			if (ssFlagSvc.checkState('hrms')) {
				vm.hrmsEnabled = true;
			}
		});

		const go = $scope.go = {
			checkIfHeadingOrInstructions(field){
				switch(field._fieldType){
					case 'readOnlyHeading':
						return false;
					case 'readOnlySubheading':
						return false;
					case 'instructions':
						return false;
					default:
						return true;
				}
			},
			hideIfHrms: (field) => {
				if (vm.hrmsEnabled){
					return !field._hideForHRMS;
				}else {
					return true;
				}
			}
		}
	}
}

// #-- END Component (cmp-secondary-assignment) --# //
// #----------------------------------------------# //

angular.module('ec-forms-components').directive('cmpSecondaryAssignment', () => new SecondaryAssignment(props, 'secondary-assignment') );
