import { Component } from 'base-component';
// #---------------------------------------# //
// #---- Component (cmp-fl-share-form) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	template: '=',
};

class FlShareForm extends Component {
	/*@ngInject*/
	controller($scope, $element, $document, ssAsync, ssEventSvc, ssToastHlp, ssFormTemplatesApi) {
		// view model for tracking state
		const vm = $scope.vm = {
			formName: $scope.template._name,
			org: [],
			saving: false,
		};

		// get ref to modal-fade and show it
		const faderClass = 'shown';
		const fader = angular.element($document[0].getElementById('modal-fade'));
		fader.addClass(faderClass);

		// behavior that needs to be bound to the view
		const go = $scope.go = {
			cancel: close,

			share() {
				vm.saving = true;
				let org = vm.org[0];
				ssAsync(function*() {
					yield ssFormTemplatesApi.shareToOrg($scope.template.id, {
						destinationOrgId: org.Id,
						destinationTemplateName: vm.formName
					}).then(res => {
						if (res.status === -1 || res.status >= 400) {
							if (res.status === 422) {
								ssToastHlp.error(res.data.errors.Title, `Error sharing "${vm.formName}" to ${org.Name} (${org.Id})`);
							} else {
								ssToastHlp.error('', `Error sharing "${vm.formName}" to ${org.Name} (${org.Id})`);
							}
							console && console.error && console.error(`Error sharing "${vm.formName}" to ${org.Name} (${org.Id})`, res);
							vm.saving = false;
							return;
						}
						ssToastHlp.success('', `"${vm.formName}" has been shared to ${org.Name} (${org.Id})`);
						close();
					}).catch(err => {
						if (err.status === 422) {
							ssToastHlp.error(err.data.errors.Title, `Error sharing "${vm.formName}" to ${org.Name} (${org.Id})`);
						} else {
							ssToastHlp.error(err, `Error sharing "${vm.formName}" to ${org.Name} (${org.Id})`);
						}
						console && console.error && console.error(`Error sharing "${vm.formName}" to ${org.Name} (${org.Id})`, err);
						vm.saving = false;
					});
				})();
			}
		};

		// move the element to the body so that the z-index can be higher than the modal-fade
		angular.element(document.body).append($element);

		function close() {
			// remove the scope and element
			$scope.$destroy();
			$scope = null;
			$element.remove();
			$element = null;

			// hide modal-view
			fader.removeClass(faderClass);

			// emit event so parent can catch and hide widget so further clicks will show the component again
			ssEventSvc.emit('fl-share-form-cancel');
		}
	}
}

// #-- END Component (cmp-fl-share-form) --# //
// #---------------------------------------# //

angular.module('ec-forms-components').directive('cmpFlShareForm', () => new FlShareForm(props, 'fl-share-form'));
