import {Segment} from 'base-segment';

// #------------------------------------------# //
// #---- Segment (/my-forms/segment-content) ----# //

class Content extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $stateParams, $location, ssEnum, ssAsync, ssActiveUserSvc, ssFormInstancesApi, ssFormTemplatesApi, ssStorage, ssOrganizationSvc, ssToastHlp) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm;

		// hookup segment logic
		const logic = $injector.get('segmentMyFormsContentLogic');// jshint ignore:line

		var emplId = null;

		// view model for tracking state
		const vm = $scope.vm = { // jshint ignore:line
			selectedFilter: 'current'
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line

			filter(type) {
				vm.selectedFilter = type;
				$stateParams.activeTab = type;
				$location.search('activeTab', type);
			},

			getCurrentForms() {

				let params = {
					id: emplId,
					include: 'steps',
					'filter[search]': routeVm.search
				};

				if (resolveMethod() !== null) {
					params['resolvemethod'] = resolveMethod();
				};

				return {
					api: ssFormInstancesApi.queryCurrent,
					config: params
				};
			},

			getSentForms() {
				return {
					api: ssFormInstancesApi.querySubmitted,
					config: {
						id: emplId,
						include: 'steps',
						'filter[search]': routeVm.search
					}
				};
			},

			getCompletedForms() {
				return {
					api: ssFormInstancesApi.query,
					config: {
						id: emplId,
						include: 'steps',
						'filter[status]':  ssEnum.formStatus.complete,
						'filter[search]': routeVm.search
					}
				};
			},

			getInitiatedForms() {
				return {
					api: ssFormTemplatesApi.query,
					config: {
						include: 'steps,statusHistory',
						latestVersion: true,
						'filter[isemployeeinitiated]': 'true',
						'filter[name]': routeVm.search
					}
				};
			},

		};

		function resolveMethod() {
			const resolveMethod = $location.search().resolvemethod;
			if (resolveMethod === 'ReadModel' || resolveMethod === 'Mongo') {
				return resolveMethod;
			} else {
				return null;
			}
		};

		ssAsync(function* () {
			try{	
				try {
					emplId = yield ssActiveUserSvc.getIdentityByType('employee');
				} catch(err) {
					throw {error: err, msg: 'There was an error retrieving employee data. Forms will not display. Please see the console for more detail.'};
				}
	
				if ($stateParams.activeTab && $stateParams.activeTab !== 'undefined' && $stateParams.activeTab.length) {
					go.filter($stateParams.activeTab);
				} else {
					go.filter('current');
				}
			} catch (errObj) {
				ssToastHlp.error(errObj.msg, '', false);
				console && console.error && console.error(errObj.error);
			}
		})();
	}
}

// #-- END Segment (/my-forms/segment-content) --# //
// #------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentMyFormsContent', () => new Content('content'));
