import {Component} from 'base-component';
// #-------------------------------------# //
// #---- Component (cmp-box-creator) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	startingPoint: '=',
	boxCreated: '&'
};

class BoxCreator extends Component {
	/*@ngInject*/
	controller($scope) {
		// view model for tracking state
		const vm = $scope.vm = { // jshint ignore:line
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
		};
	}

	link(scope, elem, attrs) {
		const parent = elem.parent();
		const parentEl = parent[0];
		var anchorPoint;
		var parentRect;
		var top, left, height, width;

		scope.$watch('startingPoint', function (pos) {
			if (pos == null) { return; }

			anchorPoint = pos;
			parentRect = parentEl.getBoundingClientRect();
			height = width = 0;

			elem.css({
				top: `${pos.y}px`,
				left: `${pos.x}px`,
				height: `${height}px`,
				width: `${width}px`
			});

			parent.on('mousemove', sizeBox);
			parent.on('mouseup', finalizeBox);
		});

		function sizeBox(evt) {
			let leftCalc = window.pageXOffset || document.documentElement.scrollLeft || document.body.scrollLeft || 0;
			let topCalc = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0;

			const mousePos = {
				x: evt.pageX - parentRect.left - leftCalc,
				y: evt.pageY - parentRect.top - topCalc
			};

			top = (mousePos.y < anchorPoint.y ? mousePos.y : anchorPoint.y);
			left = (mousePos.x < anchorPoint.x ? mousePos.x : anchorPoint.x);
			height = Math.abs(mousePos.y - anchorPoint.y);
			width = Math.abs(mousePos.x - anchorPoint.x);

			elem.css({
				top: `${top}px`,
				left: `${left}px`,
				height: `${height}px`,
				width: `${width}px`
			});
		}

		function finalizeBox() {
			parent.off('mouseup', finalizeBox);
			parent.off('mousemove', sizeBox);
			// box needs to have some size
			if (height < 5 || width < 5) { return; }

			const bottom = parentEl.offsetHeight - (top + height);
			const right = parentEl.offsetWidth - (left + width);

			scope.boxCreated({$box: {
				top: (top / parentEl.offsetHeight) * 100,
				right: (right / parentEl.offsetWidth) * 100,
				bottom: (bottom / parentEl.offsetHeight) * 100,
				left: (left / parentEl.offsetWidth) * 100
			}});
		}
	}
}

// #-- END Component (cmp-box-creator) --# //
// #-------------------------------------# //

angular.module('ec-forms-components').directive('cmpBoxCreator', () => new BoxCreator(props, 'box-creator') );
