/*@ngInject*/
function logic($location) {
	// logic api
	return {
		buildInstance(recipient, baseObj, data, exclusions = []) {
			const instance = Object.assign({}, baseObj);
			const employeeId = recipient.id;

			if (exclusions != null && exclusions.length) {
				let excludedEmployeeIds = exclusions.map(e => e.id);
				if (excludedEmployeeIds.includes(employeeId)) { return; }
			}

			instance.employee = { employeeId };
			data.attributes.push(instance);
		},
		getDefaultBatchObj() {
			return { 
				_requestType: 0,
				_templateId: '',
				_filters: {},
				_includedEmployeeIds: [],
				_excludedEmployeeIds: []
			}
		},

	//	helper function to create a style object for each batch list
		getBatchListStyles(items) {
			let batchListHeight = (items.length * 100) + 'px';
			return {
				'height': batchListHeight,
				'transition': 'height 0.5s',
				'transition-delay': '1s'
			};
		}
	};


}
angular.module('ec-forms-segment-logic').factory('segmentSendHeaderLogic', logic);
