import { State } from 'base-state';
// #---------------------------------# //
// #---- State (//form-tracking) ----# //


// view model that is shared by all segments on this route,
// used for segment coordination. Define initial state here.
const routeVm = {};

const stateName = 'formTracking';

// config object for ui-router
// http://angular-ui.github.io/ui-router/site/#/api/ui.router.state.$stateProvider#methods_state
const stateConfig = {
	url: '/form-tracking',
	templateUrl: 'routes/form-tracking/form-tracking.html',
	resolve: {
		form: /*@ngInject*/ function() {},
		org: /*@ngInject*/ function(ssAsync, ssOrganizationSvc, ssToastHlp) {
			return ssAsync(function*() {
				try {
					routeVm.org = yield ssOrganizationSvc.getActiveOrg();
				} catch (err) {
					ssToastHlp.error('There was an error retrieving organization data. Due Dates may not display. Please see the console for more detail.', '', false);
					console && console.error && console.error('Error getting Org data', err);
				}
			})();
		},
		$title: /*@ngInject*/ () => {
			return 'Frontline Central - Track Forms';
		},
		isDIPEnabled: /*@ngInject*/ function(ssMultiInitiativeFeatureDetectorSvc) {
			return routeVm.isDIPEnabled = ssMultiInitiativeFeatureDetectorSvc.isDataIntegrationsEnabled();
		}
	},
	$permissions: (permissionSvc) => permissionSvc.isPermittedAction('formInstances', 'track')
};



// #-- END State (//form-tracking) --# //
// #---------------------------------# //

angular.module('ec-forms').config( /*@ngInject*/ ($stateProvider) => {
	$stateProvider.state(`base.${stateName}`, new State(stateConfig, routeVm));
});
