/*@ngInject*/
function logic(ssAsync, $interpolate) {
	// logic api
	return {
		definitiontoLocalData,
		removeFile,
		convertBracketValueToCsv
	};

	/**
	 * Convert a server mapping definition to a client's mapping definition
	 *
	 * @param {object} definition the PrintViewDefinition's printPreviewPage object that defines a page and it's related field mappings according to the server
	 */
	function definitiontoLocalData(definition, labels) {
		if (definition == null) { return; }
		return definition.pageObjects.map(d => {
			const obj = {
				type: d.fieldType || 'text',
				top: d.locationY,
				left: d.locationX,
				right: (100 - (d.width + d.locationX)),
				bottom: (100 - (d.height + d.locationY)),
				formFieldGuids: d.formFieldGuids,
				stringFormat: d.stringFormat || null,
				matchValue: d.matchValue
			};
			if(['text', 'date'].includes(obj.type)) {
				obj._template = `{{${d.formFieldGuids.map(x => `id${x.split('-').join('')}`).join('}} {{')}}}`;
				let value = $interpolate(obj._template)(labels);
				obj.templateString = convertBracketValueToCsv(value) || '';
			}
			return obj;
		});
	}

	/**
	 * Removed a file from the template
	 *
	 * @param {Object} template
	 */
	function removeFile(template) {
		return ssAsync(function*() {
			let file = template._files.find(file => file._fileId === template._printPreviewDefinition.templateFileId);
			if(file) {
				yield file.$remove();
			}
			template._printPreviewDefinition.printPreviewPages = [];
			template._printPreviewDefinition.templateFileId = null;
			yield template.$save();
			yield template.$refresh();
		})();
	}

	function convertBracketValueToCsv(value){
		if(!value.length) { return ''; }
		return value.split('] [').join(',').replace('[', '').replace(']','');
	}
}
angular.module('ec-forms-segment-logic').factory('segmentFormBuilderOverlayBuilderOverlayLogic', logic);
