import {Component} from 'base-component';
// #-----------------------------------------------# //
// #---- Component (cmp-secondary-assignments) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	field: '=',
	model: '=',
};

class SecondaryAssignments extends Component {
	/*@ngInject*/
	controller($scope, ssFlagSvc) {
		// view model for tracking state
		const field = $scope.field;
		
		const vm = $scope.vm = {
			hrmsEnabled: false
		};
		
		ssFlagSvc.ready.then(() => {
			if (ssFlagSvc.checkState('hrms')) {
				vm.hrmsEnabled = true;
			}
		});

		if (field.step){
			vm.formFieldValues = field.step.formFieldValues[field.id] ? field.step.formFieldValues[field.id] : '';
		}

		const go = $scope.go = {
			showGroupLabel: () => {
				let labelIsVisible = field.step ? vm.formFieldValues.length : true;
				return labelIsVisible;
			},
			hideIfHrms: (field) => {
				if (vm.hrmsEnabled){
					return !field._hideForHRMS;
				}else {
					return true;
				}
			}
		}
	}
}

// #-- END Component (cmp-secondary-assignments) --# //
// #-----------------------------------------------# //

angular.module('ec-forms-components').directive('cmpSecondaryAssignments', () => new SecondaryAssignments(props, 'secondary-assignments') );
