/*@ngInject*/
function logic() {
	// logic api
	return {
		// Map a category id to a name
		mapCategory(categories, id) {
			var ret = '';
			var tmp;

			if (id == null) { return ''; }
			tmp = categories.filter(v => { return v.value === id; });
			if (tmp.length > 0) {
				ret = tmp[0].description;
			}
			return ret;
		},

		/**
		* return an object containing the 'sort' query value that is passed to the enpoint
		* @param {obj} sort - The 'value' property of the selected sort option
		*/
		addSort(sort) {
			var o = {};
			o['sort[' + sort.field + ']'] = sort.dir;
			return o;
		},

		/**
		* return an object containing the 'filter' query value that is passed to the enpoint
		* @param {string} filter - The 'name' property of the selected filter option
		* @param {object} value - The 'value' property of the selected filter option
		*/
		addFilter(filter, value) {
			var o = {};
			o['filter['+ filter +']'] = value;
			return o;
		}

	};
}
angular.module('ec-forms-segment-logic').factory('segmentFormTemplatesContentLogic', logic);
