import {Component} from 'base-component';
// #------------------------------------------------# //
// #---- Component (cmp-emp-initiated-template) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	search: '=',
	filter: '=',
	type: '@',
	endpoint: '&'
};

class EmpInitiatedTemplate extends Component {
	/*@ngInject*/
	controller($scope, $state, ssAsync, ssListSvc, $location, ssActiveUserSvc, ssFormInstancesApi, ssEmployeeApi, ssToastHlp, ssStorage, enumFormAdministratorType) {

		const PAGESIZE = 50;
		var currentPage = 0,
			totalRetrieved = 0,
			getFormsPromise = null,
			count;

		const postTemplate = {
			'type': 'FormTemplate',
			'attributes': [{
				'dueDateUtc': null,
				'formTemplate': {
				'formTemplateId': ''
				},
				'employee':{
					'employeeId': ''
				}
			}]
		};

		// view model for tracking state
		const vm = $scope.vm = {
			formsAndPackets: [],
			showInfScroll: true,
			isLoading: false,
			isSearching: false,
			isSorting: false,
			sortCriteria: [{
				name: 'Form Category',
				value: {
					field: 'categoryname',
					dir: 'asc'
				}
			},{
				name: 'Form Administrator Type',
				value: {
					field: 'formadministratortype',
					dir: 'desc'
				}
			},{
				name: 'Form Name (A-Z)',
				value: {
					field: 'name',
					dir: 'asc'
				}
			}],

		};

		vm.sortBy = vm.sortCriteria[0].value;

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line

			initiateForm(form) {
				form.initiating = true;
				let redirectUrl = null;

				ssAsync(function* () {
					try {
					//	Get the user's info
						let userKey = yield ssActiveUserSvc.getIdentityByType('employee');

					//	throw an error if formAdministratorType is 1 (sender)
						if(form._formAdministrator.formAdministratorType === 1) {
							throw 'An Employee-Initiated form cannot be started by an employee who is also assigned as the Form Administrator.'
						}
					//	Grab a copy of the post object
						var post = angular.copy(postTemplate);
						post.attributes[0].formTemplate.formTemplateId = form.id;
						post.attributes[0].employee.employeeId = userKey;
					//	Send the form instance to myself
						let result = yield ssFormInstancesApi.send(post, { 'isemployeeinitiated': true });

					/*
					1. if the logged in user is the step participant of the first step, redirect to the form instance view, otherwise, show a toast message that the form was sent and remain on the form templates page
					*/
						if(userKey === result[0].currentStep._stepParticipant.employeeIds[0]) {
							let url = $state.href('base.formInstance', {
								formInstanceId: result[0].id,
								returnUrl: $location.url(),
								target: '_blank'
							});
							window.open(url, '_blank');
						} else {
							ssToastHlp.success(`"${result[0].name}" has been sent`, 'Form Sent', false);
						}
					} catch(error) {
						console && console.error && console.error(error);
						ssToastHlp.error(error,'Error', false);
					} finally {
						form.initiating = false;
					}
				})();
			},
		};

		//Reset page settings and fetch the data
		function refreshData() {
			currentPage = 0;
			totalRetrieved = 0;
			vm.formsAndPackets = [];
			fetchFormData();
		}

		//	Sorting
		$scope.$on('$destroy', $scope.$watch('vm.sortBy', (newval, oldval) => {
			if (newval === oldval) { return; }
			vm.formsAndPackets = [];
			vm.isSorting = true;
			vm.isLoading = false;
			vm.isSearching = false;
			vm.sortBy = newval;
			fetchFormData();
		}));

		// filter the forms by search value
		$scope.$on('$destroy', $scope.$watch('search', (newval, oldval) => {
			if (newval === oldval) { return; }
			vm.isSearching = true;
			vm.isLoading = false;
			vm.isSorting = false;
			refreshData(); 	// A new search should reset the page that we are on.
		}));

		// method to handle scrolling
		$scope.scrollData = function scrollData() {
			vm.isLoading = true;
			if ($scope.filter !== $scope.type && currentPage < 0) { return; }
			if (!vm.showInfScroll || getFormsPromise != null) { return; }
			fetchFormData();
		};

		function fetchFormData() {

			//	abort if a call is already in progress
			if (getFormsPromise != null) {
				getFormsPromise.$abort();
				getFormsPromise = null;
			}

			getFormsPromise = ssAsync(function*() {
				try {
				// 	update infinite scroll
					currentPage = vm.isSorting ? currentPage : currentPage+1;
					var offset = !vm.isSearching && !vm.isSorting ? (currentPage - 1) * PAGESIZE : 0
					totalRetrieved = vm.isSorting ? totalRetrieved : totalRetrieved + PAGESIZE;
				// 	get the passed in endpoint object
					var endpoint = $scope.endpoint();
				// 	add the limit and offset queries to the endpoint config object
					endpoint.config.limit = vm.isSorting ? totalRetrieved : PAGESIZE;
					endpoint.config.offset = offset;
				//	Add the sort filter
					endpoint.config = Object.assign(endpoint.config, sortOrNull(vm.sortBy));
				//	run the endpoint
					var allForms = yield endpoint.api(endpoint.config);
				// 	get a total count
					count = parseInt(allForms.$headers['total-results'], 10);
				// 	extend the form instance with necessary properties
					allForms = extendForms(allForms);
				//	filter out in-active forms
					allForms = allForms.filter(form => form.status !== 'inactive');
				//	Aggregate the list
					vm.formsAndPackets = vm.isSorting ? [...allForms] : [...vm.formsAndPackets, ...allForms];
				// 	figure out when we've reached the end of the list
					let diff = totalRetrieved - allForms.length;
					vm.showInfScroll = allForms.length < (count - diff);
				} catch (e) {
					vm.showInfScroll = false;
					console && console.error && console.error('Error getting data', e);
				} finally {
					vm.isLoading = false;
					vm.isSearching = false;
					vm.isSorting = false;
					getFormsPromise = null;
				}
			})();

			return getFormsPromise;
		}

		function extendForms(forms) {

			forms.forEach(form => {
				if(form._formAdministrator.employeeId !== null) {
					form.adminName = form._formAdministrator.name;
				} else {
					form.adminName = enumFormAdministratorType.byValue(form._formAdministrator.formAdministratorType) || 'None';
				}

			});

			return forms;
		}

		/**
		 * return an object containing the 'sort' query value that is passed to the enpoint
		 * @param {obj} sort - The 'value' property of the selected sort option
		 */
		function sortOrNull(sort) {
			var o = {};
			o['sort[' + sort.field + ']'] = sort.dir;
			return o;
		}

	}
}

// #-- END Component (cmp-emp-initiated-template) --# //
// #------------------------------------------------# //

angular.module('ec-forms-components').directive('cmpEmpInitiatedTemplate', () => new EmpInitiatedTemplate(props, 'emp-initiated-template') );
