import {Component} from 'base-component';
// #-----------------------------------------------# //
// #---- Component (cmp-properties-emp-record) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	fields: '=',
	canEdit: '='
};

class PropertiesEmpRecord extends Component {
	/*@ngInject*/
	controller($scope, ssEventSvc) {

		let actionableFields = $scope.fields.filter(f => { return f._fieldDefId != null });

	//	view model for tracking state
		const vm = $scope.vm = { //eslint-disable-line
			commit: !!actionableFields[0]._updateEmployeeRecord,
			hasExternalData: true
		};

		$scope.$watch('canEdit', (nv, ov) => {
			if (nv === false) {
				vm.commit = false;
			}
		});

		$scope.$watch('vm.commit', (nv, ov) => {
			if (nv === ov) {return;}

			for(let i = 0; i < $scope.fields.length; i++) {
				if(actionableFields.includes($scope.fields[i])) {
					$scope.fields[i]._updateEmployeeRecord = nv;
				}
			};
			ssEventSvc.emit('emp-record-commit', nv, $scope.fields);
		});

		// checks to see if every field in an editable field group has an external data property which we use to write back to the employee record. If a field group doesn't have a field with this property, then we can safely disable the 'commit to employe record' checkbox and prevent these fields from being displayed in the employee review step.
		vm.hasExternalData = actionableFields.every((field) => {
			return field._externalData && field._externalData !== null;
		});
	}
}

// #-- END Component (cmp-properties-emp-record) --# //
// #-----------------------------------------------# //

angular.module('ec-forms-components').directive('cmpPropertiesEmpRecord', () => new PropertiesEmpRecord(props, 'properties-emp-record') );
