import {Segment} from 'base-segment';
// #-----------------------------------------------------------# //
// #---- Segment (/form-builder/settings/segment-settings) ----# //

class Settings extends Segment {
	/*@ngInject*/
	controller($injector, $scope, ssAsync, ssListSvc, ssEmployeeApi, ssEnum, ssToastHlp, ssModalSvc, formsHlp, rAF) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // eslint-disable-line
		const logic = $injector.get('segmentFormBuilderSettingsSettingsLogic'); // eslint-disable-line
		const formVm = $scope.formVm;
		const formAdminEnums = ssEnum.formAdministratorType.getEnums();

		$scope.validator = logic.validator;

		// view model for tracking state
		const vm = $scope.vm = { // eslint-disable-line
			loading: false,
			form: formVm.formData,
			showSpecificPerson: false,
			formAdminList: formAdminEnums,
			specificEmployeeHolder: [],
			lastStepText: '',
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line
		};

		$scope.$watch('vm.form._formAdministrator.formAdministratorType', (nv, ov) => {
			if (!nv || nv <= 0 || nv === ov) { return; }
			if (nv !== ssEnum.formAdministratorType.specificPerson) {
				vm.form._formAdministrator.employeeId = null;
				vm.specificEmployeeHolder = [];
			}
		});

		$scope.$watch('vm.specificEmployeeHolder', (nv, ov) => {
			if (nv === ov) { return; }
			if (nv && nv.length) {
				vm.form._formAdministrator.employeeId = nv[0].id;
			} else {
				vm.form._formAdministrator.employeeId = null;
			}
		});

		// This ensures that we can't select 'form sender' as the form administrator for an employee-initiated form template
		$scope.$watch('vm.form._isEmployeeInitiated', nv => {
			if (nv) {
				if (vm.form._formAdministrator.formAdministratorType === ssEnum.formAdministratorType.formPacketSender) {
					ssModalSvc.showStandard({
						title: 'Invalid form administrator',
						content: 'An employee initiated form cannot have the form sender as its form administrator. Please choose another form administrator',
						primaryText: 'Ok',
						showSecondary: false,
						alwaysOnTop: true
					}).then(() => {
						vm.form._formAdministrator.formAdministratorType = null;
					})
				}
				vm.formAdminList = vm.formAdminList.filter(f => {
					return f.value !== 1;
				});
			} else {
				vm.formAdminList = formAdminEnums;
			}
		});
		
		// Initialization
		ssAsync(function*() {
			vm.loading = true;
			// Get form categories
			try {
				vm.categories = yield ssListSvc.getByExternalId('form.categories');
			} catch (e) {
				console && console.error && console.error('There was an error retrieving form categories', e);
				ssToastHlp.error('There was an error retrieving form categories', '');
			}

			yield vm.form.$refresh();
			formVm.modelChanged(false);

			// get the employee object of the specific form administrator, if it exists
			if (vm.form._formAdministrator.formAdministratorType != null && vm.form._formAdministrator.formAdministratorType === ssEnum.formAdministratorType.specificPerson) {
				if (vm.form._formAdministrator.employeeId) {
					try {
						var e = yield ssEmployeeApi.get({
							id: vm.form._formAdministrator.employeeId,
							fields: 'firstName,lastName'
						}, null, false);
						vm.specificEmployeeHolder.push(e);
					} catch (err) {
						// employee was deleted
						if (err.data.errors.Id === '404') {
							ssToastHlp.warn(
								'The assigned form administrator for this template cannot be found. Please choose a new form administrator, or change the form administrator type',
								'Form Administrator not found');
						} else {
							ssToastHlp.error('There was a problem retrieving the assigned form administrator. Please see the console.','Error');
							//error('There was a problem retrieving the assigned form administrator', err)
						}
					}
				}
			}

			formsHlp.sortByOrderField(vm.steps, vm.form._stepOrder);

		//	get a handle on the last step to get the step actions
			var lastStep = vm.form._steps.find(s => s._stepAction === ssEnum.stepAction.formCompleted);

			lastStep._stepEvents[0].eventActions.forEach(action => {
				if (action.eventAction === ssEnum.stepEventAction.emailOthers) {
					ssAsync(function*() {
						let emps = yield ssEmployeeApi.queryByIds({
							fields: 'firstName,lastName'
						}, {ids:action.employeeIds}, null, false);
						emps.forEach(e => {
							if (vm.lastStepText.length) { vm.lastStepText += ', '; }
							vm.lastStepText += `Email ${e._firstName} ${e._lastName}`;
						});
					})();
				} else {
					if (vm.lastStepText.length) { vm.lastStepText += ', '; }
					vm.lastStepText += ssEnum.stepEventAction.byValue(action.eventAction);
				}
			});

		//	filter out the form complete step from further processing
			vm.steps = vm.form._steps.filter(s => s._stepAction !== ssEnum.stepAction.formCompleted);

			vm.steps.forEach((step, i) => {
				// watch the custom notification to set the focus
				$scope.$watch(`vm.steps[${i}]._useCustomNotification`, (nv, ov) => {
					if (nv === ov) { return; }
					let stepIndex = i;
					if (nv) {
						rAF(() => document.getElementById(`step-ready-message-${step.id}`).focus(), $scope);
					} else {
						vm.steps[stepIndex]._notificationBody = '';
					}
				});

				$scope.$watch(`vm.steps[${i}]._useCustomResendNotification`, (nv, ov) => {
					if (nv === ov) { return; }
					let stepIndex = i;
					if (nv) {
						rAF(() => document.getElementById(`step-resent-message-${step.id}`).focus(),$scope);
					} else {
						vm.steps[stepIndex]._resendNotificationBody = '';
					}
				});

				
			//	add a custom property to each step which indicates if we should show the custom resend step message control
				step.showResendMessage = i+1 !== vm.steps.length ? vm.steps[i+1]._stepAction === ssEnum.stepAction.reviewApproveOrDeny : false; 

				let participant = step._stepParticipant;
				if (participant.participantType === ssEnum.stepParticipantType.specificPerson) {
					if (participant.employeeIds && participant.employeeIds.length) {
						ssAsync(function*() {
							let emp = yield ssEmployeeApi.get({
								id: participant.employeeIds[0],
								fields: 'firstName,lastName'
							});
							step.participantLabel = `${emp._firstName} ${emp._lastName}`;
						})();
					}
				} else if (participant.participantType || participant.participantType === ssEnum.stepParticipantType.employeeChosenWhenSent) {
					step.participantLabel = ssEnum.stepParticipantType.byValue(participant.participantType);
				}
			});

			formsHlp.initSaveInterrupter(vm.form);
			vm.loading = false;
		})();
	}

	link(scope, elem, attrs) {
		// This code is here in order to focus the super-employee-search element when selecting the 'specific person' option of the form administrator select menu
		scope.$watch('vm.form._formAdministrator.formAdministratorType', (nv, ov) => {
			angular.element(document).ready(function() {
				if (nv === ov) { return; }
				if (nv === 3) { // ssEnum.formAdministratorType.specificPerson
					let searchField = document.getElementById('form-admin-employee-search');
					let input = searchField.getElementsByTagName('input');
					input[0].focus();
				}
			});
		});
	}
}

// #-- END Segment (/form-builder/settings/segment-settings) --# //
// #-----------------------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentFormBuilderSettingsSettings', () => new Settings('settings') );
