import {Component} from 'base-component';
// #------------------------------------------------# //
// #---- Component (cmp-properties-custom-text) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	fields: '='
};

class PropertiesCustomText extends Component {
	/*@ngInject*/
	controller($scope, enumFieldWidths, fieldsHlp) {
		// view model for tracking state
		const vm = $scope.vm = { // eslint-disable-line
			field: $scope.fields[0],
			widths: enumFieldWidths.getEnums(),
		};

		let fieldValidator = fieldsHlp.getValidator(vm.field._fieldType);

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line
			validate(field) {
				if(typeof fieldValidator === 'function'){
					let result = fieldValidator.call(null, field);
					return !result ? true : result;
				}
				return true;
			}
		};
	}
}

// #-- END Component (cmp-properties-custom-text) --# //
// #------------------------------------------------# //

angular.module('ec-forms-components').directive('cmpPropertiesCustomText', () => new PropertiesCustomText(props, 'properties-custom-text') );
