import { Component } from 'base-component';
// #--------------------------------------------# //
// #---- Component (cmp-template-list-form) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	template: '=',
	org: '=',
};

class TemplateListForm extends Component {
	/*@ngInject*/
	controller($scope, $state, $location, buildConfig, ssAsync, ssActiveUserSvc, ssModalSvc, ssFormTemplatesApi, ssStatusApi, enumFormTemplateStatus, enumFormAdministratorType, 
				ssFormInstancesApi, ssArrayHlp, ssToastHlp, ssEventSvc, ssObjectHlp, ssSleepHlp, ssFlagSvc) {

		const routeVm = $scope.routeVm = $scope.$root.utils.findInParentScope('routeVm', $scope);

		const testOrgValue = buildConfig.sharedFormsOrg;

		const isIdmAdmin = ssObjectHlp.glean($scope, _ => _.$root.isPermitted.activeUser.idmAdministrator, false);

		const postTemplate = {
			'type': 'FormTemplate',
			'attributes': [{
				'dueDateUtc': null,
				'formTemplate': {
					'formTemplateId': ''
				},
				'employee': {
					'employeeId': ''
				}
			}]
		};

		const vm = $scope.vm = {
			pendingMessage: '',
			showShare: false,
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line
			delete(template) {
				var config = {
					title: 'Delete Form Template',
					content: 'Once deleted, this form template will be removed and cannot be retrieved.',
					primaryText: 'Delete Template',
					secondaryText: 'Cancel',
					alwaysOnTop: true
				};

				ssModalSvc.showDestructive(config).then(() => {
					template.isPending = true;
					vm.pendingMessage = `Deleting "${template._name}"`;
					ssAsync(function*() {
						try {
							yield template.$remove();
							yield ssSleepHlp.sleep();
							yield routeVm.refreshData();
							ssToastHlp.success(`"${template._name}" has been deleted`, '');
						} catch (e) {
							console && console.error && console.error(`Error deleting "${template._name}"`, e);
						} finally {
							template.isPending = false;
							vm.pendingMessage = null;
						}
					})();
				})
			},
			
			sendToSelf: ssAsync(function*(template) {
				template.isPending = true;
				vm.pendingMessage = `Initiating "${template._name}"`;
				let redirectUrl = null;

				//	Get the user's info
				let userKey = yield ssActiveUserSvc.getIdentityByType('employee');

				try {
					//	throw an error if formAdministratorType is 1 (sender)
					if (template._formAdministrator.formAdministratorType === enumFormAdministratorType.formPacketSender) {
						throw 'An Employee-Initiated form cannot be started by an employee who is also assigned as the Form Administrator.'
					}
					// Grab a copy of the post object
					var post = angular.copy(postTemplate);
					post.attributes[0].formTemplate.formTemplateId = template.id;
					post.attributes[0].employee.employeeId = userKey;
					// Send the form instance to myself & redirect
					let result = yield ssFormInstancesApi.send(post, { 'isemployeeinitiated': true });

					/*
					If the logged in user is the step participant of the first step, redirect to the form instance view, otherwise show a toast message that the form was sent.
					*/
					if(userKey === result[0].currentStep._stepParticipant.employeeIds[0]) {
						if (ssFlagSvc.checkState('fc-enable-merged-forms-modules')) {
							$state.go('base.formInstance', { 
								formInstanceId: result[0].id,
								returnUrl: '/ec-forms/my-forms?activeTab=current',
							});
						} else {
							$location.url(`/ec-forms/form-instance/${result[0].id}?returnUrl=/ec-forms/my-forms?activeTab=current`);
						}
					} else {
						ssToastHlp.success(`"${result[0].name}" has been sent`, 'Form Sent', false);
					}

					template.isPending = false;
					vm.pendingMessage = '';

				} catch (error) {
					console && console.error && console.error(error);
					ssToastHlp.error(error, 'Error', false);
					template.isPending = false;
					vm.pendingMessage = '';
				}
			}),

			send(template) {
				$state.go('base.send', {
					id: template.id,
					type: 'FormTemplate'
				});
			},

			preview(template) {
				$state.go('base.formBuilder.preview', {
					formId: template.id,
					returnTo: '/ec-forms/form-templates' 
				});
			},

			edit(template) {
				if (template.status === 'active') {
					var editTemplateConfig = {
						title: 'Edit Form Template',
						content: 'Editing will deactivate and disable it from being sent until it has been reactivated. Prior recipients will not receive changes made.',
						primaryText: 'Edit Template',
						secondaryText: 'Cancel',
						alwaysOnTop: true
					};

					ssModalSvc.showStandard(editTemplateConfig).then(() => {
						template.isPending = true;
						vm.pendingMessage = 'Deactivating "' + template._name + '"';

						ssFormTemplatesApi.editActive(template).then((deactivatedForm) => {
							$state.go('base.formBuilder.builder', {
								formId: deactivatedForm.id
							});
						}).catch((err) => {
							ssToastHlp.error('There was an error deactivating the form template', '');
							console && console.error && console.error(err);
						});
					});

				} else {
					if (ssFlagSvc.checkState('fc-enable-merged-forms-modules')) {
						$state.go('base.formBuilder.builder', {
							formId: template.id
						});
					} else {
						$location.url(`/ec-forms/form-builder/${template.id}/builder`);
					}
				}
			},

			copy(template) {
				template.isPending = true;
				vm.pendingMessage = `Copying ${template._name}`;
				ssAsync(function*() {
					try {
						var copiedForm = yield ssFormTemplatesApi.copy({
							id: template.id
						});
						yield ssSleepHlp.sleep();
						yield routeVm.refreshData();
						//	find the newly copied template in the list of templates and apply the highlight
						ssArrayHlp.findWhere(routeVm.displayTemplates, { id: copiedForm.id }).isHighlighted = true;
						ssToastHlp.success('', `${copiedForm._name} created`);
					} catch (e) {
						console && console.error && console.error(`Error copying "${template._name}"`, e);
					} finally {
						template.isPending = false;
						vm.pendingMessage = null;
					}
				})();
			},

			archive(template) {

				var config = {
					title: 'Archive Form Template',
					content: `${template._name} will not be deleted, but will be removed from this list.`,
					primaryText: 'Archive Template',
					secondaryText: 'Cancel',
					alwaysOnTop: true
				};

				ssModalSvc.showDestructive(config).then(() => {
					template.isPending = true;
					vm.pendingMessage = `Archiving "${template._name}"`;
					ssAsync(function*() {
						try {
							yield ssStatusApi.update({ id: template.id }, {
								statusId: enumFormTemplateStatus.archived,
								from: new Date().toISOString()
							});
							yield ssSleepHlp.sleep();
							yield routeVm.refreshData();
							ssToastHlp.success('', `"${template._name}" has been archived`);
						} catch (e) {
							console && console.error && console.error(`Error archiving "${template._name}"`, e);
						} finally {
							template.isPending = false;
							vm.pendingMessage = null;
						}
					})();
				});
			},

			deactivate(template) {
				let deactivateTemplateConfig = {
					title: 'Deactivate Form Template',
					content: 'Deactivating this template will disable it from being sent.',
					primaryText: 'Deactivate',
					secondaryText: 'Cancel',
					alwaysOnTop: true
				};

				ssModalSvc.showDestructive(deactivateTemplateConfig).then(() => {
					routeVm.loading = true;
					routeVm.loadingMessage = `Deactivating "${template._name}"`;

					ssFormTemplatesApi.editActive(template).then(updatedTemplate => {
						ssSleepHlp.sleep().then(() => {
							routeVm.refreshData().then(() => {
								routeVm.loading = false;
								routeVm.loadingMessage = null;
							}).catch(err => {
								ssToastHlp.error('There was a problem fetching the form templates', '');
								console && console.error && console.error(err);
							});
						});						
					}).catch(err => {
						ssToastHlp.error(`There was a problem deactivating "${template._name}"`, '');
						console && console.error && console.error(err);
					});
				});
			},

			canShare(template) {
				return isIdmAdmin && template.status === 'active' && $scope.org.id === testOrgValue;
			},

			share() {
				vm.showShare = true;
			}
		};

		ssEventSvc.on('fl-share-form-cancel', () => {
			vm.showShare = false;
		}, $scope);

	}
}

// #-- END Component (cmp-template-list-form) --# //
// #--------------------------------------------# //

angular.module('ec-forms-components').directive('cmpTemplateListForm', () => new TemplateListForm(props,
	'template-list-form'));
