import {Segment} from 'base-segment';
// #-------------------------------------------------------------# //
// #---- Segment (/form-builder/preview/segment-attachments) ----# //

class Attachments extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $window, ssAsync, filenameHlp, ssFormTemplatesApi) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line

		// hookup segment logic
		const logic = $injector.get('segmentFormBuilderPreviewAttachmentsLogic'); // jshint ignore:line

		// view model for tracking state
		const vm = $scope.vm = { // jshint ignore:line
			isLoading: true
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
			download(url) {
				$window.open(url, '_blank');
			}
		};

		ssAsync(function*() {
			let ids = routeVm.formData._files.map(file => file._fileId);
			try {
				vm.files = yield ssFormTemplatesApi.getFiles({}, {
					ids: ids
				});
				
				vm.files = vm.files.filter((file) => {
					return routeVm.formData._printPreviewDefinition ? file.id !== routeVm.formData._printPreviewDefinition.templateFileId : true;
				}).map(file => {
					let ext = filenameHlp.getExtension(file._filename);
					file.icon = filenameHlp.getIcon(ext);
					return file;
				});
			} catch(e) {
				console && console.log && console.log('Error getting file records', e);
			} finally {
				vm.isLoading = false;
			}
		})();
	}
}

// #-- END Segment (/form-builder/preview/segment-attachments) --# //
// #-------------------------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentFormBuilderPreviewAttachments', () => new Attachments('attachments') );
