import {Segment} from 'base-segment';
// #-------------------------------------------------# //
// #---- Segment (/form-instance/segment-header) ----# //

class Header extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $state, $stateParams, $http, $sce, $location, $window, ssActiveUserSvc, ssAsync, ssDateByOrgHlp, ssToastHlp, ssDateHlp, ssEnum, ssModalSvc, rAF, formsHlp) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm;
		routeVm.showSensitiveFields = false;
		const sensitiveFieldDefIds = [
			'aa736224-2a09-490a-b069-7cc945a040ea',
			'ba2d2021-59b6-45b9-9e4b-c501d064e012'
		];
		let instanceWithSensitive;
		let sensitiveFields;
		const sensitiveFieldValues = [];
		const redactedRegex = /[*]{6,}/g;

		// hookup segment logic
		const logic = $injector.get('segmentFormInstanceHeaderLogic'); // eslint-disable-line

		const vm = $scope.vm = { // eslint-disable-line
			fromView: $state.params.from || '',
			firstSave: routeVm.formTemplate._isEmployeeInitiated && ssDateHlp.isForeverPast(routeVm.formInstance._changedUtc),
			formIsComplete: routeVm.formInstance._statusId === ssEnum.formStatus.complete || routeVm.formInstance._statusId === ssEnum.formStatus.archived,
			deleting: false,
		};

		const go = $scope.go = { // eslint-disable-line

			toggleFormView: ssAsync(function*(showPDF) {
				if(showPDF){
					try {
						routeVm.isLoading = true;
						routeVm.loadingStatus = 'Generating PDF...';
						if(!routeVm.PDFdata) {
							routeVm.PDFdata = yield formsHlp.generatePDFOverlay(routeVm.formInstance, routeVm.showSensitiveFields);
							//	sanitize the '_string' property as a trusted URL for use in the iFrame
							document.getElementById("form-overlay-frame").src = $sce.trustAsResourceUrl(routeVm.PDFdata.data.data.attributes.string);
						}
						routeVm.showPDFOverlay = true
						routeVm.isLoading = false;
						routeVm.loadingStatus = '';
					} catch(err) {
						routeVm.isLoading = false;
						routeVm.loadingStatus = '';
						console && console.error && console.error(err);
					}
				} else {
					routeVm.showPDFOverlay = false;
				}
			}),

			toggleSensitiveFields: function(checked) {
				routeVm.showSensitiveFields = checked;

				ssAsync(function*() {
					try {
						if(routeVm.showPDFOverlay) {
							routeVm.isLoading = true;
							routeVm.loadingStatus = 'Generating PDF...';
						}
						if(instanceWithSensitive == null) {
							routeVm.isLoading = true;
							routeVm.loadingStatus = 'Retrieving sensitive values...';
							instanceWithSensitive = yield getFormInstance(routeVm.showSensitiveFields);
						}

						processSensitiveFields(instanceWithSensitive);

						routeVm.PDFdata = yield formsHlp.generatePDFOverlay(routeVm.formInstance, routeVm.showSensitiveFields);
					//	sanitize the '_string' property as a trusted URL for use in the iFrame
						document.getElementById("form-overlay-frame").src = $sce.trustAsResourceUrl(routeVm.PDFdata.data.data.attributes.string);

						routeVm.setFieldValidations();
						routeVm.isLoading = false;
						routeVm.loadingStatus = '';

					} catch(err) {
						routeVm.isLoading = false;
						routeVm.loadingStatus = '';
						console && console.error && console.error(err);
					}
				})();
			},

			save() {
				ssAsync(function*() {
					routeVm.isLoading = true;
					routeVm.loadingStatus = 'saving progress...';

					try {

						let reviewStep = routeVm.formInstance._steps.find(step => {
							return step._stepAction === ssEnum.stepAction.employeeProfileReview;
						});

					//	force change to _employeeProfileReviewStepFieldValues
						if (reviewStep) {
							reviewStep._employeeProfileReviewStepFieldValues = reviewStep._employeeProfileReviewStepFieldValues.slice();
						}

						for (let iStep of routeVm.formInstance._steps) {
							// Do not attempt to save disabled steps
							if (iStep.disabled || iStep._stepAction === ssEnum.stepAction.formCompleted) {
								continue;
							}
							yield iStep.$save(true, false, false);
						}

						routeVm.PDFdata = null;

						routeVm.isLoading = false;
						routeVm.loadingStatus = '';
						ssToastHlp.success(``, 'Progress Saved', false);

						if (vm.firstSave) {
							vm.firstSave = false;
							let modalConfig = {
								title: 'Progress has been saved. The form will appear in the "current" tab of My Forms until submitted.',
								primaryText: 'Continue',
								alwaysOnTop: true,
								showSecondary: false
							};
							ssModalSvc.showStandard(modalConfig);
						}

					} catch (err) {
						routeVm.isLoading = false;
						routeVm.loadingStatus = '';
						ssToastHlp.error('There was an error saving the form', 'Error', false);
						console && console.error && console.error(err);
					}
				})();
			},

			close() {
				$location.search('activeTab', null);
				if($stateParams.target && $stateParams.target === '_blank'){
					window.close();
				} else {
					$location.url(setRedirectLocation());
				}
			},

			print() {
				rAF(() => $window.print(), $scope);
			},

			showDelete() {
				let isFirstStep = routeVm.formInstance._currentStepId === routeVm.formTemplate._stepOrder.split(',')[0];
				return routeVm.formInstance._isEmployeeInitiated && isFirstStep;
			},

			deleteForm() {
				vm.deleting = true;
				var config = {
					title: 'Delete Form',
					content: 'Are you sure you want to delete this form? This form will be deleted and cannot be retrieved.',
					primaryText: 'Delete',
					secondaryText: 'Cancel'
				};

				ssModalSvc.showDestructive(config).then(() => {
					ssAsync(function*() {
						try {
							yield routeVm.formInstance.$remove();
							ssToastHlp.success('', `"${routeVm.formTemplate._name}" has been deleted`);
							let returnUrl = !$stateParams.from || !$stateParams.from.length ? setRedirectLocation() : $stateParams.from;
							$location.url(returnUrl);
						} catch (e) {
							ssToastHlp.error('', `Error deleting form "${routeVm.formTemplate._name}"`);
							console && console.error && console.error(`Error deleting form "${routeVm.formTemplate._name}"`, e);
						} finally {
							vm.deleting = false;
						}
					})();
				}).catch(() => {
					// user hit cancel
					vm.deleting = false;
				});
			},
		};

		ssAsync(function*() {

			// collect all 'sensitive fields, including RTE fields with sensitive field tokens
			sensitiveFields = routeVm.formTemplate._formFields.filter((field) => { 
				return field._sensitive === true || sensitiveFieldDefIds.includes(field._fieldDefId) || isRTEFieldWithSensitiveValues(field);
			});

			let sentDateMoment = ssDateByOrgHlp.utcIsoToRelativeMoment(routeVm.formInstance._createdUtc, routeVm.org);
			routeVm.formInstance.sentDate = sentDateMoment.calendar(null, {
				sameDay: '[Today at] LT',
				lastDay: '[Yesterday at] LT',
				lastWeek: 'ddd [at] LT',
				sameElse: 'L [at] LT'
			});
		})();

		function isRTEFieldWithSensitiveValues(field) {
			// we look for the 'sensitive' (no underscore) property of token fields because token fields collecttion does not pass through JSON api
			return Array.isArray(field._tokenFields) && !!field._tokenFields.find(field => field.sensitive);
		}

		//	Using $http to get the form instance here to avoid the data going through JSON API, which overwrites what's currently in memory (wipes out any user changes)
		function getFormInstance(includeSensitive){
			const baseUrl = buildConfig.endpoint.formTemplates;
			let url = `${baseUrl}/forminstances/${$stateParams.formInstanceId}?include=steps&includeSensitive=${includeSensitive}`;
			return $http({
				url,
				method: 'GET'
			});
		};

		function setRedirectLocation() {
			let returnUrl = !$stateParams.returnUrl || !$stateParams.returnUrl.length ? setLocationByParticipant() : $stateParams.returnUrl;
			function setLocationByParticipant() {
				let userId = ssActiveUserSvc.getEmployeeSync().id;
				let participantIds = routeVm.formInstance._steps
					.filter(step => step._stepParticipant !== null)
					.map(step => step._stepParticipant.employeeIds[0]);
				return participantIds.includes(userId) ? '/ec-forms/my-forms?activeTab=current' : '/ec-dashboard';
			}
			return returnUrl;
		}

		// this logic handles retrieving the form instance with sensitive values (without running it through JSON API) and replacing the matching form field values inline with the sensitive values
		function processSensitiveFields(instanceWithSensitive) {
			sensitiveFields.forEach((field) => {

				// match up the steps for the data we want to replace
				let refStep = instanceWithSensitive.data.included.find(obj => obj.attributes.formFieldValues.includes(field.id));

				let step = routeVm.formInstance._steps.find(step => step._formFieldValues.includes(field.id));

				// an example of where a matching ref step would not be present is in the case of a form that contains an employee profile review step - only the participant of that step (or admin), has permissions to view/edit it.
				if(refStep && step && step._meta.permissions.permittedActions.viewSensitive) {
					// update profile review step values
					if(step._stepAction === ssEnum.stepAction.employeeProfileReview) {
						if(!routeVm.showSensitiveFields) {
							step._employeeProfileReviewStepFieldValues.find(pfield => pfield.formFieldId === field.id).profileDisplayValue = '******';
							return;
						}
						// capture the value for employee profile review step sensitive field
						if(!sensitiveFieldValues[field.id]) {
							sensitiveFieldValues[field.id] = refStep.attributes.employeeProfileReviewStepFieldValues.find(pfield =>  pfield.formFieldId === field.id).profileValue;
						}
						step._employeeProfileReviewStepFieldValues.find(pfield => pfield.formFieldId === field.id).profileDisplayValue = sensitiveFieldValues[field.id];
						return;
					}

					// cache sensitive field values
					if(!sensitiveFieldValues[field.id]) {
						sensitiveFieldValues[field.id] = {
							visibleValue: JSON.parse(refStep.attributes.formFieldValues)[field.id],
							redactedValue: step.formFieldValues[field.id]
						}
					}
					// update sensitive field values via cache
					if(step.formFieldValues[field.id]) {
						step.formFieldValues[field.id] = step.formFieldValues[field.id].search(redactedRegex) > -1 ? sensitiveFieldValues[field.id].visibleValue : sensitiveFieldValues[field.id].redactedValue;
					}
				}
				// finally, redact/show the field
				field.isRedacted = !routeVm.showSensitiveFields;
			});
		}
	}
}

// #-- END Segment (/form-instance/segment-header) --# //
// #-------------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentFormInstanceHeader', () => new Header('header') );
