import {Component} from 'base-component';
// #----------------------------------------# //
// #---- Component (cmp-checkbox-group) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	field: '='
};

class CheckboxGroup extends Component {
	/*@ngInject*/
	controller($scope, $attrs, $window) {

		const array = new Uint32Array(1);
		var crypto = $window.crypto || $window.msCrypto;
		var radioGroupId = crypto.getRandomValues(array);

		// view model for tracking state
		const vm = $scope.vm = {
			template: '',
			validate: () => true,
			isDisabled: $scope.field.disabled
		};
		
		if ($scope.field._required) {
			vm.validate = (val, fieldName) => {
				// if any options in the val are checked (true) then we are validated
				if($scope.field.config.multiple) {
					for (var i = 0; i < $scope.field._options.length; i++) {
						if (val[`${$scope.field.id}-${i}`] === true) { return true; }
					}
				}
				if(val[`${$scope.field.id}`]) { return true; }
				return `${fieldName} is required.`;
			};
		}

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line
		};

		$scope.$watch('field.config.multiple', (val) => {
			$scope.field.config.selectType = val === false ? 'super-input-radio' : 'super-input-checkbox';
			renderTemplate($scope.field.config.selectType);
		});	

		function renderTemplate(tag) {
			if (tag == null) { return; }
			if (tag === 'super-input-radio') {
				vm.template = `<${tag}
					ng-repeat="option in field._options track by $index"
					label="{{option}}"
					value="{{option}}"
					name="radio-group-${radioGroupId[0]}"
					model="field.step.formFieldValues[\'' + field.id + '\']"
					is-disabled="vm.isDisabled"
					edit-mode="field.editMode"
					ng-style="{width: field.config.colwidth}"
					></${tag}>`;
			} else {
				vm.template = `<${tag}
					ng-repeat="option in field._options track by $index"
					label="{{option}}"
					value="{{option}}"
					name="{{option}}"
					model="field.step.formFieldValues[\'' + field.id + '-' + $index + '\']"
					required="field._required"
					is-disabled="vm.isDisabled"
					edit-mode="field.editMode"
					ng-style="{width: field.config.colwidth}"
					></${tag}>`;
			}
		}
	}

}

// #-- END Component (cmp-checkbox-group) --# //
// #----------------------------------------# //

angular.module('ec-forms-components').directive('cmpCheckboxGroup', () => new CheckboxGroup(props, 'checkbox-group'));
