import { Segment } from 'base-segment';
// #------------------------------------------------------# //
// #---- Segment (/inbox/form-instance/segment-close) ----# //

class Close extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $state, $stateParams, $window, $location, rAF, ssAsync, ssToastHlp, ssModalSvc, ssDateHlp, enumFormStatus, ssEnum, ssActiveUserSvc, $q) {
		
		const routeVm = $scope.routeVm;

		// hookup segment logic
		const logic = $injector.get('segmentInboxFormInstanceCloseLogic'); // eslint-disable-line

		const vm = $scope.vm = {
			fromView: $state.params.from || '',
			firstSave: routeVm.formTemplate._isEmployeeInitiated && ssDateHlp.isForeverPast(routeVm.formInstance._changedUtc),
			formIsComplete: routeVm.formInstance._statusId === enumFormStatus.complete || routeVm.formInstance._statusId ===
				enumFormStatus.archived,
			deleting: false
		};

		const go = $scope.go = { // eslint-disable-line no-unused-vars
			save() {
				ssAsync(function*() {
					vm.loading = true;
					try {
						let reviewStepSourceFields = [];

						let reviewStep = routeVm.formInstance._steps.find(step => {
							return step._stepAction === ssEnum.stepAction.employeeProfileReview;
						});

						if (reviewStep) {
							
							//cache the sourceField in order to re-add after step save; server does not provide a full form field object
							reviewStepSourceFields = reviewStep._employeeProfileReviewStepFieldValues.map(reviewStepFieldValue => {
								return {
									sourceFormFieldId: reviewStepFieldValue.sourceFormFieldId,
									sourceField: reviewStepFieldValue.sourceField,
								};
							});

							//force change to _employeeProfileReviewStepFieldValues
							reviewStep._employeeProfileReviewStepFieldValues = reviewStep._employeeProfileReviewStepFieldValues.slice();
						}

						//Do not deep save formInstance, need to check step.disabled first
						yield routeVm.formInstance.$save(false, true, false);
	
						for (let iStep of routeVm.formInstance._steps) {
							//Do not attempt to save disabled steps
							if (iStep.disabled) {
								continue;
							}
	
							let step = yield iStep.$save(true, true, false);

							//once step is saved, need to grab the cached sourceField (reviewStepSourceFields) and add them back to _employeeProfileReviewStepFieldValues 
							if (step._stepAction === ssEnum.stepAction.employeeProfileReview) {
								step._employeeProfileReviewStepFieldValues = step._employeeProfileReviewStepFieldValues.map(val => {
									let reviewStepSourceField = reviewStepSourceFields.find(field => field.sourceFormFieldId === val.sourceFormFieldId);
									val.sourceField = reviewStepSourceField.sourceField;
									return val;
								});
							}
						}

						vm.loading = false;
						ssToastHlp.success(``, 'Progress Saved', false);

						if (vm.firstSave) {
							vm.firstSave = false;

							let modalConfig = {
								title: 'Progress has been saved. The form will appear in My Forms until submitted.',
								primaryText: 'Continue',
								alwaysOnTop: true,
								showSecondary: false
							};
							ssModalSvc.showStandard(modalConfig);
						}

					} catch (err) {
						vm.loading = false;
						ssToastHlp.error(`There was an error saving the form`, 'Error', false);
						console && console.error && console.error('ERROR saving formInstance', err);
					}
				})();
			},

			close() {
				$location.search('activeTab', null);
				$location.url(setRedirectLocation());
			},

			print() {
				rAF(() => $window.print(), $scope);
			},

			showDelete() {
				let isFirstStep = routeVm.formInstance._currentStepId === routeVm.formTemplate._stepOrder.split(',')[0];
				return routeVm.formInstance._isEmployeeInitiated && isFirstStep;
			},

			deleteForm() {
				vm.deleting = true;
				var config = {
					title: 'Delete Form',
					content: 'Are you sure you want to delete this form? This form will be deleted and cannot be retrieved.',
					primaryText: 'Delete',
					secondaryText: 'Cancel'
				};

				ssModalSvc.showDestructive(config).then(() => {
					ssAsync(function*() {
						try {
							yield routeVm.formInstance.$remove();
							ssToastHlp.success('', `"${routeVm.formTemplate._name}" has been deleted`);
							// $state.go('base.index', {}, { reload: true });
							let returnUrl = !$stateParams.from || !$stateParams.from.length ? setRedirectLocation() : $stateParams.from;
							$location.url(returnUrl);
						} catch (e) {
							ssToastHlp.error('', `Error deleting form "${routeVm.formTemplate._name}"`);
							console && console.error && console.error(`Error deleting form "${routeVm.formTemplate._name}"`, e);
						} finally {
							vm.deleting = false;
						}
					})();
				}).catch(() => {
					// user hit cancel
					vm.deleting = false;
				});
			},
		};

		function setRedirectLocation() {
			let returnUrl = !$stateParams.returnUrl || !$stateParams.returnUrl.length ? setLocationByParticipant() : $stateParams.returnUrl;
			function setLocationByParticipant() {
				let userId = ssActiveUserSvc.getEmployeeSync().id;
				let participantIds = routeVm.formInstance._steps.filter((step) =>  { return step._stepParticipant !== null}).map((step) => {
					return step._stepParticipant.employeeIds[0];
				});
				return participantIds.includes(userId) ? '/ec-forms/my-forms' : '/ec-dashboard';
			}
			return returnUrl;
		}
	}
}

// #-- END Segment (/inbox/form-instance/segment-close) --# //
// #------------------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentInboxFormInstanceClose', () => new Close('close'));
