import { State } from 'base-state';
// #--------------------------------------# //
// #---- State (/inbox/form-instance) ----# //

// view model that is shared by all segments on this route,
// used for segment coordination. Define initial state here.
const routeVm = {
	props: false,
	formInstance: null,
	formTemplate: null,
	hasPDFOverlay: null,
	showPDFOverlay: null
};

const stateName = 'myFormsFormInstance';

// config object for ui-router
// http://angular-ui.github.io/ui-router/site/#/api/ui.router.state.$stateProvider#methods_state
const stateConfig = {
	url: '/form-instance/{formInstanceId:[0-9A-Za-z-]+}?from&returnUrl',
	templateUrl: 'routes/inbox/form-instance/form-instance.html',
	resolve: {
		formInstanceCompletion: /*@ngInject*/ function ($stateParams, $state, $sce, ssAsync, ssFormInstancesApi, ssFormTemplatesApi, ssToastHlp, enumStepAction, enumFormStatus) {

			return ssAsync(function* () {
				try {

					routeVm.hasPDFOverlay = false;
					routeVm.showPDFOverlay = false;

					//	Need to get the form Instance and Form Template here
					routeVm.formInstance = yield ssFormInstancesApi.get({
						id: $stateParams.formInstanceId,
						include: 'steps'
					}, null, false);

					routeVm.formTemplate = yield ssFormTemplatesApi.get({
						id: routeVm.formInstance._formTemplate.formTemplateId,
						include: 'steps,formFields,files'
					});

				//	Check to see if the form instance has a PDF overlay
					routeVm.hasPDFOverlay = routeVm.formTemplate._printPreviewDefinition && routeVm.formTemplate._printPreviewDefinition.templateFileId != null;

				//	Generate & display the PDF overlay for completed or archived form instances upon page load	
					if (routeVm.hasPDFOverlay && (routeVm.formInstance._statusId === enumFormStatus.complete || routeVm.formInstance._statusId === enumFormStatus.archived)) {

					//	set a flag to toggle form versions
						routeVm.showPDFOverlay = true
					
						let formFieldValues = routeVm.formInstance._steps.filter((step) => {
							return step._stepAction !== enumStepAction.formCompleted
						}).map((step) => {
							return step._formFieldValues;
						}).reduce((a, c) => {
							a = typeof a === 'string' ? JSON.parse(a) : a;
							return Object.assign(a, JSON.parse(c));
						});

						ssFormInstancesApi.generatePDFOverlay({
							formInstanceid: routeVm.formInstance.id,
							formFieldValues: formFieldValues,
							showSensitive: false
						}).then((res) => {

						//	sanitize the '_string' property as a trusted URL for use in the iFrame
							document.getElementById("form-overlay-frame").src = $sce.trustAsResourceUrl(res.data.data.attributes.string);

						}).catch((err) => {
							ssToastHlp.error('There was an error generating the PDF', '');
							console && console.error && console.error(err);
						});
					}
				} catch (e) {
					if (e.status === 403) {
						$state.transitionTo('403');
					} else {
						ssToastHlp.error(e.data.errors.Title, 'There was a problem loading the form.');
					}
					console.error('Error getting form data', e);
				}
			})();
		},
		org: /*@ngInject*/ function(ssAsync, ssOrganizationSvc, ssToastHlp) {
			return ssAsync(function*() {
				try {
					routeVm.org = yield ssOrganizationSvc.getActiveOrg();
				} catch (err) {
					ssToastHlp.error('There was an error retrieving organization data. Due Dates may not display. Please see the console for more detail.', '', false);
					console && console.error && console.error('Error getting Org data', err);
				}
			})();
		},
		$title: /*@ngInject*/ (formInstanceCompletion) => {
			return `Frontline Central - ${routeVm.formInstance.name}`;
		}
	}
};

// #-- END State (/inbox/form-instance) --# //
// #--------------------------------------# //

angular.module('ec-forms').config( /*@ngInject*/($stateProvider) => {
	sidekick.events.on('feature-flags-ready', (sff) => {
		if(sff.checkState('fc-enable-merged-forms-modules')) {
			stateConfig.redirectToState = 'base.formInstance';
		}
		$stateProvider.state(`base.${stateName}`, new State(stateConfig, routeVm));
	}, {greedy: true});
});
