import {Component} from 'base-component';
// #------------------------------------------------# //
// #---- Component (cmp-properties-text-editor) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	text: '@'
};

class PropertiesTextEditor extends Component {
	/*@ngInject*/
	controller($scope) {
		// view model for tracking state
		const vm = $scope.vm = {
			test: 'testing...'
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = {
			click(evt) {
				vm.test = $scope.text;
			}
		};
	}
}

// #-- END Component (cmp-properties-text-editor) --# //
// #------------------------------------------------# //

angular.module('ec-forms-components').directive('cmpPropertiesTextEditor', () => new PropertiesTextEditor(props, 'properties-text-editor') );
