import {Component} from 'base-component';
// #-------------------------------------------------# //
// #---- Component (cmp-properties-custom-input) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	fields: '='
};

class PropertiesCustomInput extends Component {
	/*@ngInject*/
	controller($scope, enumFieldHeights, enumFieldWidths) {
		// view model for tracking state
		const vm = $scope.vm = { //eslint-disable-line
			field: $scope.fields[0],
			heights: enumFieldHeights.getEnums(),
			widths: enumFieldWidths.getEnums()
		};

		if (vm.field._label == null || vm.field._label.length === 0) {
			vm.field._label = 'Input Label';
		}
	}
}

// #-- END Component (cmp-properties-custom-input) --# //
// #-------------------------------------------------# //

angular.module('ec-forms-components').directive('cmpPropertiesCustomInput', () => new PropertiesCustomInput(props, 'properties-custom-input') );
