import {Segment} from 'base-segment';
// #--------------------------------------------------------------------# //
// #---- Segment (/form-builder/overlay-builder/segment-data-panel) ----# //

class DataPanel extends Segment {
	/*@ngInject*/
	controller($injector, $scope, ssEventSvc, ssArrayHlp, ssStringHlp, ssMultiInitiativeFeatureDetectorSvc, formsHlp, fieldsHlp) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line
		let uniqueFieldGroupNames = null;
		const boxFieldTypes = {
			text: [
				'super-select-list',
				'super-input-text',
				'super-input-redacted',
				'readOnlyText',
				'super-digital-signature',
				'text'
			],
			date: [
				'super-input-datepicker',
				'super-iso-org-date',
				'readOnlyDate',
			],
			checkbox: [
				'cmp-checkbox-group',
				'super-select-list',
				'readOnlyText',
			]
		};

		const dateTimeRegex = /-(\d{13})?$/;

		// hookup segment logic
		const logic = $injector.get('segmentFormBuilderOverlayBuilderDataPanelLogic'); // jshint ignore:line

		// view model for tracking state
		const vm = $scope.vm = {
			steps: [],
			fields: [],
			fieldGroups: []
		};
		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
			startExpanded(group) {
				let formIds = group.fields.map((field) => {
					return field.id;
				});
				return !!routeVm.selectedBox.formFieldGuids.filter(e => formIds.indexOf(e) !== -1).length;
			}
		};

		// filter out any fields we don't need to process (custom 'text' field is not mappable)
		let fields = routeVm.formTemplate._formFields.filter(field => field._active);

		// remove fields not viewable for HRMS orgs
		if (ssMultiInitiativeFeatureDetectorSvc.isHRMSEnabled()) {
			fields = fields.filter(f => {
				let field = fieldsHlp.getFieldById(f);
				if (field == null) { return true; } // return true to allow this 'f' through
				return !field._hideForHRMS; // remove HRMS fields
			});
		}

		ssEventSvc.on('pvb.select-field', selectField, $scope);
		ssEventSvc.on('pvb.select-option', selectOption, $scope);

		function selectField(field) {
			const fieldIdx = routeVm.selectedBox.formFieldGuids.indexOf(field.id);
			if (fieldIdx >= 0) {
				routeVm.selectedBox.formFieldGuids.splice(fieldIdx, 1);
			} else {
				routeVm.selectedBox.formFieldGuids.push(field.id);
			}
		}

		function selectOption(field, opt) {
			routeVm.selectedBox.formFieldGuids = [field.id];
			if (opt != null) {
				routeVm.selectedBox.matchValue = opt;
			}
		}

	//	setup watchers
		$scope.$watch('routeVm.selectedBox', box => {
			if (box == null) {
				vm.steps = [];
				return;
			}
			buildVmSteps(box);
			calcFieldsPresent(box._template || '');
		});
		$scope.$watch('routeVm.selectedBox._template', t => {
			if (t == null) { return; }
			calcFieldsPresent(t);
		});

		function calcFieldsPresent(template) {
			fields.forEach(f => {
				switch (routeVm.selectedBox.type) {
					case 'text':
						f.isAdded = template.includes(f.uniqueId);
						break;
					case 'date':
						f.isAdded = template.includes(f.uniqueId);
						break;
				}
			});
		}

		// builds the list of fields that get displayed in the data mapping panel
		function buildVmSteps(box) {

			// sort the steps by the step order
			const orderedSteps = formsHlp.sortByOrderField(routeVm.formTemplate._steps, routeVm.formTemplate._stepOrder);

			orderedSteps.forEach(step => {

				uniqueFieldGroupNames = new Set();
				step.fieldGroups = [];
				step.hasUnmappableFields = false;
				var stepFields = [];
				var customFields = [];
				var tokenFields = [];

				// grab all the fields that belong to this step
				stepFields = logic.filterAndSortByIdList(fields, step._fieldOrder, {'_tokenField': false});

				// set a property on the step that indicates if that step contains any unmappable fields
				step.hasUnmappableFields = hasUnmappableFields(stepFields, box);

				stepFields.forEach(field => {

					// we don't need to process unsupported field types for the selected mapping type (rich text editor fields excluded from this rule since we need to grab their token fields)
					if(!isSupportedFieldType(field, box)) return;

					field['uniqueId'] = `id${field.id.split('-').join('')}`;
					field.config = JSON.parse(field._configJson);
					field['resolvedLabel'] = resolveLabel(field);

					// trim off any millisecond time strings from existing 'fieldGroup' property values
					let timeStringIdx = field._fieldGroup ? field._fieldGroup.search(dateTimeRegex) : -1;
					if(timeStringIdx !== -1) {
						field._fieldGroup = field._fieldGroup.slice(0, timeStringIdx)
					}

					// Grab all form field objects from a rich text editor field's 'tokenFields' collection, and put them into their own collection
					if(Array.isArray(field._tokenFields) && field._tokenFields.length){
						field._tokenFields.forEach((tokenField) => {
							let matchingField = fields.find((f) => f._tokenField === true && f._fieldDefId === tokenField.fieldDefId);
							if(matchingField) {
								matchingField['uniqueId'] = `id${matchingField.id.split('-').join('')}`;
								matchingField.config = JSON.parse(matchingField._configJson);
								matchingField['resolvedLabel'] = resolveLabel(matchingField);
								tokenFields.push(matchingField);
							}
						});
						return;
					}

					// fields with no '_fieldGroup' property (custom fields) go in their own collection
					if (field._fieldGroup == null) {
						customFields.push(field);
						return;
					}

					// create non-custom/non-token field groups for each step
					if (step.fieldGroups.length === 0) {
						step.fieldGroups.push({fieldGroupName: ssStringHlp.toTitleCase(field._fieldGroup) || 'Field Group', fields: [field]});
						return;
					}
					let group = step.fieldGroups[step.fieldGroups.length - 1];
					if (field._fieldGroup != null && group.fields[0]._fieldGroup === field._fieldGroup && !group.fields.find(f => f._fieldDefId === field._fieldDefId)) {
						group.fields.push(field);
					} else {
						step.fieldGroups.push({fieldGroupName: ssStringHlp.toTitleCase(field._fieldGroup) || 'Field Group', fields: [field]});
					}
				});

				// Append the custom fields group to the field groups collection, as long as they exist and there is at least one mappable field
				if(customFields.length && customFields.some(isMappable)) {
					step.fieldGroups.push({fieldGroupName: 'custom fields', fields: customFields});
				}

				// Append the token fields group to the field groups collection, as long as they exist and there is at least one mappable field
				if(tokenFields.length && tokenFields.some(isMappable)) {
					tokenFields = ssArrayHlp.unique(tokenFields.filter(f => boxFieldTypes[box.type].includes(f._fieldType)));
					step.fieldGroups.push({fieldGroupName: 'token fields', fields: tokenFields, config: {orderBy: 'resolvedLabel'}});
				}

			});

			// Determine which groups get displayed based on the existence of mappable fields
			orderedSteps.forEach((step) => {
				step.fieldGroups = step.fieldGroups.filter((group) => {
					return group.fields.length && group.fields.some(isMappable);
				});
			});

			vm.steps = orderedSteps.filter(s => s.fieldGroups.length);
		}

		// determines if a step has unmappable fields within the supported field types of the selected mapping type
		function hasUnmappableFields(fields, box) {
			const allFields = fields.reduce((acc, cur) => {
				return JSON.parse(cur._configJson).fields ? [...acc, ...JSON.parse(cur._configJson).fields] : [...acc, cur];
			}, []).filter(field => isSupportedFieldType(field, box));

			return allFields.some(field => !isMappable(field));
		}

		// filters out unsupported field types for the selected mapping (excludes rich text editor fields with content)
		function isSupportedFieldType(field, box) { 
			return (boxFieldTypes[box.type].includes(field._fieldType) || (field._tokenFields && field._tokenFields.length));
		};

		function isMappable(field) {
			const config = !field.config ? JSON.parse(field._configJson) : field.config || {};
			return config.hasOwnProperty('mappable') ? config['mappable'] === true : true;
		}

		function getFirstUniqueFor(label, count) {
			// first pass, num is null
			if (count == null) {
				// already taken, recurse with a number
				if (uniqueFieldGroupNames.has(label)) { return getFirstUniqueFor(label, 2); }
				// not taken yet, hot dog!
				if (!uniqueFieldGroupNames.has(label)) {
					uniqueFieldGroupNames.add(label);
					return label;
				}
			} else {
				// this label + num combination is taken, increment and recurse
				if (uniqueFieldGroupNames.has(`${label} (${count})`)) { return getFirstUniqueFor(label, ++count); }
				// if we've come this far, it must be available
				uniqueFieldGroupNames.add(`${label} (${count})`);
				return `${label} (${count})`;
			}
		}

		function resolveLabel(field) {
			try {
				let defaultField = fieldsHlp.getFieldById(field);
				if(defaultField._label !== field._label || !field.config.uniqueLabel) {
					return field._label;
				} else {
					return field.config.uniqueLabel;
				}
			} catch(e) {
				return ''
			}
			
		}
	}
}

// #-- END Segment (/form-builder/overlay-builder/segment-data-panel) --# //
// #--------------------------------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentFormBuilderOverlayBuilderDataPanel', () => new DataPanel('data-panel') );
