/*@ngInject*/
function logic(ssValidationHlp) {
	// logic api
	let required = ssValidationHlp.required();
	return {
		validator: {
			required,
			conditionalRequired(val, fieldName, conditionalObj) {
				return conditionalObj ? required(val, fieldName) : true;
			},
		}
	};
}
angular.module('ec-forms-segment-logic').factory('segmentFormBuilderSettingsSettingsLogic', logic);
