import { Segment } from 'base-segment';
// #---------------------------------------------------------------# //
// #---- Segment (/form-builder/preview/segment-form-workflow) ----# //

class FormWorkflow extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $anchorScroll, ssEventSvc, ssEnum, ssMultiInitiativeFeatureDetectorSvc, formsHlp, fieldsHlp) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm;
		const logic = $injector.get('segmentFormBuilderPreviewFormWorkflowLogic'); // eslint-disable-line
		const stepMap = new Map();

		// view model for tracking state
		const vm = $scope.vm = {
			steps: [],
			reviewStep: {
				'_name': 'Review Profile Updates',
				'review': true
			},
			stepAction: ssEnum.stepAction,
			selectedStep: null,
			loading: true
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
			selectStep(index, step) {
				vm.selectedStepId = step.id;
				ssEventSvc.emit('form-builder.builder.step-clicked', index, step);
			}
		};

		ssEventSvc.on('form-builder.summary.step-clicked', function(index, step) {
			vm.selectedStepId = stepMap.get(step.id).id;
			$anchorScroll('fb-step-' + index);
		}, $scope);

		(function initVmFromBuilderData() {

			formsHlp.sortByOrderField(routeVm.formData._steps, routeVm.formData._stepOrder);

			routeVm.formData._steps.forEach(function(step) {
				step.fields = [];
				if (typeof step.id === 'string') {
					stepMap.set(step.id, step);
				}
			});

			// add fields to their steps
			for (let i = routeVm.formData._formFields.length; i--;) {
				let field = routeVm.formData._formFields[i];
				if (!field._tokenField && field._step && stepMap.has(field._step.id)) {
					stepMap.get(field._step.id).fields.push(field);
				} else {
					routeVm.formData._formFields.splice(i, 1);
				}
			}

			routeVm.formData._steps.forEach(function(step) {

				formsHlp.sortByOrderField(step.fields, step._fieldOrder);

				step.participantLabel = step._stepParticipant.name || ssEnum.stepParticipantType.byValue(step._stepParticipant.participantType);

				// make step fieldGroups
				step.fieldGroups = [];
				step.fields.forEach(function(field) {
					if (ssMultiInitiativeFeatureDetectorSvc.isHRMSEnabled()) {
						// remove those not viewable for HRMS
						let templateField = fieldsHlp.getFieldById(field);

						if (templateField != null && templateField._hideForHRMS) { return; }
					}

					/* TEMP - remove the '_fieldGroup' property from the custom field objects */
					let customFieldIds = [
						'951626b1-a422-46b4-9fa1-3b5acf7ee8fc',
						'977a2273-62c1-4eef-8d63-91f9d8d1e0bf',
						'10a88a23-f8f3-48eb-aa48-c466db06378b',
						'17ff0b2c-4973-4181-92ce-b1c50c851165',
						'c2e31de2-09e8-4563-acb1-0e190a8e3b11',
						'5299a28c-56d5-4dc3-81ae-6f9f741eb324',
						'e215f04f-037a-479e-b5da-9bb3155b55d5',
						'9eabf24b-ae80-4488-a164-2a6f78a50cbd',
						'b6390770-0590-418c-b516-2080b243e9b7',
						'2056c221-97c8-4994-962c-f4bfc319a9e4'
					];

					if (customFieldIds.includes(field._fieldDefId) && field.fieldGroup) {
						field['_fieldGroup'] = null;
					}
					/* ************************************************************ */

					// reset the fields to appear enabled
					field.disabled = false;

					// put the fields into their correct groups
					if (step.fieldGroups.length === 0) {
						step.fieldGroups.push([field]);
						return;
					}

					let group = step.fieldGroups[step.fieldGroups.length - 1];
					if (field._fieldGroup != null && group[0]._fieldGroup === field._fieldGroup) {
						group.push(field);
					} else {
						step.fieldGroups.push([field]);
					}
				});
				delete step.fields;
			});
			vm.steps = routeVm.formData._steps;
			vm.selectedStepId = vm.steps[0].id;
			vm.loading = false;
		}());

	}

	link(scope, elem, attrs) {
		angular.element(document).ready(function() {
			elem.find('input').on('focus', function(e) {
				e.preventDefault();
				e.target.blur();
			});
		});
	}
}

// #-- END Segment (/form-builder/preview/segment-form-workflow) --# //
// #---------------------------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentFormBuilderPreviewFormWorkflow', () => new FormWorkflow('form-workflow'));
