import {Segment} from 'base-segment';
// #-------------------------------------------------------# //
// #---- Segment (/inbox/form-instance/segment-header) ----# //

class Header extends Segment {
	/*@ngInject*/
	controller($injector, $scope, ssAsync, ssDateByOrgHlp, ssFormInstancesApi, $sce, enumStepAction) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm;

		// hookup segment logic
		const logic = $injector.get('segmentInboxFormInstanceHeaderLogic'); // eslint-disable-line

		let sensitiveFields = null;
		let showSensitiveFields = false;

		const vm = $scope.vm = { // eslint-disable-line
			hasSensitiveFields: false,
		};

		const go = $scope.go = { // eslint-disable-line
			toggleFormView: (showPDF) => {
				if(showPDF){
					routeVm.isLoading = true;
					routeVm.loadingStatus = 'generating PDF...';
					generatePDFOverlay({ showSensitive: showSensitiveFields }).then((res) => {
					//	sanitize the '_string' property as a trusted URL for use in the iFrame
						document.getElementById("form-overlay-frame").src = $sce.trustAsResourceUrl(res.data.data.attributes.string);
						routeVm.showPDFOverlay = true;
						routeVm.isLoading = false;
						routeVm.loadingStatus = '';
					}).catch((err) => {
						routeVm.isLoading = false;
						routeVm.loadingStatus = '';
						ssToastHlp('There was an error generating the PDF', '');
						console && console.error && console.error(err);
					});
				} else {
					routeVm.showPDFOverlay = false;
				}
			},
			toggleSensitiveFields: (checked) => {
				sensitiveFields.forEach((field) => {
					field.overrideVisibility = !checked;
				});

				showSensitiveFields = checked;

				if(routeVm.hasPDFOverlay && routeVm.showPDFOverlay) {
					routeVm.isLoading = true;
					routeVm.loadingStatus = 'generating PDF...';
					generatePDFOverlay({showSensitive: checked}).then((res) => {
						document.getElementById("form-overlay-frame").src = $sce.trustAsResourceUrl(res.data.data.attributes.string);
						routeVm.isLoading = false;
						routeVm.loadingStatus = '';
					}).catch((err) => {
						routeVm.isLoading = false;
						routeVm.loadingStatus = '';
						ssToastHlp('There was an error generating the PDF', '');
						console && console.error && console.error(err);
					});
				}
			}
		};

		function generatePDFOverlay({ showSensitive = false } = {}) {

			routeVm.isLoading = true;
			routeVm.loadingStatus = 'generating PDF...';

			let formFieldValues = routeVm.formInstance._steps.filter((step) => {
				return step._stepAction !== enumStepAction.formCompleted
			}).map((step) => {
				return step._formFieldValues;
			}).reduce((a, c) => {
				a = typeof a === 'string' ? JSON.parse(a) : a;
				return Object.assign(a, JSON.parse(c));
			});
			
			return ssFormInstancesApi.generatePDFOverlay({
				formInstanceid: routeVm.formInstance.id,
				formFieldValues: formFieldValues,
				showSensitive: showSensitive
			});
		}
			
		ssAsync(function*() {

			let sentDateMoment = ssDateByOrgHlp.utcIsoToRelativeMoment(routeVm.formInstance._createdUtc, routeVm.org);

			routeVm.formInstance.sentDate = sentDateMoment.calendar(null, {
				sameDay: '[Today at] LT',
				lastDay: '[Yesterday at] LT',
				lastWeek: 'ddd [at] LT',
				sameElse: 'L [at] LT'
			});
			
		//	grab all sensitive fields
			sensitiveFields = routeVm.formTemplate._formFields.filter((field) => {
				return field._sensitive === true;
			});
		//	redact sensitive fields by default
			if(sensitiveFields.length) {
				sensitiveFields.forEach((field) => {
					field.overrideVisibility = true;
				});
				showSensitiveFields = false;
			}

		//	determine if the user has view access to at least one sensitive field
			let canViewSensitive = routeVm.formInstance._steps.some((step) => {
				return step._meta.permissions.permittedActions.viewSensitive === true;
			});

		//	create a property for the view to determine if the sensitive fields toggle should display
			vm.hasSensitiveFields = !!sensitiveFields.length && canViewSensitive;

		})();
	
	}
}

// #-- END Segment (/inbox/form-instance/segment-header) --# //
// #-------------------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentInboxFormInstanceHeader', () => new Header('header'));
