import {Component} from 'base-component';
// #--------------------------------------# //
// #---- Component (cmp-tracked-form) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	form: '=',
	statusUpdated: '&?',
	type: '='
};

const employeeMap = new Map();

class TrackedForm extends Component {
	/*@ngInject*/
	controller($scope, $location, $window, $state, filenameHlp, ssStorage, ssAsync, ssEnum, ssFormInstancesApi, ssEmployeeApi, ssFormTemplatesApi, ssToastHlp, ssModalSvc, ssObjectHlp) {

		const routeVm = $scope.routeVm = $scope.$root.utils.findInParentScope('routeVm', $scope);
		const form = $scope.form;
		const regex = /.*{"filename":.*/;

		const vm = $scope.vm = {
			loadingContact: true,
			openDetails: false,
			showContactToggle: true
		};

		vm.showContactToggle = getEmpPropPermissions($scope.form);

		$scope.$watch('form', (nForm, oForm) => {
			if(nForm.isPacket){
				nForm.content.forEach(f => f.filesOnInstance = processFileAttachments(f));
			}else{
				nForm.filesOnInstance = processFileAttachments(nForm);
			}
		});

		function processFileAttachments(form) {
			let filesOnInstance = [];
			const formFieldValues = form._steps
										.filter(stp => regex.test(stp._formFieldValues) && stp._stepAction !== ssEnum.stepAction.formCompleted)
										.map(stp => stp._formFieldValues);
			if(formFieldValues.length) {
				formFieldValues.forEach((formFieldValue) => {
					const parsedValue = JSON.parse(formFieldValue);
					Object.keys(parsedValue)
					.filter(key => Array.isArray(parsedValue[key]))
					.filter(key => parsedValue[key].every(item => !!item.filename))
					.forEach((key) => {
						filesOnInstance = filesOnInstance.concat(...processFileDisplay(parsedValue[key]))
					});
				})
			}
			return filesOnInstance;
		}

		function processFileDisplay(files) {
			return files.map((file) => {
				let ext = filenameHlp.getExtension(file.filename);
				let icon = filenameHlp.getIcon(ext);
				return { id: file.id, filename: file.filename, ext, icon }
			})
		}


		const go = $scope.go = { // jshint ignore:line
			download(files){
				files.forEach((file) => {
					ssFormTemplatesApi.getFileUrl({
						id: file.id
					}).then(success => {
						$window.open(success._string, '_blank');
					});
				});
			},
			remove(form) {
				let headerType = form.isInPacket ? 'Form in Packet' : 'Form';
				let msg = `Form removed successfully.`;
				var config = {
					title: `Delete ${headerType}`,
					content: `Are you sure you want to delete this form? This form will be deleted and cannot be retrieved.`,
					primaryText: 'Delete',
					secondaryText: 'Cancel'
				};
				ssModalSvc.showDestructive(config).then(() => {
					ssAsync(function*() {
						try {
							yield form.$remove();
							ssToastHlp.success(msg);
						} catch (e) {
							console && console.error && console.error(`error deleting ${headerType}`, e);
							ssToastHlp.error(`There was an error deleting "${form.name}"`, 'Error');
						} finally {
							$scope.statusUpdated();
						}
					})();
				});
			},
			toggleContact() {
				ssAsync(function*() {
					var emp;
					try {
						vm.loadingContact = true;
						if (!employeeMap.has(form.primaryRecipient.employeeId)) {
							emp = yield ssEmployeeApi.get({
								'id': form.primaryRecipient.employeeId,
								'include': 'PhoneNumbers,Emails'
							});
							console && console.log && console.log(emp);
							employeeMap.set(form.primaryRecipient.employeeId, emp);
						} else {
							emp = employeeMap.get(form.primaryRecipient.employeeId);
						}
						vm.empPhone = emp.bestContactNumber && emp.bestContactNumber.formatted || 'None';
						vm.empEmail = emp.bestContactEmail || 'None';
					} catch (e) {
						console && console.error && console.error('ERROR', e);
						ssToastHlp.error(`There was an error retreiving employee information for "${form.primaryRecipient.name}"`, 'Error');
					} finally {
						vm.loadingContact = false;
					}
				})();
			},

			updateFormStatus(form, status) {
				let statusId = ssEnum.formStatus[status.toLowerCase()];
				let type = (form.isInPacket) ? 'Packet' : 'Form';
				var config = {
					title: `Archive ${type}`,
					content: `Are you sure you want to archive this ${type}?`,
					primaryText: 'Archive',
					secondaryText: 'Cancel'
				};
				ssModalSvc.showStandard(config).then(() => {
					ssAsync(function*() {
						try {
							yield ssFormInstancesApi.updateStatus({
								id: form.id,
								newStatus: statusId
							});
							ssToastHlp.success(`${form.name} has been marked as ${status}`, 'Form Status Updated', false);
							$scope.statusUpdated();
						} catch (e) {
							console && console.error && console.error(`error deleting "${type}"`, e);
							ssToastHlp.error(`There was an error updating the status of ${form.name}`, 'Error', false);
						}
					})();
				});
			},

			updateEmpRecord(form) {
				var modalConfig = {
					title: '<h2>Move to Employee Record</h2>',
					personnel: ssEnum.personnelCategory,
					medical: ssEnum.medicalCategory,
					webFormSubCategory: '0-8',
					content: `<p>Please select a location to move "${form.name}"<br/>to ${form.primaryRecipient.name}'s Employee Record</p>
						<super-select-list model="webFormSubCategory">
							<li><h4>Personnel Documents</h4></li>
							<super-select-list-item ng-repeat="item in personnel.enums" value="'0-' + item.value" label="item.name"></super-select-list-item>
							<li><hr class="divider"/></li>
							<li><h4>Medical Documents</h4></li>
							<super-select-list-item ng-repeat="item in medical.enums" value="'1-' + item.value" label="item.name"></super-select-list-item>
						</super-select-list>`,
					primaryText: 'Continue',
					secondaryText: 'Cancel'
				};

				ssModalSvc.showStandard(modalConfig).then((config) => {
					let category = config.webFormSubCategory.split('-')[0];
					let subCategory = config.webFormSubCategory.split('-')[1];

					ssAsync(function*() {
						try {
							yield ssEmployeeApi.addFileToRecord({
								employeeId: form._employee.employeeId,
								formInstanceId: form.id,
								name: form.name,
								category: category,
								subCategory: subCategory
							});
							$scope.statusUpdated();
							ssToastHlp.success(`${form.name} has been added to ${form._employee.name}'s employee record`,
								'Form added to Employee Record ', false);
						} catch (e) {
							console && console.error && console.error('Error adding form to employee record', e);
							ssToastHlp.error(`${form.name} was not added to ${form._employee.name}'s employee record successfully`,
								'Add to Employee Record ', false);
						}
					})();
				});
			},

			updateWorkflowStep(form) {

				let config = {
					id: form.id,
					targetStep: form.prevStep._formTemplateStepId,
					params: {
						targetStatus: 0
					}
				};

				ssFormInstancesApi.updateCurrentStepId(config).then((success) => {
					if (success != null) {
						var participant = (form.prevStep._stepParticipant.name) ? `to ${form.prevStep._stepParticipant.name}` : ``;
						ssToastHlp.success(`Workflow step re-sent ${participant}`, 'Success!', false);
						$scope.statusUpdated();
					}
				}).catch((err) => {
					ssToastHlp.error('There was an error resending the workflow step. Please try again.', 'Error');
					console && console.error && console.error('Error resending the workflow step', e);
				});
			},

			sendReminder: ssAsync(function*(form) {
				try {
					ssFormInstancesApi.postReminder({
						id: form.id
					}).then((success) => {
						var participant = (form.currentStepParticipantName) ? `to ${form.currentStepParticipantName}` : ``;
						if (success != null) {
							ssToastHlp.success(`${participant}`, 'Reminder Sent', false);
							$scope.statusUpdated();
						}
					});
				} catch (e) {
					console && console.error && console.error('Error posting reminder', e);
					ssToastHlp.error(`There was an error sending a reminder to ${participant}`, 'Error');
				}
			}),

			resolveEmpRecordPath: () => {
				ssEmployeeApi.get({
					'id': form.primaryRecipient.employeeId,
					'include': 'PhoneNumbers,Emails'
				}).then((emp) => {
					if(routeVm.isDIPEnabled) {
						if(emp._orgPersonId) {
							$location.path(`/ec-directory/person-detail/${emp._orgPersonId}/personal`);
						} else {
							ssToastHlp.error('The user does not have a valid Org Person ID and therefore their employee record cannot be accessed')
							return '';
						}
					} else {
						$location.path(`/ec-employee/record/${emp.id}/personal`);
					}
				}).catch((err) => {
					ssToastHlp.error('There was an error retriveing the employee')
				});
			},
			resolveFormIcon: () => {
				let icon = form._isEmployeeInitiated ? 'li-form-e-large' : 'li-form-large';
				return icon;
			}
		};

		// helper functions

		function getEmpPropPermissions(form) {

			if(ssObjectHlp.glean($scope, _=>_.$root.isPermitted.activeUser.idmAdministrator, false)) {
				return true;
			};

			const propsToCheck = ['emails', 'phoneNumbers'];
			let propVals = propsToCheck.map((p) => {
				if(form._employee.meta) {
					return form._employee.meta.permissions.permittedActualProperties[p];
				}
				return false;
			});
			return propVals.some(p => p !== 0);
		}
	}
}

// #-- END Component (cmp-tracked-form) --# //
// #--------------------------------------# //

angular.module('ec-forms-components').directive('cmpTrackedForm', () => new TrackedForm(props, 'tracked-form') );
