import {Segment} from 'base-segment';
// #-----------------------------------------------------------# //
// #---- Segment (/packet-builder/builder/segment-builder) ----# //

class Builder extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $state, ssEnum, enumStepAction, ssAsync, ssFormTemplatesApi, ssListSvc, ssDebounce, formsHlp, ssToastHlp) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // eslint-disable-line
		// hookup segment logic
		const logic = $injector.get('segmentPacketBuilderBuilderBuilderLogic'); // eslint-disable-line
		const packetVm = $scope.packetVm;
		const BATCH_SIZE = 30;
		let currentPage = 1;
		let dataLoadCall = null;
		let totalForms = 0;

		$scope.added = [];

		// view model for tracking state
		const vm = $scope.vm = {
			showInfScroll: false,
			loading: false,
			scrolling: false,
			loadingPacket: false,
			// loadingMore: false,
			packet: packetVm.packetData,
			selectedCategory: 'All',
			sortBy: '-changedutc',
			search: '',
			sortCriteria: [{
				name: 'Recently Modified',
				field: '-changedutc'
			}, {
				name: 'Name ASC',
				field: 'name'
			}, {
				name: 'Name DESC',
				field: '-name'
			}],
			stepAction: enumStepAction,
			forms:[]
		};

		ssAsync(function*() {
			try {
				vm.categories = yield ssListSvc.getByExternalId('form.categories');
				if(vm.packet._formTemplateIds.length){
					vm.loadingPacket = true;
					let templateOrder = vm.packet._formTemplateIds.toString();
					ssFormTemplatesApi.formTemplatesByIds(vm.packet._formTemplateIds,
						{include: 'steps,statusHistory'}
					).then((templates) => {
						formsHlp.sortByOrderField(templates, templateOrder);
						processForms(templates);
						$scope.added = templates;
						vm.loadingPacket = false;
					}).catch((e) => {
						ssToastHlp.error('There was an error retrieving form templates in the packet', `Error`);
						vm.loadingPacket = false;
						console && console.log && console.log('Error retrieving form templates in the packet', e);
					});
				}
			} catch (e) {
				console && console.log && console.log('Error retrieving form template categories', e);
			}
		})();

		$scope.$watchGroup(['vm.search', 'vm.selectedCategory', 'vm.sortBy'], (nv, ov) => {
			if(nv === ov) { return; }
			vm.scrolling = false;
			vm.forms = [];
			totalForms = 0;
			currentPage = 1;
			ssDebounce(() => {
				loadForms();
			}, 350)();
		});

		$scope.$watchCollection('added', (nv, ov) => {
			if(nv === ov) { return; }
			const newIds = nv
				.map(item => item.id);

			if (JSON.stringify(newIds) !== JSON.stringify(vm.packet._formTemplateIds)) {
				vm.packet._formTemplateIds = newIds;
			}
		});

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line

			scrollData() {
				if(vm.scrolling) { return; }
				vm.scrolling = true;
				currentPage++;
				var offset = (currentPage - 1) * BATCH_SIZE;

				dataLoadCall = ssAsync(function*() {
					try {
						yield getForms({offset: offset});
						vm.scrolling = false;
						vm.showInfScroll = vm.forms.length < totalForms && totalForms > BATCH_SIZE;
					} catch(err) {
						ssToastHlp.error('There was an error getting form templates', `Error`);
						vm.scrolling = false;
						console && console.error && console.error(err);
					}
				})();
			},
			add(index, form) {
				$scope.added.push(form);
				form.inPacket = true;
			},
			remove(index, form) {
				$scope.added.splice(index, 1);
				let formInst = vm.forms.find(x => x.id === form.id);
				if(formInst){
					formInst.inPacket = false;
				}
			},
			dropForm(event, index, type, item) {
				let formInPacket = $scope.added.find(x => x.id === item.id);
				if(formInPacket){
					let currIndex = $scope.added.indexOf(formInPacket);
					move(currIndex, index);
				}else{
					$scope.added.splice(index, 0, item);
					let formInst = vm.forms.find(x => x.id === item.id);
					if(formInst){
						formInst.inPacket = true;
					}
				}
			},
			preview(form) {
				$state.go('base.formBuilder.preview', {
					formId: form.id,
					returnTo: $state.href($state.current.name),
					showPDFoverlay: $state.is('base.formBuilder.overlayBuilder')
				});
			}
		};

		loadForms();

		function loadForms(){
			if(dataLoadCall) { dataLoadCall.$abort(); }
			vm.loading = true;

			dataLoadCall = ssAsync(function*() {
				try {
					yield getForms();
					vm.showInfScroll = vm.forms.length < totalForms && totalForms > BATCH_SIZE;
					vm.loading = false;
				} catch(err) {
					ssToastHlp.error('There was an error getting form templates', `Error`);
					console && console.error && console.error(err);
				}
			})();
		};

		function getForms ({offset = 0} = {}) {
			return ssAsync(function*() {
				let forms = yield ssFormTemplatesApi.query({
					include: 'steps,statusHistory',
					limit: BATCH_SIZE,
					offset,
					filter: filterString(),
					sort: sortString(),
					latestVersion: true
				});
				processForms(forms);
				vm.forms = [...vm.forms, ...forms];
				totalForms = parseInt(forms.$headers['total-results'], 10);
			})();
		};

		function filterString(){
			let nameFilter = `filter[name]=${vm.search}`;
			let categoryFilter = '';
			if(vm.selectedCategory !== 'All'){
				let category = vm.categories.find(x => x.value === vm.selectedCategory);
				categoryFilter = `filter[categoryname]=${category.name}`
			}
			if(categoryFilter.length) { return `${nameFilter}&${categoryFilter}`; }
			else{ return nameFilter; }
		}

		function sortString(){
			let dir = vm.sortBy.startsWith('-') ? 'DESC' : 'ASC';
			let sortBy = vm.sortBy.replace('-','');
			return `sort[${sortBy}]=${dir}`;
		}

		function processForms(forms){
			forms.forEach((form) => {
				form.inPacket = vm.packet._formTemplateIds.some(y => y === form.id);

				formsHlp.sortByOrderField(form._steps, form._stepOrder);

				form._steps.forEach(step => {
					step.participantLabel = step._stepParticipant.participantType == null ? 'no participant' : ssEnum.stepParticipantType.byValue(step._stepParticipant.participantType);
					step.stepActionLabel = step._stepAction == null ? 'no step action' : ssEnum.stepAction.byValue(step._stepAction);
				});
			})
		}

		function move(from, to){
			$scope.added.splice(to,0,$scope.added.splice(from,1)[0]);
		}
	}
}

// #-- END Segment (/packet-builder/builder/segment-builder) --# //
// #-----------------------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentPacketBuilderBuilderBuilder', () => new Builder('builder') );
