import {Component} from 'base-component';
// #-----------------------------------------------------# //
// #---- Component (cmp-properties-custom-selectbox) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	fields: '='
};

class PropertiesCustomSelectbox extends Component {
	/*@ngInject*/
	controller($scope) {
		// view model for tracking state
		const vm = $scope.vm = { // eslint-disable-line
			field: $scope.fields[0],
			cols: [{
				name: 'One',
				value: '100%'
			}, {
				name: 'Two',
				value: '50%'
			}, {
				name: 'Three',
				value: '33%'
			}, {
				name: 'Four',
				value: '25%'
			}]
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line
		}; 
	}
}

// #-- END Component (cmp-properties-custom-selectbox) --# //
// #-----------------------------------------------------# //

angular.module('ec-forms-components').directive('cmpPropertiesCustomSelectbox', () => new PropertiesCustomSelectbox(props, 'properties-custom-selectbox') );
