import {Component} from 'base-component';
// #------------------------------------------# //
// #---- Component (cmp-box-date-options) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	box: '='
};

class BoxDateOptions extends Component {
	/*@ngInject*/
	controller($scope, moment) {

		//https://stackoverflow.com/questions/105034/create-guid-uuid-in-javascript
		let guid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
		    var r = Math.random()*16|0, v = c == 'x' ? r : (r&0x3|0x8);
		    return v.toString(16);
		});

		// view model for tracking state
		const vm = $scope.vm = { // eslint-disable-line
			displayString: '',
			dateOptions:[{
				name: `Month`,
				guid: `Month-${guid}`,
				selected: false,
				selectedVal: '',
				options: [{
					name: 'MM',
					cSharpFormat: 'MM',
					jsFormat: 'MM'
				},{
					name: 'Month',
					cSharpFormat: 'MMMM',
					jsFormat: 'MMMM'
				}]
			},{
				name: `Date`,
				guid: `Date-${guid}`,
				selected: false,
				selectedVal: '',
				options: [{
					name: 'DD',
					cSharpFormat: 'dd',
					jsFormat: 'DD'
				}]
			},{
				name: `Year`,
				guid: `Year-${guid}`,
				selected: false,
				selectedVal: '',
				options: [{
					name: 'YYYY',
					cSharpFormat: 'yyyy',
					jsFormat: 'YYYY'
				},{
					name: 'YY',
					cSharpFormat: 'yy',
					jsFormat: 'YY'
				}]
			}]
		};

		if($scope.box.stringFormat){
			let format = $scope.box.stringFormat;
			let seperator = format.startsWith('MMMM') ? ' ' : '/';
			let formatArray = format.split(seperator);
			formatArray.forEach(item => setJsFormat(item))
			update();
		}


		function setJsFormat(item){
			const isCSharpFormat = x => x.cSharpFormat === item;
			var flatten = vm.dateOptions.reduce(function(a, b){ return a.concat(b.options) },[]);
			var option = flatten.find(isCSharpFormat);
			var dateOption = vm.dateOptions.find(x => x.options.filter(isCSharpFormat).length > 0);
			dateOption.selected = true;
			dateOption.selectedVal = option.jsFormat;

		}

		function update() {
			var dateFormats = [];
			vm.dateOptions.forEach((dateOption) => {
				if(dateOption.selected && dateOption.selectedVal.length) {
					let val = dateOption.options.find(x => x.jsFormat === dateOption.selectedVal);
					if(val) { dateFormats.push(val); }
				}
			});
			var hasMonth = !!dateFormats.find(x => x.name === 'Month');
			var seperator = hasMonth ? ' ' : '/';
			var jsFormatArray = dateFormats.map(x => x.jsFormat);
			/* Following peice of code with turn on the Ordinal Number  */
			/* -------------------------------------------------------- */
			//var indexOfDate = jsFormatArray.indexOf('D');
			//if(indexOfDate >= 0 && hasMonth) { jsFormatArray[indexOfDate] = 'Do'; }
			/* -------------------------------------------------------- */
			var stringFormat = jsFormatArray.join(seperator);
			if(dateFormats.length){
				vm.displayString = moment().format(stringFormat);
				$scope.box.stringFormat = dateFormats.map(x => x.cSharpFormat).join(seperator);
			}else{
				vm.displayString = '';
				$scope.box.stringFormat = '';
			}
		}

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line
			update
		};
	}
}

// #-- END Component (cmp-box-date-options) --# //
// #------------------------------------------# //

angular.module('ec-forms-components').directive('cmpBoxDateOptions', () => new BoxDateOptions(props, 'box-date-options') );
