import {Component} from 'base-component';
// #----------------------------------------# //
// #---- Component (cmp-field-renderer) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	field: '=',
	group: '=',	
	ignoreValidations: '=',
	org: '=?'
};


class FieldRenderer extends Component {
	/*@ngInject*/
	controller($scope, ssValidationHlp, ssObjectHlp, fieldRendererHlp) {

		const { glean } = ssObjectHlp;
		const field = $scope.field;
		const group = $scope.group;
		const requiredErr = f => `${f} is required`;

		// view model for tracking state
		const vm = $scope.vm = {
			template: ''
		};

		$scope.validator = {
			required: ssValidationHlp.required(),
			numeric: ssValidationHlp.numeric(),
			numericRequired: ssValidationHlp.numeric({ required: true }),
			phone: ssValidationHlp.phone(),
			phoneRequired: ssValidationHlp.phone({ required: true }),
			ssn: ssValidationHlp.ssn(),
			ssnRequired: ssValidationHlp.ssn({ required: true}),
			email: ssValidationHlp.email(),
			emailRequired: ssValidationHlp.email({ required: true }),
			zip: ssValidationHlp.zip(),
			zipRequired: ssValidationHlp.zip({ required: true }),
			digitalSignatureRequired: ssValidationHlp.object({ required: true }, [(v, f) => glean( v,x => (x.isSigned && x.name != null && x.name.trim() !== "") || requiredErr(f), requiredErr(f))]),
			fileUploadRequired: ssValidationHlp.string({ required: true }),
		};

	/*	FC-3214 **************************************
	**	(TEMP) Replace the field type of any fields within
	**	existing templates that have a '_fieldType'
	**	of 'super-input-datepicker'
	*/
		if(field._fieldType === 'super-input-datepicker') {
			field._fieldType = 'super-iso-org-date';
		}
	/* ********************************************* */

		fieldRendererHlp.bindConfigObject($scope, 'field._configJson', 'field.config');

		$scope.$watchGroup(['field._fieldType', 'field.isRedacted', 'field.ignoreValidations'], () => renderTemplate(field));
		$scope.$watch(fieldRendererHlp.getVisibility(field), val => vm.visibility = val);

		function renderTemplate(field) {			
			var tag = fieldRendererHlp.getTag(field);
			var properties = fieldRendererHlp.getProperties(field, group);
			var content = fieldRendererHlp.getContent(field, {scope: $scope.$root});
			vm.template = `<${tag}${properties}>${content}</${tag}>`;
		}
	}
}

// #-- END Component (cmp-field-renderer) --# //
// #----------------------------------------# //

angular.module('ec-forms-components').directive('cmpFieldRenderer', () => new FieldRenderer(props,
	'field-renderer'));
