import { Component } from 'base-component';
// #--------------------------------------------------# //
// #---- Component (cmp-multiple-employee-search) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	label: '@',
	model: '=',
	exclusions: '='
};

class MultipleEmployeeSearch extends Component {
	/*@ngInject*/
	controller($scope, $rootScope, $document, $element, ssAsync, ssListSvc, ssLocationSvc,
		ssJobTypeSvc, ssDepartmentSvc, ssEmployeeApi, enumEmployeeStatus, ssStringHlp, ssDateByOrgHlp, moment, ssToastHlp ) {

		$scope.routeVm = $scope.$root.utils.findInParentScope('routeVm', $scope);
		var now = moment().format('MM/DD/YYYY');
		var groupPromise = null;
		var newEmps = null;

	//	Object that holds the filter reset values
		var resetOptions = {
			selected: {},
			exclusions: []
		};

		const vm = $scope.vm = { // eslint-disable-line
			asOf: now,
			formOpened: false,
			filtering: false,
			attempted: false,
			selected: {
				location: [],
				jobType: [],
				employmentGroup: [],
				department: [],
				status: null,
				hireDateStart: null,
				hireDateEnd: null
			},
			employmentGroupDisabled: false
		};

	//	Prepare data for dropdowns and what not
		ssAsync(function* () {
			try {
				vm.locations = yield ssLocationSvc.getAll();
				vm.jobTypes = vm.jobTypesAll = yield ssJobTypeSvc.getAll();
				vm.employmentGroups = yield ssListSvc.getByExternalId('jobtype.employmentgroups');
				vm.employmentGroups = vm.employmentGroups.map(o => {
					return {
						_name: o.name,
						id: o.value
					};
				});

				vm.departments = yield ssDepartmentSvc.getAll();
				vm.status = enumEmployeeStatus.getEnums().map(o => {
					return {
						_name: ssStringHlp.toProperCase(o.name.toLowerCase()),
						id: o.value
					};
				});

			//	Default status filter to 'active' employees
				vm.selected.status = [vm.status.find(s => s.id === enumEmployeeStatus.active)];

			} catch (e) {
				console.error('ERROR', e);
			}
		})();

		const go = $scope.go = { // eslint-disable-line

			cancel() {
				vm.formOpened = false;
				vm.filtering = false;

			//	Reset filter/exclusion values if there is a current recipient group
				if ($scope.model.length) {
					vm.selected = angular.copy(resetOptions.selected, vm.selected);
					$scope.exclusions = angular.copy(resetOptions.exclusions, $scope.exclusions);
				}
			},

			resetFilters() {
				vm.status[1].checked = true;
				vm.selected.status.push(vm.status[1]);
			},

		//	clear all filters & appropriate variables
			clearAllFilters() {
				vm.selected.location = [];
				vm.selected.jobType = [];
				vm.selected.employmentGroup = [];
				vm.selected.department = [];
				vm.selected.status = [];
				vm.selected.hireDateStart = '';
				vm.selected.hireDateEnd = '';
				vm.locations.forEach(uncheck);
				vm.jobTypes.forEach(uncheck);
				vm.employmentGroups.forEach(uncheck);
				vm.departments.forEach(uncheck);
				vm.status.forEach(uncheck);
				$scope.exclusions = [];
				vm.attempted = false;
				vm.filtering = false;
			},

			toggleFilter() {
				if (!$scope.model.length) {
					go.clearAllFilters();
					go.resetFilters();
				}
				vm.formOpened = !vm.formOpened;
			},

			addGroup() {
				vm.filtering = true;
				let hireStart = vm.selected.hireDateStart && vm.selected.hireDateStart.toISOString()
					? ssDateByOrgHlp.startOfDayRelativeTo(moment(vm.selected.hireDateStart.toISOString()), $scope.routeVm.org)
					: null;
				let hireEnd = vm.selected.hireDateEnd && vm.selected.hireDateEnd.toISOString()
					? ssDateByOrgHlp.endOfDayRelativeTo(moment(vm.selected.hireDateEnd.toISOString()), $scope.routeVm.org)
					: null;

				var searchFilter = Object.assign({}, {
					location: filterOrNull(vm.selected.location),
					jobtype: filterOrNull(vm.selected.jobType),
					department: filterOrNull(vm.selected.department),
					status: filterOrNull(vm.selected.status),
					employmentgroup: filterOrNull(vm.selected.employmentGroup),
					hiredatestart: hireStart,
					hiredateend: hireEnd
				})
					Object.keys(searchFilter).forEach((key) => (searchFilter[key] == null) && delete searchFilter[key]);

					var filter = Object.assign({}, {
							fields: 'firstname,lastname,id',
							limit: 10000,
							asOfTimeUtc: ssDateByOrgHlp.safeAsOfDateRelativeTo(moment(vm.asOf).startOf('day'), $scope.routeVm.org),
							filter: searchFilter,
						}
					);


				groupPromise = ssAsync(function* () {
					try {
						newEmps = yield ssEmployeeApi.search({}, filter);

						newEmps = newEmps.map(function (emp) {
							return { id: emp.id }
						});

						vm.attempted = true;

						validateExclusions(newEmps, $scope.exclusions);

						$scope.model = filterExclusions(newEmps);

						if ($scope.model.length) {
							vm.formOpened = false;
						//	update the reset values
							resetOptions.exclusions = angular.copy($scope.exclusions, resetOptions.exclusions);
							resetOptions.selected = angular.copy(vm.selected, resetOptions.selected);
						}

					} catch (e) {
						console.error('error retrieving employees', e);
					} finally {
						vm.filtering = false;
						groupPromise = null;
					}
				})();
			}

		};
	//	This method is on routeVm so that the header segment can remove the recipient group & reset the filters after successfully sending a form
		$scope.routeVm.removeGroup = function() {
			if(vm.formOpened) { go.toggleFilter(); }
			$scope.model = [];
			go.clearAllFilters();
			go.resetFilters();
		};

	//	Bind the change of the effective date to the parent value.
		$rootScope.$on('$destroy', $rootScope.$on('parentAsOfDate-Change', function (event, data) {
			if (data == null || data.asOf == null || data.asOf.toString() === '') {
				vm.asOf = now;
			}
			else {
				vm.asOf = moment(data.asOf).format('MM/DD/YYYY');
			}
		}));

		$scope.$on('destroy', $scope.$watch('vm.selected.jobType.length', (nv, ov) => {

			if (nv === ov) { return; }

			if (nv > 0) {
				vm.employmentGroupDisabled = true;
				vm.selected.employmentGroup = [];
				vm.employmentGroups.forEach(uncheck);
			} else {
				vm.employmentGroupDisabled = false;
			}
		}));

		$scope.$on('destroy', $scope.$watch('vm.selected.employmentGroup.length', (nv, ov) => {

			if (nv === ov) { return; }

			if (nv > 0) {
				const empGroupIds = vm.selected.employmentGroup.map(g => g.id);
				const jobTypeBargainingUnitIds = vm.jobTypesAll.map(t => t._bargainingUnit.id);
				vm.jobTypes = [];

				let associatedBargainingUnitIds = jobTypeBargainingUnitIds.filter(id => {
					let empGroupId = id.split('_')[1];
					return empGroupIds.includes(empGroupId) === true;
				});

				vm.jobTypes = vm.jobTypesAll.filter(jobType => {
					return associatedBargainingUnitIds.includes(jobType._bargainingUnit.id) === true;
				});
			} else {
				vm.jobTypes = vm.jobTypesAll;
			}
		}));

		//	Watch the filters and if any are changed while a group is being added, cancel the add group action
		$scope.$on('destroy', $scope.$watch('vm.selected', function (nv, ov) {
			if (nv === ov) { return; }
			if (groupPromise) {
				groupPromise.$abort();
				vm.filtering = false;
				vm.attempted = false;
			}
		}, true));

	//	Remove any valid exclusions	from the current recipient group
		function filterExclusions(emps) {
			let excludedEmployeeIds = $scope.exclusions.map((emp) => {
				return { id: emp.id };
			});
			return emps.filter((emp) => {
				return !excludedEmployeeIds.find((exemp) => {
					return exemp.id === emp.id;
				});
			});
		}

	//	make sure the selected exclusion(s) are part of the current recipient group and if not, remove them from the exclusions collection
		function validateExclusions(recipients, exclusions) {
		//	create a simplified recipient collection containing just IDs
			let recipientIds = recipients.map((emp) => {
				return emp.id;
			});
		//	Validate current exclusions are part of the current recipient group
			let exemps = exclusions.filter((exemp) => {
				return !recipientIds.includes(exemp.id);
			});
		//	Remove any non-valid exclusion from the exclusion collection
			exemps.forEach((exemp) => {
				exclusions.splice(exclusions.indexOf(exemp), 1);
			});
		//	Handle user messaging
			if(exemps.length) {
				ssToastHlp.warn('One or more of the selected exclusions are not members of the current recipient group and have therefore been removed', '')
			} else {
				return;
			}
		}

	}

}

//	Helper Functions

	function uncheck(item) {
		item.checked = false;
	}

	function check(item) {
		item.checked = true;
	}

	function filterOrNull(filtersList) {
		if (filtersList.length > 0) {
			var o = []

			filtersList.map((item) => {
				o.push(item.id)
			})

			return o.join(',');
		} else {
			return null;
		}
	}

	function stringFilter(val, type) {
		var o = {};

		if (val != null) {
			let joinedValue = val.split(' ').join(',');

			o['filter[' + type + ']'] = joinedValue;

			return o;
		} else {
			return null;
		}
	}

// #-- END Component (cmp-multiple-employee-search) --# //
// #--------------------------------------------------# //

angular.module('ec-forms-components').directive('cmpMultipleEmployeeSearch', () => new MultipleEmployeeSearch(props, 'multiple-employee-search'));
