import {Segment} from 'base-segment';
// #-----------------------------------------# //
// #---- Segment (/inbox/segment-header) ----# //

class Header extends Segment {
	/*@ngInject*/
	controller($injector, $scope) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line

		// hookup segment logic
		const logic = $injector.get('segmentInboxHeaderLogic');// jshint ignore:line

		const vm = $scope.vm = { // jshint ignore:line
		};

		routeVm.search = '';
	}
}
// #-- END Segment (/inbox/segment-header) --# //
// #-----------------------------------------# //

angular.module('ec-forms-segments').directive('segmentInboxHeader', () => new Header('header'));
