function getConfig() {
	// Define changes to default configuration here.
	// For complete reference see:
	// https://ckeditor.com/docs/ckeditor4/latest/api/CKEDITOR_config.html
	// The toolbar groups arrangement, optimized for a single toolbar row.
    return {
        contentCss: '/text-editor.styl',
        copyFormatting_allowedContexts: ['text'],
        disallowedContent: 'script',
        disableNativeTableHandles: false,
        div_wrapTable: true,
        // extraAllowedContent = 'style;*[id,rel](*)',
        extraPlugins: 'autocomplete,textmatch,richcombo,placeholder,placeholder_select,sharedspace,justify,preview,autogrow,format',
     // htmlEncodeOutput: true,
        removeButtons: 'Anchor,Source,SpecialChar,HorizontalRule,Maximize',
        removeDialogTabs: 'link:advanced',
        toolbar: [{ 
            name: 'basicstyles',
            groups: [ 'basicstyles', 'cleanup' ],
            items: [ 'Bold', 'Italic', 'Underline', 'Strike', 'Subscript', 'Superscript', '-', 'RemoveFormat' ]
        },{
            name: 'paragraph',
            groups: [ 'list', 'indent', 'align' ],
            items: [ 'NumberedList', 'BulletedList', '-', 'Outdent', 'Indent', '-', 'JustifyLeft', 'JustifyCenter', 'JustifyRight', 'JustifyBlock' ]
        },
        '/',
        {
            name: 'others',
            items: [ 'placeholder_select' ]
        },{
            name: 'links',
            items: [ 'Link', 'Unlink' ]
        },{
            name: 'insert',
            groups: ['insert' ],
            items: [ 'Table', 'HorizontalRule']
        },{
            name: 'styles',
            items: [ 'Format', 'FontSize', 'TextColor' ]
        }],
        startupFocus: 'start',
        uiColor: '#e5e9ec',
        // toolbarGroups: [
        //     { name: 'basicstyles', groups: [ 'basicstyles', 'cleanup' ] },
        //     { name: 'insert' },
        //     { name: 'links' },
        //     // { name: 'document', groups: [ 'mode' ] },
        //     // { name: 'clipboard', groups: [ 'clipboard', 'undo' ] },
        //     // { name: 'editing', groups: [ 'find', 'selection', 'spellchecker' ] },
        //     // { name: 'forms' },
        //     { name: 'paragraph', groups: [ 'list', 'indent', 'align' ] },
        //     { name: 'styles' },
        //     // { name: 'colors' },
        //     { name: 'tools' },
        //     { name: 'holders', groups: ['placeholder_select']}
        //     // { name: 'others' },
        // ],

    }
};

export { getConfig }