import {Segment} from 'base-segment';
// #---------------------------------------------------------# //
// #---- Segment (/form-builder/builder/segment-builder) ----# //

class Builder extends Segment {
	/*@ngInject*/
		controller($injector, $scope, $state, $stateParams, $location, ssAsync, ssFormTemplatesApi, ssEventSvc, ssEnum, formsHlp, fieldsHlp, ssArrayHlp, ssToastHlp, ssModalSvc) {
		// used for segment coordination
		const routeVm = $scope.routeVm;
		let formVm = $scope.formVm;

		// hookup segment logic
		const logic = $injector.get('segmentFormBuilderBuilderBuilderLogic'); // eslint-disable-line

		let moveTarget = null;
		let commitToEmployeeRecord = [];

		// view model for tracking state
		const vm = $scope.vm = routeVm.vm = {
			form: formVm.formData,
			steps: [],
			selectedFormObject: null,
			stepAction: ssEnum.stepAction,
			stepEventType: ssEnum.stepEventType,
			stepParticipant: ssEnum.stepParticipantType
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = {
			stepSettings(step, index) {
				vm.selectedFormObject = step;
				step.index = index + 1; // used to display current step on properties
				$scope.$emit('component-properties', 'cmp-properties-step', step);
			},
			formComplete() {
				vm.selectedFormObject = vm.lastStep;
				$scope.$emit('component-properties', 'cmp-properties-form-complete', vm.lastStep);
			},
			reviewStep() {
				vm.selectedFormObject = vm.reviewStep;
				$scope.$emit('component-properties', 'cmp-properties-profile-review', vm.reviewStep);
			},
			addStep() {
				const stepCount = vm.form._steps.length;
				const newStep = formsHlp.makeStep();
				vm.form._steps.splice(stepCount-1, 0, newStep);
				vm.steps.push(newStep);
				formVm.modelChanged();
				go.stepSettings(newStep, vm.steps.length - 1);
			},
			movedStep(index, step) {
				vm.steps[moveTarget] = step;
				vm.steps.splice(index, 1);
			},
			dropStep(event, index, item) {
				formVm.modelChanged();
				moveTarget = index;
				return {};
			}
		};

		formVm.updateStepOrder = function() {
			return formsHlp.updateStepOrder(vm.form, vm.steps);
		}

		formVm.updateFieldOrder = function() {
			return formsHlp.updateFieldOrder(vm.steps);
		}

		formVm.addStepIdToField = function() {
			return fieldsHlp.addStepIdToField(vm.steps);
		}

		formVm.updateFormFields = function() {
			let fieldGroups = vm.steps.reduce((acc, cur) => {
				return [].concat.apply(acc, cur.fieldGroups);
			}, []);
			// Because we are completing reassigning the '_formFields' collection here, we have to make sure we dont overwrite any existing token fields
			let tokenFields = vm.form._formFields.filter((f) => f._tokenField && f._tokenField === true);
			let allFields = [...fieldGroups, ...tokenFields];
			vm.form._formFields.splice(0, vm.form._formFields.length, ...allFields);
		}

		ssEventSvc.on('ss-json-api.addedRecord.formFields', (obj, model) => {
			const step = vm.steps.find(s => s.id === obj._step.id);
			if (step == null) { return; }
			const groupIdx = step.fieldGroups.findIndex(g => g.includes(obj));
			if (groupIdx === -1) { return; }
			const fieldIdx = step.fieldGroups[groupIdx].indexOf(obj);
			if (fieldIdx === -1) { return; }
			step.fieldGroups[groupIdx][fieldIdx] = model;
		}, $scope);

		ssEventSvc.on('ss-json-api.addedRecord.steps', (obj, model) => {
			const stepIdx = vm.steps.indexOf(obj);
			if (stepIdx === -1) { return; }
			vm.steps[stepIdx] = model;
		}, $scope);

		ssEventSvc.on('emp-record-commit', (val, data) => {
			updateStepsThatCommitToRecord(val, data);
			toggleReviewStep(val);
		}, $scope);

		formVm.processTemplate = function() {
			return ssAsync(function* () {

				yield vm.form.$refresh();

				let updatedFields = [];
				vm.steps = [];

				formsHlp.sortByOrderField(vm.form._steps, vm.form._stepOrder);

				vm.form._steps.forEach(step => {
					step.fields = [];
					vm.steps.push(step);
				});

				vm.steps.forEach((step) => {

					step.fieldGroups = [];

					step.fields = vm.form._formFields.filter((field) => {
						return !field._tokenField && field._step && field._step.id === step.id;
					});

					formsHlp.sortByOrderField(step.fields, step._fieldOrder);

					step.fields.forEach(field => {

					//	track existing field groups
						if (field._fieldGroup != null) {
							routeVm.added[field._fieldGroup] = true;
						}

					//	TEMP - remove the '_fieldGroup' property from the custom field objects
						let customFieldIds = [
							'951626b1-a422-46b4-9fa1-3b5acf7ee8fc',
							'977a2273-62c1-4eef-8d63-91f9d8d1e0bf',
							'10a88a23-f8f3-48eb-aa48-c466db06378b',
							'17ff0b2c-4973-4181-92ce-b1c50c851165',
							'c2e31de2-09e8-4563-acb1-0e190a8e3b11',
							'5299a28c-56d5-4dc3-81ae-6f9f741eb324',
							'e215f04f-037a-479e-b5da-9bb3155b55d5',
							'9eabf24b-ae80-4488-a164-2a6f78a50cbd',
							'b6390770-0590-418c-b516-2080b243e9b7',
							'2056c221-97c8-4994-962c-f4bfc319a9e4'
						];

						if(customFieldIds.includes(field._fieldDefId) && field._fieldGroup !== null ) {
							field['_fieldGroup'] = null;
						}
						/* ************************************************************ */

					//	update existing field objects with missing keys/values
						if (field._fieldDefId != null) {
							let updatedAttrs = fieldsHlp.updateFieldAttributes(field);
							let updatedOptions = fieldsHlp.updateFieldOptions(field);
							updatedFields = [...updatedAttrs, ...updatedOptions];
						}

					/*	FC-3214 **************************************
					**	(TEMP) Replace the field type of any fields within
					**	existing templates that have a '_fieldType'
					**	of 'super-input-datepicker'
					*/
						if(field._fieldType === 'super-input-datepicker') {
							field._fieldType = 'super-iso-org-date';
						}
					/* ********************************************* */

					//	make step fieldGroups
						if (step.fieldGroups.length === 0) {
							step.fieldGroups.push([field]);
							return;
						}
						let group = step.fieldGroups[step.fieldGroups.length - 1];
						if (field._fieldGroup != null && group[0]._fieldGroup === field._fieldGroup) {
							group.push(field);
						} else {
							step.fieldGroups.push([field]);
						}
					});
					delete step.fields;
				});

				initLastStep();
				initReviewStep();

			//	if any field's "_options" have been updated, force a change so it will save
				if(updatedFields.length) {
					updatedFields.forEach((field) => {
						if(field.$isDirty) {
							field.$$forceChangedState('_options');
						}
					});
				}
				
			//	Add another save here in case anything on the template was updated
				yield vm.form.$save();

				formsHlp.initSaveInterrupter(vm.form);

			})();
		};

		function initLastStep() {
			// look for an existing form complete step
			let lastStep = vm.steps.find(s => { return s._stepAction === ssEnum.stepAction.formCompleted });

			//	if it's not found, we designate the last step as the form complete step (new template) and remove it from the collection, otherwise just remove it
			if (lastStep == null) {
				lastStep = vm.steps.pop();
				lastStep.last = true;
				lastStep._stepAction = ssEnum.stepAction.formCompleted;
				lastStep._stepParticipant.participantType = vm.stepParticipant.employeeChosenWhenSent;
				lastStep._name = 'Form and Workflow is Complete';
			} else {
				lastStep.last = true;
				vm.steps.splice(vm.steps.indexOf(lastStep), 1);
			}

			// automatically set the step participant of the last step
			lastStep._stepParticipant.participantType = vm.stepParticipant.employeeChosenWhenSent;
			vm.lastStep = lastStep;
		}

		function initReviewStep() {
			// look for an existing review step
			vm.reviewStep = findReviewStep(vm.form._steps);
			// create the collection of committed field group names
			commitToEmployeeRecord = getCommittedFieldGroups();
			/*
			If there's an existing review step, splice it out;
			else if the collection of committed field groups contains a field group, yet no review step exists, add it;
			else return - nothing to do
			*/
			if(vm.reviewStep) {
				vm.steps.splice(vm.steps.indexOf(vm.reviewStep), 1);
			} else if (commitToEmployeeRecord.length) {
					toggleReviewStep(true);
			} else {
				return;
			}
		}

	//	checks to see if there are any field groups set to update the employee record, and creates a unique collection from their field group names
		function getCommittedFieldGroups() {
			let fieldsToCommit = [];

			vm.steps.forEach(s => {
				if (s.fieldGroups.length > 0) {
					s.fieldGroups.forEach(group => {
						let fieldsWithUpdate = group.map(field => {
							if (field._updateEmployeeRecord === true) {
								return field._fieldGroup;
							}
						});
						fieldsToCommit = fieldsToCommit.concat(fieldsWithUpdate);
					});
				}
			});
			return ssArrayHlp.unique(fieldsToCommit.filter(f => f != null));
		}

		function toggleReviewStep(willUpdateRecord) {
			if (willUpdateRecord) {
				if (findReviewStep(vm.form._steps) == null) {
					addReviewStep();
				}
			} else if (!willUpdateRecord && commitToEmployeeRecord.length < 1) {
				removeReviewStep();
			} else {
				return;
			}
			formVm.modelChanged();
		}

		function addReviewStep() {
			const newStep = formsHlp.makeStep({
				_name: 'Review Profile Updates',
				_stepAction: ssEnum.stepAction.employeeProfileReview,
				_stepEvents: [{
					eventType: vm.stepEventType.onEmployeeProfileReview,
					eventActions: []
				}],
				_permissions: [{
					canView: true,
					canEdit: true,
					canViewSensitive: true,
					canEditSensitive: true,
					employeeId: ''
				}]
			});
			vm.form._steps.splice(vm.form._steps.length-1, 0, newStep);
			vm.reviewStep = newStep;
		}

		function removeReviewStep() {
			let reviewStep = vm.form._steps.find(step => {
				return step._stepAction === ssEnum.stepAction.employeeProfileReview;
			});

			if (reviewStep != null) {
				vm.form._steps.splice(vm.form._steps.indexOf(reviewStep), 1);
				vm.reviewStep = null;
			} else {
				console && console.warn && console.warn('You attempted to remove a review step that does not exist');
			}
		}

		function updateStepsThatCommitToRecord(willUpdateRecord, fields) {
			if (willUpdateRecord) {
				if (!commitToEmployeeRecord.includes(fields[0]._fieldGroup)) {
					commitToEmployeeRecord.push(fields[0]._fieldGroup);
				}
			} else {
				if (commitToEmployeeRecord.includes(fields[0]._fieldGroup)) {
					commitToEmployeeRecord.splice(commitToEmployeeRecord.indexOf(fields[0]._fieldGroup), 1);
				}
			}
		}

		function findReviewStep(steps) {
			return steps.find(s => s._stepAction === ssEnum.stepAction.employeeProfileReview);
		}

	//	initialization
		ssAsync(function*() {
			try {

				//	if we have somehow gotten to this page with an active template, force the user to deactivate or return to form templates page
				if($stateParams.deactivate === 'true' || vm.form._statusId === ssEnum.formTemplateStatus.active) {

					var modalConfig = {
						title: 'Edit Form Template',
						content: 'This is an active template and will need to be deactivated to continue',
						primaryText: 'Deactivate and continue',
						secondaryText: 'Cancel',
						alwaysOnTop: true
					};

					ssModalSvc.showStandard(modalConfig).then(() => {
						formVm.loading = true;
						formVm.loadingMsg = 'Deactivating "'+vm.form._name+'"';
						ssFormTemplatesApi.editActive(vm.form).then((res)=> {
							$location.url(`/ec-forms/form-builder/${res.id}/builder`);
						}).catch((err) => {
							ssToastHlp.error('there was an error deactivating the form template', '');
							$state.go('base.formTemplates');
							console && console.error && console.error(err);
						});
					}).catch(() => {
						$state.go('base.formTemplates');
					});
				}

				formVm.loading = true;
				formVm.loadingMsg = 'loading form template...';
				formVm.modelChanged(false);

				// reset the routeVm.added props to false
				Object.keys(routeVm.added).forEach(k => routeVm.added[k] = false);

				yield formVm.processTemplate();

				formVm.loading = false;
				formVm.loadingMsg = '';
				go.stepSettings(vm.steps[0], 0);

			} catch (e) {
				formVm.loading = false;
				formVm.loadingMsg = '';
				console.error(e);
			}
		})();
	}
}

// #-- END Segment (/form-builder/builder/segment-builder) --# //
// #---------------------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentFormBuilderBuilderBuilder', () => new Builder('builder') );
