/*@ngInject*/
function logic(statusLabelHlp) {
	// logic api
	return {
		getStatusLabel,
		getStatus
	};

	function getStatus(form, org) {
		return statusLabelHlp.getLabel(form, org).status
	}

	function getStatusLabel(form, org) {
		return statusLabelHlp.getLabel(form, org).statusLabel;
	}
}
angular.module('ec-forms-segment-logic').factory('segmentFormBuilderPreviewPageHeaderLogic', logic);
