/*@ngInject*/
function logic() {
	// logic api
	return {

		filterAndSortByIdList: (array, order, props = {}) => {
			return order.split(',').map(
				id => array.find(item => item.id === id)
			).filter(item => {
				if(item != null) {
					return Object.keys(props).every(key => (item[key] != null && props[key] === item[key]));
				}
			})
		}

	};
}
angular.module('ec-forms-segment-logic').factory('segmentFormBuilderOverlayBuilderDataPanelLogic', logic);
