const _hlp = {
// #------------------------------------# //
// #---- Helper (viewportOffsetHlp) ----# //

	formatText(text) {
		return text;
	},
	/**
	 * Calculates the current "window" position taking into account your scroll position
	 * @param {Any} win - The browser window object
	 * @returns {Any} lt - Object containing the left and top of the current viewport.
	 */
	getViewportOffsets(win) {
		// Default case, likely to never happen, -1 to help differentiate the code paths in unit tests
		var lt = { left: -1, top: -1 };

		if (win == null) { return lt; }

		// If when we try to figure out the position and it fails,
		//	default to 0.  Probably the safe bet, so that we can still draw a box
		try {
			lt.left = win.pageXOffset ||
				win.document.documentElement.scrollLeft ||
				win.document.body.scrollLeft ||
				0;
		}
		catch(err) {
			lt.left = 0;
		}

		try {
			lt.top = win.pageYOffset ||
				win.document.documentElement.scrollTop ||
				win.document.body.scrollTop ||
				0;
		}
		catch(err) {
			lt.top = 0;
		}

		return lt;
	}
// #-- END Helper (viewportOffsetHlp) --# //
// #------------------------------------# //
};

function viewportOffsetHlp() { return _hlp; }
angular.module('ec-forms-helpers').factory('viewportOffsetHlp', viewportOffsetHlp);
