import {Segment} from 'base-segment';
// #--------------------------------------------------------# //
// #---- Segment (/form-instance/segment-steps-summary) ----# //

class StepsSummary extends Segment {
	/*@ngInject*/
	controller($injector, $scope, ssEventSvc, ssEnum, enumStepAction, formsHlp) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm;
		// hookup segment logic
		const logic = $injector.get('segmentInboxFormInstanceStepsLogic');// jshint ignore:line

		const vm = $scope.vm = {
			steps: [],
			selectedStep: null,
			loading: true,
			employeeProfileReview: ssEnum.stepAction.employeeProfileReview
		};

		const go = $scope.go = { //jshint ignore:line
			selectStep(step) {
				vm.selectedStep = step;
				ssEventSvc.emit('select-form-step', step);
			}
		};

		ssEventSvc.on('select-summary-step', function(step) {
			const clickedStep = vm.steps.find(s => s.id === step.id);
			vm.selectedStep = clickedStep;
		}, $scope);

	//	Since we are just displaying the step data here, we can make a copy and filter out the 'form complete' step
		vm.steps = angular.copy(routeVm.formTemplate._steps).filter(step => step._stepAction !== enumStepAction.formCompleted);

		try {

	//	sort the steps by the step order
		formsHlp.sortByOrderField(vm.steps, routeVm.formTemplate._stepOrder);

	//	using form instance steps to build the participant list since it has resolved employee names
		let participants = logic.buildParticipantList(routeVm.formInstance._steps);

		vm.steps.forEach((step) => {

		//	find the matching form instance step
			step.instance = routeVm.formInstance._steps.find(s => s._formTemplateStepId === step.id);

			step.participantPermission = logic.getPermissionLabel(step);
			step.stepActionName = enumStepAction.byValue(step._stepAction);

		//	if we cant find a matching form instance step
		//	OR the step action of the step is 'complete form', return
			if (step.instance == null) { return; }

			step.otherParticipants = participants.reduce((prev, curr) => {
			//	we don't need to include the current step participant
				if(curr.templateStepId === step.id) {
					return prev;
				}
			//	find the matching permission object for this participant
				let matchingPerm = step._permissions.find((perm) => {
					return logic.matchByParticipants(perm, curr);
				});
			//	we are not displaying any participants without 'view' permission
				if(!matchingPerm || !matchingPerm.canView) { 
					return prev;
				}

				prev.push({
					name: curr.participantName,
					permissions: `view${matchingPerm.canEdit ? ' & edit' : ''}`
				});

				return prev;

			}, []);

		});

		} catch(err) {

		} finally {
			vm.loading = false;
		}

	}
}

// #-- END Segment (/form-instance/segment-steps-summary) --# //
// #--------------------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentFormInstanceStepsSummary', () => new StepsSummary('steps-summary') );
