import {Segment} from 'base-segment';
// #-------------------------------------------------# //
// #---- Segment (/form-builder/segment-content) ----# //

class Content extends Segment {
	/*@ngInject*/
	controller($injector, $scope, ssPermissionsSvc) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // eslint-disable-line

		// hookup segment logic
		const logic = $injector.get('segmentFormBuilderContentLogic'); // eslint-disable-line

		$scope.formVm = routeVm;

		// view model for tracking state
		const vm = $scope.vm = { // eslint-disable-line
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line
		};
	}
}

// #-- END Segment (/form-builder/segment-content) --# //
// #-------------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentFormBuilderContent', () => new Content('content') );
