import {Component} from 'base-component';
// #-------------------------------------# //
// #---- Component (cmp-box-resizer) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	box: '=',
	text: '=',
	onDelete: '&',
	isActive: '&'
};

class BoxResizer extends Component {
	/*@ngInject*/
	controller($scope, rAF, viewportOffsetHlp) {
		// view model for tracking state
		$scope.rAF = rAF;
		$scope.viewportOffsetHlp = viewportOffsetHlp;

		const vm = $scope.vm = { // jshint ignore:line
			optionsTemplate: `<cmp-box-${$scope.box.type}-options box="box"></cmp-box-${$scope.box.type}-options>`
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line

		};
	}

	link(scope, elem, attrs) {
		const box = scope.box;
		const el = elem[0];
		const parent = elem.parent();
		const parentEl = parent[0];
		const handlePoint = {x:0, y:0};
		const grabbing = {
			top: false,
			right: false,
			bottom: false,
			left: false
		};

		var parentRect, elRect;

		/////
		// setup the initial box size
		//
		el.style.top = `${box.top}%`;
		el.style.right = `${box.right}%`;
		el.style.bottom = `${box.bottom}%`;
		el.style.left = `${box.left}%`;

		/////
		// bind up our events
		//
		elem.on('mousedown', startDrag);
		elem.on('mouseup', stopDrag);
		// --
		parent.on('mouseup', stopDrag);
		// --
		scope.$on('$destroy', function () {
			parent.off('mouseup', stopDrag);
			parent.off('mousemove', trackMouse);
		});

		/////
		// event handlers
		//
		function startDrag(evt) {
			evt.stopPropagation();
			if (!scope.isActive()) { return; }

			parentRect = parentEl.getBoundingClientRect();
			elRect = el.getBoundingClientRect();

			var offsets = scope.viewportOffsetHlp.getViewportOffsets(window);

			handlePoint.x = evt.pageX - elRect.left - offsets.left;
			handlePoint.y = evt.pageY - elRect.top - offsets.top;

			// reset grabbed edges
			const edges = Object.keys(grabbing);
			edges.forEach(p => grabbing[p] = false);
			if (handlePoint.x <= 6) { grabbing.left = true; }
			if (el.offsetWidth - handlePoint.x <= 6) { grabbing.right = true; }
			if (handlePoint.y <= 6) { grabbing.top = true; }
			if (el.offsetHeight - handlePoint.y <= 6) { grabbing.bottom = true; }
			let grabbingEdge = edges.reduce((p, k) => p || grabbing[k], false);
			if (!grabbingEdge) {
				// if we are not grabbing any, we are grabbing all
				// ...profound, I know
				edges.forEach(p => grabbing[p] = true);
			}

			parent.on('mousemove', trackMouse);
		}

		function stopDrag(evt) {
			scope.isDrawing = false;
			scope.$root.safeApply();
			parent.off('mousemove', trackMouse);
		}

		function trackMouse(evt) {
			var offsets = scope.viewportOffsetHlp.getViewportOffsets(window);

			const pos = {
				x: evt.pageX - parentRect.left - offsets.left,
				y: evt.pageY - parentRect.top - offsets.top
			};

			if (grabbing.top) {
				let top = ((pos.y - handlePoint.y) / parentRect.height) * 100;
				box.top = (top <= 0) ? 0 : top;
				el.style.top = `${box.top}%`;
			}
			if (grabbing.left) {
				let left = ((pos.x - handlePoint.x) / parentRect.width) * 100;
				box.left = (left <= 0) ? 0 : left;
				el.style.left = `${box.left}%`;
			}
			if (grabbing.bottom) {
				let pxFromTop = pos.y + (elRect.height - handlePoint.y);
				let pxFromBottom = parentRect.height - pxFromTop;
				let bottom = (pxFromBottom / parentRect.height) * 100;
				box.bottom = (bottom <= 0) ? 0 : bottom;
				el.style.bottom = `${box.bottom}%`;
			}
			if (grabbing.right) {
				let pxFromLeft = pos.x + (elRect.width - handlePoint.x);
				let pxFromRight = parentRect.width - pxFromLeft;
				let right = (pxFromRight / parentRect.width) * 100;
				box.right = (right <= 0) ? 0 : right;
				el.style.right = `${box.right}%`;
			}

		}
	}
}

// #-- END Component (cmp-box-resizer) --# //
// #-------------------------------------# //

angular.module('ec-forms-components').directive('cmpBoxResizer', () => new BoxResizer(props, 'box-resizer') );
