import {Segment} from 'base-segment';
// #---------------------------------------------------------------# //
// #---- Segment (/form-builder/preview/segment-steps-summary) ----# //

class StepsSummary extends Segment {
	/*@ngInject*/
	controller($injector, $scope, ssEventSvc, ssEnum, formsHlp) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // eslint-disable-line
		const formVm = $scope.formVm;
		const logic = $injector.get('segmentFormBuilderPreviewStepsSummaryLogic'); // eslint-disable-line
		const personalCategoryEnums = ssEnum.personnelCategory.getEnums();
		const medicalCategoryEnums = ssEnum.medicalCategory.getEnums();
		const stepMap = new Map();

		// view model for tracking state
		const vm = $scope.vm = { // eslint-disable-line
			steps: [],
			stepAction: ssEnum.stepAction,
			selectedStep: null,
			loading: true
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line
			selectStep(index, step) {
				vm.selectedStepId = step.id;
				ssEventSvc.emit('form-builder.summary.step-clicked', index, step);
			}
		};

		ssEventSvc.on('form-builder.builder.step-clicked', function(index, step) {
			vm.selectedStepId = stepMap.get(step.id).id;
		}, $scope);

		(function initVmFromBuilderData() {

		//	lets make a copy of the form step data since we're not mutating it in any way, and to prevent any processing conflicts with the builder segment
			vm.steps = angular.copy(routeVm.formData._steps);
			
		//	sort the steps by the step order 
			formsHlp.sortByOrderField(vm.steps, routeVm.formData._stepOrder);

		//	build an active participant list	
			let participants = logic.buildParticipantList(vm.steps);

			vm.steps.forEach(function(step) {

				if (typeof step.id === 'string') {
					stepMap.set(step.id, step);
				}

				if(step._stepAction !== ssEnum.stepAction.formCompleted) {
					let stepParticipant = participants.find(x => x.stepIds.includes(step.id));
					step.participantLabel = stepParticipant ? stepParticipant.participantName : '';
					step.participantPermission = logic.getPermissionLabel(step);
					step.stepActionLabel = ssEnum.stepAction.byValue(step._stepAction);
				}
				step.eventDetails = logic.getEventDetails(step);
				step.otherParticipants = logic.getOtherParticipants(participants, step);
			});

			// find and separate the review & final step from the rest of the steps
			let finalStep = vm.steps.find((step) => {
				return step._stepAction === ssEnum.stepAction.formCompleted;
			});

			let reviewStep = vm.steps.find((step) => {
				return step._stepAction === ssEnum.stepAction.employeeProfileReview;
			});

			vm.finalStep = vm.steps.splice(vm.steps.indexOf(finalStep), 1)[0];
			if(reviewStep) {
				vm.reviewStep = vm.steps.splice(vm.steps.indexOf(reviewStep), 1)[0]
			};

			if (routeVm.formData._employeeRecord.addToEmployeeRecord) {
				let personalAndMedicalDic = buildEnumDictionary(personalCategoryEnums, medicalCategoryEnums);
				let category = routeVm.formData._employeeRecord.category;
				let subCategory = routeVm.formData._employeeRecord.subCategory;
				vm.finalStep.employeeRecord = personalAndMedicalDic[category + '-' + subCategory];
			}

			vm.selectedStepId = vm.steps[0].id;
			vm.loading = false;
		}());

		function buildEnumDictionary() {
			let enumList = Array.from(arguments);
			let dictionary = {};
			enumList.forEach((enumItem, index) => {
				enumItem.forEach((item) => {
					dictionary[index + '-' + item.value] = item.name;
				});
			});
			return dictionary;
		}
	}
}

// #-- END Segment (/form-builder/preview/segment-steps-summary) --# //
// #---------------------------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentFormBuilderPreviewStepsSummary', () => new StepsSummary('steps-summary') );
