/*@ngInject*/
function logic(ssEnum, enumFormStatus, enumStepAction, fieldsHlp) {
	let isFormCompleted = false;

	// logic api
	return {
		processStep,
		mapName
	};

	/**
	 * Calculates the disabled and editMode property of each step.
	 *
	 * @param {Object} formTemplate - the form template object which the form instance is based on
	 * @param {Object} formInstance - the current Form instance
	 * @param {Object} activeUser - current logged-in user
	 */
	function processStep({tStep, formInstance}) {

		let step = Object.assign({}, tStep);

		//	Is the form instance complete?
		isFormCompleted = formInstance._statusId === enumFormStatus.complete;

		/*
		The View/Edit permissions for each step of the form instance should be based on the following criteria:
		** 1. If the user has the IDM administrator role, they CAN VIEW and CAN EDIT all steps
		** 2. If the user has the Employee.FormInstances permission (Can edit another employee's form instances), they CAN VIEW and CAN EDIT all steps
		** 3. If the user is the FormAdministrator, they CAN VIEW and CAN EDIT all steps
		** 4. All other step visibility is governed by the step permissions object on the step, based on the participant type and whether the "canView" and/or "canEdit" permission is selected.
		** NOTE: if "canView" is false, the fields of a step are not returned by the server.
		*/
		let canEditStep = step.instance._meta.permissions.permittedActions.edit;

		//	Determine what the edit and disabled state of a step should be based on the resolved permissions
		step.editMode = true;
		step.disabled = false;

		if (!canEditStep) {
			step.editMode = false;
			step.disabled = true;
		}

	//	disregard any permissions disable the step if the form is complete
		if(isFormCompleted) {
			step.editMode = false;
			step.disabled = false;
		}

		//	make step permissions available via routeVm
		step.instance.editMode = step.editMode;
		step.instance.disabled = step.disabled;

		//	Determine which action buttons to show
		step = stepActions({ tStep: step });

		processFields(step);

		step.fieldGroups = makeFieldGroups(step);

		return step;
	}

	function stepActions({ tStep }) {
		let showFormEntry = false;
		let showReviewForm = false;
		let showReviewApproveOrDeny = false;
		let showEmployeeProfileReview = false;

		if ((tStep.currentStep || !tStep.disabled) && !isFormCompleted) {
			showFormEntry = tStep._stepAction === enumStepAction.formEntry;
			showReviewForm = tStep._stepAction === enumStepAction.reviewForm;
			showReviewApproveOrDeny = tStep._stepAction === enumStepAction.reviewApproveOrDeny,
			showEmployeeProfileReview = tStep._stepAction === enumStepAction.employeeProfileReview;
		}

		let step = Object.assign({}, tStep, { showFormEntry, showReviewForm, showReviewApproveOrDeny, showEmployeeProfileReview })

		return step;
	}

//	process the fields to determine access permissions, config etc.
	function processFields(step) {

	//	make sure we are only acting on fields that have a 'fieldDefId' property
		let actionableFields = step.fields.filter(f => { return f._fieldDefId != null });

		actionableFields.forEach(field => {

		//	find the matching default field and update it with any missing properties
			let defaultField = fieldsHlp.getFieldById(field);
			if (defaultField != null) {
				updateFieldAttributes(field, defaultField);
			}

			field.editMode = step.editMode;
			field.disabled = step.disabled;

			if (field._fieldType === 'super-select-list' ||
				field._fieldTypeOptions &&
				field._fieldTypeOptions.view === 'readOnlyText') {

					let json = JSON.parse(field._configJson);
					if (json.externalSource != null && ssEnum[json.externalSource] != null) {
						field.externalSourceOptions = ssEnum[json.externalSource].enums;
					}
			} else if (field._fieldType === 'super-digital-signature') {
				field.onBehalf = false;
				try {
					// set onBehalf to true if the step participant is not the current logged in activeUser
					field.onBehalf = !(field.step._stepParticipant && (field.step._stepParticipant.employeeIds[0] === activeUser.id || activeUser.idmAdministrator));
				} catch (e) {
					// TODO - what to do?
				}
			}
		//	override step edit permission for sensitive fields
			if(field._sensitive) {
				if(!isFormCompleted) {
					field.editMode = step.instance._meta.permissions.permittedActions.editSensitive;
				}
				field.sensitiveRedacted = !step.instance._meta.permissions.permittedActions.viewSensitive;
			}

		// TEMP - removes '_fieldGroup' property from custom field groups
			let customFieldIds = [
				'951626b1-a422-46b4-9fa1-3b5acf7ee8fc',
				'977a2273-62c1-4eef-8d63-91f9d8d1e0bf',
				'10a88a23-f8f3-48eb-aa48-c466db06378b',
				'17ff0b2c-4973-4181-92ce-b1c50c851165',
				'c2e31de2-09e8-4563-acb1-0e190a8e3b11',
				'5299a28c-56d5-4dc3-81ae-6f9f741eb324',
				'e215f04f-037a-479e-b5da-9bb3155b55d5',
				'9eabf24b-ae80-4488-a164-2a6f78a50cbd',
				'b6390770-0590-418c-b516-2080b243e9b7',
				'2056c221-97c8-4994-962c-f4bfc319a9e4'
			];

			if(customFieldIds.includes(field._fieldDefId)) {
				field._fieldGroup = undefined;
			}

		});
	
	}

//	this function will add any missing keys to existing field objects
	function updateFieldAttributes(field, matchingField) {
		Object.keys(matchingField).forEach(matchingFieldKey => {
			var fieldKeys = Object.keys(field);

			if (!fieldKeys.includes(matchingFieldKey)) {
				field[matchingFieldKey] = matchingField[matchingFieldKey];
			}
		});
	}

//	separate the fields into their respective field groups
	function makeFieldGroups(step){
		let fieldGroups = [];
		step.fields.forEach(field => {
			field.step = step.instance;

			if (fieldGroups.length === 0) {
				fieldGroups.push([field]);
				return;
			}

			let group = fieldGroups[fieldGroups.length - 1];
			if (field._fieldGroup != null && group[0]._fieldGroup === field._fieldGroup) {
				group.push(field);
			} else {
				fieldGroups.push([field]);
			}
		});
		return fieldGroups;
	}

	function mapName(key, list) {
		if (key === null || key === '') { return ''; }

		let obj = list.find(v => v.id === key);
		let name = '';

		if (obj != null) { name = obj._name; }

		return name;
	}

}
angular.module('ec-forms-segment-logic').factory('segmentInboxFormInstanceFormLogic', logic);
