import {Component} from 'base-component';
// #----------------------------------------------------# //
// #---- Component (cmp-properties-custom-filelink) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	fields: '='
};

class PropertiesCustomFilelink extends Component {
	/*@ngInject*/
	controller($scope, ssValidationHlp) {

		const field = $scope.field = $scope.fields[0];
		const validator = $scope.validator = ssValidationHlp; // jshint ignore:line

		// view model for tracking state
		const vm = $scope.vm = { // jshint ignore:line
			validator: {
				requiredUrl: ssValidationHlp.url({ require:true })
			}
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
		};

		const urlPreRegEx = /^((http|https):\/\/)/;

		$scope.$watch('field.config', function(val) {

			$scope.$watch('field.config.location', function(nv, ov) {
				
				if (nv === ov){return;}

				//set the display text of the link to the value of the link location if no link name is defined
				if (nv && nv.length !== 0 && !field.config.name) {
					field.config.display = nv;
				} else if(field.config.name){
					field.config.display = field.config.name; 
				} else {
					field.config.display = 'link will display here';
				}

				if (vm.validator.requiredUrl(nv) === true) {
					
					field.config.isValid = true;
					if(!urlPreRegEx.test(field.config.location)) {
						field.config.location = `http://${field.config.location}`;
					}
				} else {
					field.config.isValid = false;
				}
			});

			// set the link display text to the value of the link name field as long as there is a value
			$scope.$watch('field.config.name', function(nv, ov) {
				
				if (nv === ov){return;}

				if(nv && nv.length !== 0){
					field.config.display = nv;
				} else if (field.config.location) {
					field.config.display = field.config.location;
				} else {
					field.config.display = 'link will display here';
				}

			});

		});
		
	}
}

// #-- END Component (cmp-properties-custom-filelink) --# //
// #----------------------------------------------------# //

angular.module('ec-forms-components').directive('cmpPropertiesCustomFilelink', () => new PropertiesCustomFilelink(props, 'properties-custom-filelink') );
