import {Component} from 'base-component';
// #-----------------------------------------------------# //
// #---- Component (cmp-properties-custom-signature) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	fields: '='
};

class PropertiesCustomSignature extends Component {
	/*@ngInject*/
	controller($scope, ssAsync, ssSignatureApi, ssEventSvc, ssFlagSvc, ssPermissionsSvc) {
		// view model for tracking state
		const vm = $scope.vm = { //eslint-disable-line
			field: $scope.fields[0],
			selectedSignatures: [],
			orientation: $scope.fields[0].config.orientation,
			fcEnableSignatures: ssFlagSvc.checkState('fc-enable-signatures'),
			signatureDisplay: $scope.fields[0].config.signatureDisplay || 'step-participant' //defaulting for old templates
		};

		vm.canAssociateSignatures = ssPermissionsSvc.isPermittedAction('formTemplates', 'associateSignatures') || !!$scope.$root.isPermitted.activeUser.idmAdministrator;
		
		// behavior that needs to be bound to the view
		const go = $scope.go = { //eslint-disable-line
			dropSignature(event, index, type, item) {
				let matchedSignature = vm.selectedSignatures.find(x => x.id === item.id);
				if (matchedSignature) {
					let currIndex = vm.selectedSignatures.indexOf(matchedSignature);
					move(currIndex, index);
				} else {
					vm.selectedSignatures.splice(index, 0, item);
				}
			}
		};

		function move(from, to) {
			vm.selectedSignatures.splice(to, 0, vm.selectedSignatures.splice(from,1)[0]);
		}

		if(vm.fcEnableSignatures) {
			ssAsync(function*() {
				try {
					vm.loadingDropdown = true;
	
					vm.allSignatures = yield ssSignatureApi.query();

					if (vm.field.config.signatureIds != null) {
						vm.field.config.signatureIds.forEach(id => {
							let match = vm.allSignatures.find(s => {
								return s.id === id;
							});
		
							$scope.$applyAsync(() => {
								vm.selectedSignatures.push(match);
								vm.loadingDropdown = false;
							});
						});
					}
	
					$scope.$watchCollection('vm.selectedSignatures', (nv, ov) => {
						if (nv === ov) { return; }
						vm.field.config.signatureIds = nv.map(s => s.id);
						vm.field._configJson = JSON.stringify(vm.field.config);
					});
	
					$scope.$watch('vm.orientation', (nv, ov) => {
						if (nv === ov) { return; }
						vm.field.config.orientation = nv;
						vm.field._configJson = JSON.stringify(vm.field.config);
					});

					$scope.$watch('vm.signatureDisplay', (nv, ov) => {
						if (nv === ov) { return; }
						vm.field._required = nv === 'step-participant' ? true : false;
						vm.field.config.signatureDisplay = nv;
						vm.field._configJson = JSON.stringify(vm.field.config);
					});
	
				} catch (e) {
					console && console.error && console.error('ERROR', e);
				} finally {
					vm.loadingDropdown = false;
				}
			})();
		}
	}
}

// #-- END Component (cmp-properties-custom-signature) --# //
// #-----------------------------------------------------# //

angular.module('ec-forms-components').directive('cmpPropertiesCustomSignature', () => new PropertiesCustomSignature(props, 'properties-custom-signature') );
