/*@ngInject*/
function logic(ssEnum) {
	// logic api
	return {
		buildParticipantList,
		getEventDetails,
		getPermissionLabel,
		getOtherParticipants,
		makeStep,
		matchByParticipants,
	};

	function buildParticipantList(steps) {
		let parts = [];
		steps.forEach(step => {
			//	skip any steps without participants
			if (!step._stepParticipant || step._stepAction == ssEnum.stepAction.formCompleted ) { return; };
			//	make sure the employee id is a valid value
			let spEmpId = angular.isArray(step._stepParticipant.employeeIds) && step._stepParticipant.employeeIds.length ? step._stepParticipant.employeeIds[0] : '';

			let spLabel = step._stepParticipant.name ? step._stepParticipant.name : ssEnum.stepParticipantType.byValue(step._stepParticipant.participantType);

			//	if we already added a participant of this type, add the step ID to it and return
			let existingPart = parts.find((part) => matchByParticipants(part, step));
			if(existingPart) {
				existingPart.stepIds.push(step.id);
				return;
			};
		
			//	if not, add a new one
			parts.push({
				stepIds: [step.id],
				participantName: spLabel,
				participantType: step._stepParticipant.participantType,
				employeeId: spEmpId
			});
		});
		return parts;
	}

	function getEventDetails(step) {
		if(!step._stepEvents) { return []; };
		return step._stepEvents.reduce(function(prev, curr) {
			if (!curr.eventActions.length) { return prev; };
			prev.push({
				title: ssEnum.stepEventType.byValue(curr.eventType),
				actions: curr.eventActions.reduce((p, c) => {
					if (c.eventAction === ssEnum.stepEventAction.stopWorkFlow) { return p; };
					p.push(ssEnum.stepEventAction.byValue(c.eventAction));
					return p;
				}, [])
			});
			return prev;
		}, []);
	}

	function getOtherParticipants(participants, step) {
		if(!participants.length) { return []; };
		return participants.reduce((prev, curr) => {
			//	omit the main step participant
			if (curr.stepIds.includes(step.id)) { return prev; };
			//	find a matching permission object
			let matchingPerm = step._permissions.find((perm) => {
				return matchByParticipants(perm, curr);
			});

			/*
			 **	if there is no matching perm OR the matching permission object
			 **	does not allow 'view', return
			 */
			if (!matchingPerm || !matchingPerm.canView) {
				return prev;
			}
			/*	create the returned object */
			prev.push({
				name: curr.participantName,
				permissions: `view${matchingPerm.canEdit ? ' & edit' : ''}`
			});
			return prev;
		}, []);
	}

	function getPermissionLabel(step) {
		if(!step._permissions) { return ''; };
		let permission = step._permissions.find((perm) => {
			return matchByParticipants(perm, step);
		});
		return permission ? `view${permission.canEdit ? ' & edit' : ''}` : '';
	}

	function makeStep() {
		return { _name: '', _fieldOrder: '', fieldGroups: [] };
	}

	function matchByParticipants(source, target) {
		let targetId = target._stepParticipant ? target._stepParticipant.employeeIds[0] : target.employeeId;
		let targetParticipantType = target._stepParticipant ? target._stepParticipant.participantType : target.participantType;

		if (source.participantType === ssEnum.stepParticipantType.specificPerson) {
			return source.employeeId === targetId;
		}
		return source.participantType === targetParticipantType;
	}
}
angular.module('ec-forms-segment-logic').factory('segmentFormBuilderPreviewStepsSummaryLogic', logic);
