import {Component} from 'base-component';
// #------------------------------------------------------# //
// #---- Component (cmp-properties-custom-fileupload) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	fields: '='
};

class PropertiesCustomFileupload extends Component {
	/*@ngInject*/
	controller($scope, enumPersonnelCategory, enumMedicalCategory) {
		// view model for tracking state
		const formVm = $scope.$root.utils.findInParentScope('formVm', $scope);
		const vm = $scope.vm = { // jshint ignore:line
			field: $scope.fields[0],
			personnel: enumPersonnelCategory,
			medical: enumMedicalCategory,

			// default to Personnel Other
			webFormCategory: formVm.formData._formFields[0]._documentCategory,
			webFormSubCategory: formVm.formData._formFields[0]._documentSubCategory,
			webFormCatSubCat: formVm.formData._formFields[0]._documentCategory + '-' + formVm.formData._formFields[0]._documentSubCategory,
			addToEmpRecord: true
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
			click(evt) { }
		};

		$scope.$watch('vm.addToEmpRecord', (nv, ov) => {
			if (nv === ov) {
				return;
			}

			formVm._updateEmployeeRecord = nv;
			formVm.modelChanged();
		});

		$scope.$watch('vm.webFormCatSubCat', (nv, ov) => {
			if (nv === ov) {
				return;
			}

			// The dropdown value is formated: 'category-subcategory' 
			//	since the select is a merged list of personnel and medical selections
			let category = nv.split('-')[0];
			let subCategory = nv.split('-')[1];

			vm.field._updateEmployeeRecord = true;
			vm.field._documentCategory = category;
			vm.field._documentSubCategory = subCategory;
			formVm.modelChanged();
		});
	}
}

// #-- END Component (cmp-properties-custom-fileupload) --# //
// #------------------------------------------------------# //

angular.module('ec-forms-components').directive('cmpPropertiesCustomFileupload', () => new PropertiesCustomFileupload(props, 'properties-custom-fileupload') );
