import {Segment} from 'base-segment';
// #-------------------------------------------------------------# //
// #---- Segment (/form-builder/preview/segment-page-header) ----# //

class PageHeader extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $stateParams, $location, ssAsync, ssEventSvc, ssOrganizationSvc, rAF, ssDateHlp, ssDateByOrgHlp, ssFormTemplatesApi, $sce) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // eslint-disable-line

		const formVm = $scope.formVm;

		// hookup segment logic
		const logic = $injector.get('segmentFormBuilderPreviewPageHeaderLogic'); // eslint-disable-line

		var modifiedText = null;

		rAF(() => {
			ssEventSvc.emit('allow-fullscreen');
		}, $scope);

		// view model for tracking state
		const vm = $scope.vm = { // eslint-disable-line
			form: formVm.formData
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line
			close() {
				$location.url($stateParams.returnTo);
			},
			toggleFormView: (showPDF) => {
				if (showPDF) {
					ssAsync(function*() {
						try {
							let formOverlay = yield ssFormTemplatesApi.generatePDFOverlay({
								formTemplateId: routeVm.formData.id
							});

							if (formOverlay._string !== null) {
								// sanitize the '_string' property as a trusted URL for use in the iframe
								routeVm.PDFOverlayUrl = $sce.trustAsResourceUrl(formOverlay._string);
								routeVm.showPDFOverlay = showPDF;
							} else {
								ssToastHlp.error('There was a problem generating the PDF Overlay preview. Please refresh the page and try again.');
							}
						} catch(e) {
							console && console.log && console.log('Error getting form', e);
						}
					})();
				} else {
					routeVm.showPDFOverlay = showPDF;
				}
			}
		};

		ssAsync(function* () {
			const org = yield ssOrganizationSvc.getActiveOrg();
			vm.formStatusLabel = logic.getStatusLabel(formVm.formData, org);
			vm.formStatus = logic.getStatus(formVm.formData, org);

			if (isValidModified(vm.form._modifiedDt)) {
				let modifiedTimeMoment = ssDateByOrgHlp.utcIsoToRelativeMoment(vm.form._modifiedDt, org);

				let modifiedText = modifiedTimeMoment.calendar(null, { // jshint ignore:line
					sameDay: '[Today at] LT',
					lastDay: '[Yesterday at] LT',
					lastWeek: 'ddd [at] LT',
					sameElse: 'L [at] LT'
				});

				vm.modifiedDate = `Modified ${modifiedText} by ${vm.form._modifier.name}`;
			}
		})();

		function isValidModified(date) {
			return !ssDateHlp.isForeverFuture(date) && !ssDateHlp.isForeverPast(date);
		} 
	}
}

// #-- END Segment (/form-builder/preview/segment-page-header) --# //
// #-------------------------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentFormBuilderPreviewPageHeader', () => new PageHeader('page-header') );
