import {Component} from 'base-component';
// #-----------------------------------------------------# //
// #---- Component (cmp-properties-step-permissions) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	form: '=',
	step: '='
};

class PropertiesStepPermissions extends Component {
	/*@ngInject*/
	controller($scope, ssAsync, ssEmployeeApi, enumStepParticipantType, ssModalSvc, ssEnum, formsHlp) {
		const form = $scope.form;
		let deregs = [];
        $scope.routeVm = $scope.$root.utils.findInParentScope('routeVm', $scope);
		$scope.formVm = $scope.$root.utils.findInParentScope('formVm', $scope);
		 
		// view model for tracking state
		const vm = $scope.vm = { // eslint-disable-line
		};

		const go = $scope.go = {
			matchByParticipant: (source, target) => {
				return formsHlp.matchByParticipant(source, target)
			},
			adjustStepAccess: (level, permission) => {

				let isPrimaryParticipant = formsHlp.matchByParticipant(permission,$scope.step._stepParticipant);

				if(level === 'canView' && !permission[level]) {
					permission.canEdit = false;
					permission.canViewSensitive = false;
					permission.canEditSensitive = false;
					return;
				}

				if(level === 'canViewSensitive' && !permission[level]) {
					permission.canEditSensitive = false;
					return;
				}

				if(level === 'canEdit') {
					if(permission[level]) {
						if(!$scope.formVm.suppressEditAccessModal && !isPrimaryParticipant) {
							go.showEditModal().then(() => {
								permission[level] = permission.canView = true;
							}).catch((rej) => {
								permission[level] = false;
								permission.canView = !permission.canView ? false : true;
							});
						} else {
							permission.canView = true;
						}
					} else {
						permission.canEditSensitive = false;
					}
					return;
				}

				if(level === 'canEditSensitive') {
					if(!$scope.formVm.suppressEditAccessModal && !isPrimaryParticipant && !permission.canEdit) {
						go.showEditModal().then(() => {
							permission.canEdit = permission.canViewSensitive = true;
						}).catch((rej) => {
							permission[level] = permission.canEdit = false;
							permission.canViewSensitive = !permission.canViewSensitive ? false : true;
						});
					} else {
						permission.canEdit = permission.canViewSensitive = true;
					}
				}
			},
			showEditModal: () => {
				var config = {
					title: 'Edit Step Access',
					content: 'This form participant will have the ability to modify this form step only after it has been submitted by the primary participant.',
					primaryText: 'Ok',
					secondaryText: 'cancel',
					showSuppress: true,
					alwaysOnTop: true
				};
				return ssModalSvc.showStandard(config).then((scope) => {
					$scope.formVm.suppressEditAccessModal = scope.suppressTheModal;
				}).catch((scope) => {
					$scope.formVm.suppressEditAccessModal = scope.suppressTheModal;
					throw 'cancel';
				});
			}
		};

		(function init() {
			if ($scope.step._permissions && $scope.step._permissions.length) {
				// call deregister for existing watches
				deregs.forEach(d => d());
				// create new watches
				for (let i = 0; i < $scope.step._permissions.length; i++) {
					deregs.push($scope.$watchGroup([`step._permissions[${i}].canView`, `step._permissions[${i}].canEdit`, `step._permissions[${i}].canViewSensitive`, `step._permissions[${i}].canEditSensitive`], (nv, ov) => {
						if(nv != null && (angular.equals(nv, ov))) { return; }
						// create a new reference for json api to pick up
						$scope.step._permissions = $scope.step._permissions.slice();
					}));
				}
			}
		}());

	//	watch for addition of senstive fields and show sensitive field step access controls
		$scope.$watchCollection('step.fieldGroups', (nv, ov) => {
			vm.hasSensitiveFields = hasSensitiveFields();
		});

		$scope.$watch('formVm.saving', (nv, ov) => {
			if(nv == null || (angular.equals(nv, ov))) { return; }
			if (nv === false && ov === true) {
				permissionDisplay($scope.step)
			}
		});

		$scope.$watch('form._steps.length', (nv, ov) => {
			if(angular.equals(nv, ov)) { return; }
			if(nv && nv < ov) {
				formsHlp.updatePermissions({
					steps: form._steps,
					currentStep: $scope.step,
					options: {
						isHRMS: $scope.routeVm.isHRMSEnabled
					}
				});
				permissionDisplay($scope.step);
			}
		});

		$scope.$watch('step._stepParticipant', (nv, ov) => {
			// display the step participants
			permissionDisplay($scope.step);
			// if there's no step participant (new step) or the step participant hasnt changed, do not update permissions
			if(nv._participantType && nv._participantType === ov._participantType && nv.employeeIds[0] ===  ov.employeeIds[0]) { return; }
			// update step permissions
			formsHlp.updatePermissions({
				steps: form._steps,
				currentStep: $scope.step,
				newStepParticipant: nv,
				oldStepParticipant: ov,
				options: {
					isHRMS: $scope.routeVm.isHRMSEnabled
				}
			});
			// update the display after the permission updates
			permissionDisplay($scope.step);
		}, true);

		function hasSensitiveFields() {
			if($scope.step.fieldGroups.length) {
				return $scope.step.fieldGroups.reduce((prev, curr) => {
					return [...prev, ...curr];
				}, $scope.step.fieldGroups[0]).some((field) => {
					return field._sensitive == true || Array.isArray(field._tokenFields) && !!field._tokenFields.find(field => field.sensitive);
				})
			} else {
				return false;
			}
		}

		// @param {Obj} stepPerm: a step permission object
		function permissionDisplay(step) {
			step._permissions.forEach((perm) => {
				if (perm.participantType == null) {
					perm.display = 'select participant';
				} else if (perm.participantType === enumStepParticipantType.specificPerson) {
					if (perm.emp) {
						perm.display = perm.emp.fullName;
					} else if (perm.employeeId.length) {
						ssAsync(function*() {
							try {
								perm.display = 'loading participant...';
								// supressing internal error handling for the get employee call so that the activation endpoint can handle all errors.
								perm.emp = yield ssEmployeeApi.get({
									id: perm.employeeId,
									fields: 'firstname,lastname'
								}, '', false);
									perm.display = perm.emp.fullName;
							} catch(err) {
								console && console.error && console.error(err);
							}
						})();
					} else {
						perm.display = 'select participant';
					}
				} else {
					perm.display = enumStepParticipantType.byValue(perm.participantType);
				}
			});
		}
		
	}
}

// #-- END Component (cmp-properties-step-permissions) --# //
// #-----------------------------------------------------# //

angular.module('ec-forms-components').directive('cmpPropertiesStepPermissions', () => new PropertiesStepPermissions(props, 'properties-step-permissions') );
