import { State } from 'base-state';
import { PLUGIN_DEF as placeholder_select } from '../../components/cmp-text-editor/plugins/placeholder_select.js';

// #--------------------------------# //
// #---- State (/form-builder) ----# //

// view model that is shared by all segments on this route,
// used for segment coordination. Define initial state here.
const routeVm = {};

const stateName = 'formBuilder';

// config object for ui-router
// http://angular-ui.github.io/ui-router/site/#/api/ui.router.state.$stateProvider#methods_state
const stateConfig = {
	url: '/form-builder/{formId:[0-9A-Za-z-]+}',
	templateUrl: 'routes/form-builder/form-builder.html',
	resolve: {
		form: /*@ngInject*/ function($stateParams, ssAsync, ssFormTemplatesApi, ssOrganizationSvc) {
			return ssAsync(function*() {
				try {
					routeVm.formData = yield ssFormTemplatesApi.get({
						id: $stateParams.formId,
						include: 'steps,formFields,statusHistory'
					});
					routeVm.org = yield ssOrganizationSvc.getActiveOrg();
				} catch (e) {
					console && console.log && console.log('Error getting form', e);
				}
			})();
		},
		$title: /*@ngInject*/ (form) => {
			return `Frontline Central - ${routeVm.formData._name}`;
		}
	},
	$permissions: (permissionSvc) => permissionSvc.isPermittedAction('formTemplates', 'create') || permissionSvc.isPermittedAction('formTemplates', 'edit') || permissionSvc.isPermittedAction('formInstances', 'send')
};

