// 	script.src = 'https://cdn.ckeditor.com/4.13.0/basic/ckeditor.js';
// 	// script.src = CKEDITOR_BASEPATH + 'ckeditor.js';
// 	d.getElementsByTagName('head')[0].appendChild(script);
// };

function loadCKeditor(d) {
	let ckeditor = d.createElement('script');
	ckeditor.type = 'text/javascript';
	ckeditor.async = true;
	ckeditor.src = 'https://cdn.ckeditor.com/4.13.1/full-all/ckeditor.js';
	d.getElementsByTagName('head')[0].appendChild(ckeditor);
	ckeditor.onload = function(){
		CKEDITOR.plugins.add(placeholder_select.name, placeholder_select.definition);
	};
};

// #-- END State (/form-builder) --# //
// #--------------------------------# //
angular.module('ec-forms').run(loadCKeditor(window.document));

angular.module('ec-forms').config( /*@ngInject*/ ($stateProvider) => {
	$stateProvider.state(`base.${stateName}`, new State(stateConfig, routeVm));
});
