function getPlaceholders() {
	// Define changes to default configuration here.
	// For complete reference see:
	// https://ckeditor.com/docs/ckeditor4/latest/api/CKEDITOR_config.html
	// The toolbar groups arrangement, optimized for a single toolbar row.
    const PLACEHOLDER_TOKENS = [{
        title: 'Prefix',
        value: 'Employee.Title'
    },{
        title: 'First Name',
        value: 'Employee.FirstName'
    },{
        title: 'Middle Name',
        value: 'Employee.MiddleName'
    },{
        title: 'Last Name',
        value: 'Employee.LastName'
    },{
        title: 'Suffix',
        value: 'Employee.Suffix'
    },{
        title: 'Other First Name',
        value: 'Employee.AliasFirstName'
    },{
        title: 'Other Middle Name',
        value: 'Employee.AliasMiddleName'
    },{
        title: 'Other Last Name',
        value: 'Employee.AliasLastName'
    },{
        title: 'Address Line 1',
        value: 'Employee.Addresses.Primary.Street1'
    },{
        title: 'Address Line 2',
        value: 'Employee.Addresses.Primary.Street2'
    },{
        title: 'City',
        value: 'Employee.Addresses.Primary.City'
    },{
        title: 'State',
        value: 'Employee.Addresses.Primary.State'
    },{
        title: 'Zip Code',
        value: 'Employee.Addresses.Primary.Zip'
    },{
        title: 'Work Phone Number',
        value: 'Employee.PhoneNumbers.Work.Number'
    },{
        title: 'Work Extension',
        value: 'Employee.PhoneNumbers.Work.Extension'
    },{
        title: 'Personal Phone Number',
        value: 'Employee.PhoneNumbers.Personal.Number'
    },{
        title: 'Other Phone Number',
        value: 'Employee.PhoneNumbers.Other.Number'
    },{
        title: 'Work Email Address',
        value: 'Employee.Emails.Work.EmailAddress'
    },{
        title: 'Personal Email Address',
        value: 'Employee.Emails.Personal.EmailAddress'
    },{
        title: 'Birth Date',
        value: 'Employee.BirthDateUtc'
    },{
        title: 'SSN',
        value: 'Employee.SSN'
    },{
        title: 'External ID',
        value: 'Employee.ExternalId'
    },{
        title: 'Marital Status',
        value: 'Employee.MaritalStatus'
    },{
        title: 'Gender',
        value: 'Employee.Gender'
    },{
        title: 'Ethnicity',
        value: 'Employee.Ethnicity'
    },{
        title: 'Race',
        value: 'Employee.Race'
    },{
        title: 'Hire Date',
        value: 'Employee.HireDate'
    },{
        title: 'Seniority Date',
        value: 'Employee.SeniorityDate'
    },{
        title: 'Tenure Date',
        value: 'Employee.TenureDate'
    },{
        title: 'Primary Assignment - Start Date',
        value: 'Employee.Assignments.Primary.StartDateTimeUTC'
    },{
        title: 'Primary Assignment - End Date',
        value: 'Employee.Assignments.Primary.EndDateTimeUTC'
    },{
        title: 'Primary Assignment - Employment Group',
        value: 'Employee.Assignments.Primary.JobType.EmploymentGroup.Name',
        hideForHrms: true
    },{
        title: 'Primary Assignment - Job Type',
        value: 'Employee.Assignments.Primary.JobType.Name'
    },{
        title: 'Primary Assignment - Position Name',
        value: 'Employee.Assignments.Primary.Name'
    },{
        title: 'Primary Assignment - Location',
        value: 'Employee.Assignments.Primary.Location.Name'
    },{
        title: 'Primary Assignment - Department',
        value: 'Employee.Assignments.Primary.Department.Name'
    },{
        title: 'Primary Assignment - External ID',
        value: 'Employee.Assignments.Primary.ExternalId',
    },{
        title: 'Primary Assignment - FTE',
        value: 'Employee.Assignments.Primary.FTE',
        hideForHrms: true
    },{
        title: 'Primary Assignment - Hours Per Day',
        value: 'Employee.Assignments.Primary.HoursPerDay',
        hideForHrms: true
    },{
        title: 'Primary Assignment - Days Per Year',
        value: 'Employee.Assignments.Primary.WorkDays',
        hideForHrms: true
    },{
        title: 'Primary Assignment - Pay Schedule',
        value: 'Employee.Assignments.Primary.PaySchedule.Name',
        hideForHrms: true
    },{
        title: 'Primary Assignment - Pay Lane',
        value: 'Employee.Assignments.Primary.PayLane.Name',
        hideForHrms: true
    },{
        title: 'Primary Assignment - Pay Step',
        value: 'Employee.Assignments.Primary.PayStep.Name',
        hideForHrms: true
    },{
        title: 'Primary Assignment - # Pays Per Year',
        value: 'Employee.Assignments.Primary.PayPeriodsPerYear',
        hideForHrms: true
    },{
        title: 'Primary Assignment - Pay Per Period',
        value: 'Employee.Assignments.Primary.PayPerPeriod',
        hideForHrms: true
    },{
        title: 'Primary Assignment - Salary',
        value: 'Employee.Assignments.Primary.Salary',
        hideForHrms: true
    },{
        title: 'Primary Assignment - Sick Days Per Year',
        value: 'Employee.Assignments.Primary.SickDaysPerSchoolYear',
        hideForHrms: true
    },{
        title: 'Primary Assignment - Personal Days Per Year',
        value: 'Employee.Assignments.Primary.PersonalDaysPerSchoolYear',
        hideForHrms: true
    },{
        title: 'Primary Assignment - Vacation Days Per Year',
        value: 'Employee.Assignments.Primary.VacationDaysPerSchoolYear',
        hideForHrms: true
    },{
        title: 'Primary Assignment - Paid Holidays Per Year',
        value: 'Employee.Assignments.Primary.PaidHolidaysPerSchoolYear',
        hideForHrms: true
    },{
        title: 'Primary Assignment - Retirement System',
        value: 'Employee.Assignments.Primary.RetirementSystem.Name',
        hideForHrms: true
    },{
        title: 'Emergency Contact - Name',
        value: 'Employee.EmergencyContacts.Primary.ContactName'
    },{
        title: 'Emergency Contact - Relationship',
        value: 'Employee.EmergencyContacts.Primary.RelationshipType'
    },{
        title: 'Emergency Contact - Phone',
        value: 'Employee.EmergencyContacts.Primary.PrimaryPhone'
    },{
        title: 'Emergency Contact - Alternate Phone',
        value: 'Employee.EmergencyContacts.Primary.AlternatePhone'
    },{
        title: 'Secondary Contact - Name',
        value: 'Employee.EmergencyContacts.Secondary.ContactName'
    },{
        title: 'Secondary Contact - Relationship',
        value: 'Employee.EmergencyContacts.Secondary.RelationshipType'
    },{
        title: 'Secondary Contact - Phone',
        value: 'Employee.EmergencyContacts.Secondary.PrimaryPhone'
    },{
        title: 'Secondary Contact - Alternate Phone',
        value: 'Employee.EmergencyContacts.Secondary.AlternatePhone'
    }]

    // Add some more props (needed for autocomplete) and return the final collection
    return PLACEHOLDER_TOKENS.map((p, i) => { 
        let obj = Object.assign(p, { id: i+1});
        obj['name'] = obj.title.toLowerCase().replace('/s/g', '');
        return obj;
    });
};

export { getPlaceholders }