import {Segment} from 'base-segment';
// #-----------------------------------------------------------------# //
// #---- Segment (/form-builder/overlay-builder/segment-overlay) ----# //

var interpolate, vpOffsetHlp, logic;

class Overlay extends Segment {

	/*@ngInject*/
	controller($injector, $scope, $state, $stateParams, $interpolate, $q, $location, rAF, ssAsync, FileUploader, ssAuthSvc, ssToastHlp, ssFormTemplatesApi, ssModalSvc, ssSaveInterrupterSvc, ssEventSvc, ssEnum, formsHlp, filenameHlp, viewportOffsetHlp) {

		interpolate = $interpolate;
		vpOffsetHlp = viewportOffsetHlp;

		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // eslint-disable-line
		let formVm = $scope.formVm;

		let dataWatchDereg = null;

		// hookup segment logic
		logic = $injector.get('segmentFormBuilderOverlayBuilderOverlayLogic'); // eslint-disable-line

		const MAX_FILESIZE = 52428800;
		const MAX_FILESIZE_MSG = 'The file upload size cannot exceed 50MB. Please upload a smaller file.';
		const FILE_UPLOAD_ERROR_TITLE = 'File Upload Failed';
		const UPLOAD_FAILED_MSG = 'The selected file did not upload correctly. Please try again.';
		const OVERLAY_FILETYPE = 3; //Overlay pdf
		const INVALID_EXT_ERROR_MSG = 'The selected file does not have a valid extension. Please choose a file with the ".pdf" extension.';

		const UPLOADER_CONFIG = {
			withCredentials: false,
			headers: {
				Authorization: `Bearer ${ssAuthSvc.token.access}`
			},
			filters: [{
				name: 'validExt',
				fn: function(fileItem) {
					if (!filenameHlp.getExtension(fileItem.name) || filenameHlp.getExtension(fileItem.name) !== 'pdf') {
						ssToastHlp.error(INVALID_EXT_ERROR_MSG, FILE_UPLOAD_ERROR_TITLE);
						return false;
					};
					return true;
				}
			},{
				name: 'fileSize',
				fn: function(fileItem) {
					if (fileItem.size != null && fileItem.size >= 0 && fileItem.size >= MAX_FILESIZE) {
						ssToastHlp.error(MAX_FILESIZE_MSG, FILE_UPLOAD_ERROR_TITLE);
						return false;
					};
					return true;
				}
			}]
		};

		const overwriteConfirmation = {
			title: 'You will lose all your overlay mappings',
			content: 'Uploading a new file will result in losing all the mappings done on the current form.',
			primaryText: 'Continue',
			secondaryText: 'Cancel'
		};

		// view model for tracking state
		const vm = $scope.vm = {
			explodedImageUrls: [],
			currentPage: -1,
			bgImageLoaded: false,
			newBoxStartingPoint: null,
			labelsById: makeLabelMap(),
			data: [],
			processing: false,
			processingStatus: '',
			canViewOverlayBuilder: false
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line
			nextPage() {
				if (vm.explodedImageUrls.length === vm.currentPage) { return; }
				vm.currentPage++;
				loadPage(vm.currentPage);
			},
			previousPage() {
				if (vm.currentPage === 1) { return; }
				vm.currentPage--;
				loadPage(vm.currentPage);
			},
			selectBox(box, evt) {
				$scope.$root.safeApply(() => routeVm.selectedBox = box);
				ssEventSvc.emit('pvb.select-box', box);
				if (evt != null) { evt.stopPropagation(); }
			},
			createdBox(box) {
				box.type = routeVm.selectedTool.type;
				box.shape = routeVm.selectedTool.shape;
				box.template = '';
				box.formFieldGuids = [];
				vm.data = [...vm.data, box];
				rAF(() => go.selectBox(box), $scope);
			},
			removeBox(box) {
				vm.data = vm.data.filter(b => b !== box);
			},
			calculateImageWidth() {
				return { 'max-width': $scope.canvasWidth + 'px' };
			},
			remove() {
				let resolver = $q.when();
				let confirmation = {
					title: 'You will lose all your overlay mappings',
					content: 'Removing this file will result in losing all the mappings done on the current form.',
					primaryText: 'Continue',
					secondaryText: 'Cancel',
					alwaysOnTop: true
				};
				if(vm.data.length) {
					resolver = ssModalSvc.showDestructive(confirmation);
				}
				resolver.then(() => {
					vm.processing = true;
					vm.processingStatus = 'Removing PDF Overlay...';
					logic.removeFile(routeVm.formTemplate).then(res => {
						dataWatchDereg();
						dataWatchDereg = null;
						ssSaveInterrupterSvc.clear();
						vm.explodedImageUrls = [];
						vm.processing = false;
						vm.processingStatus = '';
					});
				});
			}
		};

		function makeLabelMap(opt){
			return routeVm.formTemplate._formFields.reduce(function (o, f) {
				if(!f._fieldDefId) { return o };
				let fieldVal = opt != null && f._options.length ? f._options[f._options.indexOf(opt)] : f._label;
				o[`id${f.id.split('-').join('')}`] = `${fieldVal}`;
				return o;
			}, {})
		};

		/**
		* Loads a specific page of mapped fields into the view model and renders them into the view
		* @param {int} pageNum The requested page number to load, limited to a page between 1 and the max number of exploded image urls
		*/
		function loadPage(pageNum) {

			var allPages;
			var definition = routeVm.formTemplate._printPreviewDefinition;
			vm.data = [];

			if (definition == null) { return; }
			if (definition.printPreviewPages == null) { return; }
			if (definition.printPreviewPages.length === 0) { return; }

			// Dont go past the end
			if (pageNum > vm.explodedImageUrls.length) { pageNum = vm.explodedImageUrls.length; }
			// or before the beginning
			if (pageNum < 1) { pageNum = 1; }

			// All of the pages that were stored on the Print Preview Definition
			allPages = definition.printPreviewPages;
			// Take the definition's data, convert it to the client dataset and set it so that the canvas can render any boxes
			vm.data = logic.definitiontoLocalData(allPages[pageNum - 1], vm.labelsById);
			vm.currentPage = pageNum;
		}

		/**
		* Monitor the mapper's data of mapped fields for a page of the overlay builder
		* and convert to the Definition's model as things change to keep things in sync
		*/

		function initializeDataWatch() {
			if (dataWatchDereg) dataWatchDereg();
			dataWatchDereg = $scope.$watch('vm.data', function (nv, ov) {
				if (nv == null || nv === ov || routeVm.formTemplate._printPreviewDefinition == null) { return; }
				const pageIdx = vm.currentPage - 1;
				// Get the current page's mappings
				const { printPreviewPages } = routeVm.formTemplate._printPreviewDefinition;
				// Convert the mapper's definition to the Server's definition
				printPreviewPages[pageIdx] = localDataToDefinition(nv);
				// Manually make the array dirty so it can be saved
				routeVm.formTemplate.$$forceChangedState('_printPreviewDefinition');
				formVm.modelChanged();
			}, true);
		}
		
		/**
		* Convert the local overlay mapping to a printViewDefinition's PrintPreviewPage data item
		* @param {object} localData The overlay mapper's definition of what a drawn mapping is
		*/
		function localDataToDefinition(localData) {
			return {
				pageNumber: vm.currentPage - 1,
				pageObjects: localData.map(d => ({
					fieldType: d.type,
					locationY: d.top,
					locationX: d.left,
					width: 100 - d.right - d.left,
					height: 100 - d.bottom - d.top,
					formFieldGuids: d.formFieldGuids,
					matchValue: d.matchValue,
					stringFormat: d.stringFormat || null,
				}))
			};
		}

		// For the uploader, so that we can post data for the org
		var uploader = $scope.uploader = new FileUploader(UPLOADER_CONFIG);

		uploader.onAfterAddingFile = function(fileItem) {
			let resolver = $q.when();
			// if the form is an edit we need to show a change confirmation dialog
			if (vm.explodedImageUrls.length) {
				resolver = ssModalSvc.showDestructive(overwriteConfirmation);
			}
			resolver.then(() => {
				addFile(fileItem);
			}, () => {
				fileItem.cancel();
			});
		};

		function addFile(fileItem) {
			vm.processing = true;
			vm.processingStatus = 'Adding PDF Overlay...';

			ssFormTemplatesApi.attachFile({}, {
				filename: fileItem.file.name,
				fileType: OVERLAY_FILETYPE
			}).then(file => {
				$scope.ctxDomEl.style.display = 'block';
				vm.file = file;
				vm.file.filename = file._filename;
				fileItem.updatedUrl = ssFormTemplatesApi.getUploadFileUrl({ id: vm.file.id });
				fileItem.upload();
			}).catch((err) => {
				ssToastHlp.error('File upload cancelled.');
			});
		}

		uploader.onBeforeUploadItem = function (item) {
			item.url = item.updatedUrl;
		};

		uploader.onErrorItem = function (fileItem, response, status, headers) {
			vm.processing = false;
			vm.processingStatus = '';
			ssToastHlp.error(UPLOAD_FAILED_MSG, FILE_UPLOAD_ERROR_TITLE);
			fileItem.remove();
			console && console.error && console.error('Error uploading file', response);
		};

		uploader.onCancelItem = function (fileItem, response, status, headers) {
			ssToastHlp.info('File upload cancelled.');
			fileItem.remove();
			document.getElementById('underlay-uploader').value = '';
			vm.bgImageLoaded = false;
		};

		uploader.onSuccessItem = function (fileItem, response, status, headers) {
			ssAsync(function* () {
				try {
					yield ssFormTemplatesApi.associateFile({
						id: routeVm.formTemplate.id
					}, {
							fileId: vm.file.id
						}
					);

					// Patch form template with a printviewdefinition with the fileId
					if (routeVm.formTemplate._printPreviewDefinition == null) { routeVm.formTemplate._printPreviewDefinition = {}; }
					routeVm.formTemplate._printPreviewDefinition.templateFileId = vm.file.id;

					// Create the printView pages object for managing overlay pages
					routeVm.formTemplate._printPreviewDefinition.printPreviewPages = response.explodedImageUrls.map(v => {
						return {
							pageNumber: 0,
							pageObjects: []
						};
					});

					yield routeVm.formTemplate.$save();
					yield init();

				} catch (err) {
					vm.processing = false;
					vm.processingStatus = '';
					console && console.error && console.error('Error associating file with form template', err);
				} finally {
					uploader.clearQueue();
				}
			})();
		};

		function init() {
			return ssAsync(function*() {
				formVm.modelChanged(false);
				routeVm.selectedBox = null;
				try {
				//	if we have somehow gotten to this page with an active template, force the user to deactivate or return to form templates page
				if($stateParams.deactivate === 'true' || routeVm.formTemplate._statusId === ssEnum.formTemplateStatus.active) {

					var modalConfig = {
						title: 'Edit Form Template',
						content: 'This is an active template and will need to be deactivated to continue',
						primaryText: 'Deactivate and continue',
						secondaryText: 'Cancel',
						alwaysOnTop: true
					};

					ssModalSvc.showStandard(modalConfig).then(() => {
						formVm.loading = true;
						formVm.loadingMsg = 'Deactivating "'+routeVm.formTemplate._name+'"';
						ssFormTemplatesApi.editActive(routeVm.formTemplate).then((res)=> {
							$location.url(`/ec-forms/form-builder/${res.id}/overlay-builder`);
							formVm.loading = false;
							formVm.loadingMsg = '';
						}).catch((err) => {
							ssToastHlp.error('there was an error deactivating the form template', '');
							$state.go('base.formTemplates');
							console && console.error && console.error(err);
						});
					}).catch(() => {
						$state.go('base.formTemplates');
					});
				}
				//	Check to see if we have a file associated to the printPreviewDefinition
					if (routeVm.formTemplate._printPreviewDefinition && routeVm.formTemplate._printPreviewDefinition.templateFileId) {
						vm.processing = true;
						vm.processingStatus = 'Loading PDF overlay';
						// Get the file that is linked as the overlay document
						vm.file = yield ssFormTemplatesApi.getFile({ id: routeVm.formTemplate._printPreviewDefinition.templateFileId });
						// grab each page of the pdf that was exploded into images
						vm.explodedImageUrls = vm.file._explodedImageUrls;

						rAF.raw(function () {
							const firstImage = document.querySelector('.pages img:first-child');
							firstImage.onload = function () {
								vm.bgImageLoaded = true;
								$scope.$digest();
								$scope.$emit('canvas-resize');
							};
						});

					//	Dirty form detection initialization
						formsHlp.initSaveInterrupter(routeVm.formTemplate);
					//	thats it, lets open the first page and see what happens.
						loadPage(1);
						initializeDataWatch();
						vm.processing = false;
						vm.processingStatus = '';
					}
				} catch(err) {
					ssToastHlp.error('There was an error initializing the overlay builder', 'Error');
					console && console.error && console.error(err);
					vm.processing = false;
					vm.processingStatus = '';
				}
			})();
		};
		init();
	}

	link(scope, elem, attrs) {
		const MAX_CANVAS_WIDTH = 907;

		const vm = scope.vm;
		const routeVm = scope.routeVm;
		const win = angular.element(window);
		const el = elem[0];
		const ctxElem = elem.find('canvas');
		const wrapper = angular.element(el.getElementsByClassName('mapping-canvas')[0]);
		const ctxDomEl = ctxElem[0];
		scope.ctxDomEl = ctxDomEl;
		const ctx = ctxDomEl.getContext('2d');
		// var heightMultiplier = 1.2942; //Letter
		var heightMultiplier = 1.6471; //legal
		var elWidth = ctxDomEl.offsetWidth;
		var elHeight = 0;

	//	set the canvas dimensions and do the initial paint
		updateHeight(wrapper.width);

	//	listen and bind for events
		win.on('resize', handleResize);
		win.on('scroll', handleResize);
		scope.$on('$destroy', () => win.off('resize', handleResize));
		scope.$on('$destroy', () => win.off('scroll', handleResize));
		scope.$on('canvas-resize', handleResize);

		sidekick.events.on('nav.expanded-footprint', () => { // eslint-disable-line
			setTimeout(() => handleResize(),0);
		});
	//	capture the mouse events on the canvas
		ctxElem.on('mousedown', startNewBox);
		ctxElem.on('mouseup', finishNewBox);

		scope.$watch('vm.data', paint, true);
		scope.$watch('vm.bgImageLoaded', paint);

		function handleResize() {
			const newElWidth = el.clientWidth;
			if (newElWidth === elWidth) { return; }
			updateHeight(newElWidth);
		}

		function finishNewBox() {
			scope.$root.safeApply(() => vm.newBoxStartingPoint = null);
		}

		function startNewBox(evt) {
			paint();
			/*
			if we have a box selected, prevent drawing a new one until we have deselected the one we are on
			*/
			if (routeVm.selectedBox != null) { return; }

			/*
			get the current size of our element and use it to calc the relative mouse position
			*/
			const elRect = wrapper[0].getBoundingClientRect();

			scope.$root.safeApply(function () {
				var offsets = vpOffsetHlp.getViewportOffsets(window);

				vm.newBoxStartingPoint = {
					x: evt.pageX - elRect.left - offsets.left,
					y: evt.pageY - elRect.top - offsets.top
				};
			});
		}

		function updateHeight(width) {
			const firstImage = document.querySelector('.pages .visible img');
			var height = 0;

			if (firstImage) {
				/*
				attempt to calculate what the height should be based upon the actual size of the image, and not assume letter or legal because scaling legal placements to letter placements causes fields to render in the wrong place.
				*/
				heightMultiplier = firstImage.height / firstImage.width;

				/*
				The canvas needs to be the same width as the image or the server will generate the wrong placements If somehow the image is larger or smaller than the canvas originally calculated, match them up
				*/
				width = firstImage.width;
				height = firstImage.height;

			}
			if (width > MAX_CANVAS_WIDTH) { width = MAX_CANVAS_WIDTH; }
			if (height === 0) { height = width * heightMultiplier; } // in case the height can't be calced at load time

			elWidth = width;
			elHeight = height;
			ctxDomEl.width = scope.canvasWidth = ctxDomEl.style.width = elWidth;
			ctxDomEl.height = scope.canvasHeight = ctxDomEl.style.height = elHeight;

			paint();
		}

		function paint() {
			ctx.clearRect(0, 0, elWidth, elHeight);
			if (!(scope.vm.data instanceof Array)) { return; }

			ctx.fillStyle = '#000000';
			if (scope.vm.bgImageLoaded) {
				ctx.strokeStyle = '#000000';
				ctx.lineWidth = 2;
				scope.vm.data = scope.vm.data
					.filter(c => 100 - c.left - c.right > 1 || c === scope.vm.drawingBox);
				scope.vm.data.forEach(paintCell);
			} else {
				ctx.fillRect(0, 0, elWidth, elHeight);
			}
		}

	//	painting an individual cell data
		function paintCell(cell) {
			const box = getPixels(cell);
			var cellType = cell.type;

			if (['text', 'date', 'checkbox'].includes(cellType)) {
				if (box.width < 10 || box.height < 10) { return; }
				var value;
				try {
					cell._template = `{{${cell.formFieldGuids.map(g => `id${g.split('-').join('')}`).join('}} {{')}}}`;
					value = interpolate(cell._template)(scope.vm.labelsById);
					cell.templateString = logic.convertBracketValueToCsv(value);
				} catch (err) {
					console && console.error && console.error('Error painting the cell', err);
					value = 'TEMPLATE ERROR';
				}

				ctx.font = `12px monospace`;
				ctx.textBaseline = "ideographic";
				let bottom = box.top + box.height;
				let fontWidth = ctx.measureText(value).width;

			//	reduce value until the text fits
				while (fontWidth > box.width) {
					value = value.slice(0, -1);
					fontWidth = ctx.measureText(value).width;
				}

			//	placement is bottom left
				ctx.fillText(value, box.left, bottom);
			}
		}

		function getPixels(cell) {
			const box = {};
			const heightCalc = elHeight / 100;
			const widthCalc = elWidth / 100;

			box.top = heightCalc * cell.top;
			box.right = widthCalc * (100 - cell.right);
			box.bottom = heightCalc * (100 - cell.bottom);
			box.left = widthCalc * cell.left;
			box.width = box.right - box.left;
			box.height = box.bottom - box.top;

			if (box.top < 0) { box.top = 0; }
			if (box.left < 0) { box.left = 0; }

			return box;
		}

	}
}

// #-- END Segment (/form-builder/overlay-builder/segment-overlay) --# //
// #-----------------------------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentFormBuilderOverlayBuilderOverlay', () => new Overlay('overlay') );
