import { State } from 'base-state';
// #----------------------------------# //
// #---- State (//packet-builder) ----# //


// view model that is shared by all segments on this route,
// used for segment coordination. Define initial state here.
const routeVm = {};

const stateName = 'packetBuilder';

// config object for ui-router
// http://angular-ui.github.io/ui-router/site/#/api/ui.router.state.$stateProvider#methods_state
const stateConfig = {
	url: '/packet-builder/{packetId:[0-9A-Za-z-]+}',
	templateUrl: 'routes/packet-builder/packet-builder.html',
	resolve: {
		packet: /*@ngInject*/ function($stateParams, ssAsync, ssPacketsApi) {
			return ssAsync(function*() {
				try {
					routeVm.packetData = yield ssPacketsApi.get({
						id: $stateParams.packetId
					});
				} catch(e) {
					ssToastHlp.error('There was an error retrieving packet data.', '', false);
					console && console.error && console.error('Error getting packet', e);
				}
			})();
		},
		org: /*@ngInject*/ function(ssAsync, ssOrganizationSvc, ssToastHlp) {
			return ssAsync(function*() {
				try {
					routeVm.org = yield ssOrganizationSvc.getActiveOrg();
				} catch (err) {
					ssToastHlp.error('There was an error retrieving organization data. Due Dates may not display. Please see the console for more detail.', '', false);
					console && console.error && console.error('Error getting Org data', err);
				}
			})();
		},
		$title: /*@ngInject*/ (packet) => {
			return `Frontline Central - ${routeVm.packetData._name}`;
		}
	},
	$permissions: (permissionSvc) => permissionSvc.isPermittedAction('packets', 'create') ||
		permissionSvc.isPermittedAction('packets', 'edit') ||
		permissionSvc.isPermittedAction('packetInstances', 'send')
};



// #-- END State (//packet-builder) --# //
// #----------------------------------# //

angular.module('ec-forms').config( /*@ngInject*/ ($stateProvider) => {
	$stateProvider.state(`base.${stateName}`, new State(stateConfig, routeVm));
});
