import {Segment} from 'base-segment';
// #-------------------------------------------------# //
// #---- Segment (/form-tracking/segment-header) ----# //

class Header extends Segment {
	/*@ngInject*/
	controller($injector, $scope, ssLocationSvc, ssStorage) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line

		// hookup segment logic
		const logic = $injector.get('segmentFormTrackingHeaderLogic'); // jshint ignore:line

		//	initialize some props to be shared between segments/components
		routeVm.selectedCategory = 'urgent';
		routeVm.sentByParticipant = '0';
		routeVm.sentToLocation = '-1';
		routeVm.sortBy = 'duedate';
		routeVm.counts = [];
		routeVm.dateSent = getDateSentOptions()[1];


		const vm = $scope.vm = { // eslint-disable-line
			sentByCriteria: [
				{ value: '0', name: 'All' },
				{ value: 'creatorId', name: 'Form Sender - Myself' },
				{ value: 'formadministrator', name: 'Form Admin - Myself' }
			],
			sortOptions: [{ 
				id: 0, value: 'duedate', name: 'Due Date', categories: ['urgent', 'denied', 'not-started', 'in-progress', 'complete']
			},{
				id: 1, value: 'primaryrecipient', name: 'Employee Name (A-Z)', categories: ['urgent', 'denied', 'not-started', 'in-progress', 'complete']
			},{ 
				id: 2, value: 'formname', name: 'Form/Packet Name (A-Z)', categories: ['urgent', 'denied', 'not-started', 'in-progress', 'complete']
			},{ 
				id: 3, value: 'recentlysent', name: 'Recently Sent', categories: ['urgent', 'denied', 'not-started', 'in-progress']
			},{ 
				id: 4, value: 'completionpercent', name: 'Workflow Progress', categories: ['urgent', 'denied', 'in-progress']
			}],
			locations: [],
			dateSentOptions: getDateSentOptions(),
			dateSentIdx: 1
		};

		const go = $scope.go = { // eslint-disable-line
			filter(type) {
				routeVm.selectedCategory = type;
			}
		};

		function getDateSentOptions() {
			let now = moment();
			let fcCreation = logic.getFCAddedDate(routeVm.org._applications);
			let yearsToGoBack = logic.getYearsSinceCreation(now, fcCreation); // decide how far back to go
			let lowerLimitYears = 2; // set limit to 2 years of historical data
		
			let options =  [
				logic.lastXDays(now, 7),
				logic.lastXDays(now, 14), // dateSentIdx: defaults to second one on list
				logic.lastXDays(now, 60),
				logic.yearToDate(now),
				logic.currentSchoolYear(now),
			];
			
			for (let i = 1; i <= yearsToGoBack && i <= lowerLimitYears; i++ ){ // add school year data up to 3 years in past
				options.push(logic.getPreviousSchoolyear(now, i));
			}
			
			return options;
		};

	//	get the locations to be used for filtering
		vm.locations = ssLocationSvc.getAll();
	//	Add 'Anywhere'(all) item to locations list
		vm.locations.splice(0, 0, { _name: 'Anywhere', id: '-1' });

	//	default sortBy options to duedate if current sortBy filter is not a valid selection
		$scope.$watch('routeVm.selectedCategory', function(nv, ov) {
			if(nv === ov) {return;}

			let idx = vm.sortOptions.findIndex(option => {
				return option.value === routeVm.sortBy;
			});

			if (!vm.sortOptions[idx].categories.includes(routeVm.selectedCategory)) {
				routeVm.sortBy = 'duedate';
			}

			ssStorage.save('trackFormsFilters', [{
				'category': routeVm.selectedCategory,
				'sort': routeVm.sortBy,
				'search': routeVm.search,
				'sentby': routeVm.sentByParticipant,
				'location': routeVm.sentToLocation
			}], { persistent: true, expires: false });
		});

		$scope.$watchGroup(['routeVm.getFormsPromise', 'routeVm.getFormCountsPromise'], function(nv, ov) {
			let promises = nv;
			vm.disableFilters = promises.some(promise => {
				return (promise != null);
			});
		});

		$scope.$watch('vm.dateSentIdx', function(nv, ov) {
			if (nv === ov) {return;}
			routeVm.dateSent = vm.dateSentOptions[nv];
		});
	}
}

// #-- END Segment (/form-tracking/segment-header) --# //
// #-------------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentFormTrackingHeader', () => new Header('header') );
