/**
 * Prevents duplicated options
 * @param {Object} field
 * @return {String|Boolean} true if no error. Error message otherwise.
 */
function dupeOptionCheck(field) {
	var counts = [];
	for(var i = 0; i <= field._options.length; i++) {
		if(counts[field._options[i]] === undefined) {
			counts[field._options[i]] = 1;
		} else {
			return 'Cannot have duplicate options.';
		}
	}
	return true;
}

/**
 * Limits the text length of an option
 * @param {Object} field
 * @param {Number} limit Option character length limit
 * @return {String|Boolean} true if no error. Error message otherwise.
 */
function optionLimitCheck(field, limit = 100) {
	let options = field._options;
	if (options == null || options.length < 1) {
		return true;
	}
	if (options.filter(f => f.length > limit).length > 0) {
		return `Anything greater than ${limit} character${limit === 1 ? '' : 's' } cannot be mapped.`;
	}
	return true;
}

function textLimitCheck(field, limit = 100) {
	let fieldConfig = field.config || JSON.parse(field._configJson);
	if (!fieldConfig.mappable || limit == null || limit === -1) { return true; }
	return field._label.length <= limit || `Anything greater than ${limit} character${limit === 1 ? '' : 's'} cannot be mapped.`;
}

function validateAll(val, validators = []) {
	for (let i = 0; i < validators.length; i += 1) {
		if (typeof validators[i] !== 'function') {
			return `validator ${JSON.stringify(validators[i])} must be a function`;
		}
		let result = validators[i].call(null, val);
		if (result === true) { continue; }
		return result;
	}
}

const validators = {
	'cmp-checkbox-group': field => {
		return dupeOptionCheck(field);
	},
	'super-select-list': field => {
		return dupeOptionCheck(field);
	},
	'text': field => {
		return textLimitCheck(field);
	}
};

function fieldsHlp(ssEnum, ssObjectHlp) {
	// #----------------------------# //
	// #---- Helper (fieldsHlp) ----# //

//	put together an array of all the fields
	let allFields = [];
	Object.keys(fieldTypes).forEach((key) => {
		allFields = [...allFields, ...fieldTypes[key]];
	});

	const _hlp = {

		allFields,

		getFieldById(field) {
			return allFields.find((targetField) => {
				return field._fieldDefId === targetField._fieldDefId;
			});
		},

		getFields(type, cmp) {
			if(fieldTypes[type]) {
				return fieldTypes[type].map(function (f, idx, arr) {

					const fieldDef = _hlp.mergeRecursive(angular.copy(defaultFieldDef), f);

					if(!type.startsWith('custom')) {
						Object.assign(fieldDef, { _fieldGroup: type });
					}

					var config = JSON.parse(fieldDef._configJson);

					if (config.fields){
						config.fields.forEach(function(field, index){
							config.fields[index] = _hlp.mergeRecursive(angular.copy(defaultFieldDef), field);
						});
					}
					
					config.cmp = cmp;

					fieldDef._configJson = JSON.stringify(config);

					_hlp.updateFieldOptions(fieldDef);

					return fieldDef;
				});
			} else {
				console.error(`fields for the "${type}" field group could not be rendered.`)
				return [];
			}
		},

		// update existing field objects with missing keys - handles keys with JSON strings as values (i.e.: "_configJson")
		updateFieldAttributes(field) {
			let updatedKeys = [];
			let parentField = this.getFieldById(field);
			if (parentField != null) {
				let updatedParent = Object.assign({}, defaultFieldDef, parentField);
				let addKeys = function ak(source, target) {
					var targetKeys = Object.keys(target);
					Object.keys(source).forEach(key => {
						if(typeof source[key] === "string" && source[key].startsWith('{"')) {
							let newsource = JSON.parse(source[key]);
							let newtarget = JSON.parse(target[key]);
							ak(newsource, newtarget);
							target[key] = JSON.stringify(newtarget);
						} else if(source[key] && source[key].length && typeof source[key] === "object" && !Array.isArray(source[key])) {
							ak(source[key], target[key]);
						} else if(!targetKeys.includes(key)) {
							target[key] = source[key];
							updatedKeys.push(key);
						}
					})
				}
				addKeys(updatedParent, field);
			}
			return updatedKeys;
		},

		// This updates the "_options" array of any field that uses enums with the latest enums and returns that field (if updated).
		updateFieldOptions(field) {
			const config = JSON.parse(field._configJson);
			let updatedOptions = [];
			try {
				if (config.externalSource != null && ssEnum[config.externalSource] != null) {
					if(!field._options.length || field._options.length !== ssEnum[config.externalSource].enums.length ) {
						field._options = ssEnum[config.externalSource].enums.map((item) => {
							return item.name;
						});
						updatedOptions = [...field._options];
					} 
				}
				return updatedOptions;
			} catch(err) {
				console.log(err);
			}
		},

		addStepIdToField: (steps) => {
			steps.forEach((step) => {
				if(step.fieldGroups) {
					step.fieldGroups.forEach(group => {
						group.forEach(f => {
							if (f._step == null) { f._step = {}; }
							f._step.id = step.id;							
						});
					});
				}
			});
		},

		updateActiveCredentialSelection(field, selectActive){
			var externalDataVals = field._externalData.split('.')

			// Doesn't include active
			if (externalDataVals.length < 4) {
				if (selectActive) {externalDataVals.push('Active')} 
				field._externalData = externalDataVals.join('.')
			} else {
				if (!selectActive) {externalDataVals.pop()} 
				field._externalData = externalDataVals.join('.')
			}

		},

		getValidator(type) {
			return validators[type];
		},

		mergeRecursive(to = {}, from = {}, override = true) {

			if(Object.prototype.toString.call(to) !== '[object Object]' || Object.prototype.toString.call(from) !== '[object Object]') {
				console.error('all arguments must be of type [object Object]');
				return;
			}
		
			for(var p in from) {
				try {
					if(from.hasOwnProperty(p)) {
						if(ssObjectHlp.isObject(from[p]) && ssObjectHlp.isObject(to[p])) {
							to[p] = _hlp.mergeRecursive(to[p], from[p], override);
						} else if(to[p] && Array.isArray(from[p]) && from[p].length && from[p].every(ssObjectHlp.isObject)) {
							to[p] = from[p].map((p, i) => {
								return _hlp.mergeRecursive(to[p][i], p, override);
							});
						} else if ( ssObjectHlp.isObject(JSON.parse(from[p])) ) {
							to[p] = JSON.stringify(_hlp.mergeRecursive(JSON.parse(to[p]), JSON.parse(from[p]), override));
						} else {
							if(to[p] && override || !to[p]) {
								to[p] = from[p];
							}
						}
					}
				} catch(e) {
					if(to[p] && override || !to[p]) {
						to[p] = from[p];
					}
				}
			}
			return to;
		}
	}

	return _hlp;

	// #-- END Helper (fieldsHlp) --# //
	// #----------------------------# //

};

angular.module('ec-forms-helpers').factory('fieldsHlp', fieldsHlp);
