function fieldRendererHlp($filter, ssToastHlp, ssEnum, ssFlagSvc) {

	const phoneFieldIds = [
		'5e989212-29f0-43e4-8d02-1a6a047b7e48',
		'0977c048-185c-4eec-8800-74842a7fbb4f',
		'f658f159-910a-485f-89b7-f20559222f73',
		'14827dc2-9d54-400d-a556-47656bc72f83',
		'e0ec2374-be6a-4d6f-9f12-5cbf58f5a5cc',
		'4efae0cb-6d68-4eeb-8ce3-031b11678275',
		'3a4799b5-168e-4f19-91a6-27299c543f69'
	]

	return {

		/**
		 * Gets the template tag
		 * @function getTag
		 * @instance
		 * @param {String} field -
		 * @return {String} HTML tag name
		 */
		getTag: field => {
			switch (field._fieldType) {
				case 'heading': return 'h4';
				case 'subheading': return 'h5';
				case 'readOnlySubheading': return 'h5';
				case 'readOnlyHeading': return 'h4';
				case 'instructions': return 'p';
				case 'text': return 'p';
				case 'readOnlyText': return 'dl';
				case 'readOnlyDate': return 'dl';
				case 'readOnlyCredentials': return 'p';
				case 'link': return 'a';
				default: return field._fieldType;
			}
		},

		/**
		 * Gets the attributes for the HTML tag returned in getTag
		 * @function getProperties
		 * @instance
		 * @param {String} field -
		 * @param {String} group -
		 * @return {String} Attributes for the HTML tag returned in getTag
		 */
		getProperties: (field, group) => {

			const props = [''];

		//	no need to process these fields.
			if(['heading','subheading', 'readOnlySubheading', 'readOnlyHeading','instructions','text','readOnlyText','readOnlyDate','readOnlyCredentials'].includes(field._fieldType)) {
				return '';
			}

			if (field._fieldType.startsWith('cmp-')) {
				props.push('field="field"');
				if(field._fieldType === 'cmp-text-editor' && field.step){
					let modelVal = `field.step.formFieldValues['${field.id}']`;
					props.push(`model="${modelVal}"`);
				}
				return props.join(' ');
			}

			if (field._fieldType.startsWith('cmp-')) {
				props.push('field="field"');
				return props.join(' ');
			}

			if (field._fieldType === 'link') {
				props.push('class="file-link"');
				props.push('ss-icon="chain"');
				props.push('href="{{field.config.isValid ? field.config.location : \'\'}}"');
				props.push('ng-class="{\'is-valid\': field.config.isValid}"');
				props.push('target="_blank"');
				return props.join(' ');
			}

			if (field._fieldType.startsWith('super-')) {

				let modelVal = `field.step.formFieldValues['${field.id}']`;

				props.push('label="{{field._label}}"');
				props.push('required="field._required"');
				props.push('org="org"');

				//	we only want to add the "edit mode" attribute when we want the component to be read only. setting it to 'true' here is unnecessary because the component should have an inferred edit-mode if the attribute does not exist.
				if(field.editMode != null) {
					props.push('edit-mode="field.editMode"');
				}

				if(field.isRedacted) {
					props.push('permissions="{ view: false, edit: field.editMode, redacted: true }"');
				}

				//	We only want to add the "disabled" attribute to the component if we need it to appear editable but temporarily non-interactive
				if(field.disabled != null ) {
					props.push(`is-disabled="${field.disabled}"`);
				}

				if(!field._label || field._label == null) {
					props.push('aria-label="{{field._placeholderLabel}} field"');
				}

				let phonefieldIds = [
					'5e989212-29f0-43e4-8d02-1a6a047b7e48',
					'0977c048-185c-4eec-8800-74842a7fbb4f',
					'f658f159-910a-485f-89b7-f20559222f73',
					'14827dc2-9d54-400d-a556-47656bc72f83',
					'e0ec2374-be6a-4d6f-9f12-5cbf58f5a5cc',
					'4efae0cb-6d68-4eeb-8ce3-031b11678275',
					'3a4799b5-168e-4f19-91a6-27299c543f69'
				];

				if (phonefieldIds.includes(field._fieldDefId)){
					props.push('mask="phone"');
				};
				
				if (field._fieldType === 'super-digital-signature') {
					props.push(`signatures="field.config.signatureIds"`);
					props.push(`orientation="field.config.orientation"`);
					props.push(`signature-display="field.config.signatureDisplay"`);
					props.push(`mappable="field.config.mappable"`);

					modelVal = `field.step.formFieldValues['${field.id}'].config.signature`;

					props.push('on-behalf="{{field.onBehalf}}"');
				} 
				
				if (field._fieldType === 'super-input-radio') {

					props.push(`name="${field.config.name}"`);
					props.push(`value="${field._label}"`);

					var groupIdx = group.findIndex(v => {
						return v === field;
					});
					var groupName = field.config.name || '';
					var firstFieldInGroup = field;
					for (; groupIdx--;) {
						if (group[groupIdx]._fieldType === 'super-input-radio' && group[groupIdx].config.name === groupName) {
							firstFieldInGroup = group[groupIdx];
						}
					}
					modelVal = `field.step.formFieldValues['${firstFieldInGroup.id}']`;
				}

			/*	these validators are only applied to fields in a form instance by checking the
			**	'disabled' property of a field - we need to figure out a better way to distinguish
			**	between a form template & form instance
			*/
				if(!field.disabled) {

					let required = field._required && !field.disabled ? 'Required' : '';
				//	encode single/double quotes so they don't close the attribute
					let label = field._label.replace(/'/g, '\\\'').replace(/"/g, '&quot;');

					if (field._validator) {
						
						let applyValidators = !field._validator.applyOnRequired || field._required && field._validator.applyOnRequired;
						if(applyValidators) {
							props.push(`ss-validate="${field.ignoreValidations} || validator.${field._validator.type}${required}(${modelVal}, '${label}')"`);
						}
					} else if(field._required) {
						props.push(`ss-validate="${field.ignoreValidations} || validator.required(${modelVal}, '${label}')"`);
					}
				}

				props.push(`model="${modelVal}"`);
				return props.join(' ');
			}
		},
		
		/**
		 * Gets the visibility
		 * @function getVisibility
		 * @instance
		 * @param {String} field -
		 * @return {Boolean} True|False if visible
		 */
		getVisibility: field => {
			switch (field._fieldType) {
				case 'heading': return '!!field._label.length && field._active';
				case 'readOnlyHeading': return '!!field._label.length && field._active';
				case 'readOnlySubheading': return '!!field._label.length && field._active';
				case 'subheading': return '!!field._label.length && field._active';
				case 'instructions': return '!!field._label.length && field._active';
				case 'readOnlyText': return 'field._active';
				case 'readOnlyDate': return 'field._active';
				case 'link': return '!!field._label.length && field._active';
				default: return 'field._active';
			}
		},

		/**
		 * Gets the content
		 * @function getContent
		 * @instance
		 * @param {String} field -
		 * @param {Object} options -
		 * @return {Boolean} The content
		 */
		getContent: (field, {scope} = {}) => {
			var formattedValue;
			let ariaLabel = '';

			switch (field._fieldType) {
				case 'heading': return '{{field._label}}';
				case 'subheading': return '{{field._label}}';
				case 'readOnlySubheading': return '{{field._label}}&nbsp;<em>Read-only</em>';
				case 'readOnlyHeading': return '{{field._label}}&nbsp;<em>Read-only</em>';
				case 'instructions': return '{{field._label}}';
				case 'text': return '{{field._label}}';
				case 'link': return '{{field.config.display}}';

				case 'readOnlyText':
					// if an instance & theres a valid field value...
					if (field.step && field.step.formFieldValues[field.id] != null) {
						if (field._fieldTypeOptions && field._fieldTypeOptions.edit === 'super-select-list'){
							let option;
							if (field._fieldDefId === '07ebdcec-0ff6-46ad-92aa-121b8573b8a6') { 
								option = ssEnum.states.enums.find((e) => {
									return e.value === field.step.formFieldValues[field.id]
								}).name;
							} else {
								option = field._options.find(o => { return field._options.indexOf(o) === field.step.formFieldValues[field.id] });
							}
							formattedValue = (option != null) ? option : '--';
						} else if (phoneFieldIds.includes(field._fieldDefId)) {
							formattedValue = field.step.formFieldValues[field.id].replace(/(\d{3})(\d{3})(\d{4})/,"($1) $2-$3");
						} else {
							formattedValue = field.step.formFieldValues[field.id];
						}
					} else { //...else a template
						formattedValue = '--';
					}

					if(field.isRedacted) {
						ariaLabel = 'aria-label="value is redacted"';
						formattedValue = '******';
					}	
					return `<dt class="field-label">${field._label}</dt><dd ${ariaLabel}>${formattedValue}</dd>`;

				case 'readOnlyDate':

					formattedValue = (field.step && field.step.formFieldValues[field.id] != null) ? $filter('date')(field.step.formFieldValues[field.id], 'MM/dd/yyyy') : '--/--/--';

					if(field.isRedacted) {
						ariaLabel = 'aria-label="value is redacted"';
						formattedValue = '******';
					}

					return `<dt class="field-label">${field._label}</dt><dd ${ariaLabel}>${formattedValue}</dd>`;

				case 'readOnlyCredentials':
					if (field.config.readOnly != null && (field.config.populate == null || field.config.populate)) {
						return `<span ng-if="field.primaryParticipant == null">--</span><cmp-credentials-list ng-repeat="option in field.primaryParticipant.${field.config.readOnly}" model="option"></cmp-credentials-list>`;
					} else {
						return '--';
					}

				case 'super-select-list':
					if (field._options && field._options.length) {
						//	Render custom drop-down & multi-select fields using the string value from the "_options" property of the field as the value for the select list item. Since at the time of this comment there is a disparity between saved form field values for these fields, we need to check the type of value and render it diifferently if its a STRING. (FC-3482)
						let customFieldsWithOptions = [
							'977a2273-62c1-4eef-8d63-91f9d8d1e0bf',
							'10a88a23-f8f3-48eb-aa48-c466db06378b',
						];
						if(customFieldsWithOptions.includes(field._fieldDefId)) {
							if(field.step && typeof field.step.formFieldValues[field.id] === "string") {
								return `<${field._fieldType}-item ng-repeat="option in field._options track by $index" value="option" label="option"></${field._fieldType}-item>`;
							}
						}

						// Find the 'address.state' field using its 'fieldDefId' property and render it using the two-character state code string as the value of the option, rather than using an integer ($index)' 
						if(field.step && field._fieldDefId === '07ebdcec-0ff6-46ad-92aa-121b8573b8a6') {
							return `<${field._fieldType}-item ng-repeat="option in field._options track by $index" value="field.optionValues[$index]" label="option"></${field._fieldType}-item>`;
						}
						return `<${field._fieldType}-item ng-repeat="option in field._options track by $index" value="$index" label="option"></${field._fieldType}-item>`;
					} else {
						ssToastHlp.error(`The options for the "${field._label}" menu could not be found.`, 'Error');
						return `<${field._fieldType}-item value="-1" label="'--'"></${field._fieldType}-item>`;
					}
				default: return '';
			}
		},

		/**
		 * Binds
		 * @function bindConfigObject
		 * @instance
		 * @param  {Object} scope -
		 * @param  {Object} jsonPropExp -
		 * @param  {Object} configPropExp -
		 */
		bindConfigObject: (scope, jsonPropExp, configPropExp) => {
			const json = scope.$eval(jsonPropExp);
			const cfgPathArr = configPropExp.split('.');
			const cfgProp = cfgPathArr.pop();
			const cfgPath = cfgPathArr.join('.');
			const cfgParent = (cfgPath ? scope.$eval(cfgPath) : scope);

			if (typeof json === 'string'){
				cfgParent[cfgProp] = JSON.parse(json);
			}else{
				cfgParent[cfgProp] = json;
			}

			scope.$watch(jsonPropExp, jsonString => {
				if (JSON.stringify(cfgParent[cfgProp]) === jsonString) { return; }
				if (typeof json === 'string'){
					cfgParent[cfgProp] = JSON.parse(jsonString);
				}else{
					cfgParent[cfgProp] = jsonString;
				}
			});

			scope.$watch(configPropExp, configObject => {
				scope.$eval(`${jsonPropExp} = jsonString`, {
					jsonString: JSON.stringify(configObject)
				});
			}, true);
		}
	};
	// #-- END Helper (fieldRendererHlp) --# //
	// #------------------------------# //
}

angular.module('ec-forms-helpers').factory('fieldRendererHlp', fieldRendererHlp);
