import { Component } from 'base-component';
// #----------------------------------------------# //
// #---- Component (cmp-template-list-packet) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	template: '='
};

class TemplateListPacket extends Component {
	/*@ngInject*/
	controller($scope, $state, $location, ssAsync, ssPacketsApi, ssModalSvc, ssFormTemplatesApi, ssStatusApi, enumFormTemplateStatus, ssToastHlp) {

		const routeVm = $scope.routeVm = $scope.$root.utils.findInParentScope('routeVm', $scope);

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line

			send(template) {
				$state.go('base.send', {
					id: template.id,
					type: 'Packet'
				});
			},

			toggleDetails(packet) {
				if (!packet.detailsOpen) {
					if (!packet.contents) {
						packet.loading = true;
						ssAsync(function* () {
							var ids;

							try {
								packet.contents = yield ssPacketsApi.get({ id: packet.id });
								ids = packet.contents._formTemplateIds;
								packet.contents.forms = yield ids.map(id => {
									return ssFormTemplatesApi.get({
										include: 'steps,statusHistory',
										id: id
									});
								});

							//	call the route-level method that gives packet forms the necessary properties
								packet.contents.forms.forEach(routeVm.extendCommonProps);

							} catch (error) {
								console && console.error && console.error(error);
							} finally {
								packet.loading = false;
							}
						})();
					}
				}
				packet.detailsOpen = !packet.detailsOpen;
			},

			editPacket(template) {
				let path = `/ec-forms/packet-builder/${template.id}/builder`;
				$location.url(path);
			},

			editForm(form){
				if (form.status === 'active') {
					var editTemplateConfig = {
						title: 'Edit Form Template',
						content: 'Editing will deactivate and disable it from being sent until it has been reactivated. Prior recipients will not receive changes made.',
						primaryText: 'Edit Template',
						secondaryText: 'Cancel',
						alwaysOnTop: true
					};

					ssModalSvc.showStandard(editTemplateConfig).then(() => {
						form.isPending = true;
						form.pendingMessage = 'Deactivating "' + form._name + '"';

						ssFormTemplatesApi.editActive(form).then((newForm) => {
							$state.go('base.formBuilder.builder', {
								formId: newForm.id
							});
						}).catch((err) => {
							ssToastHlp.error('There was an error deactivating the form template', '');
							console && console.error && console.error(err);
						});
					});
					
				} else {
					let path = `/ec-forms/form-builder/${form.id}/builder`;
					$location.url(path);
				}
			},

			preview(form) {
				if (ssFlagSvc.checkState('fc-enable-merged-forms-modules')) {
					$state.go('base.formBuilder.preview', {
						formId: form.id,
						returnTo: $state.current.name
					});
				} else {
					$location.url(`/ec-forms/form-builder/${form.id}/preview?returnTo=/ec-forms/`);
				}
			},

		/*
		** Disabling code - archiving packets is not currently supported	
		*/

		// 	archive(template) {
		// 		var config = {
		// 			title: 'Archive Packet',
		// 			content: `${template._name} will be removed from this list. The forms within the packet <strong>will not</strong> be archived.`,
		// 			primaryText: 'Archive Packet',
		// 			secondaryText: 'Cancel'
		// 		};

		// 		ssModalSvc.showDestructive(config).then(() => {

		// 			routeVm.loading = true;
		// 			routeVm.loadingMessage = 'Archiving "'+template._name+'"';

		// 			ssAsync(function* () {
		// 				try {
		// 					yield ssStatusApi.update({ id: template.id }, {
		// 						statusId: enumFormTemplateStatus.archived,
		// 						from: new Date().toISOString()
		// 					});
		// 					yield routeVm.loadFormTemplates(false);
		// 					ssToastHlp.success('"'+template._name+'" has been archived')

		// 				} catch(e) {
		// 					console.error(`error archiving "${template._name}"`, e)
		// 				} finally {
		// 					routeVm.loading = false;
		// 					routeVm.loadingMessage = null;
		// 				}
		// 			})();
		// 		});
		// 	}
		};

	}
}

// #-- END Component (cmp-template-list-packet) --# //
// #----------------------------------------------# //

angular.module('ec-forms-components').directive('cmpTemplateListPacket', () => new TemplateListPacket(props, 'template-list-packet'));
