import {Segment} from 'base-segment';
// #------------------------------------------------# //
// #---- Segment (/form-builder/segment-header) ----# //

class Header extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $state, $window, $q, $rootScope, $timeout, ssAsync, ssEventSvc, ssStatusApi, ssToastHlp, rAF, ssValidationHlp, ssPermissionsSvc, ssStorage, ssDateByOrgHlp, statusLabelHlp, ssModalSvc, ssSaveInterrupterSvc, ssSleepHlp, ssEnum, fieldsHlp) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm;
		// hookup segment logic
		const logic = $injector.get('segmentFormBuilderHeaderLogic'); // eslint-disable-line
		const formData = routeVm.formData;
		const cacheKey = `${routeVm.org.id}_formbuilder_${formData.id}`;
		var autoSaveTimer;

		const validator = $scope.validator = {
			required: ssValidationHlp.required()
		};

		$scope.activate = false;

		// view model for tracking state
		const vm = $scope.vm = {
			savedFormTitle: JSON.parse(JSON.stringify(formData._name)),
			activationDate: ssDateByOrgHlp.todaysStartRelativeTo(routeVm.org).format('MM/DD/YYYY'),
			formStatusLabel: statusLabelHlp.getLabel(formData, routeVm.org).statusLabel,
			activate: null,
			editTitle: false,
			modelHasChanged: false,
			autoSaved: false,
			printViewBuilderEnabled: false,
			statusConfig: {
				statusId: 1
			}
		};

		const go = $scope.go = {
			editTitle() {
				vm.editTitle = true;
				vm.previousAutoSaved = vm.autoSaved;
			},

			preview() {
				if(!routeVm.saving) {
					if(vm.modelHasChanged) {
						var config = {
							title: 'Preview Form Template',
							content: 'You will need to save this form template in order to preview it.',
							primaryText: 'save and continue',
							secondaryText: 'cancel',
							alwaysOnTop: true
						};

						return ssModalSvc.showStandard(config).then((scope) => {
							// run field validators since the 'preview' action does not trigger the submit event, which triggers validators
							let errors = runAllValidators();
							if(!errors.length) {
								go.saveFormTemplate().then(() => {
									ssSaveInterrupterSvc.clear();
									$state.go('base.formBuilder.preview', {
										returnTo: $state.href($state.current.name),
										showPDFoverlay: $state.is('base.formBuilder.overlayBuilder')
									});
								});
							} else {
								errors.forEach((err) => {
									ssToastHlp.error(`${err.field._label ? 'The field group labeled' +  ' "' + err.field._label + '" ' : err.field.config.fieldGroup + ' field groups'} ${err.error}`, 'Validation Error');
								})
								
							}
							
						});
					} else {
						ssSaveInterrupterSvc.clear();
						$state.go('base.formBuilder.preview', {
							returnTo: $state.href($state.current.name),
							showPDFoverlay: $state.is('base.formBuilder.overlayBuilder')
						});
					}
				}
			},

			saveFormTemplate() {
				resetAutoSave();
				if (routeVm.saving || !vm.modelHasChanged) { return $q.when(); }

				routeVm.saving = true;
				routeVm.loadingMsg = 'saving form template...';
				vm.editTitle = false;

				// force a change to all step permissions
				formData._steps.forEach((step) => {
					step._permissions = angular.copy(step._permissions, []);
				});

				return ssAsync(function* () {
					try {
						// force a change to all step permissions
						formData._steps.forEach((step) => {
							step._permissions = angular.copy(step._permissions, []);
						});
						// save top-level form data (shallow save)
						yield formData.$save(false);
						// save the steps
						yield formData._steps.$save();

						// update the step order (function def in builder segment)
						if(routeVm.updateStepOrder) { formData._stepOrder = routeVm.updateStepOrder()};
						// make sure the fields have a step reference (function def in builder segment)
						if(routeVm.addStepIdToField) routeVm.addStepIdToField();

						// update form fields collection with the current fields
						if(routeVm.updateFormFields) routeVm.updateFormFields();

						// save the form template (posts/deletes fields)
						yield formData.$save();

						// updates the field order on the steps (function def in builder segment)
						if(routeVm.updateFieldOrder) routeVm.updateFieldOrder();
						// re-save the steps after updating their field orders
						yield formData._steps.$save();

						// refresh the data & re-process the template
						if(routeVm.processTemplate) yield routeVm.processTemplate();

						vm.modelHasChanged = false;

					} catch (err) {
						console && console.error && console.error('error while saving', err);
					} finally {
						routeVm.saving = false;
						routeVm.loadingMsg = '';
						resetAutoSave();
					}
				})();
			},

			cancelTitleEdit() {
				vm.editTitle = false;
				formData._name = vm.savedFormTitle;
				vm.autoSaved = vm.previousAutoSaved;
			},

			setActivationConfig(activate = false, useDate) {
				$scope.activate = activate;
				if(useDate && useDate === false) {
					vm.activationDate = null;
				}
			},

			submitForm() {
				$scope.activate === false ? go.saveFormTemplate() : go.activateForm();
			},

			activateForm() {

				if (routeVm.saving) { return; }

			//	checks to see if a form admin is selected and throws a toast error if not
				if(!formData._formAdministrator.formAdministratorType) {
					ssToastHlp.error('Form Administrator is required', 'Select Form Administrator');
					$scope.$root.$emit('ss-menu-close');
					return;
				}

			// 	Make sure step one has fields before allowing activation. Assumes the steps have been ordered correctly by the step order
				if(formData._steps[0].fieldGroups && !formData._steps[0].fieldGroups.length) {
					ssToastHlp.warn('Step 1 of this form template contains no fields. Please add fields in order to activate.', '');
					$scope.$root.$emit('ss-menu-close');
					return;
				}

				// check for step participant & step action values
				for (let i = 0; i < formData._steps.length; i++) {//leaving out last step
					let step = formData._steps[i];
					if (step._stepAction !== ssEnum.stepAction.formCompleted) {
						if (step._stepParticipant.participantType == null) {
							ssToastHlp.error('Step Participant is required for each step', 'Activation Failed');
							$scope.$root.$emit('ss-menu-close');
							return;
						}
						if (step._stepAction == null) {
							ssToastHlp.error('Action Performed is required for each step', 'Activation Failed');
							$scope.$root.$emit('ss-menu-close');
							return;
						}
					}
				}
			//	check for a valid form category value
				if (formData._category.id == null) {
					ssToastHlp.error('Category is required to activate this form.', 'Activation Failed');
					$scope.$root.$emit('ss-menu-close');
					// go to settings
					$state.go('base.formBuilder.settings');
					return;
				}

				if (vm.activationDate !== null) {
					vm.statusConfig.from = ssDateByOrgHlp.startOfDayRelativeTo(moment(vm.activationDate), routeVm.org)
				}

				$scope.$root.$emit('ss-menu-close');

				ssAsync(function* () {
					try {

						if(vm.modelHasChanged) { yield go.saveFormTemplate(); }

						routeVm.saving = true;
						routeVm.loadingMsg = 'Activating form template...';

					// update the status to active
						yield ssStatusApi.update({
							id: formData.id
						}, vm.statusConfig, false);

						yield ssSleepHlp.sleep();

					} catch(err) {
						if(err.data) {
							let msgs = err.data.errors.Title.substr(err.data.errors.Title.indexOf(':')+1);
							msgs = msgs.split(',');
							msgs.forEach((msg) => {
								ssToastHlp.error(msg, 'Activation Failed');
							});
						} else {
							ssToastHlp.error(err, '');
						}
						console && console.error && console.error(err);
					} finally {
						routeVm.saving = false;
						routeVm.loadingMsg = '';
						// go back to form templates page
						$state.go('base.formTemplates');
					}
				})();
			}
		};

		routeVm.submitForm = go.submitForm;

		vm.printViewBuilderEnabled = ssPermissionsSvc.isPermittedAction('formTemplates', 'buildOverlay') || !!$scope.$root.isPermitted.activeUser.idmAdministrator;

		ssEventSvc.on('ss-json-api.changedRecord.formFields', modelChangeEventHandler, $scope);
		ssEventSvc.on('ss-json-api.changedRecord.formTemplates', modelChangeEventHandler, $scope);
		ssEventSvc.on('ss-json-api.changedRecord.steps', modelChangeEventHandler, $scope);

		$rootScope.$on('$stateChangeStart',
			function(event, toState, toParams, fromState, fromParams, options){
				if(toState.name === 'base.formBuilder.preview'){
					ssStorage.save(cacheKey, { y: window.scrollY })
				}
		});

		$rootScope.$on('$stateChangeSuccess',
			function(event, toState, toParams, fromState, fromParams){
				if(fromState.name === 'base.formBuilder.preview'){
					let template = ssStorage.load(cacheKey);
					if(template) {
						ssStorage.clear(cacheKey);
						$timeout(() => { $window.scrollTo(0, template.y); }, 1000);
					}
				}
		});

		$scope.$watch('routeVm.formData._name', (newVal, oldVal) => {
			rAF.raw(function () {
				resizeTitle();
			});
		});

		routeVm.modelChanged = function(changed = true) {
			vm.modelHasChanged = changed;
		};

		function modelChangeEventHandler(model, attr, attrPath, oldVal, newVal) {
			const ignoredAttrs = ['fieldOrder', 'fieldGroup', 'stepOrder'];
			if (ignoredAttrs.includes(attr)) { return; }
			routeVm.modelChanged();
		}

		function resetAutoSave() {
			// disable autosave for now
			if (true) { return; }

			if (autoSaveTimer != null) { $timeout.cancel(autoSaveTimer); }
			autoSaveTimer = $timeout(function () {
				go.saveFormTemplate(true);
			}, 30000);
		}

		function resizeTitle() {
			var titleLength;
			var titleEl = document.querySelectorAll('.form-title-ctrl');
			var titleInput = angular.element(titleEl[0]).find('input');

			if (titleInput.val() != null) {
				titleLength = titleInput.val().length;
				titleInput.attr('size', titleLength);
			}
		}

		// function to manually run field validators for actions which do not trigger the 'submit' event
		function runAllValidators() {
			let errors = [];
			formData._formFields.forEach((field) => {
				const fieldValidator = fieldsHlp.getValidator(field._fieldType);
				if(typeof fieldValidator === 'function'){
					let result = fieldValidator.call(null, field);
					if(result !== true) {
						errors.push({
							field: field,
							error: result
						});
					}
				}
			});
			return errors;
		}
	}
}

// #-- END Segment (/form-builder/segment-header) --# //
// #------------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentFormBuilderHeader', () => new Header('header') );
