import { Component } from 'base-component';
// #------------------------------------------# //
// #---- Component (cmp-properties-smart) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	form: '=',
	fields: '=',
};

class PropertiesSmart extends Component {
	/*@ngInject*/
	controller($scope, ssMultiInitiativeFeatureDetectorSvc, ssValidationHlp, ssFlagSvc) {
		const routeVm = $scope.routeVm = $scope.$root.utils.findInParentScope('routeVm', $scope);

		// view model for tracking state
		const vm = $scope.vm = {
			hasSensitiveFields: $scope.fields.some(field => field._sensitive),
			canEdit: true,
			editableFieldLabels: false // can a user change fields' labels
		};

		const actionableFields = $scope.fields.filter(f => f._fieldDefId != null);

		$scope.validator = {
			label: ssValidationHlp.string({ maxLength: 30 })
		};

		const go = $scope.go = { //eslint-disable-line
			switchFieldTypes: (canEdit) => {
				let fieldType = canEdit ? 'edit' : 'view';
				//	update the field group heading type
				$scope.fields[0]._fieldType = !canEdit ? 'readOnlyHeading' : 'heading';
				//	update the fieldType of all fields
				actionableFields.forEach(field => {
					if (field._fieldTypeOptions && field._fieldTypeOptions.view && field._fieldTypeOptions.edit) {
						field._fieldType = field._fieldTypeOptions[fieldType];
					}
					//	uncheck 'required' for all non-editable fields
					if (!canEdit) {
						field._required = false;
					}
				});
			},
			showActionableFields: (field) => {
				if (ssMultiInitiativeFeatureDetectorSvc.isHRMSEnabled()) {
					// remove those not viewable for HRMS
					return field._fieldDefId != null && !field._hideForHRMS;
				} else { 
					return field._fieldDefId != null;
				}
			}
		};

		// If the '_fieldType' of every actionable field does not contain the string 'read', then the user can edit the fields
		vm.canEdit = actionableFields.every(field => {
			return field._fieldType && !field._fieldType.includes('read');
		});

		/*
		In order for the toggle to be enabled:
		ORG cannot be HRMS;
		All fields must all have both 'edit' and 'view' fieldTypeOptions values
		*/
		vm.canToggleFieldTypes = function() {
			let hasFieldTypeOptions = actionableFields.every((field) => field._fieldTypeOptions && field._fieldTypeOptions.view && field._fieldTypeOptions.edit);
			return !routeVm.isHRMSEnabled && hasFieldTypeOptions;
		}();

		// If org has feature flag on, they can change field label names
		ssFlagSvc.ready.then(() => {
			if (ssFlagSvc.checkState('fc-enable-editable-form-labels')) {
				vm.editableFieldLabels = true;
			}
		});

		// protect against all heading fields that may not have this attribute that already exist
		if ($scope.fields[0]._active == null) {
			$scope.fields[0]._active = true;
		}
	}
}

// #-- END Component (cmp-properties-smart) --# //
// #------------------------------------------# //

angular.module('ec-forms-components').directive('cmpPropertiesSmart', () => new PropertiesSmart(props, 'properties-smart'));
