import {Component} from 'base-component';
// #-------------------------------------# //
// #---- Component (cmp-dnd-options) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	options: '=',
	field: '='
};

class DndOptions extends Component {
	/*@ngInject*/
	controller($scope, $element, ssValidateHlp, fieldsHlp) {

		const fieldValidator = fieldsHlp.getValidator($scope.field._fieldType);
		let moveTarget = null;
		
		// view model for tracking state
		const vm = $scope.vm = {
			options: $scope.options.map(value => { return { value }; })
		};

		if ($scope.options == null) { $scope.options = []; }

		$scope.$watch('vm.options', (nv, ov) => {
			if (nv === ov) { return; }
			updateFieldOptions();
		}, true);

		$scope.$watch('field.config.multiple', (nv, ov) => {
			if (nv === ov) { return; }
			if (nv === false) {
				if (vm.options.length < 2) {
					vm.options.push({ value: 'Option 2' });
					updateFieldOptions();
				}
			}
		});

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line no-unused-vars
			addOption() {
				vm.options.push({ value: `Option ${vm.options.length + 1}` });
				updateFieldOptions();
			},
			dropOption(ev, index, item) {
				moveTarget = index;
				return item;
			},
			moveOption(index, option) {
				if (index > moveTarget) { index++; }
				vm.options.splice(index, 1);
				updateFieldOptions();
			},
			removeOption(index) {
				if ($scope.field.config.selectType && $scope.field.config.selectType === 'super-input-radio') {
					if (vm.options.length === 2) { return; }
				} else {
					if (vm.options.length === 1) { return; }
				}
				vm.options.splice(index, 1);
				updateFieldOptions();
			},
			validate(field) {
				if(typeof fieldValidator === 'function'){
					let result = fieldValidator.call(null, field);
					return !result ? true : result;
				}
				return true;
			}
		};

		function updateFieldOptions() {
			$scope.options = vm.options.map(o => o.value);
		}
	}
}

// #-- END Component (cmp-dnd-options) --# //
// #-------------------------------------# //

angular.module('ec-forms-components').directive('cmpDndOptions', () => new DndOptions(props, 'dnd-options') );
