import {Component} from 'base-component';

// #-------------------------------------------# //
// #---- Component (cmp-credential-group) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	field: '=',
	model: '=',
};


class CredentialGroup extends Component {
	/*@ngInject*/
	controller($scope, ssAsync, ssEventSvc, ssToastHlp, fieldsHlp) {
		// view model for tracking state
		const field = $scope.field;

		
		const vm = $scope.vm = {
		};
		
		if (field.step){
			vm.formFieldValues = field.step.formFieldValues[field.id] ? field.step.formFieldValues[field.id] : '';
		}

		const go = $scope.go = {
			hideHeadings: (field) => {
				return field._fieldType != 'heading';
			},
			
			showGroupLabel: () => {
				let labelIsVisible = field.step ? vm.formFieldValues.length : true;
				return labelIsVisible;
			}
		}

		
		ssEventSvc.on('cred-select-active', (val, data) => {
			data.forEach(field => {
				fieldsHlp.updateActiveCredentialSelection(field, val);
			});
		}, $scope);
	}
}

// #-- END Component (cmp-credential-group) --# //
// #-------------------------------------------# //

angular.module('ec-forms-components').directive('cmpCredentialGroup', () => new CredentialGroup(props, 'credential-group') );
