import {Component} from 'base-component';
// #----------------------------------------------------------# //
// #---- Component (cmp-properties-secondary-assignments) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	fields: '='
};

class PropertiesSecondaryAssignments extends Component {
	/*@ngInject*/
	controller($scope, ssValidationHlp, ssFlagSvc) {
		// view model for tracking state


		const vm = $scope.vm = {
			fields: $scope.fields,
			canEdit: true,
			editableFieldLabels: true, // can a user change fields' labels
			hrmsEnabled: false
		};

		ssFlagSvc.ready.then(() => {
			if (ssFlagSvc.checkState('hrms')) {
				vm.hrmsEnabled = true;
			}
		});


		$scope.validator = {
			label: ssValidationHlp.string({ maxLength: 25 })
		}

		// behavior that needs to be bound to the view
		const go = $scope.go = {
			hideInstructions: (field) => {
				return field._fieldType != 'instructions';
			},
			checkIfHeadingOrInstructions(field){
				switch(field._fieldType){
					case 'readOnlyHeading':
						return false;
					case 'readOnlySubheading':
						return false;
					case 'instructions':
						return false;
					default:
						return true;
				}
			},
			hideIfHrms: (field) => {
				if (vm.hrmsEnabled){
					return !field._hideForHRMS;
				}else {
					return true;
				}
			}
		};

		vm.canEdit = $scope.fields.some(field => {
			return field._fieldType && !field._fieldType.includes('read');
		});
	}
}

// #-- END Component (cmp-properties-secondary-assignments) --# //
// #----------------------------------------------------------# //

angular.module('ec-forms-components').directive('cmpPropertiesSecondaryAssignments', () => new PropertiesSecondaryAssignments(props, 'properties-secondary-assignments') );
