import {Segment} from 'base-segment';
// #--------------------------------------------------# //
// #---- Segment (/packet-builder/segment-header) ----# //

class Header extends Segment {
	/*@ngInject*/
	controller($injector, $q, $scope, $timeout, ssAsync, ssEventSvc, rAF, statusLabelHlp) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line

		// hookup segment logic
		const logic = $injector.get('segmentPacketBuilderHeaderLogic'); // jshint ignore:line

		const vm = $scope.vm = {
			formTitle: routeVm.packetData._name,
			editTitle: false,
			nothingToSave: true,
			autoSaved: false,
			packetStatusLabel: statusLabelHlp.getLabel(routeVm.packetData, routeVm.org).statusLabel
		};

		$scope.validator = logic.validator;

		let lastChangeTimeDate, autoSaveTimer;

		const go = $scope.go = {
			editTitle() {
				vm.editTitle = true;
				vm.previousAutoSaved = vm.autoSaved;
				rAF.raw(function() {
					resizeTitle();
				});
			},

			savePacket(triggeredByAuto = false) {
				resetAutoSave();
				if (routeVm.saving || vm.nothingToSave) { return $q.when(); }

				const lastChangeWhenSaveStart = lastChangeTimeDate;
				routeVm.saving = true;

				return ssAsync(function*() {
					try {
						vm.editTitle = false;
						routeVm.packetData._name = vm.formTitle;
						yield routeVm.packetData.$save(true, !triggeredByAuto);

						if (lastChangeWhenSaveStart === lastChangeTimeDate) {
							vm.nothingToSave = true;
							vm.autoSaved = triggeredByAuto;
						}
					} catch (err) {
						console && console.warn && console.warn('error while saving', err);
						vm.nothingToSave = false;
					} finally {
						routeVm.saving = false;
						resetAutoSave();
					}
				})();
			},

			confirmTitleEdit() {
				if (vm.formTitle !== '') {
					vm.editTitle = false;
					vm.currentValue = vm.formTitle;
					vm.nothingToSave = false;
				}
			},

			cancelTitleEdit() {
				vm.editTitle = false;
				vm.formTitle = vm.currentValue;
				vm.autoSaved = vm.previousAutoSaved;
			}

		};

		resetAutoSave();

		routeVm.modelChanged = () => {
			vm.nothingToSave = vm.autoSaved = false;
			lastChangeTimeDate = Date.now();
		};

		ssEventSvc.on('ss-json-api.changedRecord.packets', routeVm.modelChanged, $scope);

		routeVm.saveFormTemplate = go.savePacket;

		function resetAutoSave() {
			// disable autosave for now
			if (true) { return; }

			if (autoSaveTimer != null) { $timeout.cancel(autoSaveTimer); }
			autoSaveTimer = $timeout(() => {
				go.savePacket(true);
			}, 30000);
		}

		$scope.$watch('vm.formTitle', (newVal, oldVal) => {
			if (newVal === oldVal) {
				vm.currentValue = oldVal;
			}
			rAF.raw(function() {
				resizeTitle();
			});
		});

		function resizeTitle() {
			var titleLength;
			var titleEl = document.querySelectorAll('.form-title-ctrl');
			var titleInput = angular.element(titleEl[0]).find('input');

			if (titleInput.val() != null) {
				titleLength = titleInput.val().length > 45 ? 45 : titleInput.val().length;
				titleInput.attr('size', titleLength);
			}
		}

	}
}

// #-- END Segment (/packet-builder/segment-header) --# //
// #--------------------------------------------------# //

angular.module('ec-forms-segments').directive('segmentPacketBuilderHeader', () => new Header('header') );
