import {Component} from 'base-component';
// #--------------------------------------------------# //
// #---- Component (cmp-properties-custom-common) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	fields: '=',
	showLabel: '@?',
	showRequired: '@?'
};

class PropertiesCustomCommon extends Component {
	/*@ngInject*/
	controller($scope) {
		// view model for tracking state
		const vm = $scope.vm = { // eslint-disable-line
			field: $scope.fields[0],
			showLabel: $scope.showLabel !== 'false',
			showRequired: $scope.showRequired !== 'false'
		};
	}
}

// #-- END Component (cmp-properties-custom-common) --# //
// #--------------------------------------------------# //

angular.module('ec-forms-components').directive('cmpPropertiesCustomCommon', () => new PropertiesCustomCommon(props, 'properties-custom-common') );
