import {Component} from 'base-component';
// #-------------------------------------------------# //
// #---- Component (cmp-overlay-builder-toolbox) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
};

class OverlayBuilderToolbox extends Component {
	/*@ngInject*/
	controller($scope, ssEventSvc) {

		$scope.routeVm = $scope.$root.utils.findInParentScope('routeVm', $scope);

		// view model for tracking state
		const vm = $scope.vm = {
			tools: [{
				type: 'text',
				icon: 'pencil-square',
				title: 'Text Box'
			}, {
				type: 'date',
				icon: 'calendar',
				title: 'Date Picker'
			}, {
				type: 'checkbox',
				shape: 'square',
				icon: 'check-square',
				title: 'CheckBox'
			}]
		};

		$scope.routeVm.selectedTool = vm.tools[0];

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
			highlightSelectedBox: function(box) {
				$scope.routeVm.selectedTool = vm.tools.find((tool) => {
					return tool.type === box.type;
				});
			}
		};
		ssEventSvc.on('pvb.select-box', go.highlightSelectedBox, $scope);
	}
}

// #-- END Component (cmp-overlay-builder-toolbox) --# //
// #-------------------------------------------------# //

angular.module('ec-forms-components').directive('cmpOverlayBuilderToolbox', () => new OverlayBuilderToolbox(props, 'overlay-builder-toolbox') );
