/*@ngInject*/
function logic() {
	// logic api
	return {
		lastXDays(now, numberOfDays) {
			return {
				from: now.clone().subtract(numberOfDays, 'days'),
				to: now.clone(),
				title: `Last ${numberOfDays} Days`
			};
		},
	
		currentSchoolYear(now) {
			var start, end;
	
			if (now.clone().month() > 6) {
				let nextYr = now.clone().utc().add(1, 'years');
				start = `${now.clone().year()}-08-01T00:00:00.000Z`;
				end = `${nextYr.format('YYYY')}-07-31T23:59:59.999Z`;
			} else {
				let lastYr = now.clone().utc().subtract(1, 'years');
				start = `${lastYr.format('YYYY')}-08-01T00:00:00.000Z`;
				end = `${now.clone().year()}-07-31T23:59:59.999Z`;
			}

			return {
				from: moment(start, moment.ISO_8601).utc(),
				to: moment(end, moment.ISO_8601).utc(),
				title: `${moment(start, moment.ISO_8601).utc().format('MMM YYYY')}  ${moment(end, moment.ISO_8601).utc().format('MMM YYYY')}`
			};
		},

		getFCAddedDate(applicationList){
			var fCDetails = applicationList.filter((app) =>{
				return app._applicationId === "EC";
			});

			if (fCDetails !== undefined && fCDetails.length !== 0){
				return fCDetails[0]._createdUtc;
			}else {
				return moment().toISOString();
			};
		},

		getPreviousSchoolyear(now, numberOfYearsBack = 1) {
			let currentSchoolYear = this.currentSchoolYear(now);

			let start = moment(currentSchoolYear.from).subtract(numberOfYearsBack, 'years');
			let end = moment(currentSchoolYear.to).subtract(numberOfYearsBack, 'years');	

			return {
				from: start,
				to: end,
				title:`${start.format('MMM YYYY')}  ${end.format('MMM YYYY')}`
			};

		},

		getYearsSinceCreation(now, orgCreationDate){
			return Math.ceil(now.diff(moment(orgCreationDate), 'years', true));
		},

		yearToDate(now) {
			return {
				from: now.clone().startOf('year'),
				to: now.clone(),
				title: `Jan 1 - Today`
			};
		}

	};
}
angular.module('ec-forms-segment-logic').factory('segmentFormTrackingHeaderLogic', logic);
