import {Component} from 'base-component';
// #----------------------------------------# //
// #---- Component (cmp-field-renderer) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	field: '=',
	group: '=',
};

class templateFieldRenderer extends Component {
	/*@ngInject*/
	controller($scope, $filter, fieldsHlp, fieldRendererHlp) {
		const field = $scope.field;
		const group = $scope.group;
		const routeVm = $scope.routeVm = $scope.$root.utils.findInParentScope('routeVm', $scope);
		const fieldValidator = fieldsHlp.getValidator($scope.field._fieldType);

		fieldRendererHlp.bindConfigObject($scope, 'field._configJson', 'field.config');

		// view model for tracking state
		const vm = $scope.vm = {
			template: '',
			style: {}
		};

		const go = $scope.go = { // eslint-disable-line no-unused-vars
			validate(field) {
				if(typeof fieldValidator === 'function'){
					let result = fieldValidator.call(null, field);
					return !result ? true : result;
				}
				return true;
			}
		};

		if (field.config.width) {
			vm.style.width = field.config.width;
			$scope.$watch('field.config.width', nv => vm.style.width = nv);
		}

	//	change _fieldType here of employee profile fields for HRMS Orgs: 
	//	-	field must have "_externalData" property
	//	-	if the field has a "_fieldTypeOptions" property, then switch to the 'view' fieldType option, otherise use the existing fieldType value	
		if(routeVm.isHRMSEnabled) {

			let isExternalDataGroup = group.some((field) => {
				return field._externalData;
			});

			if(field._externalData){
				if(field._fieldTypeOptions && field._fieldTypeOptions.view) {
					field._fieldType = field._fieldTypeOptions.view;
				}
			}

			if(field._fieldType === 'heading' && isExternalDataGroup) {
				field._fieldType = 'readOnlyHeading'
			}
		}

	//	Bypass any validators for form templates
		field.ignoreValidations = true;
	//	Disable fields on form templates - this prevents the user from interacting with the fields
		field.disabled = true;

		$scope.$watch('field._fieldType', () => renderTemplate(field));
		
		$scope.$watch('field._label', () => renderTemplate(field));

		$scope.$watch(fieldRendererHlp.getVisibility(field), val => vm.visibility = val);

		function renderTemplate(field) { // eslint-disable-line
			var tag = fieldRendererHlp.getTag(field);
			var properties = fieldRendererHlp.getProperties(field, group);
			var content = fieldRendererHlp.getContent(field, {scope: $scope.$root});
			vm.template = `<${tag}${properties}>${content}</${tag}>`;
		}
	}
}

// #-- END Component (cmp-field-renderer) --# //
// #----------------------------------------# //

angular.module('ec-forms-components').directive('cmpTemplateFieldRenderer', () => new templateFieldRenderer(props,
	'template-field-renderer'));
